import {useQuery} from 'react-query';

import {ApiError, getAIRecommendationsMetaData, getProcessedListings} from '@src/api';
import {Recommendation} from '@src/components/LikelyToSell/RecommendationCard';
import {API_KEY} from '@src/constants/apiKey';
import {ProcessedListing, RecommendationMetadata, TaskMetadataIds} from '@src/types';

const collectAndCombineData = async (
  recommendations: Recommendation[]
): Promise<Recommendation[]> => {
  if (!recommendations.length) {
    return [];
  }

  // Collect the listing details
  const {listings} = await getProcessedListings(
    recommendations.flatMap((recommendation) => {
      return recommendation.listingIdSHA ? [recommendation.listingIdSHA] : [];
    })
  );
  // Collect task metadata
  const taskMetadataIds: TaskMetadataIds[] = recommendations.flatMap((recommendation) => [
    {
      taskId: recommendation.taskId,
      messierTransactionId: recommendation.listingIdSHA,
    },
  ]);
  const metadata = await getAIRecommendationsMetaData({taskMetadataIds});
  // Combine all the data
  const richRecommendations = recommendations.map((recommendation: Recommendation) => {
    const richRecommendation: Recommendation = {...recommendation};

    listings?.forEach((listing: ProcessedListing) => {
      if (
        listing.listingIdSHA === recommendation.listingIdSHA ||
        listing.listingIdSHA === recommendation.listing?.listingIdSHA
      ) {
        richRecommendation.listing = listing;
      }
    });

    metadata.recommendationsMetadata?.forEach((recommendationMetadata: RecommendationMetadata) => {
      if (recommendationMetadata.taskId === recommendation.taskId) {
        richRecommendation.metaData = recommendationMetadata;
      }
    });

    return richRecommendation;
  });

  return richRecommendations;
};

export const useAIRecMetaData = (recommendations: Recommendation[]) => {
  return useQuery<Recommendation[], ApiError>([API_KEY.GET_AI_REC_META_DATA, recommendations], () =>
    collectAndCombineData(recommendations)
  );
};
