import React from 'react';

import {Icon} from '@uc/cx.react';

import {ActionButton} from '../components/button';
import {CommentBox} from './../components/commentbox';

export interface ReviewBarProps {
  onShareListing({comment}: {comment: string}): void;
  onTrashListing({comment}: {comment: string}): void;
  shared: boolean;
  trashed: boolean;
  comment: string;
  setComment: React.Dispatch<React.SetStateAction<string>>;
}

export const ReviewOptionsContainer = (props: ReviewBarProps): JSX.Element => {
  const {shared, onShareListing, trashed, onTrashListing, comment, setComment} = props;

  return (
    <section className="uc-workspaceBoardReviewListingsModal-reviewOptions">
      <div
        className={`uc-reviewListingsModal-reviewOptionsOverlay ${
          shared ? 'is-shared-active' : ''
        }`}
      >
        <Icon.Star className="uc-reviewListingsModal-reviewOptionsOverlayIcon" />
        <h1 className="textIntent-title1">Shared</h1>
      </div>
      <div
        className={`uc-reviewListingsModal-reviewOptionsOverlay ${
          trashed ? 'is-shared-active' : ''
        }`}
      >
        <Icon.Trash className="uc-reviewListingsModal-reviewOptionsOverlayIcon" />
        <h1 className="textIntent-title1">Trashed</h1>
      </div>
      <div className="cx-formElement uc-workspaceBoardReviewListingsModal-reviewComment">
        <div className="cx-text-subheader2Medium uc-workspaceBoardReviewListingsModal-reviewCommentHeader">
          Share with Client
        </div>
        <div className="cx-text-body1Regular uc-workspaceBoardReviewListingsModal-reviewCommentHeader">
          Include a comment{' '}
          <span className="uc-workspaceBoardReviewListingsModal-reviewCommentSubHeader">
            {'(Optional)'}
          </span>
        </div>
        <CommentBox state={{comment, setComment}} />
      </div>
      <div className="cx-formElement uc-workspaceBoardReviewListingsModal-reviewBtnWrapper">
        <ActionButton
          size="lg"
          label="Share Listing"
          isPrimary={true}
          action={() => onShareListing({comment})}
          icon={<Icon.Star />}
        />
      </div>
      <div className="cx-formElement uc-workspaceBoardReviewListingsModal-reviewBtnWrapper">
        <ActionButton
          size="lg"
          label="Trash"
          isPrimary={false}
          action={() => onTrashListing({comment})}
          icon={<Icon.Trash />}
        />
      </div>
    </section>
  );
};
