import loadModal from '@uc/modal-loader';

import {Collection} from '@src/types';
import {getLoadedModalUrl} from '@src/utils';

const COLLECTION_SETTINGS_MODAL_URI = 'collection-settings.react/5.2';

interface ModalProps {
  collection: Collection;
  collectionId: string;
  isUpdated: boolean;
  wasDeleted: boolean;
  wasLeft: boolean;
}

export function loadSettingsModal({
  collection,
  isUsersDefaultCollection,
  onSuccess,
  onClose,
  onError,
}: {
  collection: Collection;
  isUsersDefaultCollection?: boolean;
  onSuccess: (res: ModalProps) => void;
  onError: (err: Error) => void;
  onClose: () => void;
}) {
  return loadModal<
    {
      collection: Collection;
      isCollectionsBuySideEnabled: boolean;
      isDefaultCollection: boolean;
      showNotificationsTimeSettingsLink: boolean;
    },
    ModalProps
  >(getLoadedModalUrl(COLLECTION_SETTINGS_MODAL_URI), {
    collection,
    isCollectionsBuySideEnabled: true,
    isDefaultCollection: isUsersDefaultCollection || false,
    showNotificationsTimeSettingsLink: true,
  })
    .then(onSuccess)
    .catch((e) => {
      if (e && e !== 'user clicked "Close"') {
        onError(e);
      } else {
        onClose();
      }
    });
}
