import {differenceInDays, differenceInHours, differenceInMinutes} from 'date-fns';
import React from 'react';

import {Button, Icon} from '@uc/cx.react';

import {CollectionUserInvitation, CollectionUserInvitationStatus, CollectionUser} from '@src/types';

interface CertainInvitation extends CollectionUserInvitation {
  status: CollectionUserInvitationStatus;
  statusUpdatedAt: number;
}

interface CertainInvitationWithStatusText extends CertainInvitation {
  statusText: string;
}

interface PendingCollaborator extends CollectionUser {
  invitation: CertainInvitationWithStatusText;
}

export const buildInvitationStatusText = (invitation: CollectionUserInvitation): string => {
  if (invitation.status === undefined || invitation.statusUpdatedAt === undefined) {
    return '';
  }

  const invitationType =
    invitation.status === CollectionUserInvitationStatus.INVITATION_SENT
      ? 'invitation'
      : 'invitation reminder';
  const currentDateTime = new Date();
  const statusUpdatedAt = new Date(invitation.statusUpdatedAt);
  const diffInMinutes = differenceInMinutes(currentDateTime, statusUpdatedAt);
  const diffInHours = differenceInHours(currentDateTime, statusUpdatedAt);
  const diffInDays = differenceInDays(currentDateTime, statusUpdatedAt);
  let statusText = '';

  if (diffInMinutes < 60) {
    statusText = `in ${diffInMinutes > 0 ? diffInMinutes : '1'} minute${
      diffInMinutes > 1 ? 's' : ''
    }`;
  } else if (diffInHours >= 1 && diffInHours < 24) {
    statusText = `in ${diffInHours} hour${diffInHours > 1 ? 's' : ''}`;
  } else if (diffInDays >= 1) {
    statusText = `in ${diffInDays} day${diffInDays > 1 ? 's' : ''}`;
  }

  return `has not accepted their ${invitationType} ${statusText}`;
};

const getShouldShowReminderButton = (collaborator: PendingCollaborator): boolean => {
  const currentDateTime = new Date();
  const statusUpdatedAt = new Date(collaborator.invitation.statusUpdatedAt);
  const diffInHours = differenceInHours(currentDateTime, statusUpdatedAt);

  return diffInHours >= 24 && Boolean(collaborator.invitation.inviteeEmail);
};

export const PendingCollaborators = ({
  users = [],
  onSendReminder,
}: {
  users: CollectionUser[];
  onSendReminder: (collaborator: CollectionUser) => void;
}) => {
  const [pendingCollaborators, setPendingCollaborators] = React.useState<PendingCollaborator[]>([]);

  React.useEffect(() => {
    const pendingCollaborators =
      users
        ?.filter((pendingCollaborator): pendingCollaborator is {invitation: CertainInvitation} => {
          const {invitation} = pendingCollaborator;

          if (!invitation || invitation.statusUpdatedAt === undefined) {
            return false;
          }

          if (pendingCollaborator.userId === window.uc?.user?._id) {
            return false;
          }

          return (
            invitation.status === CollectionUserInvitationStatus.INVITATION_SENT ||
            invitation.status === CollectionUserInvitationStatus.REMINDER_SENT
          );
        })
        .map((pendingCollaborator) => {
          const collaborator: PendingCollaborator = {
            ...pendingCollaborator,
            invitation: {...pendingCollaborator.invitation, statusText: ''},
          };

          collaborator.invitation.statusText = buildInvitationStatusText(collaborator.invitation);

          return collaborator;
        }) || [];

    setPendingCollaborators(pendingCollaborators.slice(0, 4));
  }, [users]);

  const dismissPendingCollaboratorNotification = (invitee: CollectionUser): void => {
    const remainingPendingCollaborators = pendingCollaborators.filter(
      (pendingCollaborator) => pendingCollaborator.userId !== invitee.userId
    );

    setPendingCollaborators(remainingPendingCollaborators);
  };

  return (
    <div className="collection-header-pending-invitees">
      {pendingCollaborators.map((collaborator) => (
        <div className="collection-header-pending-invitee" key={collaborator.userId}>
          <Icon.Person
            className="collection-header-pending-invitee-icon"
            data-testid="pending-invitee-icon"
          />
          <div className="collection-header-pending-invitee-text">
            <span className="collection-header-pending-invitee-name">{`${collaborator.person?.firstName} ${collaborator.person?.lastName}`}</span>
            <span className="collection-header-pending-invitee-status">
              &nbsp;{collaborator.invitation.statusText}
            </span>
          </div>
          {getShouldShowReminderButton(collaborator) && (
            <Button
              className="collection-header-pending-invitee-reminder-button"
              onClick={() => onSendReminder(collaborator)}
              variant="enclosed"
              size="sm"
            >
              Send Reminder
            </Button>
          )}
          <Icon.X
            className="collection-header-pending-invitee-dismiss-button"
            onClick={() => dismissPendingCollaboratorNotification(collaborator)}
            data-testid="invitee-notification-dismiss-button"
            tabIndex={0}
            role="button"
          />
        </div>
      ))}
    </div>
  );
};
