import {Icon} from '@uc/cx.react';

import {CollectionListing, CollectionListingReviewStage} from '@src/types';
import {getNotInterestedIcon} from '@src/utils/getNotInterestedIcon';

// The `updatedBy` and `updatedAt` field was not working properly before,
// the fix went to production on 01/10/2020,
// so we are only showing updatedBy information for listings that are updated after 01/10/2020
export const accurateUpdatedAtDateDivider = 1578661200000;

export const formatListingUpdatedBox = (collectionListing: CollectionListing) => {
  if (!collectionListing) {
    return null;
  }

  const {reviewStage = {}} = collectionListing;
  const {currentStage, updatedBy, updatedAt} = reviewStage;
  const isFavorited = currentStage === CollectionListingReviewStage.FAVORITE;
  const isNotInterested = currentStage === CollectionListingReviewStage.NOT_INTERESTED;
  const shouldShowLabel = Boolean(
    currentStage !== undefined &&
      updatedBy &&
      (isFavorited || isNotInterested) &&
      updatedAt &&
      updatedAt > accurateUpdatedAtDateDivider
  );

  if (!shouldShowLabel) {
    return null;
  }

  let dateAbbreviation;

  if (updatedAt) {
    const dateTime = new Date(updatedAt);

    dateAbbreviation =
      '' +
      (dateTime.getMonth() > 8 ? dateTime.getMonth() + 1 : '0' + (dateTime.getMonth() + 1)) +
      '/' +
      (dateTime.getDate() > 9 ? dateTime.getDate() : '0' + dateTime.getDate()) +
      '/' +
      dateTime.getFullYear();
  }

  let label;

  // Truncate to first name otherwise label will be too long
  if (updatedBy) {
    const firstName = updatedBy.displayName?.split(' ')[0];

    label = ` by ${firstName || ''} ${dateAbbreviation || ''}`;
  }

  let UpdatedIcon;

  if (isFavorited) {
    UpdatedIcon = Icon.Star;
  } else if (isNotInterested) {
    UpdatedIcon = Icon[getNotInterestedIcon()];
  }

  return {label, UpdatedIcon, isFavorited, isNotInterested};
};
