import React from 'react';

import {SearchBar} from '@src/components/SearchBar';
import {COLLECTION_MODE} from '@src/constants';
import {useSelectedIds, useIsAgent, getCurrentView, useSelector} from '@src/store';

import styles from './ActionBar.module.pcss';
import {CustomizeTable} from './CustomizeTable/CustomizeTable';
import {OptionsGroup} from './OptionsGroup/OptionsGroup';
import {OptionsGroupConsumer} from './OptionsGroupConsumer/OptionsGroupConsumer';
import {SorterAndFilter} from './SorterAndFilter/SorterAndFilter';
import {ActionBarPropsExt} from './types';
import {ViewControlGroup} from './ViewControlGroup/ViewControlGroup';

export const Counts = ({totalItems, selected}: {totalItems: number; selected: string[]}) => {
  return (
    <div className={styles.countGroup}>
      <div className={styles.countGroupInner}>
        <span
          className={styles.countGroupResults}
          data-testid="resultsCount"
        >{`${totalItems} Results`}</span>
        {selected.length > 0 && (
          <span
            className={styles.countGroupSelected}
            data-testid="selectedsCount"
          >{`${selected.length} Selected`}</span>
        )}
      </div>
    </div>
  );
};

export const ActionBar = ({
  mode,
  totalItems = 0,
  size,
  collectionId,
  listingsOnPage,
  filterDropdownCounts,
}: ActionBarPropsExt) => {
  const isAgent = useIsAgent();
  const selected = useSelectedIds();
  const view = useSelector(getCurrentView);
  const props = {mode, collectionId, listingsOnPage, size};
  const isConsumerCartView = !isAgent && view === 'card';

  return (
    <>
      <div className={styles.actionBar}>
        {!isAgent && <SearchBar />}
        {isAgent ? <OptionsGroup {...props} /> : <OptionsGroupConsumer {...props} />}
        <Counts selected={isConsumerCartView ? [] : selected} totalItems={totalItems} />
        {view === 'list' && <CustomizeTable />}
        {mode !== COLLECTION_MODE.TRASH && (
          <SorterAndFilter
            collectionId={collectionId}
            mode={mode}
            filterDropdownCounts={filterDropdownCounts}
          />
        )}
        <ViewControlGroup mode={mode} collectionId={collectionId} />
      </div>
    </>
  );
};
