import React from 'react';

import {
  FavoriteAndNotInterested,
  FavoriteAndNotInterestedProps,
} from '../FavoriteAndNotInterested/FavoriteAndNotInterested';
import './Footer.pcss';
import {TourInformationBanner} from '../TourInformationBanner';
import {WantToSee, WantToSeeProps} from '../WantToSee/WantToSee';
import {ToggleChatButton} from './ToggleChatButton';

export interface ClientFooterPros extends FavoriteAndNotInterestedProps, WantToSeeProps {
  toggleCommentsView: () => void;
  shouldShowWantToSee: boolean;
  hasComments: boolean;
  hasUnreadComments: boolean;
}

export const ClientFooter = (props: ClientFooterPros) => {
  const {shouldShowWantToSee, hasComments, hasUnreadComments} = props;

  return (
    <>
      <TourInformationBanner collectionListing={props.collectionListing} />
      <div className="ClientFooter" data-testid="clientFooter">
        {shouldShowWantToSee ? <WantToSee isMobile={true} {...props} /> : <div></div>}
        <div className="ClientFooter-RightActionGroup">
          <ToggleChatButton
            hasComments={hasComments}
            hasUnreadComments={hasUnreadComments}
            toggleCommentsView={props.toggleCommentsView}
          />
          <FavoriteAndNotInterested isMobile={true} {...props} />
        </div>
      </div>
    </>
  );
};

ClientFooter.defaultProps = {
  shouldShowWantToSee: false,
  collectionId: '',
  collectionListing: {},
  onFavoriteListing: () => undefined,
  onMarkAsNotInterested: () => undefined,
  toggleCommentsView: () => undefined,
  hasComments: false,
  hasUnreadComments: false,
};
