import {useEffect, useRef} from 'react';

import {loadPanel, PanelSize} from '@uc/panel-loader';

import {MODAL_URL_IDS, LISTING_COMMENTS_VIEWS_TAB, SIDE_PANEL_IDS} from '@src/constants';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {getSidePanel, setSidePanel, useSelector, useDispatch} from '@src/store';

const COLLECTION_ACTIVITY_PANEL_URI = 'collection-activity.react/1.1';

interface ActivityFeedPanel {
  EventsIn: {UPDATE: string};
  EventsOut: {DESTROY: string};
  addListener: (eventName: string, cb: () => void) => void;
  sendEvent: (eventName: string, data: unknown) => void;
}

export const useActivityFeed = (parent: HTMLElement | null) => {
  const internalRef = useRef(null) as {current: ActivityFeedPanel | null};
  const dispatch = useDispatch();
  const sidePanel = useSelector(getSidePanel);
  const setParams = useUpdateCurrentUrl();
  const {collectionId} = useCurrentUrlData();
  const {data: collectionsResponse} = useGetCollection({collectionId});

  // toggle show panel
  useEffect(() => {
    if (!parent || sidePanel !== SIDE_PANEL_IDS.activity) return;

    const panelParams = {
      collectionId: collectionId,
      goToListingComments: (listingId: string) =>
        setParams({
          modalId: MODAL_URL_IDS.comment,
          listingId,
          selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
        }),
      goToDiscussion: () => setParams({modalId: MODAL_URL_IDS.discussion}),
      scrollElementId: '',
    };

    loadPanel(COLLECTION_ACTIVITY_PANEL_URI, {
      parentEl: parent,
      size: PanelSize.MD,
      data: panelParams,
    })
      .then((panel: ActivityFeedPanel) => {
        internalRef.current = panel;
        const {DESTROY} = panel.EventsOut;

        panel.addListener(DESTROY, () => {
          internalRef.current = null;
          dispatch(setSidePanel(undefined));
        });
      })
      .catch(() => {
        dispatch(setSidePanel(undefined));
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [parent, sidePanel, collectionsResponse, collectionId, dispatch]);
};
