import {
  useCreateMcDocumentAction,
  UseCreateMcDocumentActionOptions,
} from '@uc/action--create-mc-document.react';

import {setToast, useDispatch} from '@src/store';
import {CollectionListing} from '@src/types';

import {useProcessedListings} from './useProcessedListings';

interface Options extends Omit<UseCreateMcDocumentActionOptions, 'listings'> {
  listings?: CollectionListing[];
}

export function usePrintListings(options: Options) {
  const dispatch = useDispatch();
  const processedListings = useProcessedListings(options.listings);
  const {onClick} = useCreateMcDocumentAction({
    listings: processedListings,
    targetTemplate: options.targetTemplate,
    onFlowSuccess: ({documentId = ''}) => {
      window.open(`/marketing-center/editor/v2/#/${documentId}`, '_blank');
    },
    onFlowError: () => {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'error',
          message: 'Document creation failed. Please try again.',
        })
      );
    },
  });

  return onClick;
}
