import {useMutation, useQueryClient} from 'react-query';

import {updateRequestShowing} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {
  useCollectionListingPaginatedQueryKey,
  UseGetCollectionListingsPaginatedResponse,
} from '@src/hooks/useGetCollectionListingsPaginated';
import {useDispatch, setToast} from '@src/store';
import {CollectionListing, ShowingStatus} from '@src/types';

import {useIsInClientCommentModal} from './useIsInClientCommentModal';

export const getRequestShowingMessage = (status: ShowingStatus) => {
  switch (status) {
    case ShowingStatus.REQUEST_CANCELLED:
      return 'Tour Cancelled';
    case ShowingStatus.SEEN:
      return 'Marked as Visited';
    default:
      return 'Tour Requested';
  }
};

export const useRequestShowing = () => {
  const client = useQueryClient();
  const dispatch = useDispatch();
  const isInClientCommentModal = useIsInClientCommentModal();
  const collectionListingsQueryKey = useCollectionListingPaginatedQueryKey();

  return useMutation(
    async ({
      collectionListing,
      collectionId,
      showingStatus,
      tourDate,
    }: {
      collectionListing: CollectionListing;
      collectionId: string;
      showingStatus?: ShowingStatus;
      tourDate?: number;
    }) => {
      const status = showingStatus
        ? showingStatus
        : collectionListing.showing && collectionListing.showing.status === ShowingStatus.REQUESTED
        ? ShowingStatus.REQUEST_CANCELLED
        : ShowingStatus.REQUESTED;
      const showings = [
        {
          listingId: collectionListing.listingId || '',
          status,
          tourDate,
        },
      ];
      const result = await updateRequestShowing({collectionId: collectionId, showings});
      const message = getRequestShowingMessage(status);

      dispatch(
        setToast({
          isOpen: true,
          variant: 'success',
          message,
        })
      );

      if (!isInClientCommentModal) {
        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_PAGINATED);
        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_FACETS);
        client.resetQueries(API_KEY.GET_BOARD_LISTING);
      }

      return result;
    },
    {
      // Update the paginatedListings cache to include the new or updated listing request
      onSuccess: (result, {collectionListing}) => {
        const {listingId} = collectionListing;

        if (isInClientCommentModal) {
          const previousListings = client.getQueryData(
            collectionListingsQueryKey
          ) as UseGetCollectionListingsPaginatedResponse;

          previousListings?.currentPageListings?.find((listing) => {
            if (listing.listingId === listingId) {
              listing.showing = result.showings?.[0];
            }
          });

          client.setQueryData(collectionListingsQueryKey, previousListings);
        }
      },
    }
  );
};
