import {useMutation, useQueryClient} from 'react-query';

import {readComments} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetCollectionResponse} from '@src/types';

export function useReadComment() {
  const queryClient = useQueryClient();

  return useMutation(
    async ({collectionId}: {collectionId: string}) => {
      const result = await readComments({
        collectionId,
        request: {},
      });

      return result;
    },
    {
      onMutate: async () => {
        const previousCollectionResponse = queryClient.getQueryData<GetCollectionResponse>(
          API_KEY.GET_COLLECTION
        );

        await queryClient.cancelQueries(API_KEY.GET_COLLECTION);

        if (previousCollectionResponse) {
          queryClient.setQueryData<GetCollectionResponse>(API_KEY.GET_COLLECTION, {
            ...previousCollectionResponse,
            collection: {
              ...previousCollectionResponse.collection,
              numUnreadDiscussions: 0,
            },
          });
        }
      },
      onSettled: () => {
        queryClient.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_PAGINATED);
        queryClient.invalidateQueries(API_KEY.BOARDS_BADGES_SUMMARY);
      },
    }
  );
}
