import {savedSearchUpdateInitiated} from '@uc/analytics-definitions';

import {CollectionLinkedSearch} from '@src/types';
import {setRedirecting} from '@src/utils';

export function editSavedSearchUrl({
  isAgent,
  savedSearchId,
  customerId,
  isFromNewConsumerSearch,
}: {
  isAgent: boolean;
  savedSearchId: string;
  customerId?: string;
  isFromNewConsumerSearch?: boolean;
}) {
  if (!isAgent && isFromNewConsumerSearch) {
    return `/saved-consumer-search/${savedSearchId}`;
  }

  return customerId
    ? `/search/edit/${savedSearchId}/${customerId}`
    : `/search/edit/${savedSearchId}`;
}

export function editSearchCriteria({
  isAgent,
  savedSearch,
  collectionId,
  currentUserId,
}: {
  isAgent: boolean;
  savedSearch: CollectionLinkedSearch;
  collectionId: string;
  currentUserId: string;
}) {
  const isFromNewConsumerSearch = savedSearch.useLocationIds;

  if (savedSearch.id) {
    if (collectionId) {
      savedSearchUpdateInitiated({
        product: 'collections',
        product_path:
          'Edit Criteria button in saved searches drop down list that linked to the collection',
        collection_id: collectionId,
        saved_search_id: savedSearch.id,
        is_owner: currentUserId === savedSearch.ownerId,
      });
    }

    const editUrl = editSavedSearchUrl({
      isAgent,
      savedSearchId: savedSearch.id,
      customerId: savedSearch.customerUserId,
      isFromNewConsumerSearch,
    });

    setRedirecting(editUrl);
  }
}
