import {useQuery} from 'react-query';

import {transformListingsToCards} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {
  BadgeType,
  ListingCardUserType,
  ListingCardViewOptions,
  ListingCardViewType,
  ProcessedListing,
} from '@src/types';
import {
  isNewAgentListingCardsOnCollectionsEnabled,
  isNewClientListingCardsOnCollectionsEnabled,
} from '@src/utils/optyUtils';

export const useTransformListingsToCards = (listings?: ProcessedListing[], isAgent?: boolean) => {
  return useQuery([API_KEY.TRANSFORM_LISTINGS_TO_CARDS, listings], () => {
    if (!listings || !listings.length) {
      return;
    }

    if (
      (isAgent && !isNewAgentListingCardsOnCollectionsEnabled()) ||
      (!isAgent && !isNewClientListingCardsOnCollectionsEnabled())
    ) {
      return;
    }

    const cardViewOptions: ListingCardViewOptions = {
      excludedBadgeTypes: [BadgeType.NEW_LISTING],
    };

    return transformListingsToCards({
      listings,
      cardViewType: ListingCardViewType.CARD,
      cardUserType: isAgent ? ListingCardUserType.AGENT : ListingCardUserType.CONSUMER,
      cardViewOptions,
    });
  });
};
