import React from 'react';

import './ModalSecondaryHeader.pcss';
import {
  FavoriteAndNotInterested,
  FavoriteAndNotInterestedProps,
} from '../FavoriteAndNotInterested/FavoriteAndNotInterested';
import {TourInformationBanner} from '../TourInformationBanner';
import {WantToSee, WantToSeeProps} from '../WantToSee/WantToSee';

export interface ModalSecondaryHeaderProps extends FavoriteAndNotInterestedProps, WantToSeeProps {
  shouldShowWantToSee: boolean;
}

export const ModalSecondaryHeader = (props: ModalSecondaryHeaderProps) => {
  return (
    <>
      <header className="modalSecondaryHeader" data-testid="modalSecondaryHeader">
        <div className="shareButtonPlaceHolder">
          {/* Share Button: to be built in another ticket: https://compass-tech.atlassian.net/browse/CDF-185 */}
        </div>
        <div>
          <FavoriteAndNotInterested isMobile={false} {...props} />
        </div>
        <div>{props.shouldShowWantToSee && <WantToSee isMobile={false} {...props} />}</div>
      </header>
      <TourInformationBanner collectionListing={props.collectionListing} />
    </>
  );
};

ModalSecondaryHeader.defaultProps = {
  shouldShowWantToSee: false,
  collectionId: '',
  collectionListing: {},
  onFavoriteListing: () => undefined,
  onMarkAsNotInterested: () => undefined,
};
