import anchorme from 'anchorme';

export const charMap = {
  '"': '&quot',
  '&': '&amp',
  "'": '&#39',
  '<': '&lt',
  '>': '&gt',
} as {
  [key: string]: string;
};

export function formatComment(commentContent: string | undefined = '') {
  return anchorme(
    commentContent.replace(/[&<>"']/g, (char) => charMap[char]),
    {
      attributes: [
        {
          name: 'class',
          value: 'modal-comment-link',
        },
        {
          name: 'target',
          value: '_blank',
        },
      ],
    }
  );
}
