import classnames from 'classnames';
import React from 'react';

import {Button} from '@uc/cx.react';
import {formatTourStatus} from '@uc/format-tour-status';
import {
  UcLcCheckbox,
  UcLcCommentFooterAction,
  UcLcFooterAction,
  UcListingCard,
  UcLoadingListingCard,
} from '@uc/listing-card-components.react';
import ListingCard from '@uc/listing-card.react';

import {
  COLLECTION_MODE,
  LISTING_COMMENTS_VIEWS_TAB,
  MODAL_URL_IDS,
  AgentCollectionMode,
} from '@src/constants';
import {Card} from '@src/cx/Card';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useShareListings} from '@src/hooks/useShareListings';
import {useToggleListing} from '@src/hooks/useToggleListing';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {useTrackCommentModalInitiated} from '@src/hooks/useTrackCommentModalInitiated';
import {useTrashListings} from '@src/hooks/useTrashListings';
import {
  CollectionUrlData,
  useDispatch,
  useIsSelected,
  useSelectedIds,
  setHoveredListing,
  setReferringListingPath,
  useNavListingIds,
} from '@src/store';
import {CollectionListing, UnreadListingUpdates, ShowingStatus} from '@src/types';
import {isNewAgentListingCardsOnCollectionsEnabled} from '@src/utils/optyUtils';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import styles from './AgentCard.module.pcss';
import './AgentCard.pcss';
import {formatListingUpdatedBox} from './formatListingUpdatedBox';

interface FooterProps {
  collectionId: string;
  listingIds?: string[];
  collectionListing: CollectionListing;
  onShareBtnClick(): void;
  disabled: boolean;
}
interface ModeConfig {
  onListingClick({
    setParams,
    listingId,
    listingIds,
  }: {
    setParams: (next: Partial<CollectionUrlData>) => void;
    listingId: string;
    listingIds?: string[];
  }): void;
  Header?({collectionListing}: {collectionListing: CollectionListing}): React.ReactElement;
  Footer({collectionListing, onShareBtnClick}: FooterProps): React.ReactElement;
  ActionFooter?({collectionListing, onShareBtnClick}: FooterProps): React.ReactElement;
}

export const CommentIcon = ({hasUnreadComments}: {hasUnreadComments: boolean}) => {
  return (
    <span
      className={classnames(`cx-icon cx-btn-icon ${styles.commentIcon}`, {
        'cx-indicator cx-indicator--new cx-btn-icon--left': hasUnreadComments,
      })}
      data-testid="commentIcon"
    >
      <svg width={16} height={16} viewBox="0 0 16 16">
        <use xlinkHref="#cx-icon-talkBubble_16x16"></use>
      </svg>
    </span>
  );
};

function onTrashClickCallback({
  listingId,
  collectionId,
  mutate,
  isLoading,
  trackCollectionListingFlowAction,
}: {
  listingId: string | undefined;
  collectionId: string;
  mutate: (props: {collectionId: string; listingIds: string[]}) => void;
  isLoading: boolean;
  trackCollectionListingFlowAction: (props: {
    actionName: string;
    productPath: PRODUCT_PATH;
  }) => void;
}) {
  return () => {
    if (listingId && !isLoading) {
      mutate({collectionId, listingIds: [listingId]});
    }

    trackCollectionListingFlowAction({
      actionName: 'Trash',
      productPath: PRODUCT_PATH.LISTING_CARD,
    });
  };
}

function onShareClickCallback(
  onShareBtnClick: () => void,
  trackCollectionListingFlowAction: (props: {actionName: string; productPath: PRODUCT_PATH}) => void
) {
  return () => {
    onShareBtnClick();
    trackCollectionListingFlowAction({
      actionName: 'Share',
      productPath: PRODUCT_PATH.LISTING_CARD,
    });
  };
}

function onCommentClickCallback({
  listingId,
  dispatch,
  setParams,
}: {
  listingId: string;
  dispatch: (action: {
    type: 'SET_REFERRING_LISTING_PATH';
    payload?: 'card' | 'table' | 'map';
  }) => void;
  setParams: (next: Partial<CollectionUrlData>) => void;
}) {
  return () => {
    dispatch(setReferringListingPath('card'));
    setParams({
      modalId: MODAL_URL_IDS.comment,
      listingIds: [listingId],
      enableModalPagination: true,
      selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
    });
  };
}

export const MODES: Record<AgentCollectionMode, ModeConfig> = {
  [COLLECTION_MODE.UNSHARED]: {
    onListingClick({setParams, listingId, listingIds}) {
      setParams({
        modalId: MODAL_URL_IDS.listings,
        listingIds: listingIds || [listingId],
        currentListingIndex: listingIds?.indexOf(listingId),
      });
    },
    Footer({collectionId, onShareBtnClick, collectionListing, disabled}: FooterProps) {
      const {listingId} = collectionListing;
      const {mutate, isLoading} = useTrashListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const onTrashClick = onTrashClickCallback({
        listingId,
        collectionId,
        mutate,
        isLoading,
        trackCollectionListingFlowAction,
      });
      const onShareClick = onShareClickCallback(onShareBtnClick, trackCollectionListingFlowAction);

      return (
        <div className={styles.cardFooter} data-testid="card-footer">
          <Button
            icon="Trash"
            onClick={onTrashClick}
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            aria-label="Trash"
          >
            <span>Trash</span>
          </Button>
          <Button
            icon="Star"
            onClick={onShareClick}
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            aria-label="Share"
          >
            <span>Share</span>
          </Button>
        </div>
      );
    },
    ActionFooter({collectionId, onShareBtnClick, collectionListing, disabled}: FooterProps) {
      const {listingId} = collectionListing;
      const {mutate, isLoading} = useTrashListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const onTrashClick = onTrashClickCallback({
        listingId,
        collectionId,
        mutate,
        isLoading,
        trackCollectionListingFlowAction,
      });
      const onShareClick = onShareClickCallback(onShareBtnClick, trackCollectionListingFlowAction);

      return (
        <div slot="footer-actions" data-testid="card-footer">
          <UcLcFooterAction
            iconName="cx-icon-trash_16x16"
            text="Trash"
            actionType="trash-action"
            onFooterActionClick={onTrashClick}
            disabled={disabled}
          />
          <UcLcFooterAction
            iconName="cx-icon-star_16x16"
            text="Share"
            actionType="share-action"
            onFooterActionClick={onShareClick}
            disabled={disabled}
          />
        </div>
      );
    },
  },
  [COLLECTION_MODE.SHARED]: {
    onListingClick({setParams, listingId}) {
      setParams({
        modalId: MODAL_URL_IDS.comment,
        listingIds: [listingId],
        enableModalPagination: true,
        selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL,
      });
    },

    Footer({collectionId, collectionListing, disabled}: FooterProps) {
      const dispatch = useDispatch();
      const setParams = useUpdateCurrentUrl();
      const {listingId = ''} = collectionListing;
      const {mutate, isLoading} = useTrashListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const {trackCommentModalInitiated} = useTrackCommentModalInitiated('grid view');
      const {comments, updatesUnreadByCurrentUser} = collectionListing;
      const hasUnreadComments = updatesUnreadByCurrentUser?.includes(
        UnreadListingUpdates.RECENT_COMMENTS
      );
      const numCommentsText = (comments?.length ?? 0) > 0 ? ` (${comments?.length})` : '';
      const onTrashClick = onTrashClickCallback({
        listingId,
        collectionId,
        mutate,
        isLoading,
        trackCollectionListingFlowAction,
      });
      const onCommentClick = onCommentClickCallback({listingId, dispatch, setParams});

      return (
        <div className={styles.cardFooter} data-testid="card-footer">
          <Button
            icon="Trash"
            onClick={onTrashClick}
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            aria-label="Trash"
          >
            <span>Trash</span>
          </Button>
          <Button
            data-testid="card-footer-comments"
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            onClick={() => {
              onCommentClick();
              trackCommentModalInitiated();
            }}
            aria-label="Comments"
          >
            <CommentIcon hasUnreadComments={Boolean(hasUnreadComments)} />
            <span>{`Comment${numCommentsText}`}</span>
          </Button>
        </div>
      );
    },

    ActionFooter({collectionId, collectionListing, disabled}: FooterProps) {
      const dispatch = useDispatch();
      const setParams = useUpdateCurrentUrl();
      const {listingId = ''} = collectionListing;
      const {mutate, isLoading} = useTrashListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const {trackCommentModalInitiated} = useTrackCommentModalInitiated('grid view');
      const {comments, updatesUnreadByCurrentUser} = collectionListing;
      const hasUnreadComments = updatesUnreadByCurrentUser?.includes(
        UnreadListingUpdates.RECENT_COMMENTS
      );
      const onTrashClick = onTrashClickCallback({
        listingId,
        collectionId,
        mutate,
        isLoading,
        trackCollectionListingFlowAction,
      });
      const onCommentClick = onCommentClickCallback({listingId, dispatch, setParams});

      return (
        <div slot="footer-actions" data-testid="card-footer">
          <UcLcFooterAction
            iconName="cx-icon-trash_16x16"
            text="Trash"
            actionType="trash-action"
            onFooterActionClick={onTrashClick}
            disabled={disabled}
          />
          <UcLcCommentFooterAction
            data-testid="card-footer-comments"
            numOfComments={comments?.length}
            hasUnreadComments={Boolean(hasUnreadComments)}
            onCommentClick={() => {
              onCommentClick();
              trackCommentModalInitiated();
            }}
            disabled={disabled}
          />
        </div>
      );
    },

    Header({collectionListing}: {collectionListing: CollectionListing}) {
      const updatedBy = formatListingUpdatedBox(collectionListing);

      return (
        <div data-testid="card-header" className={styles.cardHeader}>
          <span>{collectionListing.totalClientViews || 0} Views</span>
          {updatedBy?.UpdatedIcon ? (
            <span
              className={classnames({
                [styles.updatedArea]: true,
                [styles.svgArchived]: updatedBy.isNotInterested,
                [styles.svgInterested]: updatedBy.isFavorited,
              })}
            >
              <updatedBy.UpdatedIcon />
              <span>{updatedBy.label}</span>
            </span>
          ) : null}
        </div>
      );
    },
  },
  [COLLECTION_MODE.TRASH]: {
    onListingClick({setParams, listingId}) {
      setParams({
        modalId: MODAL_URL_IDS.comment,
        listingIds: [listingId],
        enableModalPagination: true,
        selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL,
      });
    },
    Footer({collectionId, collectionListing, onShareBtnClick, disabled}: FooterProps) {
      const dispatch = useDispatch();
      const setParams = useUpdateCurrentUrl();
      const {comments, updatesUnreadByCurrentUser, listingId = ''} = collectionListing;
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const hasUnreadComments = updatesUnreadByCurrentUser?.includes(
        UnreadListingUpdates.RECENT_COMMENTS
      );
      const numCommentsText = (comments?.length ?? 0) > 0 ? ` (${comments?.length})` : '';
      const onShareClick = onShareClickCallback(onShareBtnClick, trackCollectionListingFlowAction);
      const onCommentClick = onCommentClickCallback({listingId, dispatch, setParams});

      return (
        <div className={styles.cardFooter} data-testid="card-footer">
          <Button
            icon="Star"
            onClick={onShareClick}
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            aria-label="Share"
          >
            <span>Share</span>
          </Button>
          <Button
            data-testid="card-footer-comments"
            className={styles.cardFooterButton}
            variant="enclosed"
            disabled={disabled}
            onClick={onCommentClick}
            aria-label="Comments"
          >
            <CommentIcon hasUnreadComments={Boolean(hasUnreadComments)} />
            <span>{`Comment${numCommentsText}`}</span>
          </Button>
        </div>
      );
    },
    ActionFooter({collectionId, collectionListing, onShareBtnClick, disabled}: FooterProps) {
      const dispatch = useDispatch();
      const setParams = useUpdateCurrentUrl();
      const {comments, updatesUnreadByCurrentUser, listingId = ''} = collectionListing;
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const hasUnreadComments = updatesUnreadByCurrentUser?.includes(
        UnreadListingUpdates.RECENT_COMMENTS
      );
      const onShareClick = onShareClickCallback(onShareBtnClick, trackCollectionListingFlowAction);
      const onCommentClick = onCommentClickCallback({listingId, dispatch, setParams});

      return (
        <div slot="footer-actions" data-testid="card-footer">
          <UcLcFooterAction
            iconName="cx-icon-star_16x16"
            text="Share"
            actionType="share-action"
            onFooterActionClick={onShareClick}
            disabled={disabled}
          />
          <UcLcCommentFooterAction
            data-testid="card-footer-comments"
            numOfComments={comments?.length}
            hasUnreadComments={hasUnreadComments}
            onCommentClick={onCommentClick}
            disabled={disabled}
          />
        </div>
      );
    },
  },
  [COLLECTION_MODE.LIKELY_TO_SELL]: {
    onListingClick() {
      return;
    },
    Footer() {
      return <div />;
    },
  },
};

const BANNER_CLASSES: {[key: number]: string} = {
  [ShowingStatus.REQUESTED]: styles.requested,
  [ShowingStatus.SCHEDULED]: styles.scheduled,
  [ShowingStatus.PENDING_CONFIRMATION]: styles.seen,
  [ShowingStatus.SEEN]: styles.seen,
};
const getListingTourBanner = (showing: {
  status: ShowingStatus | undefined;
  tourDate: number | undefined;
}): [string?, string?] => {
  if (!showing || typeof showing.status === 'undefined') {
    return [];
  }

  const {tourStatus, tourStatusText = ''} = formatTourStatus(
    showing.status,
    showing.tourDate,
    true
  );
  const tourStatusClass = [styles.cardBanner, BANNER_CLASSES[tourStatus]].filter(Boolean).join(' ');

  return [tourStatusText, tourStatusClass];
};

export const AgentCard = ({
  collectionId,
  collectionListing,
  mode,
  isCardsLoading,
}: {
  collectionId: string;
  collectionListing: CollectionListing;
  mode: AgentCollectionMode;
  isCardsLoading?: boolean;
}) => {
  const dispatch = useDispatch();
  const listingId = collectionListing.listingId || '';
  const listingIds = useNavListingIds();
  const isSelected = useIsSelected(listingId);
  const {mutate, isLoading} = useShareListings();
  const selectedIds = useSelectedIds();
  const toggleListing = useToggleListing();
  const onSelect = () => {
    toggleListing({
      isSelected,
      collectionListing,
    });
  };
  const {onListingClick, Header, Footer, ActionFooter} = MODES[mode];
  const onShareBtnClick = () => {
    if (isLoading) return;

    return mutate({collectionId, listingIds: [listingId]});
  };
  const setParams = useUpdateCurrentUrl();
  const showing = {
    wantToSee: collectionListing?.showing?.status === ShowingStatus.REQUESTED,
    status: collectionListing?.showing?.status,
    tourDate: collectionListing?.showing?.tourDate,
  };
  const ltsBadge = collectionListing.likelyToSell ? styles.cardWithLTSBadge : '';
  const listingCardOptions = {userType: 'agent' as const, disableNavigation: true};
  const clickHandler = () => {
    dispatch(setReferringListingPath('card'));
    onListingClick({
      setParams,
      listingIds,
      listingId,
    });
  };
  const legacyListingCard = (
    <ListingCard
      size="md"
      isAgent
      useServerBadgesData
      listing={collectionListing.listingData || {}}
      showing={showing}
      onListingClick={clickHandler}
      isCheckable
      isChecked={isSelected}
      onCheck={onSelect}
      data-testid="listing-card"
    />
  );
  const [tourStatusText, tourStatusClass] = getListingTourBanner(showing);
  const newListingCard = (
    <UcListingCard
      className={styles.listingCard}
      data={collectionListing.listingCardData}
      options={listingCardOptions}
      onClick={() => {
        clickHandler();
      }}
      data-testid="listing-card"
      key={`${collectionListing.listingId}_${tourStatusText}`}
    >
      <div slot="actions">
        <UcLcCheckbox checked={isSelected} onCheckboxChange={onSelect} />
      </div>
      {tourStatusText && (
        <div className={tourStatusClass} data-testid="card-banner" slot="banner">
          {tourStatusText}
        </div>
      )}
      {ActionFooter && (
        <ActionFooter
          collectionId={collectionId}
          listingIds={listingIds}
          collectionListing={collectionListing}
          onShareBtnClick={onShareBtnClick}
          disabled={selectedIds.length > 0}
        />
      )}
    </UcListingCard>
  );
  const loadingListingCard = <UcLoadingListingCard key={listingId} />;
  const newComponent = isCardsLoading ? loadingListingCard : newListingCard;
  const useNewCard = isNewAgentListingCardsOnCollectionsEnabled();
  const listingCard = useNewCard ? newComponent : legacyListingCard;

  return (
    <Card
      onHandleFocus={() => dispatch(setHoveredListing(collectionListing))}
      onHandleMouseOver={() => dispatch(setHoveredListing(collectionListing))}
      onHandleMouseOut={() => dispatch(setHoveredListing())}
      onHandleBlur={() => dispatch(setHoveredListing())}
      className={classnames(
        {
          [styles.cardWithNewMatchBadge]: collectionListing.updatesUnreadByCurrentUser?.includes(
            UnreadListingUpdates.RECENTLY_RECOMMENDED
          ),
        },
        ltsBadge,
        styles.cardWithBorder
      )}
      header={Header && <Header collectionListing={collectionListing} />}
      footer={
        !useNewCard && (
          <Footer
            collectionId={collectionId}
            listingIds={listingIds}
            collectionListing={collectionListing}
            onShareBtnClick={onShareBtnClick}
            disabled={selectedIds.length > 0}
          />
        )
      }
    >
      {listingCard}
    </Card>
  );
};
