import {useIsAgent} from '@src/store';

import {useCurrentUrlData} from './url';

export const useTrackCommentModalInitiated = (productPath: 'grid view' | 'list view') => {
  const {collectionId} = useCurrentUrlData();
  const isAgent = useIsAgent();
  const params = {
    product: 'collections',
    collection_id: collectionId,
    product_path: productPath,
    is_agent: isAgent,
  };

  return {
    trackCommentModalInitiated: () =>
      window.analytics?.track('collection_comment_initiated', params),
  };
};
