import {TabCounts} from '@src/types';
import {getTabCounts, getPaginationParams} from '@src/utils';

import {useAIRecommendations} from '.';
import {useGetCollectionListingsSummary} from './useGetCollectionListingsSummary';
import {useGetCollectionSearchResults} from './useGetCollectionSearchResults';

export const useTabCounts = ({
  collectionId,
  pageSize = 1,
  page = 1,
}: {
  collectionId: string;
  pageSize?: number;
  page?: number;
}): TabCounts => {
  const {summary = {}} = useGetCollectionListingsSummary({collectionId}).data || {};
  const {numListingsPerReviewStage} = summary;
  const {totalNumResults = 0} = useGetCollectionSearchResults(collectionId).data || {};
  const {totalResults = 0} =
    useAIRecommendations({
      collectionId,
      numOfItems: pageSize,
      skip: getPaginationParams({page, pageSize}).skip,
      getDeletedTasks: false,
    }).data || {};
  const tabCounts = getTabCounts({
    numListingsPerReviewStage,
    totalSearchResults: totalNumResults,
    ltsTabCount: totalResults,
  });

  return tabCounts;
};
