import React, {useEffect, useState} from 'react';
// eslint-disable-next-line no-restricted-imports
import {useQueryClient} from 'react-query';

import {ltsListingDismissed, ltsLoaded} from '@uc/analytics-definitions';
import {LoadingSpinner} from '@uc/cx.react';

import {API_KEY} from '@src/constants/apiKey';
import {useAIRecommendations, useAIRecMetaData, useDismissTask} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useFindBoardListings} from '@src/hooks/useFindBoardListings';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useDispatch, setToast} from '@src/store';
import {TaskV2, CompleteTaskRequest, CompleteTaskResponse} from '@src/types';
import {isShowNeighborInfoEnabled} from '@src/utils/optyUtils';

import './LikelyToSell.pcss';
import {RecommendationCard, Recommendation} from './RecommendationCard';
import {extractDataFromTasks, mergeIsAdded} from './utils';

export const LikelyToSell = () => {
  const {collectionId, page, pageSize} = useCurrentUrlData();
  const dispatch = useDispatch();
  const {data: collection} = useGetCollection({collectionId});
  const setParams = useUpdateCurrentUrl();
  const showNeighborInfo = isShowNeighborInfoEnabled();
  const queryClient = useQueryClient();
  const {data: aiData} = useAIRecommendations({
    collectionId,
    numOfItems: pageSize,
    skip: (page - 1) * pageSize,
  });
  const [aiRecommendations, setAiRecommendations] = useState<TaskV2[]>(aiData?.taskV2s || []);
  let rawRecommendations: Recommendation[] = extractDataFromTasks(aiData?.taskV2s);
  // Note: Using useFindBoardListings here because there is currently no way to
  // check which listings are part of a board/collection with the collections api.
  const {data: boardListings} = useFindBoardListings(collectionId, {
    listingQuery: {
      listingIdSHAs: rawRecommendations?.flatMap((rec) => {
        return rec.listingIdSHA ? [rec.listingIdSHA] : [];
      }),
      expandListings: false,
    },
  });
  const boardListingIdSHAs =
    boardListings?.content?.listings
      ?.map((boardListing) => boardListing.listingIdSHA)
      .filter((listingIdSHA): listingIdSHA is string => Boolean(listingIdSHA)) || [];

  rawRecommendations = mergeIsAdded(rawRecommendations, boardListingIdSHAs);

  const {data: aiCombinedData, isLoading} = useAIRecMetaData(rawRecommendations);
  const [aiRecCards, setAiRecCards] = useState<Recommendation[]>(aiCombinedData || []);
  const dismissTask = useDismissTask().mutateAsync;
  const handleDismissButtonClick = (taskId: string | undefined, address: string | undefined) => {
    if (taskId) {
      const request: CompleteTaskRequest = {taskId, completed: true};

      dismissTask(request)
        .then((response: CompleteTaskResponse) => {
          if (response.taskV2?.completed) {
            setAiRecommendations(aiRecommendations.filter((task) => task.id !== taskId) || []);
            setAiRecCards(aiRecCards.filter((rec) => rec.taskId !== taskId) || []);
          }
        })
        .then(async () => {
          await queryClient.resetQueries(API_KEY.GET_COLLECTION_LISTINGS_FACETS);
        })
        .then(() => {
          ltsListingDismissed({
            product: 'collections',
            collection_id: collectionId,
            task_id: taskId,
          });
        })
        .then(() => {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'info',
              message: `${address ? address : 'Recommendation'} has been dismissed`,
            })
          );
        })
        .catch(() => {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Something went wrong. Please refresh and try again.',
            })
          );
        });
    }
  };
  const getRecommendationCards = () => {
    if (aiRecCards?.length) {
      return aiRecCards.map((rec) => {
        return (
          rec.listing && (
            <RecommendationCard
              aiRecommendation={rec}
              collectionId={collectionId}
              collectionName={collection?.collection?.name || ''}
              key={rec.taskId}
              dismissButtonClick={handleDismissButtonClick}
              setParams={setParams}
              showNeighborInfo={showNeighborInfo}
            />
          )
        );
      });
    }

    return null;
  };

  useEffect(() => {
    setAiRecCards(aiCombinedData || []);

    if (aiCombinedData && aiCombinedData.length > 0) {
      const taskIds = aiCombinedData.flatMap((rec) => {
        return rec.taskId ? [rec.taskId] : [];
      });

      ltsLoaded({
        product: 'collections',
        collection_id: collectionId,
        task_ids: taskIds,
        page_number: page,
        page_size: pageSize,
      });
    }
  }, [aiCombinedData, collectionId, page, pageSize]);

  return (
    <>
      {isLoading ? (
        <div className="ltsModalSpinner">
          <LoadingSpinner />
        </div>
      ) : (
        <div className="ltsContent" data-testid="ltsContentId">
          {getRecommendationCards()}
        </div>
      )}
    </>
  );
};
