import React, {FocusEvent, MouseEvent} from 'react';

import styles from './index.module.pcss';

export const Card = ({
  children,
  className,
  header,
  footer,
  onHandleFocus,
  onHandleMouseOver,
  onHandleMouseOut,
  onHandleBlur,
}: React.PropsWithChildren<{
  className?: string;
  header?: React.ReactElement | string | boolean;
  footer?: React.ReactElement | string | boolean;
  onHandleFocus?: (event: FocusEvent) => void;
  onHandleMouseOver?: (event: MouseEvent) => void;
  onHandleMouseOut?: (event: MouseEvent) => void;
  onHandleBlur?: (event: FocusEvent) => void;
}>) => {
  const classNames = ['card'];

  if (header) {
    classNames.push('cardWithHeader');
  }

  if (footer) {
    classNames.push('cardWithFooter');
  }

  if (className) {
    classNames.push(className);
  }

  return (
    <div
      className={classNames.join(' ')}
      onFocus={onHandleFocus}
      onMouseOver={onHandleMouseOver}
      onMouseOut={onHandleMouseOut}
      onBlur={onHandleBlur}
      data-testid="card"
    >
      {React.isValidElement(header) && <div>{header}</div>}
      <div className={styles.cardContent}>{children}</div>
      {React.isValidElement(footer) && <div>{footer}</div>}
    </div>
  );
};
