import {useCallback} from 'react';

import {CollectionUrlData} from '@src/store';

import {useUpdateCurrentUrl} from './url';

// setParams triggered from map listing clicks need to always set enableModalPagination to false
// This hook provides a wrapper for setParams that will always pass in a false value for
// enableModalPagination.  This hook is used for map listing click handlers
export const useSetParamsForMapClick = () => {
  const setParams = useUpdateCurrentUrl();

  return useCallback(
    (next: Partial<CollectionUrlData>) =>
      setParams({
        ...next,
        enableModalPagination: false,
      }),
    [setParams]
  );
};
