import {CollectionMode} from '@src/constants';
import {FilterId} from '@src/constants/filter';
import {CollectionListingsFacet, FilterDropdownCounts} from '@src/types';

import {useCollectionListingsFacets} from './useCollectionListingsFacets';

export const useFilterDropdownCounts = ({
  collectionId,
  collectionMode,
  filterId,
}: {
  collectionId: string;
  collectionMode: CollectionMode;
  filterId?: FilterId;
}): FilterDropdownCounts => {
  const facets: {[key in CollectionListingsFacet]: number} | Record<number, undefined> =
    useCollectionListingsFacets({collectionId, collectionMode, filterId}).data?.facetToCount || {};
  const active = facets[CollectionListingsFacet.ACTIVE] || 0;
  const comingSoon = facets[CollectionListingsFacet.COMING_SOON] || 0;
  const pending = facets[CollectionListingsFacet.PENDING] || 0;
  const offMarket = facets[CollectionListingsFacet.OFF_MARKET] || 0;
  const expired = facets[CollectionListingsFacet.EXPIRED] || 0;
  const sold = facets[CollectionListingsFacet.SOLD] || 0;
  const nonActive = pending + offMarket + expired + sold;
  const openHouses = facets[CollectionListingsFacet.OPEN_HOUSES] || 0;
  const likelyToSell = facets[CollectionListingsFacet.LIKELY_TO_SELL] || 0;

  return {
    active,
    comingSoon,
    pending,
    offMarket,
    expired,
    sold,
    nonActive,
    openHouses,
    likelyToSell,
  };
};
