import React from 'react';

import {collectionViewAccessed, CollectionTableViews} from '@uc/analytics-definitions';
import {Button} from '@uc/cx.react';

import {Breakpoint} from '@src/components/Breakpoint';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useShowMap} from '@src/hooks';
import {useBusinessTrackerIds} from '@src/hooks/useBusinessTrackerIds';
import {useDispatch, setCurrentView, useSelector, getCurrentView, useIsAgent} from '@src/store';

export const ViewControlGroup = ({
  mode,
  collectionId,
}: {
  mode: CollectionMode;
  collectionId: string;
}) => {
  const isAgent = useIsAgent();
  const businessTrackerIds = useBusinessTrackerIds(collectionId);
  const dispatch = useDispatch();
  const view = useSelector(getCurrentView);
  const [showMap, setShowMap] = useShowMap();
  const isShowMapToggleButton = mode !== COLLECTION_MODE.TRASH;
  const dropDownItems = isShowMapToggleButton
    ? [
        {id: 'Card', label: 'Card view'},
        {id: 'List', label: 'List view'},
        {id: 'Map', label: 'Map'},
      ]
    : [
        {id: 'Card', label: 'Card view'},
        {id: 'List', label: 'List view'},
      ];
  const isCollectionTableViews = (view: string): view is CollectionTableViews =>
    ['list', 'card', 'map'].includes(view);
  const onDropDownClick = (id: React.Key) => {
    const view = String(id).toLowerCase();

    isCollectionTableViews(view) && changeView(view);
  };

  function changeView(view: CollectionTableViews) {
    view === 'map' ? setShowMap(!showMap) : dispatch(setCurrentView(view));

    collectionViewAccessed({
      product: 'collections',
      product_path: 'action bar',
      table_view: view,
      collection_id: collectionId,
      is_agent: isAgent,
      business_tracker_ids: businessTrackerIds,
    });
  }

  return (
    <>
      <Breakpoint isDesktop isTablet>
        <ButtonGroup>
          <Button
            icon="Grid"
            variant="enclosed"
            size="sm"
            className={view === 'card' ? 'is-active' : undefined}
            aria-label="Card view"
            data-testid="card-view"
            onClick={() => changeView('card')}
          />
          <Button
            icon="StackedLines"
            variant="enclosed"
            size="sm"
            className={view === 'list' ? 'is-active' : undefined}
            aria-label="List view"
            data-testid="list-view"
            onClick={() => changeView('list')}
          />
        </ButtonGroup>
        {isShowMapToggleButton && (
          <ButtonGroup>
            <Button
              icon="FoldedMap"
              variant="enclosed"
              size="sm"
              className={showMap ? 'is-active' : undefined}
              aria-label="Show map"
              data-testid="map-view"
              onClick={() => changeView('map')}
            />
          </ButtonGroup>
        )}
      </Breakpoint>
      <Breakpoint isMobile>
        <Dropdown
          variant="enclosed"
          size="sm"
          alignment="bottomRight"
          labelIsHidden
          label="View Options"
          iconLeft="Grid"
          items={dropDownItems}
          onItemSelect={onDropDownClick}
        />
      </Breakpoint>
    </>
  );
};
