import {useQuery} from 'react-query';

import {ApiError, getCollectionListingByListingId} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetCollectionListingByListingIdResponse} from '@src/types';

import {useCurrentUrlData} from './url';

export const useGetCollectionListingByListingId = ({listingId}: {listingId?: string}) => {
  const {collectionId} = useCurrentUrlData();
  const {data, isLoading, error} = useQuery<GetCollectionListingByListingIdResponse, ApiError>(
    [API_KEY.GET_COLLECTION_LISTING_BY_LISTING_ID, collectionId, listingId],
    () =>
      getCollectionListingByListingId({
        collectionId,
        listingId,
      })
  );

  return {
    collectionListing: data?.collectionListing,
    isLoading,
    error,
  };
};
