import {useQuery} from 'react-query';

import {ApiError, findBoardListings} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {FindBoardListingsRequest, FindBoardListingsResponse} from '@src/types';

/**
 * @deprecated You probably should not use this hook unless you absolutely have to. Please use useGetCollectionListings instead.
 */
export const useFindBoardListings = (boardId: string, request: FindBoardListingsRequest) => {
  return useQuery<FindBoardListingsResponse, ApiError>(
    [API_KEY.FIND_BOARD_LISTINGS, boardId, request.listingQuery?.listingIdSHAs],
    () => findBoardListings(boardId, request)
  );
};
