import React from 'react';
import ReactDOM from 'react-dom';

import loadModal from '@uc/modal-loader';

import {App} from '@src/components/App';

declare global {
  interface Window {
    renderNav?: () => Promise<void>;
  }
}

async function init() {
  if (window.renderNav) {
    try {
      await window.renderNav();
    } catch (err) {
      console.error('RenderNav Error:', err);
    }
  }

  /**
   *  Load full screen in-app education when captured learnMore event,
   *  the event will be emitted by apps/listing.
   *  Follow: https://compass-tech.atlassian.net/browse/CH-1814
   *  @param {*} e Custom Event Object
   */
  const onLearnMore = async (e: CustomEventInit) => {
    try {
      const eventParam = e.detail && typeof e.detail === 'object' ? e.detail : {};

      await loadModal('in-app-education.react', {
        ...eventParam,
        pageName: 'BRANDING',
        modalBodyClassName: 'uc-collectionShareModal-body',
      });
    } catch (e) {
      /* eslint-disable no-console */
      console.error(`Failed to load the in-app-education.react modal, ${e}`);
    }
  };

  document.addEventListener('learnMore', onLearnMore);

  ReactDOM.render(<App />, document.getElementById('root'));
}

init().catch((err) => console.error(err));
