import {useQuery} from 'react-query';

import {getProcessedListings} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';

export const useGetProcessedListings = (listingIds?: string[]) => {
  return useQuery([API_KEY.GET_PROCESSED_LISTINGS, listingIds], () => {
    if (listingIds === undefined) return;
    return getProcessedListings(listingIds);
  });
};
