import React from 'react';

import {Button, usePoptip, Poptip} from '@uc/cx.react';

import {ButtonGroup} from '@src/cx/ButtonGroup';
import {useFavoriteListings} from '@src/hooks/useFavoriteListings';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {clearSelectedListings, store} from '@src/store';
import {isNotInterestedIconWebAbTestEnabled} from '@src/utils/optyUtils';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import {ConsumerOptionGroupProps} from '../../types';
import {NotInterestedButton} from '../NotInterestedButton';

const FavoriteButton = (props: ConsumerOptionGroupProps) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const {mutateAsync: favoriteMutate, isLoading} = useFavoriteListings({showSuccessToast: false});
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(props.collectionId);
  const onFavorite = () => {
    if (isLoading) return;

    trackCollectionListingFlowAction({
      actionName: 'Save Listing',
      productPath: PRODUCT_PATH.ACTION_BAR,
    });
    favoriteMutate({
      collectionId: props.collectionId,
      listingIds: props.listingIds,
    }).then(() => {
      store.dispatch(clearSelectedListings());
    });
  };

  return (
    <>
      <Button
        variant="enclosed"
        size="sm"
        icon="Star"
        {...getPoptipAnchorProps('Favorite', 'bottom')}
        onClick={onFavorite}
        aria-label="Favorite"
      ></Button>
      <Poptip {...poptipProps} />
    </>
  );
};

export const Matches = (props: ConsumerOptionGroupProps) => {
  const newNotInterestedIconEnabled = isNotInterestedIconWebAbTestEnabled();

  return (
    <div data-testid="matchesWrapper">
      <ButtonGroup>
        <NotInterestedButton
          {...props}
          icon={newNotInterestedIconEnabled ? 'ThumbsDownFilled' : 'X'}
          toolTip="Mark not interested"
          showSuccessToast={false}
        />
        <FavoriteButton {...props} />
      </ButtonGroup>
    </div>
  );
};
