import {parse, getTime} from 'date-fns';
import React, {useRef, useEffect} from 'react';

import Datepicker from '@uc/date-picker';

interface DatePickerContainerProps {
  date: number;
  selectDate(selectedDate: number): void;
  isOpen?: boolean;
  datepickerRef: React.MutableRefObject<Datepicker | null>;
}

export function makeOnSet({selectDate}: {selectDate(selectedDate: number): void}) {
  return function onSet(dates: Array<{human: string}>) {
    selectDate(getTime(parse(dates[0].human, 'M/d/yyyy', Date.now())));
  };
}

export const DatePicker = ({date, selectDate, isOpen, datepickerRef}: DatePickerContainerProps) => {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    datepickerRef.current = new Datepicker({
      container: containerRef.current as HTMLDivElement,
      dates: [new Date(date)],
      showOnInit: true,
      hideInputContainer: true,
      isPureCalendar: true,
      onSet: makeOnSet({selectDate}),
      max: new Date(),
    });

    return () => {
      if (datepickerRef.current) {
        datepickerRef.current.remove();
      }
    };
  }, [isOpen, date, datepickerRef, selectDate]);

  return (
    <div
      id="date-picker-input"
      className="cx-formElement"
      ref={containerRef}
      style={{height: '335px', overflow: 'hidden'}}
    />
  );
};
