function renderInBar(size: number, {displayAt}: ActionItem): boolean {
  if (size >= displayAt) {
    return true;
  }

  return false;
}

function EmptyGroup(): Record<GROUP_ID, DropdownItem[]> {
  return {
    [GROUP_ID.SELECT]: [],
    [GROUP_ID.COLLECTION]: [],
    [GROUP_ID.TOUR]: [],
    [GROUP_ID.CMA]: [],
    [GROUP_ID.SHARE]: [],
    [GROUP_ID.PRINT]: [],
  };
}

const GROUP_LABELS: [GROUP_ID, string | false][] = [
  [GROUP_ID.SELECT, false],
  [GROUP_ID.COLLECTION, 'Collection Options'],
  [GROUP_ID.TOUR, 'Tour Options'],
  [GROUP_ID.CMA, 'Create CMA'],
  [GROUP_ID.SHARE, 'Share'],
  [GROUP_ID.PRINT, 'Print'],
];

function addHeaders(more: Record<GROUP_ID, DropdownItem[]>) {
  let results: DropdownItem[] = [];

  for (const [id, label] of GROUP_LABELS) {
    const items = more[id];

    if (items.length > 0) {
      if (label) {
        results = [...results, {id: `group-${id}`, label, header: true}];
      }

      results = [...results, ...items];
    }
  }

  return results;
}

function isKey(key: Key): key is ACTION_ID {
  return key in ACTION_ID;
}

const ACTIONS = Object.values(ACTIONS_BY_ID);
const getActionIndex = (key: Key) => {
  if (isKey(key)) {
    const action = ACTIONS_BY_ID[key];

    return ACTIONS.indexOf(action);
  }

  return -1;
};

export function useAction(props: ActionProps) {
  // Be careful about modifying this. Any hooks run within a render cycle
  // must always run in the same order for _every_ render cycle.
  const hooks = ACTIONS.map((item) => item.useOnClick(props));

  return (key: Key) => {
    hooks[getActionIndex(key)]?.();
  };
}

function toMenuItem({id, label, icon, poptipParams}: ActionItem): DropdownItem {
  return {id, label, icon, poptipParams, selected: false, ['data-testid']: label};
}

export function toMenuItems({
  mode,
  selected,
  size,
  selectedListings,
  showingInfo,
}: {
  mode: CollectionMode;
  selected: string[];
  size: number;
  selectedListings?: CollectionListing[];
  showingInfo?: ShowingTimeInfoModel;
}) {
  const bar = EmptyGroup();
  const more = EmptyGroup();
  const items = BY_MODE[mode];

  for (const item of items) {
    if (item.id == ACTION_ID.SHOWING_TIME) {
      if (showingInfo?.isHidden || selected.length === 0) continue;
    } else if (item.isHidden({selected, mode})) {
      continue;
    }

    if (item.id == ACTION_ID.SHOWING_TIME && item.poptipParams) {
      item.poptipParams.message = showingInfo?.message || '';
    }

    const menuItem = toMenuItem(item);

    if (item.id == ACTION_ID.SHOWING_TIME) {
      menuItem.disabled = showingInfo?.isDisable;
    } else {
      menuItem.disabled = item.isDisabled?.({selected, selectedListings});
    }

    if (renderInBar(size, item)) {
      bar[item.groupKey].push(menuItem);
    } else {
      more[item.groupKey].push(menuItem);
    }
  }

  return {bar, more: addHeaders(more)};
}

export function useActionBarItems(
  {mode}: {mode: CollectionMode},
  showingInfo?: ShowingTimeInfoModel
): {
  more: DropdownItem[];
  bar: Record<GROUP_ID, DropdownItem[]>;
} {
  const {size} = useDeviceWidth();
  const selected = useSelectedIds();
  const {selectedListings} = useSelectedListings();

  return useMemo(() => {
    return toMenuItems({mode, selected, size, selectedListings, showingInfo});
  }, [mode, size, selected, selectedListings, showingInfo]);
}
