import {useEffect} from 'react';

import {useTable, useTableDragAndDrop, useTableColumnResize} from '@uc/cx.react';

import {setColumns, useDispatch} from '@src/store';
import {Header, HeaderConfig} from '@src/types';

export const useListingTableData = ({columns}: {columns: HeaderConfig[]}) => {
  const {
    state,
    tableRef,
    tableNode,
    dispatch: tDispatch,
    getTableProps,
    getCellProps,
    getHeaderProps,
    getDragHeaderProps,
    getResizerProps,
  } = useTable({headers: columns}, useTableDragAndDrop, useTableColumnResize);
  const dispatch = useDispatch();
  const {
    headers,
    columnReordering: {isReordering},
  }: {headers: Header[]; columnReordering: {isReordering: boolean}} = state;

  useEffect(() => {
    tDispatch({type: 'INIT', defaultProps: {headers: columns}});
    const timer = setTimeout(() => {
      tDispatch({
        type: 'COLUMNS_UPDATE_SIZE',
        tableRef: tableNode.current,
      });
    });

    return () => clearTimeout(timer);
  }, [tDispatch, columns, tableNode]);
  useEffect(() => {
    if (!isReordering) {
      const orderedIds = headers.map(({data}) => {
        const {id, hide} = data;

        return {id, hide};
      });

      dispatch(setColumns(orderedIds));
    }
    // Internally, useTable updates the associated functions on each render,
    // so we have to limit the dependencies or we max-depth error.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isReordering]);

  return {
    headers: headers.filter((header) => !header.data.hide),
    tableRef,
    getTableProps,
    getCellProps,
    getHeaderProps,
    getDragHeaderProps,
    getResizerProps,
  };
};
