import {Action as HistoryAction, Location} from 'history';

import {SidePanelId} from '@src/constants';
import {CollectionListing, TabCounts, CollectionUser, ShowingTimeInfoModel} from '@src/types';
import {toCollectionUrl} from '@src/utils/url';

import {
  RootState,
  HeaderConfigInStorage,
  LocationState,
  CollectionUrlData,
  ReferringListingPath,
  TrashListingsModalTrigger,
} from './types';

export const SET_VIEW = 'SET_VIEW' as const;
export const SET_IS_AGENT = 'SET_IS_AGENT' as const;
export const SET_SOURCE = 'SET_SOURCE' as const;
export const CLEAR_SELECTED = 'CLEAR_SELECTED' as const;
export const TOGGLE_SELECTED = 'TOGGLE_SELECTED' as const;
export const RESET_STATE = 'RESET_STATE' as const;
export const SET_PAGE_SIZE = 'SET_PAGE_SIZE' as const;
export const SELECT_LISTINGS = 'SELECT_LISTINGS' as const;
export const SET_TOAST = 'SET_TOAST' as const;
export const SET_CURRENT_USER = 'SET_CURRENT_USER' as const;
export const SET_APP_REFERRER = 'SET_APP_REFERRER' as const;
export const SET_CURRENT_LOCATION = 'SET_CURRENT_LOCATION' as const;
export const SET_SHOW_MAP = 'SET_SHOW_MAP' as const;
export const SET_SHOW_FILTER_DROPDOWN = 'SET_SHOW_FILTER_DROPDOWN' as const;
export const SET_SIDEPANEL = 'SET_SIDEPANEL' as const;
export const SET_COLUMNS = 'SET_COLUMNS' as const;
export const SET_HOVERED_LISTING = 'SET_HOVERED_LISTING' as const;
export const SET_DELIVERABLE_ID = 'CLEAR_DELIVERABLE_ID' as const;
export const SET_SHOW_LTS_BANNER = 'SET_SHOW_LTS_BANNER' as const;
export const PUSH_HISTORY = 'PUSH_HISTORY' as const;
export const SET_REFERRING_LISTING_PATH = 'SET_REFERRING_LISTING_PATH' as const;
export const CLEAR_REFERRING_LISTING_PATH_AND_DELIVERABLE_ID =
  'CLEAR_REFERRING_LISTING_PATH_AND_DELIVERABLE_ID' as const;
export const SET_NAV_LISTINGIDS = 'SET_NAV_LISTINGIDS' as const;
export const SET_NAV_TAB_COUNTS = 'SET_NAV_TAB_COUNTS' as const;
export const SET_REMINDEE = 'SET_REMINDEE' as const;
export const SET_SHOWING_TIME_INFO = 'SET_SHOWING_TIME_INFO' as const;
export const SET_TRASH_LISTINGS_MODAL_TRIGGER = 'SET_TRASH_LISTINGS_MODAL_TRIGGER' as const;
export const SET_IS_TRASH_LISTINGS_IN_PROGRESS = 'SET_IS_TRASH_LISTINGS_IN_PROGRESS' as const;
export const SET_LISTING_COUNT = 'SET_LISTING_COUNT' as const;

// ** actions
export function setDeliverableId(id?: string) {
  return {type: SET_DELIVERABLE_ID, id};
}

export function setCurrentView(payload: RootState['currentView']) {
  return {type: SET_VIEW, payload};
}

export function toggleSelectedListing(payload: CollectionListing) {
  return {type: TOGGLE_SELECTED, payload};
}

export function clearSelectedListings() {
  return {type: CLEAR_SELECTED};
}

export function setIsAgent(payload: boolean) {
  return {type: SET_IS_AGENT, payload};
}

export function setSource(payload: string | undefined) {
  return {type: SET_SOURCE, payload};
}

export function resetState() {
  return {type: RESET_STATE};
}

export function setPageSize(payload: {collectionId: string; pageSize: number}) {
  return {type: SET_PAGE_SIZE, payload};
}

export function selectListings(payload: Readonly<CollectionListing[]>) {
  return {type: SELECT_LISTINGS, payload};
}

export function setToast(payload: RootState['toast']) {
  return {type: SET_TOAST, payload};
}

export function setCurrentUser(payload: RootState['currentUser']) {
  return {type: SET_CURRENT_USER, payload};
}

export function setAppReferrer(payload: string) {
  return {type: SET_APP_REFERRER, payload};
}

export function setCurrentLocation(
  location: Location<LocationState>,
  action: HistoryAction = 'PUSH'
) {
  return {type: PUSH_HISTORY, location, action};
}

export function setCurrentNav(payload: CollectionUrlData) {
  const location = toCollectionUrl(payload);

  return setCurrentLocation(location);
}

export function setShowFilterDropdown(payload: boolean) {
  return {type: SET_SHOW_FILTER_DROPDOWN, payload};
}

export function setShowMap(payload: boolean) {
  return {type: SET_SHOW_MAP, payload};
}

export function setSidePanel(payload: SidePanelId) {
  return {type: SET_SIDEPANEL, payload};
}

export function setColumns(payload: HeaderConfigInStorage[]) {
  return {type: SET_COLUMNS, payload};
}

export function setHoveredListing(payload?: CollectionListing) {
  return {type: SET_HOVERED_LISTING, payload};
}

export function setShowLTSBanner(payload: boolean) {
  return {type: SET_SHOW_LTS_BANNER, payload};
}

export function clearReferringListingPathAndDeliverableId() {
  return {type: CLEAR_REFERRING_LISTING_PATH_AND_DELIVERABLE_ID};
}

export function setReferringListingPath(payload?: ReferringListingPath) {
  return {type: SET_REFERRING_LISTING_PATH, payload};
}

export function setNavListingIds(payload?: string[]) {
  return {type: SET_NAV_LISTINGIDS, payload};
}

export function setNavTabCounts(payload?: TabCounts) {
  return {type: SET_NAV_TAB_COUNTS, payload};
}

export function setRemindee(payload?: CollectionUser) {
  return {type: SET_REMINDEE, payload};
}

export function setShowingTimeInfoModel(payload: ShowingTimeInfoModel) {
  return {type: SET_SHOWING_TIME_INFO, payload};
}

export function setTrashListingsModalTrigger(payload?: TrashListingsModalTrigger) {
  return {type: SET_TRASH_LISTINGS_MODAL_TRIGGER, payload};
}

export function setIsTrashListingsInProgress(payload?: boolean) {
  return {type: SET_IS_TRASH_LISTINGS_IN_PROGRESS, payload};
}

export function setListingCount(payload: number) {
  return {type: SET_LISTING_COUNT, payload};
}

// We don't expose an action creator for setting the current
// location because we want to discourage directly updating
// the location data in the store. Instead, we should always push
// state onto the history object and have our nav middleware fire
// the SET_CURRENT_LOCATION event.
interface SetCurrentLocationType {
  type: typeof SET_CURRENT_LOCATION;
  location: Location<LocationState>;
  action: HistoryAction;
}

// ** union of all action types
export type Action =
  | ReturnType<typeof setCurrentView>
  | ReturnType<typeof setIsAgent>
  | ReturnType<typeof setSource>
  | ReturnType<typeof setPageSize>
  | ReturnType<typeof toggleSelectedListing>
  | ReturnType<typeof clearSelectedListings>
  | ReturnType<typeof selectListings>
  | ReturnType<typeof resetState>
  | ReturnType<typeof setToast>
  | ReturnType<typeof setCurrentUser>
  | ReturnType<typeof setAppReferrer>
  | ReturnType<typeof setShowMap>
  | ReturnType<typeof setShowFilterDropdown>
  | ReturnType<typeof setSidePanel>
  | ReturnType<typeof setColumns>
  | ReturnType<typeof setHoveredListing>
  | ReturnType<typeof setDeliverableId>
  | ReturnType<typeof setShowLTSBanner>
  | ReturnType<typeof setCurrentLocation>
  | ReturnType<typeof setReferringListingPath>
  | ReturnType<typeof clearReferringListingPathAndDeliverableId>
  | ReturnType<typeof setNavListingIds>
  | ReturnType<typeof setNavTabCounts>
  | ReturnType<typeof setRemindee>
  | ReturnType<typeof setShowingTimeInfoModel>
  | ReturnType<typeof setTrashListingsModalTrigger>
  | ReturnType<typeof setIsTrashListingsInProgress>
  | ReturnType<typeof setListingCount>
  | SetCurrentLocationType;
