import {SORT_OPTIONS, SortId, COLLECTION_LISTING_SORT_OPTIONS} from '@src/constants/sort';

export const getSortOrder = (
  sortId: SortId | undefined
): Readonly<{sortOrder?: number; boardSortOrder?: {by: number; ascending: boolean}}> => {
  return sortId ? SORT_OPTIONS[sortId] : SORT_OPTIONS.time_added;
};

export const getCollectionListingSortOrder = (
  sortId: SortId | undefined
): Readonly<{sortOrder?: number; collectionSortOrder?: number}> => {
  return sortId
    ? COLLECTION_LISTING_SORT_OPTIONS[sortId]
    : COLLECTION_LISTING_SORT_OPTIONS.time_added;
};
