import {BBox} from '@turf/helpers';
import {MapToolbarHeader, MapTypeId, ComponentNames} from '@uc-maps/agent-toolbar.react';
import {Popover, ViewportSettings, useMapProviderState, SearchMap} from '@uc-maps/api.react';
import {ClustersLayer, useClusters} from '@uc-maps/clusters.react';
import {
  ClusterFeature,
  ContainerSize,
  getDiscreteZoomLevelForContainer,
  listingRelationToClusterFeature,
} from '@uc-maps/geospatial';
import {ComponentNames as LayerComponentNames} from '@uc-maps/layer-select.react';
import {PopoverContent} from '@uc-maps/popover.react';
import {MapProvider, OnViewportChangeParams} from '@uc-maps/provider-google.react';
import React, {useEffect, useState, useMemo} from 'react';

import {SearchQuery} from '@uc/search-query';

import {useShowMap} from '@src/hooks';
import {useMapData} from '@src/hooks/useMapData';
import {useHoveredListing} from '@src/store';
import {ProcessedListing, MapClusterType, MapSearchResultsType} from '@src/types';

import {DefaultMap} from './DefaultMap';
import {computeBbox, calculateMapProviderState, calculateMapCenter} from './MapUtils';
import './Map.pcss';

const initialZoomLevel = 8;

interface Props {
  searchResults: MapSearchResultsType;
  isAgent: boolean;
  onListingClick: ({listing}: {listing: ProcessedListing}) => void;
  searchQuery?: SearchQuery;
}

export const Map = ({searchResults = [], isAgent, onListingClick, searchQuery}: Props) => {
  const [, setShowMap] = useShowMap();
  const [mapSize, setMapSize] = useState({width: 200, height: 100});
  const [computedBbox, setComputedBbox] = useState(computeBbox(searchResults));
  const [mapTypeId, setMapTypeId] = useState(MapTypeId.ROADMAP);
  const hoveredListing = useHoveredListing();
  const listingClusters = useMapData({
    searchResults,
    mapOptions: {
      bbox: computedBbox,
      containerSize: mapSize,
    },
    searchQuery,
  });
  const stringifiedListingClusters = JSON.stringify(listingClusters);
  const {mapProviderState, setMapProviderState} = useMapProviderState({
    center: calculateMapCenter(computedBbox),
    zoomLevel: initialZoomLevel,
  });
  const [clustersState, clustersApi] = useClusters({
    activeClusters: [],
    searchClusters: [],
  });
  const {activeClusters, searchClusters} = clustersState;
  const {setActiveClusters, setHoverClusters, setSearchClusters} = clustersApi;
  const activeClusterFeature = activeClusters[0] as ClusterFeature;
  const [pinSize, setPinSize] = useState<ContainerSize>({width: 0, height: 0});
  const onMouseOver = (cluster: ClusterFeature): void => setHoverClusters([cluster]);
  const onMouseOut = (): void => setHoverClusters([]);

  // eslint-disable-next-line max-params
  function onClusterClick(
    cluster: ClusterFeature,
    targetViewPort: ViewportSettings,
    targetPinSize: ContainerSize
  ) {
    if (cluster.properties.type === MapClusterType.AGGREGATE_LISTING_BUBBLE) {
      const {center, zoomLevel} = calculateMapProviderState(targetViewPort, mapSize);

      setMapProviderState({center, zoomLevel});
    }

    setActiveClusters([cluster]);
    setPinSize(targetPinSize);
  }

  const setMapPosition = useMemo(
    () =>
      (computedBbox: BBox, size: ContainerSize = mapSize) => {
        setComputedBbox(computedBbox);
        // when bbox is set, MapProvider calculates the zoom level and center and uses that
        // however, since the bbox does not always match the aspect ratio of the container
        // the zoom level is sometimes in-between (e.g., 11.6667)
        // the map does not like this, so it rounds to the nearest zoom level (e.g., 12)
        // which results in the edges of the bbox getting cut off
        // so we calculate the center and zoomLevel ourselves instead
        const zoomLevel = getDiscreteZoomLevelForContainer(computedBbox, size);
        const center = calculateMapCenter(computedBbox);

        setMapProviderState({center, zoomLevel});
      },
    [mapSize, setMapProviderState]
  );

  function loadMap(size: ContainerSize) {
    setMapSize(size);

    const initialBBox = computeBbox(searchResults);

    setMapPosition(initialBBox, size);
  }

  useEffect(() => {
    setSearchClusters(JSON.parse(stringifiedListingClusters));
  }, [setSearchClusters, stringifiedListingClusters]);

  useEffect(() => {
    if (hoveredListing) {
      const hoveredCluster = listingRelationToClusterFeature({listing: hoveredListing.listingData});

      hoveredCluster && setHoverClusters([hoveredCluster]);
      return;
    }

    setHoverClusters([]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [hoveredListing]);

  return (
    <div data-testid="map-container" className="map-container">
      <SearchMap>
        <MapToolbarHeader
          onBackClick={() => setShowMap(false)}
          componentConfig={{[ComponentNames.MAIN_TOOLBAR_HEADER]: false}}
          layerSelectProps={{
            mapType: mapTypeId,
            onMapTypeChange: setMapTypeId,
            componentConfig: {
              [LayerComponentNames.FLOOD_LAYER_TOGGLE]: false,
              [LayerComponentNames.TRANSIT_LAYER_TOGGLE]: false,
            },
          }}
        />
        {searchResults && searchResults.length > 0 ? (
          <MapProvider
            {...mapProviderState}
            zoomControl
            mapTypeId={mapTypeId}
            onViewportChange={(params: OnViewportChangeParams) => {
              setComputedBbox(params.bbox);
              setMapSize(params.size);
            }}
            onInitialSize={loadMap}
          >
            <ClustersLayer
              clusters={searchClusters}
              onClick={onClusterClick}
              onMouseOver={onMouseOver}
              onMouseOut={onMouseOut}
              showHoverTooltip
            />
            {activeClusterFeature && activeClusterFeature.properties.listings ? (
              <Popover position={activeClusterFeature.geometry.coordinates} pinSize={pinSize}>
                <PopoverContent
                  isAgent={isAgent}
                  listingRelations={activeClusterFeature.properties.listings}
                  onClose={() => {
                    setActiveClusters([]);
                  }}
                  listingCardProps={{
                    onListingClick,
                    showing: {
                      wantToSee: undefined,
                      status: undefined,
                      tourDate: undefined,
                    },
                  }}
                />
              </Popover>
            ) : (
              <></>
            )}
          </MapProvider>
        ) : (
          <DefaultMap />
        )}
      </SearchMap>
    </div>
  );
};
