import moment from 'moment';
import React from 'react';

import {ListingShowing, ShowingStatus} from '@src/types';

import styles from './ConsumerCardTourBanner.module.pcss';

const TourText = ({status, tourDate}: {status: ShowingStatus; tourDate: number | undefined}) => {
  const tourStatusClass = [styles.cardBanner, tourStatusClassMapping[status]];
  const tourStatusText = getTourStatusText(status, tourDate);

  if (!tourStatusText) {
    return null;
  }

  return (
    <div
      className={tourStatusClass.join(' ')}
      data-testid="card-banner"
      slot="banner"
      role="status"
    >
      {tourStatusText}
    </div>
  );
};

export const ConsumerCardTourBanner = ({status, tourDate}: ListingShowing) => {
  if (status === undefined || status === ShowingStatus.REQUEST_CANCELLED) {
    return null;
  }

  const hasSeen =
    status === ShowingStatus.SCHEDULED && tourDate !== undefined && tourDate < new Date().getTime();
  const computedStatus = hasSeen ? ShowingStatus.SEEN : status;

  return <TourText status={computedStatus} tourDate={tourDate} />;
};

const tourStatusClassMapping = {
  [ShowingStatus.REQUESTED]: styles.requested,
  [ShowingStatus.PENDING_CONFIRMATION]: styles.requested,
  [ShowingStatus.SCHEDULED]: styles.scheduled,
  [ShowingStatus.SEEN]: styles.seen,
  [ShowingStatus.REQUEST_CANCELLED]: '',
};
const tourStatusTextMapping = {
  [ShowingStatus.REQUESTED]: 'Tour requested',
  [ShowingStatus.PENDING_CONFIRMATION]: 'Tour requested',
  [ShowingStatus.SCHEDULED]: 'Tour scheduled for',
  [ShowingStatus.SEEN]: 'Visited on',
  [ShowingStatus.REQUEST_CANCELLED]: '',
};

export const getTourStatusText = (status: ShowingStatus, tourDate: number | undefined): string => {
  if (
    [
      ShowingStatus.REQUESTED,
      ShowingStatus.PENDING_CONFIRMATION,
      ShowingStatus.REQUEST_CANCELLED,
    ].includes(status)
  ) {
    return tourStatusTextMapping[status];
  }

  // for other two statuses, tourDate is required
  if (!tourDate) {
    return '';
  }

  const momentObj = moment(tourDate);
  const formattedDate = momentObj.format(
    status === ShowingStatus.SEEN ? 'M/D/YY' : 'M/D/YY [at] h:mmA'
  );

  return `${tourStatusTextMapping[status]} ${formattedDate}`;
};
