import React, {useCallback, useState, useEffect, useMemo} from 'react';

import {Switch, Icon, Toast, Modal, useModal} from '@uc/cx.react';

// eslint-disable-next-line no-restricted-imports
import {createGoogleSheetFromListingIds} from '@src/api';
import {
  AdvancedOptions,
  ExportListingsToGsheetRequest,
  ExportListingsToGsheetResponse,
  ColumnDefinition,
} from '@src/types';

import {AddressOption, AddressSearch} from './AddressSearch';
import {ColumnsSelector, ColumnsType, SelectorColumn} from './ColumnsSelector';
import {ListingSelect, NO_SELECTION_INDEX} from './ListingSelect';
import {testSelectors} from './testSelectors';

import './GoogleSheetCmaModal.pcss';

interface ToastProps {
  showToast: boolean;
  toastMessage?: string;
  duration?: number;
  onDismiss?: () => void;
}

interface ActionBarData {
  listingIds?: string[];
  geographies?: string[];
  isSales: boolean;
  defaultColumnIds: Set<string>;
  allColumns: SelectorColumn[];
}

interface FormData {
  // Subject Property
  address?: AddressOption;
  selectedListingIndex?: number;

  // Spreadsheet Columns
  columnsType?: ColumnsType;
  columnsToExport?: ColumnDefinition[];

  // Advanced options
  advancedOptions?: AdvancedOptions;
}

export interface GoogleSheetCmaModalProps {
  actionBarData: ActionBarData;
  initialFormData?: FormData;
  onClose?: () => void;
}

export const GoogleSheetCmaModal = (props: GoogleSheetCmaModalProps) => {
  const {initialFormData = {}, actionBarData, onClose} = props;
  const {advancedOptions} = initialFormData;
  const {defaultColumnIds, allColumns} = actionBarData;
  const {modalProps, isModalOpen, closeModal} = useModal({initialIsOpen: true});

  useEffect(() => {
    if (!isModalOpen) {
      onClose?.();
    }
  }, [isModalOpen, onClose]);

  const [showErrorMessage, setShowErrorMessage] = useState(false);
  const [isExportDisabled, setIsExportDisabled] = useState(false);
  const [toastConfig, setToastConfig] = useState<ToastProps>({showToast: false});
  // Subject Property
  const [address, setAddress] = useState<AddressOption | undefined>(initialFormData.address);
  const [selectedListingIndex, setSelectedListingIndex] = useState(
    initialFormData.selectedListingIndex ?? NO_SELECTION_INDEX
  );
  const [showListingError, setShowListingError] = useState(false);
  // Spreadsheet Columns
  const [columnsType, setColumnsType] = useState<ColumnsType>(
    initialFormData?.columnsType ?? ColumnsType.DEFAULT
  );
  const [selectedColumns, setSelectedColumns] = useState<ColumnDefinition[]>(
    initialFormData?.columnsToExport ?? []
  );
  // Advanced options
  const [groupByStatus, setGroupByStatus] = useState(advancedOptions?.groupByStatus ?? true);
  const [summaryStatistics, setSummaryStatistics] = useState(
    advancedOptions?.includeSummaryStatistics ?? true
  );
  const [clientFormat, setClientFormat] = useState(advancedOptions?.applyClientFormatting ?? true);
  // as modalProps for ColumnsSelector to keep form state in between modal switch
  const formData = useMemo(
    () => ({
      ...initialFormData,
      address,
      selectedListingIndex,
      columnsToExport: selectedColumns,
      advancedOptions: {
        groupByStatus,
        includeSummaryStatistics: summaryStatistics,
        applyClientFormatting: clientFormat,
      },
    }),
    [
      initialFormData,
      address,
      selectedListingIndex,
      selectedColumns,
      groupByStatus,
      summaryStatistics,
      clientFormat,
    ]
  );
  const getExportParams = useCallback((): ExportListingsToGsheetRequest => {
    const {address, columnsToExport, advancedOptions} = formData;
    const listingIds = actionBarData?.listingIds;

    return {
      listingIds: listingIds || [],
      subjectPropertyId: address?.listings?.[selectedListingIndex]?.listingIdSHA,
      columnsToExport: columnsToExport.filter(
        (definition): definition is ColumnDefinition => definition !== undefined
      ),
      advancedOptions,
      localToUTCTimeOffset: new Date().getTimezoneOffset(),
      geographies: actionBarData?.geographies,
      useSearchLoadListingsClient: true,
      isSales: actionBarData.isSales,
    };
  }, [actionBarData, formData, selectedListingIndex]);
  const onColumnsSelectorChange = useCallback((type: ColumnsType, columns: ColumnDefinition[]) => {
    setColumnsType(type);
    setSelectedColumns(columns);
  }, []);
  const onAddressChange = useCallback((address) => {
    setAddress(address);
    setSelectedListingIndex(NO_SELECTION_INDEX);
  }, []);
  const finishedWithReportOpenInTab = useCallback(
    (results: ExportListingsToGsheetResponse) => {
      const {spreadsheetName, spreadsheetUrl} = results;
      const toastMsg = `Done downloading: ${spreadsheetName}`;

      setToastConfig({
        showToast: true,
        toastMessage: toastMsg,
        duration: 2000,
        onDismiss: () => {
          closeModal();

          // Finally, open the spreadsheet URL in a new tab
          window.open(spreadsheetUrl);
        },
      });
    },
    [closeModal]
  );
  const onExport = useCallback(async () => {
    if (address && selectedListingIndex < 0) {
      return setShowListingError(true);
    }

    setShowErrorMessage(false);
    setIsExportDisabled(true);
    setToastConfig({
      showToast: true,
      toastMessage: 'Downloading listings to Google Sheets',
      duration: 0, // no auto-hide when downloading
    });

    const exportPrams = getExportParams();
    const showError = (): void => {
      setToastConfig({
        showToast: false,
      });
      setShowErrorMessage(true);
      setIsExportDisabled(false);
    };

    try {
      const results = await createGoogleSheetFromListingIds(exportPrams);

      setToastConfig({showToast: false});

      if (!results.spreadsheetUrl) {
        throw new Error('spreadsheetUrl does not exist');
      }

      finishedWithReportOpenInTab(results);
    } catch (error) {
      showError();
    }
  }, [finishedWithReportOpenInTab, address, selectedListingIndex, getExportParams]);

  useEffect(() => {
    if (selectedListingIndex >= 0) {
      setShowListingError(false);
    }
  }, [selectedListingIndex]);

  return (
    <Modal
      {...modalProps}
      className="googleSheetCmaModal-container"
      data-testid={testSelectors.modal}
    >
      <div className="cx-systemModal-header googleSheetCmaModal-header">
        Google Sheet CMA
        <svg
          className="cx-modal-closeIcon cx-icon"
          onClick={closeModal}
          data-testid={testSelectors.closeBtn}
        >
          <use xlinkHref="/ucfe-assets/cx-icons/4/cx-icons.cdn.svg#cx-icon-x_16x16" />
        </svg>
      </div>
      <div className="cx-systemModal-body googleSheetCmaModal-body">
        {showErrorMessage ? (
          <div className="googleSheetCmaModal-errorMessage">
            <Icon.WarningTriangle className="cx-token-icon googleSheetCmaModal-errorMessageIcon" />
            <span className="textIntent-caption1" data-testid={testSelectors.errorMessage}>
              We are unable to complete your request - please try again or contact support
            </span>
          </div>
        ) : null}
        <div className="googleSheetCmaModal-subjectProperty">
          <div className="cx-formElement-label">
            <strong>Subject Property </strong>
            (optional)
          </div>
          <div className="googleSheetCmaModal-searchBox">
            <AddressSearch initialSelected={address} onChange={onAddressChange} />
            {address && (
              <ListingSelect
                key={address.address}
                listings={address.listings}
                selectedIndex={selectedListingIndex}
                onChange={setSelectedListingIndex}
                showErrorMsg={showListingError}
              />
            )}
          </div>
        </div>
        <ColumnsSelector
          columnsType={columnsType}
          defaultColumnIds={defaultColumnIds}
          allColumns={allColumns}
          onChange={onColumnsSelectorChange}
        />
        <div className="googleSheetCmaModal-advancedOptions">
          <div className="cx-formElement-label">
            <strong>Advanced Settings</strong>
          </div>
          <div className="googleSheetCmaModal-option">
            <div className="googleSheetCmaModal-optionLabel">Group by status</div>
            <div className="googleSheetCmaModal-optionController">
              <Switch
                checked={groupByStatus}
                onChange={(e) => setGroupByStatus(e.target.checked)}
                data-testid={testSelectors.groupByStatusSwitch}
              />
            </div>
          </div>
          <div className="googleSheetCmaModal-option">
            <div className="googleSheetCmaModal-optionLabel">Summary statistics</div>
            <div className="googleSheetCmaModal-optionController">
              <Switch
                checked={summaryStatistics}
                onChange={(e) => setSummaryStatistics(e.target.checked)}
                data-testid={testSelectors.summaryStatisticsSwitch}
              />
            </div>
          </div>
          <div className="googleSheetCmaModal-option">
            <div className="googleSheetCmaModal-optionLabel">Client format</div>
            <div className="googleSheetCmaModal-optionController">
              <Switch
                checked={clientFormat}
                onChange={(e) => setClientFormat(e.target.checked)}
                data-testid={testSelectors.clientFormatSwitch}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="cx-systemModal-footer googleSheetCmaModal-footer">
        <button
          className="cx-enclosedBtn cx-enclosedBtn--sm"
          onClick={closeModal}
          data-testid={testSelectors.cancelBtn}
        >
          Cancel
        </button>
        <button
          className="cx-solidBtn cx-solidBtn--sm"
          disabled={isExportDisabled}
          onClick={onExport}
          data-testid={testSelectors.exportBtn}
        >
          Export
        </button>
      </div>
      <Toast
        className="googleSheetCmaModal-toast"
        isOpen={toastConfig.showToast}
        onDismiss={toastConfig.onDismiss}
        duration={toastConfig.duration}
        variant="success"
      >
        {toastConfig.toastMessage ?? ''}
      </Toast>
    </Modal>
  );
};
