import {useMutation, useQueryClient} from 'react-query';

import {trashListings} from '@src/api';
import {getToastMessage} from '@src/components/ActionBar/logic';
import {ACTION_ID} from '@src/constants';
import {useDispatch, setToast, setIsTrashListingsInProgress} from '@src/store';

export const useTrashListings = () => {
  const client = useQueryClient();
  const dispatch = useDispatch();

  return useMutation(
    async ({
      collectionId,
      listingIds,
      comment,
      customizedToastMessage,
    }: {
      collectionId: string;
      listingIds: string[];
      comment?: string;
      customizedToastMessage?: string;
    }) => {
      dispatch(setIsTrashListingsInProgress(true));
      const toastMessage = getToastMessage({
        actionId: ACTION_ID.TRASH,
        listingsLength: listingIds.length,
      });

      try {
        const result = await trashListings({collectionId: collectionId, listingIds, comment});

        dispatch(
          setToast({
            isOpen: true,
            variant: 'info',
            message: customizedToastMessage ? customizedToastMessage : toastMessage?.success,
          })
        );

        // TO-DO this invalidation should be more restricted to only refetch what is necessary
        await client.invalidateQueries();
        dispatch(setIsTrashListingsInProgress(false));
        return result;
      } catch (e) {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: toastMessage?.error,
          })
        );
        dispatch(setIsTrashListingsInProgress(false));
        return;
      }
    }
  );
};
