import React, {useCallback, useEffect} from 'react';
// eslint-disable-next-line no-restricted-imports
import {useQueryClient} from 'react-query';

import {API_KEY} from '@src/constants/apiKey';
import {useBoard, useReadComment} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useAddComment} from '@src/hooks/useAddComment';
import {useDeleteComment} from '@src/hooks/useDeleteComment';
import {useCurrentUser} from '@src/store';
import {IdentifiedUser, Board, Comment as CommentType} from '@src/types';
import {createModalComponent} from '@src/utils/createModalComponent';
import './DiscussionModal.pcss';

const DISCUSSION_MODAL_URI = 'collection-discussion.react/2.0';

interface DiscussionModalProps {
  collection: Board;
  currentUser: IdentifiedUser;
  onPostComment: ({content}: {content: string}) => Promise<unknown>;
  onDeleteComment: (comment: CommentType) => Promise<unknown>;
}
const Modal = createModalComponent<DiscussionModalProps, undefined, undefined>(
  DISCUSSION_MODAL_URI
);

export const DiscussionModal = () => {
  const urlData = useCurrentUrlData();
  const collectionId = urlData.collectionId;
  const {data} = useBoard({collectionId});
  const client = useQueryClient();

  // when visiting discussion modal directly from URL, the useGetCollection call is stuck at `idle` status, which led to a blank background, invalidating useGetCollection query to make sure the background can be loaded
  client.invalidateQueries(API_KEY.GET_COLLECTION);
  const collection = data?.board;
  const currentUser = useCurrentUser();
  const setParams = useUpdateCurrentUrl();
  const addCommentAsync = useAddComment().mutateAsync;
  const deleteCommentAsync = useDeleteComment().mutateAsync;
  const readComment = useReadComment().mutate;
  const onClose = useCallback(() => {
    if (!urlData) {
      return;
    }

    setParams({...urlData, modalId: undefined});
  }, [setParams, urlData]);
  const onPostComment = ({content}: {content: string}) => {
    return addCommentAsync({collectionId, content});
  };
  const onDeleteComment = (comment: CommentType) => {
    return deleteCommentAsync({collectionId, commentId: comment.commentId || ''});
  };

  useEffect(() => {
    readComment({collectionId});
  }, [readComment, collectionId]);

  return (
    <>
      {collection && (
        <Modal
          params={{
            collection: collection,
            currentUser: currentUser,
            onPostComment: onPostComment,
            onDeleteComment: onDeleteComment,
          }}
          onResolve={onClose}
          onError={onClose}
        />
      )}
    </>
  );
};
