import {useMutation, useQueryClient} from 'react-query';

import {removeRequestedShowing} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {
  useCollectionListingPaginatedQueryKey,
  UseGetCollectionListingsPaginatedResponse,
} from '@src/hooks/useGetCollectionListingsPaginated';
import {ShowingStatus} from '@src/types';

import {useIsInClientCommentModal} from './useIsInClientCommentModal';

export const useRemoveRequestedShowing = () => {
  const client = useQueryClient();
  const isInClientCommentModal = useIsInClientCommentModal();
  const collectionListingsQueryKey = useCollectionListingPaginatedQueryKey();

  return useMutation(
    async ({listingId, collectionId}: {listingId: string; collectionId: string}) => {
      const result = await removeRequestedShowing({collectionId, listingIds: [listingId]});

      if (!isInClientCommentModal) {
        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_PAGINATED);
        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_FACETS);
      }

      return result;
    },
    {
      // Update the paginatedListings cache to remove the listing request
      onSuccess: (_, {listingId}) => {
        if (isInClientCommentModal) {
          const previousListings = client.getQueryData(
            collectionListingsQueryKey
          ) as UseGetCollectionListingsPaginatedResponse;

          previousListings?.currentPageListings?.find((listing) => {
            if (
              listing.listingId === listingId &&
              listing.showing?.status === ShowingStatus.REQUESTED
            ) {
              listing.showing = undefined;
            }
          });

          client.setQueryData(collectionListingsQueryKey, previousListings);
        }
      },
    }
  );
};
