import {useGetCollection} from '@src/hooks/useGetCollection';

export const useBusinessTrackerIds = (collectionId: string) => {
  const {data} = useGetCollection({collectionId});
  const dealSummaries = data?.collection?.dealSummaries || [];
  const businessTrackerIds = dealSummaries
    .map((item) => item.id || '')
    .filter((id) => id.length > 0);

  return businessTrackerIds;
};
