import pick from 'lodash/pick';
import {useQuery} from 'react-query';

import {collectionAddressSearchAccessed} from '@uc/analytics-definitions';

import {ApiError, getCollectionListingsPaginated, readListingUpdates} from '@src/api';
import {COLLECTION_MODE, DEFAULT_PAGINATION} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {CollectionUrlData, useIsAgent} from '@src/store';
import {BoardListingUpdateType, GetCollectionListingsPaginatedResponse} from '@src/types';
import {getPaginationParams} from '@src/utils';
import {filterBadgesForCollectionListing} from '@src/utils/filterBadges';

import {useCurrentUrlData} from './url';
import {useBusinessTrackerIds} from './useBusinessTrackerIds';
import {useFilterDropdownCounts} from './useFilterDropdownCounts';
import {useFilterPillCounts} from './useFilterPillCounts';
import {useTabCounts} from './useTabCounts';

export interface UseGetCollectionListingsPaginatedResponse
  extends GetCollectionListingsPaginatedResponse {
  listingIds: string[];
}

export const useCollectionListingPaginatedQueryKey = (
  urlDataOverrides?: Partial<CollectionUrlData>
) => {
  const urlData = useCurrentUrlData();
  const urlParams = pick({...urlData, ...urlDataOverrides}, [
    'collectionId',
    'collectionMode',
    'pageSize',
    'page',
    'sortId',
    'filterId',
    'address',
    'filterByListingStatus',
    'filterByOpenHouses',
    'filterByLikelyToSell',
  ]);

  return [API_KEY.GET_COLLECTION_LISTINGS_PAGINATED, ...Object.values(urlParams)];
};

export const useGetCollectionListingsPaginated = (
  urlDataOverrides?: Partial<CollectionUrlData>
) => {
  const isAgent = useIsAgent();
  const urlData = useCurrentUrlData();
  // eslint-disable-next-line @typescript-eslint/no-extra-parens
  const overridenUrlData: CollectionUrlData = {...urlData, ...(urlDataOverrides || {})};
  const {
    collectionId,
    collectionMode,
    pageSize = DEFAULT_PAGINATION.pageSize,
    page = DEFAULT_PAGINATION.currentPage,
    sortId,
    filterId,
    filterByOpenHouses,
    filterByLikelyToSell,
    filterByListingStatus,
    address,
  } = overridenUrlData;
  const queryKey = useCollectionListingPaginatedQueryKey(overridenUrlData);
  const tabCounts = useTabCounts({collectionId, pageSize, page});
  const filterDropdownCounts = useFilterDropdownCounts({collectionId, collectionMode, filterId});
  const filterPillCounts = useFilterPillCounts({
    collectionId,
    tabCounts,
  });
  const businessTrackerIds = useBusinessTrackerIds(collectionId);
  const {data, isLoading, error} = useQuery<UseGetCollectionListingsPaginatedResponse, ApiError>(
    queryKey,
    async (): Promise<UseGetCollectionListingsPaginatedResponse> => {
      if (collectionMode === COLLECTION_MODE.LIKELY_TO_SELL) {
        return {
          totalListings: 0,
          currentPageListings: [],
          listingIds: [],
        };
      }

      const result = await getCollectionListingsPaginated({
        collectionId,
        collectionMode,
        limit: pageSize,
        skip: getPaginationParams({page, pageSize}).skip,
        sortId,
        filterId,
        shouldFilterByOpenHouses: filterByOpenHouses,
        shouldFilterByLikelyToSell: filterByLikelyToSell,
        listingStatusFilter: filterByListingStatus,
        address,
      });
      const listingIds =
        result?.currentPageListings?.map((listing) => listing.listingId || '') || [];

      // NOTE: do NOT use react-query's onSuccess handler. This handler will fire
      //       1 time for _each_ place the hook is connected in the DOM, resulting
      //       in 1 or more calls to readListingUpdates for the same data.
      if (listingIds.length > 0) {
        if (collectionMode === COLLECTION_MODE.SHARED) {
          // Both these calls need to be made to ensure that the badge on both the listing
          // and on the collection itself get cleared
          readListingUpdates({
            collectionId,
            request: {
              listingIdSHAs: listingIds,
              action: BoardListingUpdateType.ADDED_IN_REVIEW_STAGE,
            },
          });
          readListingUpdates({
            collectionId,
            request: {listingIdSHAs: listingIds, action: BoardListingUpdateType.ADDED_BY},
          });
        } else if (collectionMode === COLLECTION_MODE.UNSHARED) {
          readListingUpdates({
            collectionId,
            request: {listingIdSHAs: listingIds, action: BoardListingUpdateType.NEW_MATCH},
          });
        }
      }

      if (address) {
        collectionAddressSearchAccessed({
          product: 'collections',
          product_path: 'address search textbox',
          num_listings: result.totalListings ?? 0,
          collection_id: collectionId,
          is_agent: isAgent,
          business_tracker_ids: businessTrackerIds,
        });
      }

      return {...result, listingIds};
    }
  );
  const totalItems = data?.totalListings || DEFAULT_PAGINATION.totalItems;
  const collectionListings = data?.currentPageListings || [];
  const listingIds = data?.listingIds || [];

  collectionListings.forEach(filterBadgesForCollectionListing);

  return {
    collectionListings,
    listingIds,
    totalItems,
    pageSize,
    page,
    isLoading,
    error,
    tabCounts,
    filterCounts: filterPillCounts,
    filterDropdownCounts,
  };
};
