import {createSelector} from 'reselect';

import {RootState} from './types';

// ** selectors
export function getCurrentView(state: RootState) {
  return state.currentView;
}

export function getReferringListingPath(state: RootState) {
  return state.referringListingPath;
}

export const getSelectedListingsById = (state: RootState) => {
  return state.selectedListings;
};

export const getSelectedListings = createSelector(getSelectedListingsById, (listingsById) =>
  Object.values(listingsById)
);

export function getDeliverableId(state: RootState) {
  return state.deliverable_id;
}

export function getCurrentUser(state: RootState) {
  return state.currentUser;
}

export function getSource(state: RootState) {
  return state.source;
}

export function getPageSizes(state: RootState) {
  return state.pageSizes;
}

export function getToast(state: RootState) {
  return state.toast;
}

export function getCurrentNav(state: RootState) {
  return state.currentNav;
}

export function getAppReferrer(state: RootState) {
  return state.referrer;
}

export function getShowFilterDropdown(state: RootState) {
  return state.showFilterDropdown;
}

export function getShowMap(state: RootState) {
  return state.showMap;
}

export function getSidePanel(state: RootState) {
  return state.sidePanel;
}

export function getAnalytics(state: RootState) {
  return state.analytics;
}

export function getCurrentColumnPreferences(state: RootState) {
  const {columnConfig, currentNav} = state;

  return currentNav?.collectionMode ? columnConfig[currentNav.collectionMode] : undefined;
}

export function getGeoId(state: RootState) {
  return state.geoId;
}

export function getCurrentCollectionMode(state: RootState) {
  return state.currentNav?.collectionMode;
}

export function getCurrentIsAgent(state: RootState) {
  return getCurrentUser(state).isAgent ?? false;
}

export function getScrollId(state: RootState) {
  return state.scrollId;
}

export function getHoveredListing(state: RootState) {
  return state.hoveredListing;
}

export function getShowLTSBanner(state: RootState) {
  return state.showLTSBanner;
}

export function getNavListingIds(state: RootState) {
  return state.navListingIds;
}

export function getNavTabCounts(state: RootState) {
  return state.navTabCounts;
}

export function getRemindee(state: RootState) {
  return state.remindee;
}

export function getShowingTimeData(state: RootState) {
  return state.showingTimeInfo;
}

export function getTrashListingsModalTrigger(state: RootState) {
  return state.trashListingsModalTrigger;
}

export function getIsTrashListingsInProgress(state: RootState) {
  return state.isTrashListingInProgress;
}

export function getListingCount(state: RootState) {
  return state.listingCount;
}
