import React, {useMemo} from 'react';

import {Button, Loading} from '@uc/cx.react';

import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {useCurrentUrlData} from '@src/hooks/url';
import {Collection, CollectionListingReviewStage} from '@src/types';

import {FlyoutMenu} from './FlyoutMenu';

import './Header.pcss';

export const SPINNER_TEST_ID = 'modal-nav-spinner';
interface HeaderProps {
  isMobile: boolean;
  isAgent?: boolean;
  onViewFullListingDetails: () => void;
  onCloseModal: () => void;
  onTrashListing: () => void;
  currentIndex: number;
  totalListings: number | undefined;
  onNext: () => void;
  onPrevious: () => void;
  shouldDisableNext: boolean;
  shouldDisablePrevious: boolean;
  paginationLoading: boolean;
  collection: Collection;
  collectionMode: CollectionMode;
  viewsCount: number;
  reviewStage: CollectionListingReviewStage | null;
}

export const getContextualTitle = ({
  collection,
  collectionMode,
  isPaginationEnabled,
}: {
  collection: Collection;
  collectionMode: CollectionMode;
  isPaginationEnabled?: boolean;
}) => {
  let collectionModeStr = 'Listing';

  if (isPaginationEnabled) {
    const collectionModeToStr = {
      // agent tabs
      [COLLECTION_MODE.SHARED]: 'Shared',
      [COLLECTION_MODE.TRASH]: 'Trashed',
      // client tabs
      [COLLECTION_MODE.FAVORITES]: 'Favorite',
      [COLLECTION_MODE.MATCHES]: 'Matches',
      [COLLECTION_MODE.NOT_INTERESTED]: 'Not Interested',
    };

    if (collectionMode in collectionModeToStr) {
      collectionModeStr = collectionModeToStr[collectionMode as keyof typeof collectionModeToStr];
    }
  }

  return `${collectionModeStr} in ${collection.name}`;
};

export const ModalHeader = (props: HeaderProps) => {
  const {
    isMobile,
    isAgent,
    onViewFullListingDetails,
    onCloseModal,
    onTrashListing,
    currentIndex,
    totalListings,
    onNext,
    onPrevious,
    shouldDisableNext,
    shouldDisablePrevious,
    paginationLoading,
    collection,
    collectionMode,
    viewsCount,
    reviewStage,
  } = props;
  const {enableModalPagination} = useCurrentUrlData();
  const title = useMemo(() => {
    return getContextualTitle({
      collection,
      collectionMode,
      isPaginationEnabled: enableModalPagination,
    });
  }, [collection, collectionMode, enableModalPagination]);
  const isTrashed = reviewStage === CollectionListingReviewStage.TRASH;

  return (
    <header
      className={`header ${isAgent ? 'agentHeader' : 'clientHeader'}`}
      data-testid="modalHeader"
    >
      <>
        {isMobile ? (
          <FlyoutMenu onViewFullListingDetails={onViewFullListingDetails} />
        ) : (
          <div className="contexualTitle" data-testid="contexualTitle">
            {title}
          </div>
        )}
      </>
      <div className={`uc-workspaceBoardReviewListingModal-headerPagination`}>
        <Button
          variant="naked"
          icon="ChevronLeft"
          aria-label="Go To Previous Listing"
          data-testid="navigatorPrevious"
          disabled={shouldDisablePrevious}
          onClick={onPrevious}
        />
        <span className={`modalHeader-listingNavControls`}>
          {paginationLoading ? (
            <Loading.Spinner data-testid={SPINNER_TEST_ID} />
          ) : (
            <span>
              <span className="uc-workspaceBoardReviewListingModal-index">{currentIndex + 1}</span>{' '}
              of <span className="uc-workspaceBoardReviewListingModal-index">{totalListings}</span>
            </span>
          )}
        </span>
        <Button
          variant="naked"
          icon="ChevronRight"
          aria-label="Go To Next Listing"
          data-testid="navigatorNext"
          disabled={shouldDisableNext}
          onClick={onNext}
        />
      </div>
      <div className="rightActionGroup">
        {!isMobile && (
          <>
            {isAgent && (
              <div className="viewCounter" data-testid="viewCounter">{`${viewsCount} views`}</div>
            )}
            {isAgent && !isTrashed && (
              <Button
                className="header-btn--trashBtn"
                variant="enclosed"
                icon="Trash"
                size="sm"
                onClick={() => onTrashListing()}
              >
                Trash
              </Button>
            )}
            <Button
              type="button"
              icon="DoubleBoxedArrowUpRight"
              variant="enclosed"
              size="sm"
              onClick={onViewFullListingDetails}
              className="modalHeader-viewListingBtn cx-enclosedBtn cx-enclosedBtn--sm"
            >
              Open listing in new tab
            </Button>
          </>
        )}
        <Button
          icon="X"
          variant="naked--lowEmph"
          className="header-btn header-btn--dismissBtn cx-nakedBtn cx-nakedBtn--lowEmph"
          aria-label="Close Comments Modal"
          data-tn="header-button-dismiss"
          data-testid="header-closeModal"
          onClick={onCloseModal}
        />
      </div>
    </header>
  );
};
