import {BadgeType, CollectionListing, ProcessedListing} from '@src/types';

export function filterBadgesForSearchResult(listing: ProcessedListing) {
  if (listing?.badges?.cornerBadges) {
    listing.badges.cornerBadges = listing.badges.cornerBadges.filter(
      (badge) => badge.badgeType !== BadgeType.NEW_LISTING
    );
  }
}

export function filterBadgesForCollectionListing(listing: CollectionListing) {
  if (listing?.listingData?.badges?.cornerBadges) {
    listing.listingData.badges.cornerBadges = listing.listingData.badges.cornerBadges.filter(
      (badge) => badge.badgeType !== BadgeType.NEW_LISTING
    );
  }
}
