import React from 'react';

import {Button} from '@uc/cx.react';

import {useAddToCollection} from '@src/hooks/useAddToCollection';

import {ConsumerOptionGroupProps} from '../types';

export const AddToCollectionButton = (props: ConsumerOptionGroupProps) => {
  const addToCollection = useAddToCollection(props.collectionId);

  return (
    <Button
      variant="enclosed"
      size="sm"
      icon="Star"
      onClick={() => {
        addToCollection();
      }}
      data-testid="addToCollection"
    >
      Add to Collection
    </Button>
  );
};
