import React, {useState, useRef} from 'react';

import {Button, Flyout, Icon, Modal, Textarea} from '@uc/cx.react';

import styles from './AddCommentModal.module.pcss';

export const AddCommentModal = ({
  isOpen,
  handleCloseButtonClick,
  handleAddingComment,
}: {
  isOpen: boolean;
  handleCloseButtonClick: () => void;
  handleAddingComment: (comment: string) => void;
}) => {
  const anchorRef = useRef<HTMLDivElement>(null);
  const [showFlyout, setShowFlyout] = useState<boolean>(false);
  const [anchorText, setAnchorText] = useState<string>('Select a template (Optional)');
  const [comment, setComment] = useState<string>('');
  const templates = [
    {
      header: 'I have not reached out yet',
      subHeader: 'Saw this property and thought you might like it.',
      text: "Saw this property and thought you might like it. It's not for sale but if you like it, I'll reach out to the homeowner and see if they'd be interested in selling. Let me know.",
    },
    {
      header: 'I have reached out',
      subHeader: 'I reached out to the homeowner and they might be interested in selling.',
      text: "Saw this property and thought you might like it. It's not for sale but I reached out to the homeowner and they might be interested in selling. Let me know what you think.",
    },
  ];
  const handleTemplateSelection = (i: number) => {
    setAnchorText(templates[i].header);
    setShowFlyout(false);
    setComment(templates[i].text);
  };

  return (
    <Modal size="sm" isOpen={isOpen}>
      <Modal.Header
        closeButtonProps={{onClick: handleCloseButtonClick}}
        data-testid="addCommentModalHeader"
      >
        Add comment to listing (Optional)
      </Modal.Header>
      <Modal.Body>
        <div
          className={`cx-selectField cx-selectField--md ${styles.ACMSelect}`}
          ref={anchorRef}
          onClick={() => setShowFlyout(!showFlyout)}
          onKeyDown={() => setShowFlyout(!showFlyout)}
          role="button"
          tabIndex={0}
          data-testid="addCommentModalAnchor"
        >
          {anchorText}
          <span className={styles.ACMSelectIcon}>
            <Icon.ChevronDown />
          </span>
        </div>
        <Flyout
          anchor={anchorRef}
          isOpen={showFlyout}
          onDismiss={() => setShowFlyout(false)}
          className={styles.ACMFlyoutContainer}
        >
          {templates.map((t, i) => {
            return (
              <div
                className={`cx-optionsMenu-item ${styles.ACMFlyoutItem}`}
                key={`ACMFlyoutItem_${i}`}
                data-testid={`addCommentModalTemplate_${i}`}
                onClick={() => handleTemplateSelection(i)}
                onKeyDown={() => handleTemplateSelection(i)}
                role="button"
                tabIndex={0}
              >
                <div className={styles.ACMFlyoutItemHeader}>{t.header}</div>
                <div className={styles.ACMFlyoutItemSubHeader}>{t.subHeader}</div>
              </div>
            );
          })}
        </Flyout>
        <Textarea
          label="Add Comment"
          placeholder="Enter your comment..."
          labelIsHidden
          value={comment}
          onChange={(e) => setComment(e.target.value)}
          rows={3}
          data-testid="addCommentModalTextarea"
        />
      </Modal.Body>
      <Modal.Footer>
        <Button
          variant="enclosed"
          onClick={handleCloseButtonClick}
          data-testid="addCommentModalSkipButton"
        >
          Skip
        </Button>
        <Button
          onClick={() => handleAddingComment(comment)}
          disabled={!comment}
          data-testid="addCommentModalAddCommentButton"
        >
          Add Comment
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
