import React, {useState, useEffect} from 'react';
import {useDebounce} from 'use-debounce';

import {SearchInput} from '@uc/cx.react';

import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';

export const SearchBar = ({delay}: {delay?: number}) => {
  const {isMobile} = useDeviceWidth();
  const urlData = useCurrentUrlData();
  const {address = ''} = urlData;
  const setParams = useUpdateCurrentUrl();
  const [searchInput, setSearchInput] = useState(address);
  const [debouncedInput] = useDebounce(searchInput, delay && delay >= 0 ? delay : 800);

  useEffect(() => {
    if (address !== searchInput) {
      setSearchInput(address);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [address]);
  useEffect((): void => {
    if (debouncedInput.length === 0 || debouncedInput.replace(/\s/g, '').length > 2) {
      setParams({address: debouncedInput});
    }
    /* we're disabling the rule because adding setPrams would cause infinite loop */
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [debouncedInput]);

  return (
    <div style={{width: isMobile ? '100%' : '300px'}}>
      <SearchInput
        placeholder="Search by address"
        value={searchInput}
        onChange={setSearchInput}
        size="sm"
        aria-label="Search by address"
        data-testid="search-input-field"
      />
    </div>
  );
};
