import classnames from 'classnames';
import React, {useEffect, useRef, useState, useLayoutEffect} from 'react';

import {collectionFilterChanged} from '@uc/analytics-definitions';
import {Button, Checkbox, Flyout, FormGroup, Icon, Poptip, usePoptip} from '@uc/cx.react';
import {FlyoutProps} from '@uc/cx.react';

import {
  LIKELY_TO_SELL_FILTER_OPTION_ID,
  LISTING_STATUS_FILTER_OPTION_ID,
  OPEN_HOUSES_FILTER_OPTION_ID,
} from '@src/constants';
import {FilterDrpopdownItemId} from '@src/constants/filter';
import {useDeviceWidth} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useBusinessTrackerIds} from '@src/hooks/useBusinessTrackerIds';
import {
  getCurrentView,
  getShowFilterDropdown,
  setShowFilterDropdown,
  useDispatch,
  useIsAgent,
  useSelectedIds,
  useSelector,
} from '@src/store';
import {getSelectedListingStatusFilterIds} from '@src/utils/getSelectedListingStatusFilterIds';
import {isAIBuyerMatchingInCollectionsEnabled} from '@src/utils/optyUtils';

import {FilterDropdownCountsProps} from '../types';
import {getListingStatusFilterItems} from './filterDropdownUtils';
import styles from './SorterAndFilter.module.pcss';

const NON_ACTIVE = 'nonActive';
const nonActiveArr = [
  LISTING_STATUS_FILTER_OPTION_ID.EXPIRED,
  LISTING_STATUS_FILTER_OPTION_ID.OFF_MARKET,
  LISTING_STATUS_FILTER_OPTION_ID.PENDING,
  LISTING_STATUS_FILTER_OPTION_ID.SOLD,
];

export const FilterDropdown = ({
  filterDropdownCounts,
  collectionId,
}: {
  filterDropdownCounts: FilterDropdownCountsProps;
  collectionId: string;
}) => {
  const {isMobile} = useDeviceWidth();
  const selected = useSelectedIds();
  const dispatch = useDispatch();
  const isAgent = useIsAgent();
  const showFilterDropdown = useSelector(getShowFilterDropdown);
  const [flyoutMaxHeight, setFlyoutMaxHeight] = useState<string>('48vh');
  const {filterByListingStatus, filterByOpenHouses, filterByLikelyToSell} = useCurrentUrlData();
  const listingStatusFilter = filterByListingStatus
    ? getSelectedListingStatusFilterIds(filterByListingStatus)
    : [];
  const openHousesFilter = filterByOpenHouses ? [OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES] : [];
  const likelyToSellFilter = filterByLikelyToSell
    ? [LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL]
    : [];
  const filterParamsArr = [...listingStatusFilter, ...openHousesFilter, ...likelyToSellFilter].map(
    (filter) => filter
  );
  const [checkedFilters, setCheckedFilters] = useState<FilterDrpopdownItemId[]>(filterParamsArr);
  const businessTrackerIds = useBusinessTrackerIds(collectionId);
  const currentView = useSelector(getCurrentView);
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  useEffect(() => {
    if (
      typeof filterByListingStatus === 'undefined' &&
      typeof filterByOpenHouses === 'undefined' &&
      typeof filterByLikelyToSell === 'undefined'
    ) {
      setCheckedFilters([]);
    }
  }, [filterByLikelyToSell, filterByListingStatus, filterByOpenHouses]);
  const anchorRef = useRef<HTMLButtonElement>(null);
  const setParams = useUpdateCurrentUrl();
  const filterItems = getListingStatusFilterItems(filterDropdownCounts);
  const flyoutProps: FlyoutProps = {
    anchor: anchorRef,
    isOpen: showFilterDropdown,
    onDismiss: () => dispatch(setShowFilterDropdown(false)),
    zIndex: 400,
  };
  const nonActiveStatusItems = filterItems.filter((item) => {
    return (
      item.id !== LISTING_STATUS_FILTER_OPTION_ID.ACTIVE &&
      item.id !== LISTING_STATUS_FILTER_OPTION_ID.COMING_SOON &&
      item.id !== OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES &&
      item.id !== LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL
    );
  });
  const activeAndComingSoonItems = filterItems.filter((item) => {
    return (
      item.id === LISTING_STATUS_FILTER_OPTION_ID.ACTIVE ||
      item.id === LISTING_STATUS_FILTER_OPTION_ID.COMING_SOON
    );
  });
  const openHousesItem = filterItems.filter((item) => {
    return item.id === OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES;
  })[0];
  const LikelyToSellItem = filterItems.filter((item) => {
    return item.id === LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL;
  })[0];
  const onClickNonActiveFilter = () => {
    const checkedActiveAndComingSoon = checkedFilters.filter(
      (f) =>
        f === LISTING_STATUS_FILTER_OPTION_ID.ACTIVE ||
        f === LISTING_STATUS_FILTER_OPTION_ID.COMING_SOON
    );
    const checkedOpenHouse = checkedFilters.filter(
      (f) => f === OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES
    );
    const checkedLikelyToSell = checkedFilters.filter(
      (f) => f === LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL
    );
    const updatedCheckedFilters = nonActiveChecked
      ? [...checkedActiveAndComingSoon, ...checkedOpenHouse, ...checkedLikelyToSell]
      : [
          ...checkedActiveAndComingSoon,
          ...nonActiveArr,
          ...checkedOpenHouse,
          ...checkedLikelyToSell,
        ];

    collectionFilterChanged({
      product: 'collections',
      product_path: 'action bar',
      table_view: currentView,
      filter_option: updatedCheckedFilters,
      collection_id: collectionId,
      is_agent: isAgent,
      business_tracker_ids: businessTrackerIds,
    });
    setCheckedFilters(updatedCheckedFilters);
    setParams({filterByListingStatus: updatedCheckedFilters.join(',')});
  };
  const onClickFilter = (id: FilterDrpopdownItemId) => {
    const newCheckedFilters = [id];
    const filteredCheckedFilters = checkedFilters.filter((option) => {
      return option !== id;
    });
    const updatedCheckedFilters =
      filteredCheckedFilters.length === checkedFilters.length
        ? [...filteredCheckedFilters, ...newCheckedFilters]
        : filteredCheckedFilters;

    collectionFilterChanged({
      product: 'collections',
      product_path: 'action bar',
      table_view: currentView,
      filter_option: updatedCheckedFilters,
      collection_id: collectionId,
      is_agent: isAgent,
      business_tracker_ids: businessTrackerIds,
    });
    setCheckedFilters(updatedCheckedFilters);
    const listingStatusFilters = updatedCheckedFilters.filter(
      (f) =>
        f !== OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES &&
        f !== LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL
    );
    const openHousesFilter = updatedCheckedFilters.filter(
      (f) => f === OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES
    );
    const LikelyToSellFilter = updatedCheckedFilters.filter(
      (f) => f === LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL
    );

    setParams({
      filterByListingStatus: listingStatusFilters.join(','),
      filterByOpenHouses:
        openHousesFilter.length === 1 &&
        openHousesFilter[0] === OPEN_HOUSES_FILTER_OPTION_ID.OPEN_HOUSES,
      filterByLikelyToSell:
        LikelyToSellFilter.length === 1 &&
        LikelyToSellFilter[0] === LIKELY_TO_SELL_FILTER_OPTION_ID.LIKELY_TO_SELL,
    });
  };
  const calculateFlyoutMaxHeight = () => {
    const flyoutTopPositioned = document.querySelector('.cx-react-flyout.cx-flyoutContainer--top');

    if (flyoutTopPositioned) {
      setFlyoutMaxHeight('unset');
      return;
    }

    const dropdownButton = document.getElementById('filterDropdownMenu');

    if (!dropdownButton) {
      return;
    }

    if (isMobile) {
      setFlyoutMaxHeight('48vh');
      return;
    }

    const dropdownButtonRect = dropdownButton.getBoundingClientRect();
    const dropdownButtonBottom = dropdownButtonRect.bottom;
    const dropdownMaxHeight = window.innerHeight - dropdownButtonBottom - 5;

    setFlyoutMaxHeight(`${dropdownMaxHeight}px`);
  };

  useLayoutEffect(calculateFlyoutMaxHeight);
  window.onresize = calculateFlyoutMaxHeight;

  const nonActiveChecked = nonActiveArr.every((i) => checkedFilters.includes(i));
  const nonActiveHalfChecked =
    !nonActiveChecked &&
    checkedFilters.length > 0 &&
    nonActiveArr.some((i) => checkedFilters.includes(i));

  return (
    <>
      {!isMobile && selected.length === 0 && (
        <Button
          className={classnames('cx-selectField cx-selectField--sm', `${styles.filterDropdown}`)}
          ref={anchorRef}
          onClick={() => dispatch(setShowFilterDropdown(!showFilterDropdown))}
          variant="enclosed"
          size="sm"
          data-testid="filterDropdownMenu"
          id="filterDropdownMenu"
          aria-label="Listing Status Filter Dropdown Menu"
        >
          <div className={styles.filterBtnLabel}>
            {checkedFilters.length ? `Filters: ${checkedFilters.length} applied` : `Filter`}
            <Icon.ChevronDown />
          </div>
        </Button>
      )}
      {(isMobile || selected.length > 0) && (
        <Button
          ref={anchorRef}
          className={styles.filterDropdownIconBtn}
          onClick={() => dispatch(setShowFilterDropdown(!showFilterDropdown))}
          variant="enclosed"
          size="sm"
          data-testid="filterDropdownMenu"
          id="filterDropdownMenu"
          aria-label="Listing Status Filter Dropdown Menu"
        >
          <Icon.MixerBoard />
        </Button>
      )}
      <Flyout
        {...flyoutProps}
        aria-labelledby="listingStatusFilterDropdownMenu"
        alignment={isMobile || selected.length > 0 ? 'bottomRight' : 'bottomLeft'}
        className={styles.flyOut}
        style={{width: '193px', maxHeight: flyoutMaxHeight, minHeight: '200px'}}
      >
        <div className={`${styles.filterCategory} cx-text-body2Regular`}>Status</div>
        {activeAndComingSoonItems.map((item) => (
          <Checkbox
            key={item.id}
            className={styles.filterOption}
            checked={checkedFilters.includes(item.id)}
            onChange={() => onClickFilter(item.id)}
          >
            <div key={item.id} className={styles.filterOptionLabel}>
              {item.label}&nbsp;
              <span>({item.count})</span>
            </div>
          </Checkbox>
        ))}
        <Checkbox
          className={styles.filterOption}
          key={NON_ACTIVE}
          checked={nonActiveChecked}
          halfChecked={nonActiveHalfChecked}
          onChange={() => onClickNonActiveFilter()}
          data-testid="filterDropdownNonActiveCheckBox"
        >
          <div
            className={classnames(styles.filterOptionLabel, styles.nonActiveOptionGroupContainer)}
          >
            <div>{`Non active (${filterDropdownCounts.nonActive})`}</div>
            {isAgent && isAIBuyerMatchingInCollectionsEnabled() && LikelyToSellItem.count > 0 && (
              <div className={styles.nonActiveOptionGroupTooltip}>
                <Icon.CircleInfo
                  data-testid="nonActiveOptionGroupTooltip"
                  {...getPoptipAnchorProps('Includes shared likely to sell properties', 'top')}
                />
                <Poptip {...poptipProps} />
              </div>
            )}
          </div>
        </Checkbox>
        <FormGroup className={styles.nonActiveOptionGroup}>
          {nonActiveStatusItems.map((item) => (
            <Checkbox
              key={item.id}
              className={styles.filterOptionNonActives}
              checked={checkedFilters.includes(item.id)}
              onChange={() => onClickFilter(item.id)}
            >
              <div key={item.id} className={styles.filterOptionLabel}>
                {item.label}&nbsp;
                <span>({item.count})</span>
              </div>
            </Checkbox>
          ))}
        </FormGroup>
        <div
          className={`${styles.filterCategory} ${styles.filterCategoryOther} cx-text-body2Regular`}
        >
          Other
        </div>
        <Checkbox
          key={openHousesItem.id}
          className={styles.filterOption}
          checked={checkedFilters.includes(openHousesItem.id)}
          onChange={() => onClickFilter(openHousesItem.id)}
        >
          <div key={openHousesItem.id} className={styles.filterOptionLabel}>
            {openHousesItem.label}&nbsp;
            <span>({openHousesItem.count})</span>
          </div>
        </Checkbox>
        {isAIBuyerMatchingInCollectionsEnabled() && (
          <Checkbox
            key={LikelyToSellItem.id}
            className={styles.filterOption}
            checked={checkedFilters.includes(LikelyToSellItem.id)}
            onChange={() => onClickFilter(LikelyToSellItem.id)}
          >
            <div key={LikelyToSellItem.id} className={styles.filterOptionLabel}>
              {isAgent ? LikelyToSellItem.label : 'Potential seller'}&nbsp;
              <span>({LikelyToSellItem.count})</span>
            </div>
          </Checkbox>
        )}
        <Button
          variant="enclosed"
          size="sm"
          className={styles.clearButton}
          onClick={() => {
            setCheckedFilters([]);
            setParams({
              filterByListingStatus: undefined,
              filterByOpenHouses: undefined,
              filterByLikelyToSell: undefined,
              page: 1,
            });
          }}
        >
          Clear filters
        </Button>
      </Flyout>
    </>
  );
};
