import React from 'react';

import {useCurrentUser} from '@src/store';

const savedBuildingsUrl = '/workspace/#/saved-buildings';

export const SavedBuildingMenu = () => {
  const currentUser = useCurrentUser();
  const title = `${currentUser.firstName}'s Saved Buildings`;

  return (
    <React.Fragment>
      <ul className="cx-sideMenu-section">
        <div className="cx-sideMenu-sectionTitle">Saved Buildings</div>
        <li>
          <a className="cx-sideMenu-item" href={savedBuildingsUrl}>
            {title}
          </a>
        </li>
      </ul>
      <div className="cx-sideMenu-divider"></div>
    </React.Fragment>
  );
};
