import {useMemo} from 'react';

import {useCurrentColumns} from '@src/store';
import {ExportListingsToGsheetRequest, ColumnDefinition} from '@src/types';

export const useDefaultExportParams = (listingIds: string[]): ExportListingsToGsheetRequest => {
  const {currentShownColumnIds, currentColumns} = useCurrentColumns({
    isCollectionShared: false,
  });
  const columnsToExport = useMemo(() => {
    return currentColumns
      .filter((column) => currentShownColumnIds.has(column.id))
      .map((column) => column.exportDefinition)
      .filter(
        (exportDefinition): exportDefinition is ColumnDefinition => exportDefinition !== undefined
      );
  }, [currentShownColumnIds, currentColumns]);

  return useMemo(
    () => ({
      listingIds: listingIds || [],
      advancedOptions: {
        applyClientFormatting: true,
        groupByStatus: true,
        includeSummaryStatistics: true,
      },
      columnsToExport,
      localToUTCTimeOffset: new Date().getTimezoneOffset(),
      geographies: window.uc?.geoId ? [window.uc?.geoId] : [],
    }),
    [listingIds, columnsToExport]
  );
};
