import {useQuery} from 'react-query';

import {findNamedSearches} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';

export const useSavedSearches = () => {
  return useQuery(API_KEY.SAVED_SEARCHES, () =>
    findNamedSearches({
      includeDealSummary: true,
      includeSharedByUser: true,
      enableGeoAgnostic: true,
    })
  );
};
