import {useMutation} from 'react-query';

import {createGoogleSheetFromListingIds} from '@src/api';
import {ExportListingsToGsheetRequest} from '@src/types';

export const useCreateGoogleSheetFromListingIds = () => {
  return useMutation((exportParams: ExportListingsToGsheetRequest) =>
    createGoogleSheetFromListingIds(exportParams)
  );
};
