import classnames from 'classnames';
import React from 'react';

import {Checkbox, Td, Button} from '@uc/cx.react';

import {COLUMN_ID} from '@src/components/ListingTable/listingTableColumns';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {useInterestedListings} from '@src/hooks/useInterestedListings';
import {useNotInterestedListings} from '@src/hooks/useNotInterestedListings';
import {useShareListings} from '@src/hooks/useShareListings';
import {useToggleListing} from '@src/hooks/useToggleListing';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {useTrashListings} from '@src/hooks/useTrashListings';
import {useSelectedIdsSet} from '@src/store';
import {CollectionListing, Header} from '@src/types';
import {getNotInterestedIcon} from '@src/utils/getNotInterestedIcon';
import {isNotInterestedIconWebAbTestEnabled} from '@src/utils/optyUtils';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import styles from './ListingTable.module.pcss';
import {RequestShowingButtonAgent} from './RequestShowingButtonAgent';
import {RequestShowingButtonClient} from './RequestShowingButtonClient';

interface ColumnCellProps {
  isSelected: boolean;
  isAgent: boolean;
  collectionId: string;
  collectionListing: CollectionListing;
  listingId: string;
  cellProps: unknown;
  header: Header;
  mode: CollectionMode;
}

export const ShareButton = ({
  className,
  collectionId,
  listingId,
}: {
  className?: string;
  collectionId: string;
  listingId: string;
}) => {
  const {mutate, isLoading} = useShareListings();
  const selected = useSelectedIdsSet();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const disabled = selected.size > 0;

  return (
    <Button
      onClick={(e) => {
        e.stopPropagation();
        if (isLoading) return;
        trackCollectionListingFlowAction({
          actionName: 'Share',
          productPath: PRODUCT_PATH.TABLE_ROW,
        });
        return mutate({collectionId, listingIds: [listingId]});
      }}
      icon="Star"
      size="xs"
      variant="naked"
      className={className}
      data-testid="ShareButton"
      disabled={disabled}
    />
  );
};

export const TrashButton = ({
  collectionId,
  listingId,
}: {
  collectionId: string;
  listingId: string;
}) => {
  const {mutate, isLoading} = useTrashListings();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const selected = useSelectedIdsSet();
  const disabled = selected.size > 0;

  return (
    <Button
      className={styles.trashButton}
      onClick={(e) => {
        e.stopPropagation();
        if (isLoading) return;
        trackCollectionListingFlowAction({
          actionName: 'Trash',
          productPath: PRODUCT_PATH.TABLE_ROW,
        });
        return mutate({collectionId, listingIds: [listingId]});
      }}
      icon="Trash"
      size="xs"
      variant="naked"
      data-testid="TrashButton"
      disabled={disabled}
    />
  );
};

export const NotInterestedButton = ({
  collectionId,
  listingId,
  mode,
}: {
  collectionId: string;
  listingId: string;
  mode: CollectionMode;
}) => {
  const {mutate: notInterestedListings, isLoading: isNotInterestedListingsLoading} =
    useNotInterestedListings();
  const {mutate: interestedListings, isLoading: isInterestedListingsLoading} =
    useInterestedListings();
  const selected = useSelectedIdsSet();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const disabled = selected.size > 0;
  const isNotInterestedScreen = mode === COLLECTION_MODE.NOT_INTERESTED;
  const className = classnames(styles.notInterestedButton, {
    [styles['notInterestedButton--archive-state']]: isNotInterestedScreen,
  });

  return (
    <Button
      className={className}
      onClick={(e) => {
        e.stopPropagation();
        if (isNotInterestedListingsLoading || isInterestedListingsLoading) return;

        if (isNotInterestedScreen) {
          trackCollectionListingFlowAction({
            actionName: 'Favorite',
            productPath: PRODUCT_PATH.TABLE_ROW,
          });

          return interestedListings({collectionId, listingIds: [listingId]});
        } else {
          trackCollectionListingFlowAction({
            actionName: 'Not Interested',
            productPath: PRODUCT_PATH.TABLE_ROW,
            ab_test: isNotInterestedIconWebAbTestEnabled(),
            ab_test_version: getNotInterestedIcon(),
          });

          return notInterestedListings({collectionId, listingIds: [listingId]});
        }
      }}
      icon={isNotInterestedScreen ? 'Star' : getNotInterestedIcon()}
      size="xs"
      variant="naked"
      aria-label={isNotInterestedScreen ? 'Mark as interested' : 'Mark as not interested'}
      data-testid="NotInterestedButton"
      disabled={disabled}
    ></Button>
  );
};

export const ColumnCell = (props: ColumnCellProps) => {
  const {isSelected, isAgent, collectionId, collectionListing, listingId, header, cellProps, mode} =
    props;
  const toggleListing = useToggleListing();

  switch (header.id) {
    case COLUMN_ID.SELECT:
      return (
        <Td {...cellProps} key={header.id} className={styles.columnCell}>
          <div className={styles.selectCellWrap}>
            <Checkbox
              checked={isSelected}
              onClick={(e) => {
                e.stopPropagation();
                toggleListing({isSelected, collectionListing});
              }}
              onChange={() => {
                // If onChange missed will get warning by React in console
              }}
            />
          </div>
        </Td>
      );
    case COLUMN_ID.REQUEST_SHOWING:
    case COLUMN_ID.AGENT_REQUEST_SHOWING:
      return (
        <Td {...cellProps} key={header.id} className={styles.columnCell}>
          {isAgent ? (
            <RequestShowingButtonAgent collectionListing={collectionListing} />
          ) : (
            <RequestShowingButtonClient
              collectionId={collectionId}
              collectionListing={collectionListing}
            />
          )}
        </Td>
      );
    case COLUMN_ID.SHARE:
      return (
        <Td
          {...cellProps}
          key={header.id}
          onClick={(e) => {
            // stop the click event bubbling when button disabled
            e.stopPropagation();
          }}
          className={styles.columnCell}
        >
          <ShareButton
            className={styles.spaceBetweenOperateButtons}
            collectionId={collectionId}
            listingId={listingId}
          />
        </Td>
      );
    case COLUMN_ID.TRASH:
      return (
        <Td
          {...cellProps}
          key={header.id}
          onClick={(e) => {
            // stop the click event bubbling when button disabled
            e.stopPropagation();
          }}
          className={styles.columnCell}
        >
          <TrashButton collectionId={collectionId} listingId={listingId} />
        </Td>
      );
    case COLUMN_ID.ACTION:
      return (
        <Td
          {...cellProps}
          key={header.id}
          onClick={(e) => {
            // stop the click event bubbling when button disabled
            e.stopPropagation();
          }}
          className={styles.columnCell}
        >
          <NotInterestedButton collectionId={collectionId} listingId={listingId} mode={mode} />
        </Td>
      );
    default:
      return (
        <Td {...cellProps} key={header.id} className={styles.columnCell} data-testid={header.id}>
          {header.data.render?.(collectionListing)}
        </Td>
      );
  }
};
