import bbox from '@turf/bbox';
import {BBox, multiPoint} from '@turf/helpers';
import {ViewportSettings} from '@uc-maps/api.react';
import {
  ContainerSize,
  getGeoBoundingBox,
  toClusterFeature,
  getDiscreteZoomLevelForContainer,
  Position,
} from '@uc-maps/geospatial';
import {useMemo} from 'react';

import {convertPythonQueryToThrift} from '@uc/search-query';

import {useLoadMapSearch} from '@src/hooks/useLoadMapSearch';
import {
  ListingType,
  CollectionListing,
  ProcessedListing,
  Availability,
  RelationCluster,
  MapSearchResultsType,
} from '@src/types';

import {defaultMapData} from './DefaultMap';

/**
 * Given an array of proccessed listings, calculates a bbox
 * that encompasses all of them w/ a buffer of .25km.
 *
 * @param {LTSProcessedListing[]} listings - LTS listings
 * @returns {BBox} computed bbox
 */
export function computeBbox(searchResults: MapSearchResultsType): BBox {
  const listingsPoints = searchResults.map((listing: ProcessedListing | RelationCluster) => {
    if (listing && listing.location && listing.location.longitude && listing.location.latitude) {
      return [listing.location.longitude, listing.location.latitude] as Position;
    }

    return [];
  });
  const multiPt = multiPoint(listingsPoints);

  return bbox(multiPt);
}

export function getMapSearchQuery(
  listings: CollectionListing[],
  {bbox, containerSize}: {bbox: BBox; containerSize: ContainerSize}
) {
  const AllListingTypes = [
    ListingType.LISTING,
    ListingType.BUILDING,
    ListingType.SALES,
    ListingType.SHORT_TERM_RENTAL,
  ];
  const listingIdSHAs = listings.map((listing) => {
    const {listingData: {listingIdSHA = ''} = {}} = listing;

    return listingIdSHA;
  });
  const searchQuery = {
    listing_id_shas: listingIdSHAs,
    listingTypes: AllListingTypes,
    agent_search: true,
    availability: Availability.BOTH,
  };
  const convertedQuery = convertPythonQueryToThrift(searchQuery);
  const mapSearchQuery = {
    ...convertedQuery,
    rawQuery: '',
    debugLevel: 0,
    mapSearchProperties: {
      enableClusterSummary: true,
      mapContainerPixels: containerSize,
      boundingBox: getGeoBoundingBox(bbox),
    },
  };

  return mapSearchQuery;
}

export function useMapData(
  listings: CollectionListing[],
  mapOptions: {bbox: BBox; containerSize: ContainerSize}
) {
  const mapSearchQuery = useMemo(
    () => getMapSearchQuery(listings, mapOptions),
    [listings, mapOptions]
  );
  const {data} = useLoadMapSearch({
    enabled: listings.length > 0,
    mapSearchQuery,
  });
  const relationClusters = data?.relationClusters || [];

  return relationClusters.map(toClusterFeature);
}

export function bBoxesAreEqual(bBox1: BBox, bBox2: BBox): boolean {
  if (bBox1.length !== bBox2.length) {
    return false;
  }

  for (let i = 0; i < bBox1.length; i++) {
    if (bBox1[i] !== bBox2[i]) {
      return false;
    }
  }

  return true;
}

export function calculateMapProviderState(
  targetViewPort: ViewportSettings,
  mapSize: {
    width: number;
    height: number;
  }
) {
  const {center, zoomLevel, bbox} = targetViewPort;
  const containerSize = mapSize;
  let newCenter = center;
  let newZoomLevel = zoomLevel;

  if (containerSize && bbox) {
    newCenter = calculateMapCenter(bbox);
    newZoomLevel = getDiscreteZoomLevelForContainer(bbox, containerSize);
  }

  if (bbox) {
    newCenter = calculateMapCenter(bbox);
  }

  return {center: newCenter, zoomLevel: newZoomLevel};
}

// computeBbox(listings) will return [Infinity, Infinity, -Infinity, -Infinity]
// when the listings passed in Map is empty array.
// In this condition, calculateMapCenter will return NaN and the Map component will be crashed
// Use default center position when bbox is not available to confirm the Map component renders correctly
export const calculateMapCenter = (bbox: BBox) => {
  const result = [(bbox[0] + bbox[2]) / 2, (bbox[1] + bbox[3]) / 2];
  const isInValidData = result.some((num) => Number.isNaN(num));

  if (isInValidData) {
    return defaultMapData.center;
  }

  return result;
};
