/* eslint-disable no-restricted-imports */
import {Middleware} from 'redux';

import {CollectionMode} from '@src/constants';
import {StorageAdapter} from '@src/lib/StorageAdapter';

import {SET_VIEW, SET_PAGE_SIZE, RESET_STATE, SET_COLUMNS, SET_SHOW_LTS_BANNER} from './actions';
import {RootState, ColumnConfig} from './types';

// Note: Exported for testing only
export const mergeColumnConfigs = (
  oldColumnConfig: ColumnConfig,
  newColumnConfig: ColumnConfig
) => {
  Object.keys(newColumnConfig).forEach((tableTab) => {
    const newConfig = newColumnConfig[tableTab as CollectionMode] || [];
    const oldConfig = oldColumnConfig[tableTab as CollectionMode] || [];

    oldConfig.forEach((header, index) => {
      const headerIndexInNewConfig = newConfig.findIndex(({id}) => id === header.id);

      if (headerIndexInNewConfig === -1) {
        newConfig.splice(index, 0, header);
      }
    });

    newColumnConfig[tableTab as CollectionMode] = newConfig;
  });
};

export const createPersistMiddleware = (storage: StorageAdapter<Partial<RootState>>) => {
  const persist: Middleware<unknown, RootState> = (store) => (next) => (action) => {
    const result = next(action);

    if (action.type === SET_VIEW || action.type === SET_PAGE_SIZE) {
      const storedState = storage?.load() || {};

      storage.persist({
        ...storedState,
        currentView: store.getState().currentView,
        pageSizes: store.getState().pageSizes,
      });
    }

    if (action.type === SET_COLUMNS) {
      const storedState = storage?.load() || {};
      const {columnConfig} = store.getState();
      const newStoredState = {
        ...storedState,
        columnConfig,
      };

      if (storedState.columnConfig) {
        mergeColumnConfigs(storedState.columnConfig, newStoredState.columnConfig);
      }

      storage.persist(newStoredState);
    }

    if (action.type === SET_SHOW_LTS_BANNER) {
      const storedState = storage?.load() || {};
      const showLTSBanner = store.getState().showLTSBanner;

      storage.persist({
        ...storedState,
        showLTSBanner,
      });
    }

    if (action.type === RESET_STATE) {
      storage.clear();
    }

    return result;
  };

  return persist;
};
