import {CollectionListingReviewStage} from '@src/types';
import {TabCounts} from '@src/types';

export const getTabCounts = ({
  numListingsPerReviewStage,
  totalSearchResults,
  ltsTabCount,
}: {
  numListingsPerReviewStage?: {
    0: number;
    1: number;
    2: number;
    3: number;
  };
  totalSearchResults: number;
  ltsTabCount?: number;
}): TabCounts => {
  const unshared = totalSearchResults;
  const matches = numListingsPerReviewStage?.[CollectionListingReviewStage.REVIEW] || 0;
  const favorited = numListingsPerReviewStage?.[CollectionListingReviewStage.FAVORITE] || 0;
  const notInterested =
    numListingsPerReviewStage?.[CollectionListingReviewStage.NOT_INTERESTED] || 0;
  const shared = matches + favorited + notInterested;
  const trashed = numListingsPerReviewStage?.[CollectionListingReviewStage.TRASH] || 0;
  const likelyToSell = ltsTabCount ? ltsTabCount : 0;
  const tabCounters = {unshared, matches, favorited, notInterested, shared, trashed, likelyToSell};

  return tabCounters;
};
