import React, {useState} from 'react';

import {ltsStatusUpdated} from '@uc/analytics-definitions';
import {Button, Empty} from '@uc/cx.react';

import {LearnMore} from '@src/components/LikelyToSell/LearnMore';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {useAIRecommendations, useUpdateCurrentUrl, useUpdateLtsSetting} from '@src/hooks';
import {useCurrentUrlData} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';

import {EmptySearchFiltered} from './EmptySearchFiltered';
import {EmptySearchResults} from './EmptySearchResults';
import {EmptySharedFiltered} from './EmptySharedFiltered';
import styles from './EmptyState.module.pcss';

const EmptyShared = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">No shared listings</h1>
      <h4 className="textIntent-body">
        Add listings to share with your client and monitor activity.
      </h4>
    </div>
  );
};
const EmptyUnshared = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">No new listings to share</h1>
      <h4 className="textIntent-body">
        These listings are hidden from your client until you review and share them.
      </h4>
    </div>
  );
};
const EmptyTrash = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">No trashed listings</h1>
      <h4 className="textIntent-body">{"View listings that you've trashed here"}</h4>
    </div>
  );
};
const EmptyNotInterested = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">Not interested listings</h1>
      <h4 className="textIntent-body">{"Let your agent know which listings you don't like."}</h4>
    </div>
  );
};
const EmptyFavorites = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">Save your favorites</h1>
      <h4 className="textIntent-body">Keep track of your favorite listings.</h4>
    </div>
  );
};
const EmptyMatches = () => {
  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <h1 className="textIntent-headline1">No new matches</h1>
      <h4 className="textIntent-body">
        New results that match your search criteria will show up here.
      </h4>
    </div>
  );
};
const EmptyLikelyToSell = () => {
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const title = 'No matching likely to sell properties';
  const description = 'If likely to sell properties are found, they will be added here';
  const {collectionId} = useCurrentUrlData();
  const {data: collection} = useGetCollection({collectionId});
  const hideLikelyToSell = !!collection?.collection?.hideLikelyToSell;
  const updateLtsSetting = useUpdateLtsSetting().mutateAsync;
  const setParams = useUpdateCurrentUrl();
  const {data: aiData} = useAIRecommendations({
    collectionId,
    numOfItems: 1,
    skip: 0,
    getDeletedTasks: true,
  });
  const handleCloseButtonClick = () => {
    setIsOpen(false);
  };
  const handleTurnOffButtonClick = () => {
    updateLtsSetting({collectionId, hideLikelyToSell: true})
      .then(() => {
        setParams({collectionMode: COLLECTION_MODE.SHARED});
      })
      .then(() => {
        ltsStatusUpdated({
          product: 'collections',
          collection_id: collectionId,
          status: false,
        });
      });
  };

  return (
    <Empty
      title={title}
      description={description}
      className={styles.emptyState}
      data-testid="empty-state"
    >
      {aiData?.totalResults === 0 && !hideLikelyToSell ? (
        <Button
          className={styles.ltsEmptyStateSecondaryButton}
          variant="enclosed"
          onClick={handleTurnOffButtonClick}
          data-testid="ltsTurnOffButton"
        >
          Turn Off
        </Button>
      ) : null}
      <Button onClick={() => setIsOpen(true)}>What is Likely to Sell?</Button>
      <LearnMore isOpen={isOpen} handleCloseButtonClick={handleCloseButtonClick} />
    </Empty>
  );
};

export const EMPTY_STATE = {
  [COLLECTION_MODE.SHARED]: EmptyShared,
  [COLLECTION_MODE.UNSHARED]: EmptyUnshared,
  [COLLECTION_MODE.FAVORITES]: EmptyFavorites,
  [COLLECTION_MODE.MATCHES]: EmptyMatches,
  [COLLECTION_MODE.NOT_INTERESTED]: EmptyNotInterested,
  [COLLECTION_MODE.TRASH]: EmptyTrash,
  [COLLECTION_MODE.LIKELY_TO_SELL]: EmptyLikelyToSell,
} as const;

export const EmptyState = ({
  mode,
  hasFilters,
  hasSearchAddress,
}: {
  mode: CollectionMode;
  hasFilters?: boolean;
  hasSearchAddress?: boolean;
}) => {
  let Component = EMPTY_STATE[mode];

  if (hasFilters && hasSearchAddress) Component = EmptySearchFiltered;
  else if (hasFilters) Component = EmptySharedFiltered;
  else if (hasSearchAddress) Component = EmptySearchResults;

  return <Component />;
};
