import {useMutation, useQueryClient} from 'react-query';

import {readComments} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {
  UseGetCollectionListingsPaginatedResponse,
  useCollectionListingPaginatedQueryKey,
} from '@src/hooks/useGetCollectionListingsPaginated';
import {UnreadListingUpdates, CollectionListing} from '@src/types';

export function useReadListingComment() {
  const queryClient = useQueryClient();
  const queryKey = useCollectionListingPaginatedQueryKey();

  return useMutation(
    async ({collectionId, listingId}: {collectionId: string; listingId: string}) => {
      return readComments({
        collectionId,
        request: {
          listingIdSHA: listingId,
        },
      });
    },
    {
      onMutate: async ({listingId}) => {
        await queryClient.cancelQueries(queryKey);

        const previousCollectionListingsResponse =
          queryClient.getQueryData<UseGetCollectionListingsPaginatedResponse>(queryKey);

        if (previousCollectionListingsResponse) {
          queryClient.setQueryData<UseGetCollectionListingsPaginatedResponse>(queryKey, {
            ...previousCollectionListingsResponse,
            currentPageListings: previousCollectionListingsResponse.currentPageListings?.map(
              (collectionListing) => {
                if (collectionListing.listingId === listingId) {
                  const noUnreadComments: UnreadListingUpdates[] =
                    collectionListing.updatesUnreadByCurrentUser?.filter(
                      (update) => update !== UnreadListingUpdates.RECENT_COMMENTS
                    ) || [];
                  const modifiedListing: CollectionListing = {
                    ...collectionListing,
                    updatesUnreadByCurrentUser: noUnreadComments,
                  };

                  return modifiedListing;
                }

                return collectionListing;
              }
            ),
          });
        }
      },
      onSettled: () => {
        queryClient.invalidateQueries(API_KEY.BOARDS_BADGES_SUMMARY);
      },
    }
  );
}
