import {useMutation, useQueryClient} from 'react-query';

import {updateLtsSetting} from '@src/api';

export const useUpdateLtsSetting = () => {
  const client = useQueryClient();

  return useMutation(
    async ({collectionId, hideLikelyToSell}: {collectionId: string; hideLikelyToSell: boolean}) => {
      const result = await updateLtsSetting(collectionId, hideLikelyToSell);

      await client.invalidateQueries();

      return result;
    }
  );
};
