import classnames from 'classnames';
import React, {useRef} from 'react';

import {Button, Icon} from '@uc/cx.react';

import {RequestTourTip} from '@src/components/RequestTourTip';
import {useRefreshBoardQueryCache} from '@src/hooks';
import {useRequestShowing} from '@src/hooks/useRequestShowing';
import {CollectionListing} from '@src/types';
import {showingText, showingActionable} from '@src/utils/formatShowingStatus';

import styles from './RequestShowingButton.module.pcss';

export const RequestShowingButtonClient = ({
  collectionListing,
  collectionId,
}: {
  collectionListing: CollectionListing;
  collectionId: string;
}) => {
  const anchorRef = useRef<HTMLButtonElement>(null);
  const showText = showingText(collectionListing, false);
  const {mutate, isLoading} = useRequestShowing();
  const refreshQueryCache = useRefreshBoardQueryCache();
  const handleRequestShowing = (collectionListing: CollectionListing) => {
    return (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
      e.stopPropagation();
      if (isLoading) return;

      mutate(
        {collectionListing, collectionId},
        {
          onSuccess() {
            refreshQueryCache();
          },
        }
      );
    };
  };
  const canRequestTour = Boolean(collectionListing?.canRequestTour);

  return showingActionable(collectionListing) ? (
    <>
      <RequestTourTip collectionListing={collectionListing} alignment="bottom" tipYPosition={-10}>
        <Button
          ref={anchorRef}
          className={`${styles.button} ${styles.colorBlue}`}
          icon="MapRoute"
          variant="naked"
          onClick={handleRequestShowing(collectionListing)}
          data-testid="requestShowingButton"
          disabled={!canRequestTour}
        >
          {showText}
        </Button>
      </RequestTourTip>
    </>
  ) : (
    <>
      <span
        ref={anchorRef}
        className={classnames([
          'cx-nakedBtn',
          `${styles.button} ${styles.visitedText} ${styles.colorBlue}`,
        ])}
        data-testid="requestShowingSpan"
      >
        <Icon.MapRoute className={styles.iconMapRoute} />
        {showText}
      </span>
    </>
  );
};
