import React from 'react';

import {Button, Empty} from '@uc/cx.react';

import {useIsAgent} from '@src/store';

import styles from './EmptyState.module.pcss';

export const CollectionNotFound = () => {
  const isAgent = useIsAgent();
  const redirectUrl = isAgent ? '/app/workspaces/#!/collections' : '/app/collection';

  return (
    <div className={styles.emptyState} data-testid="empty-state" style={{flexGrow: 1}}>
      <Empty />
      <p className="cx-text-body1Regular" style={{textAlign: 'center', marginTop: '-15px'}}>
        This Collection does not exist.
      </p>
      <Button
        role="link"
        variant="enclosed--highEmph"
        onClick={() => {
          window.location.href = redirectUrl;
        }}
        style={{marginTop: '20px'}}
      >
        Go to Collections
      </Button>
    </div>
  );
};
