import {useQuery} from 'react-query';

import {getBoards} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';

export const useBoards = ({
  excludeMosaic,
  excludeComments,
  limit,
  skip,
}: {
  excludeMosaic?: boolean;
  excludeComments?: boolean;
  limit?: number;
  skip?: number;
}) => {
  return useQuery([API_KEY.GET_BOARDS, excludeMosaic, excludeComments, limit, skip], () =>
    getBoards({excludeMosaic, excludeComments, limit, skip})
  );
};
