import classNames from 'classnames';
import groupBy from 'lodash/groupBy';
import React, {useCallback, useState, useRef} from 'react';

import {Button, Icon, Flyout} from '@uc/cx.react';

// eslint-disable-next-line no-restricted-imports
import {searchSuggest} from '@src/api';
import {ListingType, EntityInfo} from '@src/types';

import {testSelectors} from './testSelectors';

import './AddressSearch.pcss';

export interface AddressOption {
  address: string;
  listings: EntityInfo[];
}

interface Properties {
  [key: string]: EntityInfo[];
}

interface AddressSearchProps {
  initialSelected?: AddressOption;
  onChange?: (listing?: AddressOption) => void;
}

const NO_SELECTION_INDEX = -1;
const search = async (query: string, geoId?: string): Promise<AddressOption[]> => {
  const data = await searchSuggest({
    q: query,
    sources: ['addresses'],
    listingTypes: [ListingType.SALES, ListingType.LISTING, ListingType.SHORT_TERM_RENTAL],
    allGeos: false,
    allowDupes: true,
    geography: geoId,
  });
  const properties = groupBy(data.addressesInfo, (listing) => {
    return `${listing.address} ${listing.zipCode}`;
  });

  return Object.entries(properties).map(([address, listings]) => ({
    address,
    listings,
  }));
};

export const AddressSearch = ({
  initialSelected,
  onChange = () => undefined,
}: AddressSearchProps) => {
  const anchorRef = useRef<HTMLDivElement>(null);
  const [keyword, setKeyword] = useState(initialSelected?.address ?? '');
  const [resultList, setResultList] = useState<AddressOption[]>([]);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(NO_SELECTION_INDEX);
  const onDismissHandler = useCallback(() => {
    setIsMenuOpen(false);
    anchorRef?.current?.focus();
  }, []);
  const onFocusHandler = useCallback((): void => {
    setIsMenuOpen(keyword !== '' && resultList.length > 0);
  }, [keyword, resultList]);
  const onClearKeyword = useCallback((): void => {
    setKeyword('');
    setResultList([]);
    onDismissHandler();
    onChange();
  }, [onDismissHandler, onChange]);
  const handleSelect = useCallback(
    (index: number): void => {
      const selected: AddressOption | undefined = resultList[index];

      setSelectedIndex(index);
      setKeyword(selected?.address);
      onChange(selected);
      onDismissHandler();
    },
    [onChange, onDismissHandler, resultList]
  );
  const onChangeHandler = useCallback(
    async (e: React.ChangeEvent<HTMLInputElement>): Promise<void> => {
      const value = e.target.value;

      setKeyword(value);
      setSelectedIndex(NO_SELECTION_INDEX);

      if (value) {
        const result = await search(value, window.uc?.geoId);

        if (result) {
          setResultList(result);
          setIsMenuOpen(true);
        }
      } else {
        setResultList([]);
        setIsMenuOpen(false);
      }
    },
    []
  );

  return (
    <div className="cx-formElement googleSheetCmaModal-addressSearch">
      <div className="googleSheetCmaModal-searchBoxInput" ref={anchorRef}>
        <Icon.MagnifyingGlass className="googleSheetCmaModal-searchIcon" />
        <input
          className="cx-textField cx-textField--sm"
          type="text"
          placeholder="Search for an address"
          value={keyword}
          title={keyword}
          onChange={onChangeHandler}
          onFocus={onFocusHandler}
          onClick={onFocusHandler}
          data-testid={testSelectors.addressSearchInput}
        />
        {keyword && (
          <Button
            className="googleSheetCmaModal-clearSearchBtn"
            icon="CircleX"
            variant="naked"
            onClick={onClearKeyword}
            data-testid={testSelectors.clearInputAddressBtn}
          />
        )}
      </div>
      <Flyout
        anchor={null}
        isOpen={isMenuOpen}
        onDismiss={onDismissHandler}
        data-testid={testSelectors.addressOptionsMenu}
      >
        <ul className="cx-optionsMenu cx-optionsMenu--lg cx-flyoutContainer">
          {resultList.map((li, index) => (
            <li
              key={li.address}
              className={classNames('cx-optionsMenu-item', {
                'is-selected': index === selectedIndex,
              })}
            >
              <button
                className="collection-button-no-style"
                data-testid={testSelectors.addressOption(li.address)}
                onClick={() => handleSelect(index)}
              >
                {li.address}
              </button>
            </li>
          ))}
        </ul>
      </Flyout>
    </div>
  );
};
