import {useEffect, useState, useCallback, useMemo} from 'react';

const NEXT = 'NEXT';
const PREV = 'PREV';

type TransitionDirection = typeof NEXT | typeof PREV;

export const usePageItemNavigation = ({
  currentPage,
  pageIds,
  pageSize,
  activeId: initialActiveId,
  totalItems,
  isPageLoading,
  closeNavigation,
  goToNextPage,
  goToPreviousPage,
  setActiveId: setActiveIdExternally,
  isDisabled,
}: {
  currentPage: number;
  pageIds?: string[];
  activeId?: string;
  totalItems?: number;
  pageSize: number;
  isPageLoading: boolean;
  goToNextPage: () => void;
  goToPreviousPage: () => void;
  setActiveId: (id: string) => void;
  closeNavigation?: () => void;
  isDisabled: boolean;
}) => {
  const [lastPagePosition, setLastPagePosition] = useState<number | undefined>();
  const [lastValidPagePosition, setLastValidPagePosition] = useState<number | undefined>();
  const [transitionDirection, setTransitionDirection] = useState<TransitionDirection | undefined>();
  // Use state to prevent race conditions that might arrive if external setActiveId function has latency
  const [activeId, setActiveIdInternally] = useState<string | undefined>(initialActiveId);
  const idsString = JSON.stringify(pageIds);
  const setActiveId = useCallback(
    (id: string) => {
      setActiveIdInternally(id);
      setActiveIdExternally(id);
    },
    [setActiveIdExternally, setActiveIdInternally]
  );
  const positionInCurrentPage = useMemo(() => {
    if (pageIds && activeId && !isPageLoading && !transitionDirection) {
      const position = pageIds.indexOf(activeId);

      return position;
    }

    return lastPagePosition;
  }, [activeId, pageIds, isPageLoading, transitionDirection, lastPagePosition]);
  const isPaginationDisabled = useMemo(() => {
    if (isDisabled || isPageLoading) {
      return true;
    }

    if (positionInCurrentPage !== undefined && positionInCurrentPage > -1) {
      return false;
    }

    // In edge case where the selected element is not found on the current page,
    // for example if the page is reloaded with old url parameters, show the same
    // item, but disable pagination because we no longer know where that element is
    return true;
  }, [isDisabled, isPageLoading, positionInCurrentPage]);
  const activeIndex = useMemo(() => {
    if (isDisabled) return 0;
    // If transition is in progress, return the next active id
    if (transitionDirection === NEXT) return 0;
    if (transitionDirection === PREV) return pageSize - 1;

    if (positionInCurrentPage !== undefined && positionInCurrentPage > -1) {
      return positionInCurrentPage;
    }

    return 0;
  }, [transitionDirection, positionInCurrentPage, pageSize, isDisabled]);
  const onNext = useCallback(() => {
    if (pageIds && activeIndex < pageIds.length - 1) {
      const nextId = pageIds[activeIndex + 1];

      setActiveId(nextId);
    } else {
      setTransitionDirection(NEXT);
      goToNextPage();
    }
  }, [pageIds, activeIndex, goToNextPage, setActiveId, setTransitionDirection]);
  const onPrevious = useCallback(() => {
    if (activeIndex === 0) {
      setTransitionDirection(PREV);
      goToPreviousPage();
    } else if (pageIds) {
      const previousId = pageIds[activeIndex - 1];

      setActiveId(previousId);
    }
  }, [pageIds, activeIndex, goToPreviousPage, setActiveId, setTransitionDirection]);

  // When page loads finishes and a transition is in progress, set next active id
  // based on the which direction the transition happened
  useEffect(() => {
    if (pageIds && !isPageLoading && transitionDirection) {
      if (transitionDirection === NEXT) {
        setActiveId(pageIds[0]);
      } else if (transitionDirection === PREV) {
        setActiveId(pageIds[pageIds.length - 1]);
      }

      setTransitionDirection(undefined);
    }
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [idsString, transitionDirection]);

  // If we can't find the activeId on the page, but a previous active id was on the page
  // it is likely because the element was deleted.  In this case, use the last index to determine
  // which id should be active
  useEffect(() => {
    if (isPageLoading || transitionDirection) {
      return;
    }

    if (
      pageIds &&
      lastPagePosition !== undefined &&
      lastPagePosition !== -1 &&
      positionInCurrentPage === -1
    ) {
      const nextIndex =
        lastValidPagePosition !== undefined && lastValidPagePosition < pageIds.length
          ? lastValidPagePosition
          : pageIds.length - 1;

      setActiveId(pageIds[nextIndex]);
    }

    if (positionInCurrentPage !== undefined) {
      setLastPagePosition(positionInCurrentPage);
    }

    if (positionInCurrentPage !== undefined && positionInCurrentPage > -1) {
      setLastValidPagePosition(positionInCurrentPage);
    }
  }, [
    pageIds,
    positionInCurrentPage,
    setActiveId,
    lastValidPagePosition,
    setLastPagePosition,
    isPageLoading,
    transitionDirection,
    lastPagePosition,
  ]);

  const onEmptyPage = () => {
    if (isDisabled) {
      // Don't take action if pagination is disabled.  When pagination is disabled,
      // the modal might be on a page without pagination results, so an empty pageIds
      // might be expected
      return;
    }

    if (isPageLoading || transitionDirection) {
      // If the page is loading or transitioning, pageIds might temporarily be empty.
      // Don't take action in this case until operation finishes
      return;
    }

    if (pageIds && pageIds.length === 0) {
      if (totalItems === 0) {
        closeNavigation && closeNavigation();
      } else {
        onPrevious();
      }
    }
  };

  useEffect(onEmptyPage, [
    isDisabled,
    isPageLoading,
    transitionDirection,
    pageIds,
    totalItems,
    closeNavigation,
    onPrevious,
  ]);

  const paginationTotal = isPaginationDisabled ? 1 : totalItems;
  const absoluteActiveIndex = isPaginationDisabled ? 0 : (currentPage - 1) * pageSize + activeIndex;
  const shouldDisableNext =
    isPageLoading || !paginationTotal || absoluteActiveIndex >= paginationTotal - 1;
  const shouldDisablePrevious = isPageLoading || absoluteActiveIndex === 0;
  const isIdNewlyInvalid =
    !isDisabled && positionInCurrentPage === -1 && lastValidPagePosition !== undefined;
  const shouldDisplayLoading = Boolean(isPageLoading || transitionDirection || isIdNewlyInvalid);

  return {
    activeIndex: absoluteActiveIndex,
    paginationTotal,
    activeId: activeId,
    isPageLoading: shouldDisplayLoading,
    shouldDisableNext,
    shouldDisablePrevious,
    onNext,
    onPrevious,
  };
};
