import classnames from 'classnames';
import React, {useEffect, useState} from 'react';

import loadModal from '@uc/modal-loader';

import {CollectionMode, CONFIRMATION_MODAL_URI} from '@src/constants';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useRemoveLinkedSearch} from '@src/hooks/useRemoveLinkedSearch';
import {useDispatch, useCurrentUser, setToast} from '@src/store';
import {CollectionLinkedSearch} from '@src/types';

import {MultipleLinkedSearches} from './MultipleLinkedSearches';
import {SingleLinkedSearch} from './SingleLinkedSearch';
import './index.pcss';

export const LinkedSearches = ({
  isAgent,
  mode,
  savedSearches = [],
}: {
  isAgent: boolean;
  mode: CollectionMode;
  savedSearches: CollectionLinkedSearch[];
}) => {
  const currentUser = useCurrentUser();
  const dispatch = useDispatch();
  const removeLinkedSearch = useRemoveLinkedSearch();
  const setParams = useUpdateCurrentUrl();
  const [currentSavedSearches, setCurrentSavedSearches] = useState(savedSearches);
  const currentUrlParams = useCurrentUrlData();
  const savedSearchId = currentUrlParams?.savedSearchId;
  const currentSavedSearchId = savedSearchId || savedSearches[0]?.id || undefined;

  useEffect(() => {
    setParams({savedSearchId: currentSavedSearchId});
  }, [currentSavedSearchId, setParams]);

  const deleteSearchCriteria = async ({
    savedSearch,
    currentSavedSearchId,
  }: {
    savedSearch: CollectionLinkedSearch;
    currentSavedSearchId: string;
  }) => {
    loadModal(CONFIRMATION_MODAL_URI, {
      bdText: 'No new listings from this search will appear in this collection.',
      hdText: 'Delete this Saved Search?',
      noText: 'Cancel',
      yesText: 'Delete',
      danger: true,
    })
      .then(async (): Promise<void> => {
        try {
          await removeLinkedSearch(savedSearch.id || '');
        } catch (e) {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Unable to delete Saved Search.',
            })
          );
          return;
        }

        const updatedSavedSearches = currentSavedSearches.filter(
          (currentSs) => currentSs !== savedSearch
        );

        setCurrentSavedSearches(updatedSavedSearches);

        if (savedSearch.id === currentSavedSearchId) {
          setParams({savedSearchId: updatedSavedSearches[0].id});
        } else {
          setParams({savedSearchId: currentSavedSearchId});
        }

        dispatch(
          setToast({
            isOpen: true,
            variant: 'info',
            message: 'Saved Search has been deleted.',
          })
        );
      })
      .catch((e) => {
        if (typeof e !== 'undefined' && e !== 'user clicked "Cancel"') {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Unable to delete Saved Search.',
            })
          );
        }
      });
  };

  return (
    <div
      className={classnames([
        'collection-header-search-criteria',
        currentUser._id === currentSavedSearches[0]?.ownerId ? '' : 'not-owner',
      ])}
    >
      {currentSavedSearches.length === 1 && (
        <SingleLinkedSearch savedSearch={currentSavedSearches[0]} isAgent={isAgent} />
      )}
      {currentSavedSearches.length > 1 && currentSavedSearchId && (
        <MultipleLinkedSearches
          mode={mode}
          currentSavedSearchId={currentSavedSearchId}
          savedSearches={currentSavedSearches}
          isAgent={isAgent}
          deleteSearchCriteria={deleteSearchCriteria}
        />
      )}
    </div>
  );
};
