import React from 'react';

import {Button, Empty} from '@uc/cx.react';

import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';

import {FILTER_OPTION_LABELS} from '../Filters/filterUtils';
import styles from './EmptyState.module.pcss';

export const EmptySearchFiltered = () => {
  const urlData = useCurrentUrlData();
  const {filterId} = urlData;
  const setParams = useUpdateCurrentUrl();
  const filterPillLabel = filterId ? `in ${FILTER_OPTION_LABELS[filterId]}` : '';

  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <Empty />
      <p className="cx-text-header4"> No Results {filterPillLabel} </p>
      <p className="cx-text-body1Regular" style={{textAlign: 'center', marginTop: '-15px'}}>
        Clear search or remove filters to see more results.
      </p>
      <Button
        variant="enclosed--highEmph"
        onClick={() =>
          setParams({
            address: '',
            filterId: 'all',
            filterByOpenHouses: undefined,
            filterByListingStatus: undefined,
          })
        }
        style={{marginTop: '20px'}}
      >
        Clear Filters
      </Button>
    </div>
  );
};
