import React, {useEffect, useState} from 'react';

import {Button} from '@uc/cx.react';

import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {
  UpdateBoardListingsReviewStageAPIResponse,
  CollectionListingReviewStage,
  CollectionListing,
} from '@src/types';
import {getNotInterestedIcon} from '@src/utils/getNotInterestedIcon';
import {isNotInterestedIconWebAbTestEnabled} from '@src/utils/optyUtils';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import './FavoriteAndNotInterested.pcss';

export interface FavoriteAndNotInterestedProps {
  isMobile?: boolean;
  collectionId: string;
  collectionListing: CollectionListing;
  onFavoriteListing: () =>
    | Promise<UpdateBoardListingsReviewStageAPIResponse | undefined>
    | undefined;
  onMarkAsNotInterested: () =>
    | Promise<UpdateBoardListingsReviewStageAPIResponse | undefined>
    | undefined;
}

const getIsFavorited = (reviewStage: CollectionListingReviewStage | undefined) => {
  return reviewStage === CollectionListingReviewStage.FAVORITE;
};
const getIsNotInterested = (reviewStage: CollectionListingReviewStage | undefined) => {
  return reviewStage === CollectionListingReviewStage.NOT_INTERESTED;
};

export const FavoriteAndNotInterested = ({
  isMobile,
  collectionId,
  collectionListing,
  onFavoriteListing,
  onMarkAsNotInterested,
}: FavoriteAndNotInterestedProps) => {
  const reviewStage = collectionListing.reviewStage?.currentStage;
  const [isFavoriteDisabled, updateIsFavoriteDisabled] = useState(getIsFavorited(reviewStage));
  const [isNotInterestedDisabled, updateIsNotInterestedDisabled] = useState(
    getIsNotInterested(reviewStage)
  );
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const handleOnClick = (e: React.MouseEvent<HTMLElement>) => {
    const targetButton = e.currentTarget.dataset.value;

    if (targetButton === 'favorite') {
      trackCollectionListingFlowAction({
        actionName: 'Favorite',
        productPath: PRODUCT_PATH.LISTING_PAGE,
      });
      updateIsFavoriteDisabled(true);
      updateIsNotInterestedDisabled(false);
      onFavoriteListing()?.catch(() => {
        updateIsFavoriteDisabled(false);
        updateIsNotInterestedDisabled(true);
      });
    }

    if (targetButton === 'notInterested') {
      trackCollectionListingFlowAction({
        actionName: 'Not Interested',
        productPath: PRODUCT_PATH.LISTING_PAGE,
        ab_test: isNotInterestedIconWebAbTestEnabled(),
        ab_test_version: getNotInterestedIcon(),
      });
      updateIsNotInterestedDisabled(true);
      updateIsFavoriteDisabled(false);
      onMarkAsNotInterested()?.catch(() => {
        updateIsNotInterestedDisabled(false);
        updateIsFavoriteDisabled(true);
      });
    }
  };

  useEffect(() => {
    updateIsFavoriteDisabled(getIsFavorited(reviewStage));
    updateIsNotInterestedDisabled(getIsNotInterested(reviewStage));
  }, [reviewStage]);

  return (
    <>
      <Button
        icon={isNotInterestedDisabled ? 'ThumbsDownFilled' : 'ThumbsDownOutlined'}
        isCircle={!isMobile}
        variant={isMobile ? 'naked' : 'enclosed'}
        className={`footer-notInterestedBtn ${isNotInterestedDisabled ? ' is-disabled' : ''}`}
        data-value="notInterested"
        data-testid="notInterestedBtn"
        disabled={isNotInterestedDisabled}
        onClick={handleOnClick}
        aria-label="Not Interested"
      />
      <Button
        /* For some reason tsLint insists the icons below don't exist */
        /* @ts-ignore */
        icon={isFavoriteDisabled ? 'StarFilled' : 'StarOutlined'}
        isCircle={!isMobile}
        variant={isMobile ? 'naked' : 'enclosed'}
        className={`footer-favoriteBtn ${isFavoriteDisabled ? ' is-disabled' : ''}`}
        data-value="favorite"
        data-testid="favoriteBtn"
        disabled={isFavoriteDisabled}
        onClick={handleOnClick}
        aria-label="Favorite"
      />
    </>
  );
};
