import React, {useState, useEffect} from 'react';
import {Link, useLocation} from 'react-router-dom';

import {Icon} from '@uc/cx.react';

import {CollectionMode} from '@src/constants';

import styles from './index.module.pcss';

export const InfoBox = ({isCurate, mode}: {isCurate?: boolean; mode: CollectionMode}) => {
  const [infoText, setInfoText] = useState('');
  const [showSetting, setShowSetting] = useState(false);
  const location = useLocation();

  useEffect(() => {
    if (isCurate) {
      switch (mode) {
        case 'UNSHARED':
          setInfoText(`Review and share listings with your client. `);
          setShowSetting(true);
          break;
        case 'TRASH':
          setInfoText(`These listings are hidden from your client. `);
          setShowSetting(false);
          break;
        default:
          setInfoText('');
          break;
      }
    } else {
      switch (mode) {
        case 'SHARED':
          setInfoText(`Search results are automatically shared with your client. `);
          setShowSetting(true);
          break;
        case 'TRASH':
          setInfoText(`These listings are hidden from your client. `);
          setShowSetting(false);
          break;
        default:
          setInfoText('');
          break;
      }
    }
  }, [isCurate, mode]);
  return infoText === '' ? null : (
    <div data-testid="infoBox" className={styles.infoBox}>
      <Icon.CircleInfo />
      <p className={styles.infoText}>{infoText}</p>
      {showSetting ? (
        <Link
          to={{
            pathname: `${location.pathname}/settings`,
            state: {modal: location},
            search: location.search,
          }}
          className={styles.link}
        >
          Edit Settings
        </Link>
      ) : null}
    </div>
  );
};
