export class StorageAdapter<T> {
  private store = window.localStorage;
  constructor(private key: string) {}

  load(): Partial<T> {
    try {
      return JSON.parse(this.store.getItem(this.key) || '{}');
    } catch (err) {
      this.clear();
    }

    return {};
  }

  clear() {
    try {
      this.store.removeItem(this.key);
    } catch (err) {
      /* noop */
    }
  }

  persist(data: T) {
    try {
      this.store.setItem(this.key, JSON.stringify(data));
    } catch (err) {
      this.clear();
    }
  }
}
