import {useGetCollectionUsers} from './useGetCollectionUsers';

export const useIsCollectionShared = ({collectionId}: {collectionId: string | undefined}) => {
  const {data: collectionUsers, isLoading} = useGetCollectionUsers({
    collectionId: collectionId || '',
  });

  if (!collectionId) {
    return {isCollectionShared: false, isLoading};
  }

  if (
    // eslint-disable-next-line @typescript-eslint/no-extra-parens
    (collectionUsers?.users?.length && collectionUsers?.users?.length > 1) ||
    collectionUsers?.sharedCollaborators?.length
  ) {
    return {isCollectionShared: true, isLoading};
  }

  return {isCollectionShared: false, isLoading};
};
