import classnames from 'classnames';
import React, {useRef} from 'react';
import {NavLink} from 'react-router-dom';

import {Icon} from '@uc/cx.react';

import {CollectionMode, COLLECTION_MODE, COLLECTION_MODE_TO_TAB} from '@src/constants';
import {useBoardLink} from '@src/hooks';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useGetCollectionListingsSummary} from '@src/hooks/useGetCollectionListingsSummary';
import {useIsCollectionShared} from '@src/hooks/useIsCollectionShared';
import {getCurrentNav, useSelector, useSource} from '@src/store';
import {CollectionListingReviewStage, TabCounts} from '@src/types';
import {isAIBuyerMatchingInCollectionsEnabled} from '@src/utils/optyUtils';

import {RefreshButton} from './RefreshButton';
import styles from './Tabs.module.pcss';

interface LiLinkProps {
  title: string;
  count?: number;
  mode: CollectionMode;
  collectionId: string;
  savedSearchId?: string;
  isNoticeIcon?: boolean;
}

const LiLink = React.forwardRef<HTMLAnchorElement, LiLinkProps>(
  ({title, mode, count, collectionId, savedSearchId, isNoticeIcon = false}: LiLinkProps, ref) => {
    const source = useSource();
    const location = useBoardLink({collectionId, mode, savedSearchId, source});
    const tab = COLLECTION_MODE_TO_TAB[mode];

    return (
      <li>
        <NavLink
          ref={ref}
          exact
          role="tab"
          className={classnames(['cx-tab', styles.linkWrapper])}
          activeClassName="cx-tab--selected"
          data-testid={`link-${tab}`}
          to={location}
        >
          {isNoticeIcon && (
            <span className={styles.noticeIcon} data-testid="notice-icon">
              <Icon.WarningTriangle className="cx-btn-icon" />
            </span>
          )}
          {`${title} (${count == null ? '-' : count})`}
        </NavLink>
      </li>
    );
  }
);

LiLink.displayName = 'LiLink';

interface TabsProps {
  isAgent: boolean;
  collectionId: string;
  savedSearchId?: string;
  tabCounts?: TabCounts;
}

export const AgentTabs = ({collectionId, tabCounts, savedSearchId}: TabsProps) => {
  const {data: {collection = {}} = {}} = useGetCollection({collectionId});
  const {savedSearches = [], isCurate = false, hideLikelyToSell = false} = collection;
  const hasSavedSearch = savedSearches.length > 0;
  const {isCollectionShared: hasInvitees, isLoading} = useIsCollectionShared({
    collectionId,
  });

  return (
    <>
      {isCurate && (
        <LiLink
          title="Unshared"
          count={tabCounts?.unshared}
          mode={COLLECTION_MODE.UNSHARED}
          collectionId={collectionId}
          savedSearchId={savedSearchId}
        />
      )}
      <LiLink
        title="Shared"
        count={tabCounts?.shared}
        mode={COLLECTION_MODE.SHARED}
        collectionId={collectionId}
        savedSearchId={savedSearchId}
        isNoticeIcon={!isLoading && !hasInvitees}
      />
      <LiLink
        title="Trash"
        count={tabCounts?.trashed}
        mode={COLLECTION_MODE.TRASH}
        collectionId={collectionId}
        savedSearchId={savedSearchId}
      />
      {!!(
        hasSavedSearch &&
        isAIBuyerMatchingInCollectionsEnabled() &&
        !hideLikelyToSell &&
        tabCounts?.likelyToSell
      ) && (
        <LiLink
          title="Likely to Sell"
          count={tabCounts?.likelyToSell}
          mode={COLLECTION_MODE.LIKELY_TO_SELL}
          collectionId={collectionId}
          savedSearchId={savedSearchId}
        />
      )}
    </>
  );
};

export const ConsumerTabs = ({collectionId, tabCounts, savedSearchId}: TabsProps) => {
  const linkRef = useRef<HTMLAnchorElement>(null);
  const {summary = {}} = useGetCollectionListingsSummary({collectionId}).data || {};
  const matches = summary.numListingsPerReviewStage?.[CollectionListingReviewStage.REVIEW] || 0;
  const hasMatches = matches > 0;

  return (
    <>
      {hasMatches && (
        <LiLink
          title="Matches"
          count={tabCounts?.matches}
          mode={COLLECTION_MODE.MATCHES}
          collectionId={collectionId}
          savedSearchId={savedSearchId}
        />
      )}
      <LiLink
        ref={linkRef}
        title="Favorites"
        count={tabCounts?.favorited}
        mode={COLLECTION_MODE.FAVORITES}
        collectionId={collectionId}
        savedSearchId={savedSearchId}
      />
      <LiLink
        title="Not Interested"
        count={tabCounts?.notInterested}
        mode={COLLECTION_MODE.NOT_INTERESTED}
        collectionId={collectionId}
        savedSearchId={savedSearchId}
      />
    </>
  );
};

export const TAB_ACTIONS_TEST_ID = 'collections-tabs-bar-actions';

const Actions = () => {
  const {isDesktop} = useDeviceWidth();
  const params = useSelector(getCurrentNav);

  if (!params?.collectionId || !isDesktop) {
    return null;
  }

  return (
    <div className={styles.collectionSecondaryActions} data-testid={TAB_ACTIONS_TEST_ID}>
      <RefreshButton collectionId={params.collectionId} />
    </div>
  );
};

export const Tabs = (props: TabsProps): React.ReactElement => {
  const tabs = props.isAgent ? <AgentTabs {...props} /> : <ConsumerTabs {...props} />;

  return (
    <ul
      className={classnames([
        'cx-tabs',
        styles.collectionTabs,
        {[styles.isConsumer]: !props.isAgent},
      ])}
      role="tablist"
      aria-label="Navigation"
    >
      {tabs}
      {props.isAgent && <Actions />}
    </ul>
  );
};
