import uniqBy from 'lodash/uniqBy';
import {useEffect, useRef} from 'react';

import {loadPanel, PanelSize} from '@uc/panel-loader';

import {SIDE_PANEL_IDS} from '@src/constants';
import {
  getSidePanel,
  setSidePanel,
  useSelector,
  useDispatch,
  useSelectedListings,
} from '@src/store';
import {
  CollectionUser,
  CollectionSharedCollaborator,
  ApiPerson,
  ProcessedListing,
} from '@src/types';

import {useCurrentUrlData} from './url';
import {useGetCollectionListingsPaginated} from './useGetCollectionListingsPaginated';
import {useGetCollectionUsers} from './useGetCollectionUsers';

const TOURS_PANEL_URI = 'tours.react/1.14';

interface Panel {
  EventsIn: {ON_LISTINGS_UPDATE: string};
  EventsOut: {ON_DESTROY: string};
  addListener: (eventName: string, cb: () => void) => void;
  sendEvent: (eventName: string, data: unknown) => void;
}

export const useAddToTour = (parent: HTMLElement | null) => {
  const internalRef = useRef(null) as {current: Panel | null};
  const dispatch = useDispatch();
  const sidePanel = useSelector(getSidePanel);
  const {collectionId} = useCurrentUrlData();
  const {collectionListings} = useGetCollectionListingsPaginated();
  const {selectedProcessedListings} = useSelectedListings();
  const {data: {users = [], sharedCollaborators = []} = {}} = useGetCollectionUsers({collectionId});
  const clients: ApiPerson[] = uniqBy(
    [...users, ...sharedCollaborators].map(
      (collaboratorOrUser: CollectionUser | CollectionSharedCollaborator) => {
        const {userId: personId = ''} = collaboratorOrUser;
        const {
          firstName = '',
          lastName = '',
          emailAddress: email = '',
          imagePath: avatarUrl = '',
        } = collaboratorOrUser.person || {};
        const client: ApiPerson = {
          personId,
          firstName,
          lastName,
          email,
          avatarUrl,
        };

        return client;
      }
    ),
    'personId'
  );

  // update the selected listings to panel
  useEffect(() => {
    const panel = internalRef.current;

    if (!panel) return;

    const {ON_LISTINGS_UPDATE} = panel.EventsIn;

    panel.sendEvent(ON_LISTINGS_UPDATE, selectedProcessedListings);
  }, [selectedProcessedListings]);

  // toggle show panel
  useEffect(() => {
    if (!parent || sidePanel !== SIDE_PANEL_IDS.tour) return;

    const panelParams: {listings: ProcessedListing[]; clients: ApiPerson[]; collectionId: string} =
      {
        listings: selectedProcessedListings,
        clients,
        collectionId,
      };

    loadPanel(TOURS_PANEL_URI, {
      parentEl: parent,
      size: PanelSize.XL,
      data: panelParams,
    }).then((panel: Panel) => {
      internalRef.current = panel;
      const {ON_DESTROY} = panel.EventsOut;

      panel.addListener(ON_DESTROY, () => {
        internalRef.current = null;
        dispatch(setSidePanel(undefined));
      });
    });
  }, [
    parent,
    sidePanel,
    collectionListings,
    clients,
    collectionId,
    dispatch,
    selectedProcessedListings,
  ]);
};
