import React, {useRef, useLayoutEffect, useState} from 'react';

import {UcLcTitle} from '@uc/listing-card-components.react';

import {ProcessedListingCard, CollectionListing, UnreadListingUpdates} from '@src/types';

import styles from './ListingCardTitleWithNewMatchBadge.module.pcss';

const LONG_BADGE_TEXT = 'New match';
const SHORT_BADGE_TEXT = 'New';

export const ListingCardTitleWithNewMatchBadge = ({
  listing,
  showNewMatchBadge,
}: {
  listing: ProcessedListingCard;
  showNewMatchBadge?: boolean;
}) => {
  const [newMatchesText, setNewMatchesText] = useState(LONG_BADGE_TEXT);
  const titleSlotElement = useRef<HTMLDivElement>(null);
  const priceElement = useRef<HTMLDivElement>(null);

  useLayoutEffect(() => {
    const setBadgeTextByWidth = () => {
      const cardElement = titleSlotElement.current?.parentElement;
      const cardWidth = cardElement?.offsetWidth || 0;
      const actionSlotElement = cardElement?.getElementsByClassName(
        'listing-card__actions-container'
      )[0];
      const actionSlotWidth = actionSlotElement
        ? (actionSlotElement as HTMLDivElement).offsetWidth
        : 0;
      const spaceOfTitleSlot = cardWidth - actionSlotWidth;
      const priceWidth = priceElement.current?.offsetWidth || 0;
      const longBadgeTextWidth = 96;
      const canPutLongBadge = spaceOfTitleSlot - priceWidth >= longBadgeTextWidth;

      setNewMatchesText(canPutLongBadge ? LONG_BADGE_TEXT : SHORT_BADGE_TEXT);
    };
    const cardElement = titleSlotElement.current?.parentElement;
    const resizeObserver = new ResizeObserver(setBadgeTextByWidth);

    if (cardElement) {
      resizeObserver.observe(cardElement);
    }

    return () => {
      if (cardElement) {
        resizeObserver.unobserve(cardElement);
      }
    };
  }, []);

  return (
    <div slot="title" ref={titleSlotElement} className={styles.wrapper}>
      <div ref={priceElement}>{listing?.title && <UcLcTitle data={listing}></UcLcTitle>}</div>
      {showNewMatchBadge && (
        <div className={styles.badge}>
          <span className={styles.text}>{newMatchesText}</span>
        </div>
      )}
    </div>
  );
};

export const isNewMatch = (collectionListing: CollectionListing) => {
  const hasNewListingBeenSeenByCurrentUser = collectionListing.updatesUnreadByCurrentUser?.includes(
    UnreadListingUpdates.RECENTLY_RECOMMENDED
  );

  return hasNewListingBeenSeenByCurrentUser;
};
