import {getAssetResolution} from '@uc/asset-resolution/client';

/**
 * Resolved versions are from the Asset Resolution Service
 * https://www.compass.com/internal/asset-resolution-dashboard/
 */
export function getResolvedAssetVersion(packageName: string, fallbackVersion: string): string {
  const {version} = getAssetResolution(packageName, {
    fallbackVersion,
  });

  return version;
}
