import {notEmpty} from '@src/utils';

import {useBoards} from './useBoards';
import {useBoardsBadgesSummary} from './useBoardsBadgesSummary';

export function useCollectionMenuData() {
  const boardsQuery = useBoards({
    excludeMosaic: true,
    excludeComments: true,
  });
  const {boards} = boardsQuery.data || {};
  const badgingQuery = useBoardsBadgesSummary({
    collectionIds: boards?.map(({id}) => id).filter(notEmpty),
  });

  return {
    boards,
    badgingSummaries: badgingQuery.data?.badgingSummaries,
    isLoading: boardsQuery.isLoading || badgingQuery.isLoading,
  };
}
