import React, {useEffect} from 'react';

import {SearchBar} from '@src/components/SearchBar';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useGetShowingTimeLinkInfo} from '@src/hooks/useGetShowingTimeLinkInfo';
import {getCurrentView, useSelector, useSelectedIds, useGetShowingTimeData} from '@src/store';

import {useActionBarItems, GROUP_ID, useAction} from '../logic';
import {ActionBarProps} from '../types';
import {CollectionActions} from './CollectionActions';
import {PrintOptions} from './PrintOptions/PrintOptions';
import {SelectOptions} from './SelectOptions';
import {ShareOptions} from './ShareOptions/ShareOptions';
import {TourOptions} from './TourOptions/TourOptions';

export const MoreOptions = (props: ActionBarProps) => {
  const selected = useSelectedIds();
  const getShowingTimeInfoAsync = useGetShowingTimeLinkInfo().mutateAsync;
  const showingInfo = useGetShowingTimeData();
  const {more} = useActionBarItems({mode: props.mode}, showingInfo);
  const onClick = useAction(props);

  useEffect(() => {
    if (selected.length > 0) {
      getShowingTimeInfoAsync(selected || []);
    }
  }, [selected, getShowingTimeInfoAsync]);

  if (more.length === 0) {
    return null;
  }

  return (
    <ButtonGroup>
      <Dropdown
        onItemSelect={onClick}
        variant="enclosed--highEmph"
        iconLeft="More"
        size="sm"
        label="Actions"
        labelIsHidden
        alignment="bottomLeft"
        items={more}
        data-testid="moreOptions-dropdown"
      />
    </ButtonGroup>
  );
};

export const CmaOptions = (props: ActionBarProps) => {
  const {mode} = props;
  const {bar} = useActionBarItems({mode});
  const items = bar[GROUP_ID.CMA];
  const onClick = useAction(props);

  if (items.length === 0) {
    return null;
  }

  return (
    <ButtonGroup>
      <Dropdown
        variant="enclosed--highEmph"
        iconLeft="PenAndPaper"
        iconRight="ChevronDown"
        size="sm"
        alignment="bottomLeft"
        label="Create CMA"
        items={items}
        onItemSelect={(id, selected) => {
          selected && onClick(id);
        }}
        data-testid="createCMADropdown"
      />
    </ButtonGroup>
  );
};

export const OptionsGroup = ({mode, collectionId, listingsOnPage, size}: ActionBarProps) => {
  const view = useSelector(getCurrentView);
  const optionProps = {mode, collectionId, listingsOnPage, size};

  return (
    <>
      {view === 'card' && <SelectOptions {...optionProps} />}
      <SearchBar />
      <CollectionActions {...optionProps} />
      <TourOptions {...optionProps} />
      <CmaOptions {...optionProps} />
      <ShareOptions {...optionProps} />
      <PrintOptions {...optionProps} />
      <MoreOptions {...optionProps} />
    </>
  );
};
