/* eslint-disable no-restricted-imports */
import {Location, History} from 'history';

import {CommentsModalTab} from '@src/constants';
import {StorageAdapter} from '@src/lib/StorageAdapter';
import {ANON_USER, DEFAULT_TOAST} from '@src/store/constants';
import {getCurrentUser, getPageSizes, getSource} from '@src/store/selectors';
import {RootState, CollectionUrlData, LocationState} from '@src/store/types';
import {UcGlobals, NYC_GEO_ID} from '@src/types';
import {getCollectionUrlData} from '@src/utils/url';

// ** store init function
declare global {
  interface Window {
    uc?: UcGlobals;
    analytics: {track: (name: string, props: unknown) => Promise<void>};
  }
}

export function calculateCurrentNav(
  rootState: RootState,
  location: Location<{selectedCommentsModalTab?: CommentsModalTab}>
): CollectionUrlData | undefined {
  const {isAgent} = getCurrentUser(rootState);
  const source = getSource(rootState);
  const pageSizesByCollectionId = getPageSizes(rootState);
  const {search, pathname, state} = location;
  const data = getCollectionUrlData(
    {search, pathname, locationState: state},
    {isAgent, pageSizesByCollectionId, source}
  );

  if (!data) {
    return;
  }

  return data;
}

export function loadState({
  storage,
  history,
}: {
  storage?: StorageAdapter<Partial<RootState>>;
  history: History<LocationState>;
}): RootState {
  const {location} = history;
  const storedState = storage?.load() || {};
  const urlParams = new URLSearchParams(location.search);
  const state1 = {
    currentLocation: location,
    currentView: 'card' as const,
    currentUser: window.uc?.user || ANON_USER,
    geoId: window.uc?.geoId || NYC_GEO_ID,
    pageSizes: {},
    currentNav: undefined,
    columnConfig: {},
    showLTSBanner: true,
    ...storedState,
    selectedListings: {},
    showMap: false,
    showFilterDropdown: false,
    sidePanel: undefined,
    toast: DEFAULT_TOAST,
    analytics: window.analytics,
    scrollId: 0,
    hoveredListing: undefined,
    deliverable_id: urlParams.get('deliverable_id') || undefined,
    navListingIds: undefined,
    navTabCounts: undefined,
    showingTimeInfo: undefined,
    source: urlParams.get('source') || undefined,
  };
  const currentNav = calculateCurrentNav(state1, location);

  return {
    ...state1,
    currentNav,
  };
}
