import React from 'react';

import {ErrorOverlay} from '@src/cx/ErrorOverlay';

const getErrorMessage = (code = 500) => {
  if (code >= 500) {
    return 'An error occured';
  }

  if (code >= 400) {
    return "Oops, we can't find what you're looking for";
  }

  return 'Hmm, something went wrong';
};
const getError = (error?: Error & {code?: number}) => {
  const code = error?.code || 500;
  const message = error?.message || getErrorMessage(code);

  return {
    message,
    code,
  };
};

declare global {
  interface Window {
    DD_RUM?: {addError(error: Error): void};
  }
}

export class ErrorPage extends React.Component<{children: React.ReactNode}, {error?: Error}> {
  state: {error?: Error} = {};

  componentDidCatch(error: Error) {
    if (window && window.DD_RUM) {
      window.DD_RUM.addError(error);
    }

    this.setState({error});
  }

  render() {
    if (this.state.error) {
      const {message, code} = getError(this.state.error);

      return <ErrorOverlay title={message}>{code}</ErrorOverlay>;
    }

    return this.props.children;
  }
}
