import {useQuery} from 'react-query';

import {ApiError, getCollectionListingsSummary} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetCollectionListingsSummaryResponse} from '@src/types';

export const useGetCollectionListingsSummary = ({
  collectionId,
  includeListingsPendingClientReview = true,
  includeListingsWithComments = true,
  includeListingsWithTourRequests = true,
}: {
  collectionId: string;
  includeListingsPendingClientReview?: boolean;
  includeListingsWithComments?: boolean;
  includeListingsWithTourRequests?: boolean;
}) => {
  return useQuery<GetCollectionListingsSummaryResponse, ApiError>(
    [API_KEY.GET_COLLECTION_LISTINGS_SUMMARY, collectionId],
    () =>
      getCollectionListingsSummary({
        collectionId,
        includeListingsPendingClientReview,
        includeListingsWithComments,
        includeListingsWithTourRequests,
      })
  );
};
