import {useQuery} from 'react-query';

import {SearchQuery} from '@uc/search-query';

import {loadMapSearch} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';

export const useLoadMapSearch = ({
  enabled,
  mapSearchQuery,
}: {
  enabled?: boolean;
  mapSearchQuery: SearchQuery;
}) => {
  return useQuery([API_KEY.MAP_SEARCH, mapSearchQuery], () => loadMapSearch(mapSearchQuery), {
    enabled,
  });
};
