import React from 'react';

import {Icon} from '@uc/cx.react';

import {useCurrentUrlData} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {GetCollectionResponse} from '@src/types';

export const getTransactionIdByReferrer = (): string | null => {
  const matchArray = document.referrer.match(/overview\/(\d+)/);

  if (!matchArray) {
    return null;
  }

  return matchArray[1];
};

export const getTransactionIdByDealSummaries = (
  data: GetCollectionResponse | undefined
): string | null => {
  const dealSummaries = data?.collection?.dealSummaries;
  const dealSummariesLength = dealSummaries && dealSummaries.filter(({id}) => id).length;

  if (dealSummariesLength) {
    return dealSummaries[0].id as string;
  }

  return null;
};

export const BackNav = () => {
  let backNavUrl = '/overview/';
  let backNavTitle = 'Back to My Compass';
  const isFromCollectionOverview = document.referrer.endsWith('/collections');
  const urlParams = useCurrentUrlData();
  const {data} = useGetCollection({collectionId: urlParams.collectionId});
  const transactionId = getTransactionIdByDealSummaries(data) || getTransactionIdByReferrer();

  if (isFromCollectionOverview) {
    backNavTitle = 'Back to Collections';

    if (transactionId) {
      backNavUrl = `/overview/${transactionId}/collections`;
    } else {
      backNavUrl = '/overview/collections';
    }
  } else {
    transactionId && (backNavUrl += transactionId);
  }

  return (
    <div className="clientDashboardHeader-backNav" data-testid="header-backNav">
      <Icon.ArrowLeft />
      <a className="cx-textLink back-nav-text" data-testid="backNav-link" href={backNavUrl}>
        {backNavTitle}
      </a>
    </div>
  );
};
