import {CollectionListingReviewStage, FilterPillCounts, TabCounts} from '@src/types';

import {useGetCollectionListingsSummary} from './useGetCollectionListingsSummary';

export const useFilterPillCounts = ({
  collectionId,
  tabCounts,
}: {
  collectionId: string;
  tabCounts?: TabCounts;
}): FilterPillCounts => {
  const {summary = {}} =
    useGetCollectionListingsSummary({
      collectionId,
    }).data || {};
  const {numListingsPerReviewStage} = summary;
  const interested = numListingsPerReviewStage?.[CollectionListingReviewStage.FAVORITE] || 0;
  const notInterested =
    numListingsPerReviewStage?.[CollectionListingReviewStage.NOT_INTERESTED] || 0;

  return {
    all: tabCounts?.shared || 0,
    pending: summary.numListingsPendingClientReview || 0,
    interested,
    notInterested,
    hasComments: summary.numListingsWithComments || 0,
    wantToSee: summary.numListingsWithTourRequests || 0,
  };
};
