import moment from 'moment';
import React from 'react';

import {ShowingStatus, CollectionListing} from '@src/types';
import './TourInformationBanner.pcss';

const tourStatusClassMapping = {
  [ShowingStatus.REQUESTED]: 'requested',
  [ShowingStatus.PENDING_CONFIRMATION]: 'requested',
  [ShowingStatus.SCHEDULED]: 'scheduled',
  [ShowingStatus.SEEN]: 'seen',
  [ShowingStatus.REQUEST_CANCELLED]: '',
};
const tourStatusTextMapping = {
  [ShowingStatus.REQUESTED]: 'Tour requested',
  [ShowingStatus.PENDING_CONFIRMATION]: 'Tour requested',
  [ShowingStatus.SCHEDULED]: 'Tour scheduled',
  [ShowingStatus.SEEN]: 'Visited on',
  [ShowingStatus.REQUEST_CANCELLED]: '',
};

export const getTourStatusText = (status: ShowingStatus, tourDate: number | undefined): string => {
  let tourStatusText = tourStatusTextMapping[status] || '';

  if ((status === ShowingStatus.SCHEDULED || status === ShowingStatus.SEEN) && tourDate) {
    const momentObj = moment(tourDate);
    const isTourAtWholeHour = momentObj.minute() === 0;
    const formattedDate = momentObj.format(
      isTourAtWholeHour ? 'M/D/YYYY [@]hA' : 'M/D/YYYY [@]h:mmA'
    );

    // 12/31/2021 @4PM or 12/31/2021 @4:30PM
    tourStatusText = `${tourStatusText} ${formattedDate}`;
  }

  return tourStatusText;
};

export const TourInformationBanner = ({
  collectionListing,
}: {
  collectionListing: CollectionListing;
}) => {
  const showingInfo = {...collectionListing.showing};

  if (
    !showingInfo ||
    showingInfo.status === undefined ||
    showingInfo.status === ShowingStatus.REQUEST_CANCELLED
  ) {
    return null;
  }

  let {status} = showingInfo;
  const {tourDate} = showingInfo;

  if (
    status === ShowingStatus.SCHEDULED &&
    tourDate !== undefined &&
    tourDate < new Date().getTime()
  ) {
    status = ShowingStatus.SEEN;
  }

  const tourStatusText = getTourStatusText(status, showingInfo.tourDate);

  return (
    <div
      className={`TourInfoBanner ${tourStatusClassMapping[status]}`}
      data-testid="TourInfoBanner"
    >
      {tourStatusText}
    </div>
  );
};
