import {useMutation, useQueryClient} from 'react-query';

import {updateBoard} from '@src/api';

export const useUpdateBoard = () => {
  const client = useQueryClient();

  return useMutation(async ({collectionId, name}: {collectionId: string; name: string}) => {
    const result = await updateBoard(collectionId, name);

    client.invalidateQueries();
    await client.refetchQueries();

    return result;
  });
};
