import {ListingStatusFilterId, LISTING_STATUS_FILTER_KEYS} from '@src/constants/filter';

export const getSelectedListingStatusFilterIds = (filterStr: string) => {
  const filtersArr = filterStr
    .split(',')
    .map((filter) => LISTING_STATUS_FILTER_KEYS.find((id) => id === filter.trim()))
    .filter((filter): filter is ListingStatusFilterId => filter !== undefined);

  return filtersArr;
};
