export function getShowingBadgeElementClasses(collectionListing: CollectionListing) {
  const showingDateClassName = [];

  if (collectionListing.showing) {
    const formattedTour = formatTourStatus(
      collectionListing.showing.status,
      collectionListing.showing.tourDate
    );

    switch (formattedTour.tourStatus) {
      case ShowingStatus.SCHEDULED:
        showingDateClassName.push(styles['showing-date']);
        break;

      case ShowingStatus.PENDING_CONFIRMATION:
        showingDateClassName.push(styles['showing-date']);
        showingDateClassName.push(styles['showing-date-visited']);
        break;

      case ShowingStatus.SEEN:
        showingDateClassName.push(styles['showing-date']);
        showingDateClassName.push(styles['showing-date-visited']);
        break;
    }
  }

  return showingDateClassName;
}

interface FooterProps {
  collectionListing: CollectionListing;
  collectionId: string;
  history: History;
  location: Location;
  isCollectionShared: boolean;
  listingIds?: string[];
  clearNewMatchBadge?: () => void;
}
interface UseActionProps {
  listing: ProcessedListing;
  collectionId: string;
  setOverlayProps: (overlayProps: OverlayProps) => void;
}
interface Action {
  handleNotInterestedClick?: () => void;
  handleCustomCollectionClick?: () => void;
}
interface ModeConfig {
  onListingClick({
    listingId,
    listingIds,
    setParams,
  }: {
    listingId: string;
    listingIds?: string[];
    setParams: (next: Partial<CollectionUrlData>) => void;
  }): void;
  useAction({listing, collectionId, setOverlayProps}: UseActionProps): Action;
  Footer({collectionListing, history, location, listingIds}: FooterProps): React.ReactElement;
}

const Footer = ({
  collectionListing,
  collectionId,
  isCollectionShared,
  clearNewMatchBadge,
}: FooterProps) => {
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const {mutate, isLoading} = useRequestShowing();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const handleRequestShowing = (e: MouseEvent) => {
    const newRequest = collectionListing?.showing?.status != ShowingStatus.REQUESTED;

    e.stopPropagation();
    e.preventDefault();
    clearNewMatchBadge?.();

    trackCollectionListingFlowAction({
      actionName: newRequest ? 'collections_showing_requested' : 'consumer_cancelled_tour_request',

      listingFlowAction: newRequest ? 'tour_requested' : 'tour_request_cancelled',

      productPath: PRODUCT_PATH.LISTING_CARD,
    });

    if (!isLoading) {
      mutate({collectionListing, collectionId});
    }
  };
  const {trackCommentModalInitiated} = useTrackCommentModalInitiated('grid view');
  const {poptipProps, getPoptipAnchorProps} = usePoptipOnHover();
  const {comments = [], updatesUnreadByCurrentUser} = collectionListing;
  const listingId = collectionListing.listingId || '';
  const numCommentString = comments.length > 0 ? ` (${comments.length})` : '';
  const unreadClassname = updatesUnreadByCurrentUser?.includes(UnreadListingUpdates.RECENT_COMMENTS)
    ? styles['card-footer-unread']
    : '';
  const showText = showingText(collectionListing, false);
  const canRequestTour = Boolean(collectionListing?.canRequestTour);

  return (
    <div className={styles['card-footer']} data-testid="card-footer">
      {isCollectionShared && (
        <div className={styles['card-footer-button-container']}>
          {showingActionable(collectionListing) ? (
            <RequestTourTip collectionListing={collectionListing} tipYPosition={12}>
              <Button
                className={`${styles['request-showing-button']} ${styles['card-footer-button']}`}
                icon="MapRoute"
                size="sm"
                variant="enclosed"
                onClick={handleRequestShowing}
                data-testid="card-request-tour"
                disabled={!canRequestTour}
              >
                <span>{showText}</span>
              </Button>
            </RequestTourTip>
          ) : (
            <div
              className={styles['showing-date-container']}
              onClick={(e) => {
                e.stopPropagation();
              }}
              aria-hidden="true"
            >
              <span
                data-testid="card-footer-request-showing"
                className={classnames(getShowingBadgeElementClasses(collectionListing))}
              >
                {showText}
              </span>
            </div>
          )}
        </div>
      )}
      <div className={styles['card-footer-button-container']}>
        <Button
          {...getPoptipAnchorProps('Comment', 'bottom')}
          aria-label="Leave a comment on the listing"
          className={styles['card-footer-button']}
          icon="TalkBubble"
          size="sm"
          variant="enclosed"
          data-testid="card-footer-comment"
          onClick={(e: MouseEvent) => {
            e.stopPropagation();
            e.preventDefault();
            dispatch(setReferringListingPath('card'));
            setParams({
              modalId: MODAL_URL_IDS.comment,
              listingIds: [listingId],
              selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
              enableModalPagination: true,
            });
            trackCommentModalInitiated();
            clearNewMatchBadge?.();
          }}
        >
          <span className={unreadClassname}>{`Comment${numCommentString}`}</span>
          <Poptip className={styles['card-footer-tooltop']} {...poptipProps} id="myPoptip" />
        </Button>
      </div>
    </div>
  );
};

export const MODES: Record<ConsumerCollectionMode, ModeConfig> = {
  [COLLECTION_MODE.MATCHES]: {
    onListingClick({setParams, listingId}) {
      setParams({
        modalId: MODAL_URL_IDS.comment,
        listingIds: [listingId],
        selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL,
        enableModalPagination: true,
      });
    },
    useAction({
      listing,
      collectionId,
      setOverlayProps,
    }: {
      listing: ProcessedListing;
      collectionId: string;
      setOverlayProps: (overlayProps: OverlayProps) => void;
    }) {
      const listingId = listing?.listingIdSHA || '';
      const {mutateAsync: favoriteMutate, isLoading: isFavoriteMutateLoading} = useFavoriteListings(
        {
          showSuccessToast: false,
        }
      );
      const {mutateAsync: notInterestedMutate, isLoading: isNotInterestedMutateLoading} =
        useNotInterestedListings({
          showSuccessToast: false,
        });
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const handleCustomCollectionClick = async () => {
        if (!listingId || isFavoriteMutateLoading) return;

        trackCollectionListingFlowAction({
          actionName: 'Favorite',
          productPath: PRODUCT_PATH.LISTING_CARD,
        });
        const result = await favoriteMutate({
          collectionId: collectionId,
          listingIds: [listingId],
        });

        if (result) {
          setOverlayProps({
            show: true,
            icon: 'Star',
            title: 'Added to Favorites',
          });
        }
      };
      const handleNotInterestedClick = async () => {
        if (!listingId || isNotInterestedMutateLoading) return;

        trackCollectionListingFlowAction({
          actionName: 'Not Interested',
          productPath: PRODUCT_PATH.LISTING_CARD,
          ab_test: isNotInterestedIconWebAbTestEnabled(),
          ab_test_version: getNotInterestedIcon(),
        });
        const result = await notInterestedMutate({
          collectionId,
          listingIds: [listingId],
        });

        if (result) {
          setOverlayProps({
            show: true,
            icon: 'Prohibited',
            title: 'Marked as Not Interested',
          });
        }
      };

      return {
        handleCustomCollectionClick,
        handleNotInterestedClick,
      };
    },
    Footer,
  },
  [COLLECTION_MODE.FAVORITES]: {
    onListingClick({listingId, setParams}) {
      setParams({
        modalId: MODAL_URL_IDS.comment,
        listingIds: [listingId],
        selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL,
        enableModalPagination: true,
      });
    },
    Footer,
    useAction({listing, collectionId}: {listing: ProcessedListing; collectionId: string}) {
      const listingId = listing?.listingIdSHA || '';
      const {mutateAsync, isLoading} = useArchiveListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const handleNotInterestedClick = async () => {
        if (!listingId || isLoading) return;

        trackCollectionListingFlowAction({
          actionName: 'Not Interested',
          productPath: PRODUCT_PATH.LISTING_CARD,
          ab_test: isNotInterestedIconWebAbTestEnabled(),
          ab_test_version: getNotInterestedIcon(),
        });
        mutateAsync({collectionId, listingIds: [listingId]});
      };

      return {
        handleNotInterestedClick,
        handleCustomCollectionClick: undefined,
      };
    },
  },
  [COLLECTION_MODE.NOT_INTERESTED]: {
    onListingClick({listingId, setParams}) {
      setParams({
        modalId: MODAL_URL_IDS.comment,
        listingIds: [listingId],
        selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL,
        enableModalPagination: true,
      });
    },
    Footer,
    useAction({listing, collectionId}: {listing: ProcessedListing; collectionId: string}) {
      const listingId = listing?.listingIdSHA || '';
      const {mutateAsync, isLoading} = useUnarchiveListings();
      const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
      const handleCustomCollectionClick = async () => {
        if (!listingId || isLoading) return;

        trackCollectionListingFlowAction({
          actionName: 'Favorite',
          productPath: PRODUCT_PATH.LISTING_CARD,
        });
        mutateAsync({collectionId: collectionId, listingIds: [listingId]});
      };

      return {
        handleCustomCollectionClick,
        handleNotInterestedClick: undefined,
      };
    },
  },
};

const MODES_CONFIG_LIST = Object.values(MODES);
const getActionIndex = (mode: ConsumerCollectionMode) => {
  return MODES_CONFIG_LIST.indexOf(MODES[mode]);
};
// Confirm action hooks in all mode are called in different render
const useActions = (props: UseActionProps & {mode: ConsumerCollectionMode}): Action | undefined => {
  const actionHooks = MODES_CONFIG_LIST.map((item) => item.useAction(props));
  const actionIndex = getActionIndex(props.mode);

  return actionHooks[actionIndex];
};

export const ConsumerCard = ({
  collectionListing,
  collectionId,
  mode,
  isCollectionShared,
}: {
  collectionListing: CollectionListing;
  mode: ConsumerCollectionMode;
  collectionId: string;
  isCollectionShared: boolean;
}) => {
  const [overlayProps, setOverlayProps] = useState({
    show: false,
  } as OverlayProps);
  const [dislikeActive, setDislikeActive] = useState(false);
  const [starActive, setStarActive] = useState(false);
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const {Footer, onListingClick} = MODES[mode];
  const history = useHistory();
  const location = useLocation();
  const actions = useActions({
    mode,
    collectionId,
    setOverlayProps,
    listing: collectionListing.listingData as ProcessedListing,
  });
  const {mutateAsync: clearBadgesMutate} = useClearBadgesForListings();
  const listingCardOptions = {userType: 'consumer' as const, disableNavigation: true};
  const clickHandler = () => {
    dispatch(setReferringListingPath('card'));
    onListingClick({
      setParams,
      listingId: collectionListing.listingId || '',
    });
  };
  const [isNewMatchListing, setIsNewMatchListing] = useState(isNewMatch(collectionListing));
  const clearNewMatchBadge = () => {
    if (isNewMatchListing) {
      clearBadgesMutate({
        collectionId,
        listingIdSHAs: [collectionListing.listingData?.listingIdSHA as string],
      });
      setIsNewMatchListing(false);
    }
  };
  const dislikeHandler = () => {
    setDislikeActive((prev) => !prev);
    actions?.handleNotInterestedClick && actions?.handleNotInterestedClick();
    clearNewMatchBadge();
  };
  const starHandler = () => {
    setStarActive((prev) => !prev);
    actions?.handleCustomCollectionClick && actions?.handleCustomCollectionClick();
    clearNewMatchBadge();
  };
  const legacyListingCard = (
    <PhotoCard
      onListingClick={clickHandler}
      useServerBadgesData
      data={collectionListing.listingData as ProcessedListing}
      handleNotInterestedClick={actions?.handleNotInterestedClick}
      handleCustomCollectionClick={actions?.handleCustomCollectionClick}
      isLoggedIn
      isInACollection
      onlyRenderNotInterested={actions?.handleCustomCollectionClick === undefined}
      footer={
        <Footer
          collectionListing={collectionListing}
          collectionId={collectionId}
          history={history}
          location={location}
          isCollectionShared={isCollectionShared}
        />
      }
      notInterestedIcon={getNotInterestedIcon()}
    />
  );
  const newListingCard = (
    <UcListingCard
      className={styles.listingCard}
      data={collectionListing.listingCardData}
      options={listingCardOptions}
      onClick={() => {
        clickHandler();
        clearNewMatchBadge();
      }}
      data-testid="listing-card"
      key={`${collectionListing.listingId}_${collectionListing.showing?.status}`}
    >
      {
        <ListingCardTitleWithNewMatchBadge
          listing={collectionListing.listingCardData as ProcessedListingCard}
          showNewMatchBadge={isNewMatchListing}
        />
      }
      {isCollectionShared && <ConsumerCardTourBanner {...collectionListing.showing} />}
      <div slot="actions" className={styles.listingToggleActionBtn}>
        {actions?.handleNotInterestedClick !== undefined && (
          <UcLcDislikeToggleAction isActive={dislikeActive} onDislikeClick={dislikeHandler} />
        )}
        {actions?.handleCustomCollectionClick !== undefined && (
          <UcLcStarToggleAction isActive={starActive} onStarClick={starHandler} />
        )}
      </div>
      <CardFooter
        collectionId={collectionId}
        collectionListing={collectionListing}
        clearNewMatchBadge={clearNewMatchBadge}
        isCollectionShared={isCollectionShared}
      />
    </UcListingCard>
  );
  const isNewCard =
    collectionListing.listingCardData && isNewClientListingCardsOnCollectionsEnabled();
  const listingCard = isNewCard ? newListingCard : legacyListingCard;

  return (
    <Card
      onHandleFocus={() => dispatch(setHoveredListing(collectionListing))}
      onHandleMouseOver={() => dispatch(setHoveredListing(collectionListing))}
      onHandleMouseOut={() => dispatch(setHoveredListing())}
      onHandleBlur={() => dispatch(setHoveredListing())}
      className={classnames({
        [styles.cardWithLTSBadge]: collectionListing.likelyToSell,
        [styles.cardWithBorder]: !isNewCard,
      })}
    >
      {listingCard}
      {mode === 'MATCHES' && (
        <Overlay overlayProps={overlayProps} isNewListingCard={isNewCard || false} />
      )}
    </Card>
  );
};
