import loadModal from '@uc/modal-loader';

import {ADD_LISTINGS_TO_COLLECTIONS_MODAL_URI} from '@src/constants';
import {useRefreshGetCollectionQueryCache} from '@src/hooks';
import {
  useSelectedListings,
  useIsAgent,
  getCurrentView,
  useSelector,
  useAnalytics,
} from '@src/store';
import {ProcessedListing} from '@src/types';
import {getLoadedModalUrl} from '@src/utils';

// Copied from modal--add-listing-to-collections.react/src/components/ModalContent/ModalContent.tsx - v1.0.15
interface ModalContentProps {
  dealId?: string;
  analytics?: unknown;
  analyticsParams?: Record<string, unknown>;
  listings: ProcessedListing[];
  isAgent: boolean;
}

export const useAddToCollection = (collectionId: string) => {
  const {selectedProcessedListings} = useSelectedListings();
  const isAgent = useIsAgent();
  const view = useSelector(getCurrentView);
  const analytics = useAnalytics();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();
  const modalUrl = ADD_LISTINGS_TO_COLLECTIONS_MODAL_URI;
  const modalParams: ModalContentProps = {
    listings: selectedProcessedListings,
    isAgent: isAgent,
    analytics,
    analyticsParams: {
      product: 'collections',
      product_path: 'action bar',
      product_id: collectionId,
      table_view: view === 'list',
    },
  };

  return () => {
    loadModal(getLoadedModalUrl(modalUrl), modalParams)
      .then(() => {
        refreshGetCollectionQueryCache();
      })
      // eslint-disable-next-line @typescript-eslint/no-empty-function
      .catch(() => {});
  };
};
