/* eslint-disable no-restricted-imports */
import {History} from 'history';
import {Middleware} from 'redux';

import {CommentsModalTab} from '@src/constants';

import {PUSH_HISTORY, SET_CURRENT_LOCATION} from './actions';
import {RootState} from './types';

export const createNavMiddleware = (
  history: History<{selectedCommentsModalTab?: CommentsModalTab}>
) => {
  const middleware: Middleware<unknown, RootState> = (store) => {
    history.listen((location, action) => {
      store.dispatch({type: SET_CURRENT_LOCATION, location, action});
    });

    return (next) => (action) => {
      if (action.type === PUSH_HISTORY) {
        history.push(action.location);
        return;
      }

      return next(action);
    };
  };

  return middleware;
};
