import React from 'react';

import {Checkbox} from '@uc/cx.react';

import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useSelectedInfo} from '@src/hooks/useSelectedInfo';
import {
  useDispatch,
  clearSelectedListings,
  selectListings,
  useSelectedIds,
  setToast,
} from '@src/store';
import {getSelectListingToastConfig} from '@src/utils/getSelectListingToastConfig';

import {GROUP_ID, useAction, useActionBarItems} from '../../logic';
import {ActionBarProps} from '../../types';
import styles from './SelectOptions.module.pcss';

const FROZEN_ARRAY = Object.freeze([]);

export const SelectOptions = (props: ActionBarProps) => {
  const {bar} = useActionBarItems(props);
  const items = bar[GROUP_ID.SELECT];
  const listingsOnPage = props.listingsOnPage || FROZEN_ARRAY;
  const {isWholePageSelected, isPartialPageSelected} = useSelectedInfo(listingsOnPage);
  const dispatch = useDispatch();
  const onClick = useAction(props);
  const selected = useSelectedIds();
  const handleCheck = () => {
    if (isWholePageSelected) {
      dispatch(clearSelectedListings());
      return;
    }

    const {maxCount, toastData} = getSelectListingToastConfig();
    const unSelectedListingsOnPage = listingsOnPage.filter((listing) => {
      return !selected.some((id) => id === listing.listingId);
    });

    if (selected.length + unSelectedListingsOnPage.length > maxCount) {
      dispatch(setToast(toastData));
    } else {
      dispatch(selectListings(listingsOnPage));
    }
  };

  if (items.length === 0) {
    return null;
  }

  return (
    <ButtonGroup>
      <Checkbox
        data-testid="select-checkbox"
        aria-hidden="true"
        halfChecked={isPartialPageSelected && !isWholePageSelected}
        checked={isWholePageSelected}
        onChange={handleCheck}
        className={styles.selectCheckbox}
      />
      <Dropdown
        onItemSelect={onClick}
        data-testid="select-dropdown"
        variant="enclosed--highEmph"
        size="sm"
        iconRight="ChevronDown"
        className={styles.selectDropdown}
        labelIsHidden
        alignment="bottomLeft"
        label="Select commands"
        items={items}
      />
    </ButtonGroup>
  );
};
