import classnames from 'classnames';
import React, {Key} from 'react';

import {Button, Icon} from '@uc/cx.react';

import {ButtonGroup} from '@src/cx/ButtonGroup';
import {DropdownItem} from '@src/cx/Dropdown';

import {GROUP_ID, useAction, useActionBarItems} from '../../logic';
import {ActionBarProps} from '../../types';
import styles from './CollectionActions.module.pcss';

const SomeButton = ({
  item,
  isLast,
  size,
  onClick,
}: {
  item: DropdownItem;
  isLast: boolean;
  size: number;
  onClick: (key: Key) => void;
}) => {
  if (isLast && item.icon) {
    return (
      <Button
        onClick={() => onClick(item.id)}
        icon={item.icon}
        variant="enclosed--highEmph"
        size="sm"
        data-testid={item.label}
      >
        {item.label}
      </Button>
    );
  }

  const LeftIcon = item.icon ? Icon[item.icon] : undefined;

  return (
    <Button
      variant="enclosed--highEmph"
      size="sm"
      onClick={() => onClick(item.id)}
      data-testid={item.label}
    >
      {LeftIcon && (
        <LeftIcon className={classnames(styles.btnIcon, size > 1 && styles.marginRight)} />
      )}
      {size >= 1 && item.label} {/* or unshare etc. depending on mode */}
    </Button>
  );
};

export const CollectionActions = (props: ActionBarProps) => {
  const {bar} = useActionBarItems(props);
  const items = bar[GROUP_ID.COLLECTION];
  const onClick = useAction(props);

  if (items.length === 0) {
    return null;
  }

  const btns = [];
  const click = (key: Key) => onClick(key);

  for (let i = 0; i < items.length; i++) {
    const item = items[i];

    btns.push(
      <SomeButton
        key={item.id}
        isLast={i === items.length - 1}
        item={item}
        size={props.size}
        onClick={click}
      />
    );
  }

  return <ButtonGroup>{btns}</ButtonGroup>;
};
