import classnames from 'classnames';
import React from 'react';

import {collectionCleanUpInitiated as trackCleanUpInitiated} from '@uc/analytics-definitions';
import {Button, OptionsMenu, useOptionsMenu, Flyout, Icon} from '@uc/cx.react';

import {ConsumerNav} from '@src/components/ConsumerNav';
import {LinkedSearches} from '@src/components/LinkedSearches';
import {
  CollectionMode,
  MODAL_URL_IDS,
  SIDE_PANEL_IDS,
  TRASH_LISTINGS_MODAL_TRIGGER,
  ANALYTICS_PRODUCT,
  ANALYTICS_PATH_CLEAN_UP_BUTTON,
} from '@src/constants';
import {LoadingSkeleton} from '@src/cx/LoadingSkeleton';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useGetCollectionUsers} from '@src/hooks/useGetCollectionUsers';
import {useUpdateBoard} from '@src/hooks/useUpdateBoard';
import {
  useDispatch,
  useIsAgent,
  setSidePanel,
  useSelector,
  getSidePanel,
  setRemindee,
  setTrashListingsModalTrigger,
} from '@src/store';
import {CollectionLinkedSearch, CollectionUser} from '@src/types';
import {getCollectionOwnerAndCollaborators} from '@src/utils';
import {isClientHomeRootEnabled, isTrashListingsModalEnabled} from '@src/utils/optyUtils';

import {AvatarsWrapper} from './Avatars/AvatarsWrapper';
import {BackNav} from './BackNav';
import {Breadcrumbs} from './Breadcrumbs';
import {HeaderTitle} from './HeaderTitle';
import {NoticeBanner} from './NoticeBanner';
import {PendingCollaborators} from './PendingCollaborators';
import './Header.pcss';

export const Header = ({collectionId, mode}: {collectionId: string; mode: CollectionMode}) => {
  const {mutate: updateBoard} = useUpdateBoard();
  const setParams = useUpdateCurrentUrl();
  const {isDesktop} = useDeviceWidth();
  const dispatch = useDispatch();
  const sidePanel = useSelector(getSidePanel);
  const isAgent = useIsAgent();
  const {data: collectionResponse = {}, isLoading: isCollectionLoading} = useGetCollection({
    collectionId,
  });
  const {data: collectionUsers = {}, isLoading: isCollectionUsersLoading} = useGetCollectionUsers({
    collectionId,
  });
  const {collection = {}} = collectionResponse;
  const hasCollaborators = Boolean(
    collectionUsers.users?.filter((user) => user.userId !== collection.owner?.userId)?.length
  );
  const linkedSavedSearches: CollectionLinkedSearch[] = collection.savedSearches || [];
  const collectionName = collection.name || '';
  const dealSummaries = collection.dealSummaries || [];
  const hasUnreadComments = (collection.numUnreadDiscussions || 0) > 0;
  const collectionOwnerAndCollaborators = getCollectionOwnerAndCollaborators(collectionUsers);
  const unreadClassname = hasUnreadComments ? 'collection-header-discussion-unread' : '';
  const unreadMobileClassname = hasUnreadComments
    ? 'collection-header-discussion-mobile-unread'
    : '';
  const {anchorProps, flyoutProps, menuProps, closeMenu} = useOptionsMenu({
    allowMultiple: false,
    closeOnSelect: true,
  });
  const Nav = isAgent || isClientHomeRootEnabled() ? undefined : <ConsumerNav isDropdown />;
  const isUsingNewHeaderStyle = !isAgent && isClientHomeRootEnabled();
  const onSaveCollectionName = (name: string) => {
    updateBoard({collectionId, name});
  };
  const handleOnClickInvite = () => {
    setParams({modalId: MODAL_URL_IDS.invite});
  };
  const handleOnClickRemind = (collaborator: CollectionUser) => {
    dispatch(setRemindee(collaborator));
    handleOnClickInvite();
  };
  const handleOnClickDiscussion = () => {
    setParams({modalId: MODAL_URL_IDS.discussion});
  };
  const handleOnClickSettings = () => {
    setParams({modalId: MODAL_URL_IDS.settings});
  };
  const handleOnClickActivity = () => {
    dispatch(
      setSidePanel(sidePanel === SIDE_PANEL_IDS.activity ? undefined : SIDE_PANEL_IDS.activity)
    );
    closeMenu();
  };
  const handleOnClickCleanUp = () => {
    dispatch(setTrashListingsModalTrigger(TRASH_LISTINGS_MODAL_TRIGGER.cleanUp));
    setParams({modalId: MODAL_URL_IDS.trashListings});

    trackCleanUpInitiated({
      collection_id: collectionId,
      product: ANALYTICS_PRODUCT,
      product_path: ANALYTICS_PATH_CLEAN_UP_BUTTON,
    });
  };

  return (
    <header
      className={classnames('collection-header', {clientDashboardHeader: isUsingNewHeaderStyle})}
    >
      {isDesktop && (
        <Breadcrumbs
          collectionName={collectionName}
          isAgent={isAgent}
          dealSummaries={dealSummaries}
        />
      )}
      {isUsingNewHeaderStyle && <BackNav />}
      <div className="collection-header-main-area">
        <div className={classnames('collection-header-title', {'single-line': !isAgent})}>
          {Nav}
          {isCollectionLoading ? (
            <LoadingSkeleton />
          ) : (
            <HeaderTitle
              collectionName={collectionName}
              onSaveCollectionName={onSaveCollectionName}
            />
          )}
          {isAgent && (
            <div className="collection-header-linked-searches" data-testid="linked-searches">
              {linkedSavedSearches && (
                <LinkedSearches mode={mode} isAgent={isAgent} savedSearches={linkedSavedSearches} />
              )}
            </div>
          )}
        </div>
        {isDesktop ? (
          <div className="collection-header-actions">
            {collectionOwnerAndCollaborators.length > 0 && (
              <AvatarsWrapper
                collectionOwnerAndcollaborators={collectionOwnerAndCollaborators}
                handleOnClickInvite={handleOnClickInvite}
              />
            )}
            <div>
              <Button
                className="collection-header-button"
                onClick={handleOnClickInvite}
                size="sm"
                variant="solid"
                icon="PersonPlus"
                data-testid="header-invite-btn"
              >
                Invite
              </Button>
              <Button
                className="collection-header-button"
                onClick={handleOnClickActivity}
                size="sm"
                variant="enclosed"
                data-testid="header-activity-btn"
              >
                Activity
              </Button>
              <Button
                className={`collection-header-button ${unreadClassname}`}
                onClick={handleOnClickDiscussion}
                size="sm"
                variant="enclosed"
                data-testid="header-discussion-btn"
              >
                Discussion
              </Button>
              <Button
                className="collection-header-button"
                onClick={handleOnClickSettings}
                icon="Gear"
                size="sm"
                variant="enclosed"
                aria-label="Settings"
              />
            </div>
          </div>
        ) : (
          <div className="collection-header-actions">
            <div className="collection-header-button-option-menu">
              <Button
                className="collection-header-button"
                size="sm"
                variant="enclosed"
                {...anchorProps}
              >
                Actions
              </Button>
              <Flyout {...flyoutProps}>
                <OptionsMenu {...menuProps} size="md" aria-label="Available items">
                  <OptionsMenu.Item indented onClick={handleOnClickInvite}>
                    <Icon.PersonPlus className="cx-optionsMenu-accessory" />
                    Invite
                  </OptionsMenu.Item>
                  <OptionsMenu.Item indented onClick={handleOnClickActivity}>
                    <Icon.PenAndPaper
                      className="cx-optionsMenu-accessory"
                      onClick={handleOnClickActivity}
                    />
                    Activity
                  </OptionsMenu.Item>
                  <OptionsMenu.Item
                    indented
                    onClick={handleOnClickDiscussion}
                    className={`${unreadMobileClassname}`}
                  >
                    <Icon.TalkBubble className="cx-optionsMenu-accessory" />
                    Discuss
                  </OptionsMenu.Item>
                  {isAgent && isTrashListingsModalEnabled() && (
                    <OptionsMenu.Item indented onClick={handleOnClickCleanUp}>
                      <Icon.Refresh
                        className="cx-optionsMenu-accessory"
                        onClick={handleOnClickCleanUp}
                      />
                      Clean up
                    </OptionsMenu.Item>
                  )}
                </OptionsMenu>
              </Flyout>
            </div>
            <Button
              className="collection-header-button"
              icon="Gear"
              size="sm"
              variant="enclosed"
              aria-label="Settings"
              onClick={handleOnClickSettings}
            />
          </div>
        )}
      </div>
      {isAgent && !hasCollaborators && !isCollectionUsersLoading && <NoticeBanner />}
      {isAgent && hasCollaborators && (
        <PendingCollaborators
          users={collectionUsers.users || []}
          onSendReminder={handleOnClickRemind}
        />
      )}
    </header>
  );
};
