import {useMutation} from 'react-query';

import {fetchListingRealistUrl} from '@src/api';
import {Recommendation} from '@src/components/LikelyToSell/RecommendationCard';
import {RealistUrlRequest, RealistUrlResponse} from '@src/types';

const buildRequest = (aiRecommendation: Recommendation): RealistUrlRequest | undefined => {
  if (aiRecommendation.listing?.location) {
    const {agentAddress, city, state, zipCode, countyFipsCode} = aiRecommendation.listing.location;
    const concatenatedAddress = [agentAddress, city, state, zipCode].join(' ');

    return {
      address: concatenatedAddress,
      countyFipsCode,
      taxId: aiRecommendation.listing.parcelNumber,
    };
  }

  return undefined;
};

export const useGetListingRealistUrl = (aiRecommendation: Recommendation) => {
  return useMutation(async () => {
    const request = buildRequest(aiRecommendation);

    if (request) {
      const response: RealistUrlResponse = await fetchListingRealistUrl(request);

      return response.url ? response.url : '';
    }

    return '';
  });
};
