import {get} from 'lodash';
import React, {useCallback} from 'react';

import {useRefreshBoardQueryCache} from '@src/hooks';
import {
  setToast,
  useDispatch,
  useSelector,
  useSelectedIds,
  getCurrentNav,
  setCurrentNav,
  useSelectedListings,
  clearSelectedListings,
} from '@src/store';
import {createModalComponent} from '@src/utils/createModalComponent';

const MARK_AS_VISITED_MODAL_URI = 'mark-as-visited.react/1.1.6';

interface ModalProps {
  boardId: string;
  listingIdSHAs: string[];
  tourDate?: number;
}

const Modal = createModalComponent<ModalProps, undefined, undefined>(MARK_AS_VISITED_MODAL_URI);

export const MarkVisitedModal = ({collectionId}: {collectionId: string}) => {
  const dispatch = useDispatch();
  const listingIds = useSelectedIds();
  const {selectedListings} = useSelectedListings();
  const tourDate = get(selectedListings[0], 'showing.tourDate', undefined);
  const refreshQueryCache = useRefreshBoardQueryCache();
  const params = useSelector(getCurrentNav);
  const onClose = useCallback(() => {
    if (params) {
      dispatch(setCurrentNav({...params, modalId: undefined}));
    }

    refreshQueryCache();
  }, [params, dispatch, refreshQueryCache]);
  const onError = useCallback(() => {
    dispatch(
      setToast({
        isOpen: true,
        variant: 'error',
        message: `We're having some trouble with this request. Please refresh the page to try again.`,
      })
    );
    onClose();
  }, [dispatch, onClose]);
  const onSuccess = useCallback(
    (closedWithChanges) => {
      closedWithChanges &&
        dispatch(
          setToast({
            isOpen: true,
            variant: 'success',
            message: 'Collection updated.',
          })
        );
      dispatch(clearSelectedListings());
      onClose();
      refreshQueryCache();
    },
    [dispatch, onClose, refreshQueryCache]
  );

  return (
    <Modal
      params={{
        boardId: collectionId,
        listingIdSHAs: listingIds,
        tourDate: tourDate,
      }}
      onResolve={onSuccess}
      onError={onError}
    />
  );
};
