import {useMemo} from 'react';

import {CollectionListing, ProcessedListing} from '@src/types';

export const useProcessedListings = (
  collectionListings?: CollectionListing[]
): ProcessedListing[] => {
  return useMemo(
    () =>
      collectionListings
        ?.map((collectionListing) => collectionListing.listingData)
        .filter((processedListing): processedListing is ProcessedListing =>
          Boolean(processedListing)
        ) || [],
    [collectionListings]
  );
};
