import {useMutation, useQueryClient} from 'react-query';

import {shareListings} from '@src/api';
import {getToastMessage} from '@src/components/ActionBar/logic';
import {ACTION_ID} from '@src/constants';
import {useDispatch, setToast, ToastType} from '@src/store';

import {useCurrentUrlData} from './url';

export const useShareListings = () => {
  const client = useQueryClient();
  const {savedSearchId} = useCurrentUrlData();
  const dispatch = useDispatch();

  return useMutation(
    async ({
      collectionId,
      listingIds,
      comment,
      toastParams,
      likelyToSell = false,
      doNotInvalidateQueries = false,
    }: {
      collectionId: string;
      listingIds: string[];
      comment?: string;
      toastParams?: ToastType;
      likelyToSell?: boolean;
      doNotInvalidateQueries?: boolean;
    }) => {
      const toastMessage = getToastMessage({
        actionId: ACTION_ID.SHARE_WITH_CLIENT_UNSHARED,
        listingsLength: listingIds.length,
      });

      try {
        const result = await shareListings({
          collectionId: collectionId,
          listingIds,
          comment,
          likelyToSell,
          savedSearchIds: !likelyToSell && savedSearchId ? [savedSearchId] : undefined,
        });

        if (toastParams) {
          dispatch(setToast({...toastParams}));
        } else {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'success',
              message: toastMessage?.success,
            })
          );
        }

        // This is to prevent cache busting when the share button is clicked. For example clicking
        // on the "Add to Collection" button on the LTS tab
        if (!doNotInvalidateQueries) {
          await client.invalidateQueries();
        }

        return result;
      } catch (e) {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: toastMessage?.error,
          })
        );
        return;
      }
    }
  );
};
