import React from 'react';

import {collectionSortChanged} from '@uc/analytics-definitions';
import {Select} from '@uc/cx.react';

import {Breakpoint} from '@src/components/Breakpoint';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {SortId} from '@src/constants/sort';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useBusinessTrackerIds} from '@src/hooks/useBusinessTrackerIds';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';
import {useSelector, getCurrentView, ViewType, useIsAgent, useSelectedIds} from '@src/store';

import {getSortItems} from '../logic';
import {FilterDropdownCountsProps} from '../types';
import {FilterDropdown} from './FilterDropdown';
import styles from './SorterAndFilter.module.pcss';

const shouldDisplayFilter = (mode: CollectionMode) => {
  return (
    mode === COLLECTION_MODE.SHARED ||
    mode === COLLECTION_MODE.MATCHES ||
    mode === COLLECTION_MODE.FAVORITES ||
    mode === COLLECTION_MODE.NOT_INTERESTED
  );
};
const shouldDisplaySorter = (view: ViewType) => {
  // sort button should display only in card view.
  return view === 'card';
};
const shouldDisplayButtonGroup = (mode: CollectionMode, view: ViewType) => {
  return shouldDisplayFilter(mode) || shouldDisplaySorter(view);
};

export const SorterAndFilter = ({
  collectionId,
  mode,
  filterDropdownCounts,
}: {
  collectionId: string;
  mode: CollectionMode;
  filterDropdownCounts: FilterDropdownCountsProps;
}) => {
  const isAgent = useIsAgent();
  const {isDesktop, isTablet} = useDeviceWidth();
  const selected = useSelectedIds();
  const sortItems = getSortItems(mode);
  const currentUrlParams = useCurrentUrlData();
  const setParams = useUpdateCurrentUrl();
  const currentView = useSelector(getCurrentView);
  const trackerIds = useBusinessTrackerIds(collectionId);
  const onClickSort = (id: React.Key) => {
    collectionSortChanged({
      product: 'collections',
      product_path: 'action bar',
      table_view: currentView,
      sort_option: id as SortId,
      collection_id: collectionId,
      is_agent: isAgent,
      business_tracker_ids: trackerIds,
    });
    setParams({sortId: id as SortId});
  };

  if (!shouldDisplayButtonGroup(mode, currentView)) {
    return null;
  }

  const _sortItems = sortItems.map((item) => ({
    ...item,
    selected: currentUrlParams.sortId === item.id,
  }));
  const _items = [];

  if (shouldDisplaySorter(currentView)) {
    _items.push(
      ...[{id: 'Sorter', label: 'Sorter', header: true, 'aria-label': 'Sorter'}, ..._sortItems]
    );
  }

  /* eslint-disable @typescript-eslint/no-extra-parens */
  const shouldDisplayDropdowns = (!isAgent && isDesktop) || (isAgent && selected.length === 0);
  const shouldDisplayBtns = (isAgent && selected.length > 0) || (isTablet && !isAgent);
  /* eslint-enable @typescript-eslint/no-extra-parens */

  return (
    <>
      {shouldDisplayDropdowns && (
        <Breakpoint isDesktop>
          {shouldDisplaySorter(currentView) && (
            <Select
              className={styles.sortSelect}
              size="sm"
              value={currentUrlParams.sortId}
              onChange={(e) => onClickSort(e.target.value)}
              data-testid="sortDropdownDesktop"
            >
              {_sortItems.map((item) => (
                <option key={item.id} value={item.id}>
                  {item.label}
                </option>
              ))}
            </Select>
          )}
          {shouldDisplayFilter(mode) && (
            <FilterDropdown
              filterDropdownCounts={filterDropdownCounts}
              collectionId={collectionId}
            />
          )}
        </Breakpoint>
      )}
      {shouldDisplayBtns && (
        <Breakpoint isTablet isDesktop>
          <ButtonGroup>
            {shouldDisplaySorter(currentView) && (
              <Dropdown
                variant="enclosed"
                size="sm"
                labelIsHidden
                label="Sorter"
                iconLeft="Sort"
                alignment="bottomRight"
                items={_sortItems}
                onItemSelect={onClickSort}
                data-testid="sortDropdownTablet"
              />
            )}
            {shouldDisplayFilter(mode) && (
              <FilterDropdown
                filterDropdownCounts={filterDropdownCounts}
                collectionId={collectionId}
              />
            )}
          </ButtonGroup>
        </Breakpoint>
      )}
      <Breakpoint isMobile>
        <ButtonGroup classNames={styles.sorterFilterDropdownBtn}>
          {shouldDisplaySorter(currentView) && (
            <Dropdown
              variant="enclosed"
              size="sm"
              labelIsHidden
              alignment="bottomRight"
              label="Sorter"
              iconLeft="Sort"
              items={_items}
              onItemSelect={onClickSort}
              data-testid="sortDropdownMobile"
            />
          )}
          {shouldDisplayFilter(mode) && (
            <FilterDropdown
              filterDropdownCounts={filterDropdownCounts}
              collectionId={collectionId}
            />
          )}
        </ButtonGroup>
      </Breakpoint>
    </>
  );
};
