import React from 'react';

import {Button, Icon} from '@uc/cx.react';

export const ToggleChatButton = ({
  hasComments,
  hasUnreadComments,
  toggleCommentsView,
}: {
  hasComments: boolean;
  hasUnreadComments: boolean;
  toggleCommentsView: () => void;
}) => {
  return (
    <Button
      variant="naked"
      size="sm"
      onClick={() => toggleCommentsView()}
      aria-label="Toggle Comments View"
      className={`chatToggleButton ${hasComments ? 'hasComments' : ''} ${
        hasComments && hasUnreadComments ? 'hasUnreadComments' : ''
      }`}
    >
      <Icon.ChatBubbles className="cx-btn-icon" />
    </Button>
  );
};
