import React from 'react';
import './EmptyCollectionsHeader.pcss';

import {ConsumerNav} from '@src/components/ConsumerNav';
import {useIsAgent} from '@src/store';
import {isClientHomeRootEnabled} from '@src/utils/optyUtils';

export const EmptyCollectionsHeader = () => {
  const isAgent = useIsAgent();
  const Nav = isAgent || isClientHomeRootEnabled() ? undefined : <ConsumerNav isDropdown />;

  return (
    <header
      className={`cx-gridPadding empty-collection-header ${
        isClientHomeRootEnabled() ? 'clientDashboardHeader' : ''
      }`}
    >
      <div className="collection-header-main-area">
        <div className="collection-header-title">
          {Nav}
          <h1 className="cx-masthead-title u-ellipsis empty-collection-header-title-margin">
            Collections
          </h1>
        </div>
      </div>
    </header>
  );
};
