import React from 'react';

import {collectionCleanUpInitiated as trackCleanUpInitiated} from '@uc/analytics-definitions';
import {Button, Poptip, usePoptip} from '@uc/cx.react';

import {
  MODAL_URL_IDS,
  TRASH_LISTINGS_MODAL_TRIGGER,
  ANALYTICS_PRODUCT,
  ANALYTICS_PATH_CLEAN_UP_BUTTON,
} from '@src/constants';
import {useGetCollection} from '@src/hooks';
import {
  getCurrentNav,
  getIsTrashListingsInProgress,
  setCurrentNav,
  setTrashListingsModalTrigger,
  useDispatch,
  useSelector,
} from '@src/store';
import {CollectionLinkedSearch} from '@src/types';
import {isTrashListingsModalEnabled} from '@src/utils/optyUtils';

import styles from './Tabs.module.pcss';

export const REFRESH_BUTTON_TEXT = 'Clean up collection';
export const POPTIP_TEXT = 'Move listings that no longer match your search to Trash';
export const MULTIPLE_SEARCHES_POPTIP_TEXT =
  'Clean up is not available for collections with multiple saved searches.';
export const IN_PROGRESS_POPTIP_TEXT = 'Collection is being updated.';
export const WRAPPER_TEST_ID = 'refresh-button-wrapper-test-id';

export const RefreshButton: React.FC<{
  collectionId: string;
}> = ({collectionId}) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const params = useSelector(getCurrentNav);
  const isTrashListingsInProgress = useSelector(getIsTrashListingsInProgress);
  const dispatch = useDispatch();
  const {data: collectionResponse = {}} = useGetCollection({
    collectionId,
  });
  const {collection = {}} = collectionResponse;
  const linkedSavedSearches: CollectionLinkedSearch[] = collection.savedSearches || [];

  if (linkedSavedSearches.length === 0 || !params || !isTrashListingsModalEnabled()) {
    return null;
  }

  const hasMultipleSavedSearches = linkedSavedSearches.length > 1;
  let poptipText = POPTIP_TEXT;

  if (hasMultipleSavedSearches) {
    poptipText = MULTIPLE_SEARCHES_POPTIP_TEXT;
  } else if (isTrashListingsInProgress) {
    poptipText = IN_PROGRESS_POPTIP_TEXT;
  }

  const shouldDisable = hasMultipleSavedSearches || isTrashListingsInProgress;
  const onClick = () => {
    dispatch(setTrashListingsModalTrigger(TRASH_LISTINGS_MODAL_TRIGGER.cleanUp));
    dispatch(
      setCurrentNav({
        ...params,
        modalId: MODAL_URL_IDS.trashListings,
      })
    );

    trackCleanUpInitiated({
      collection_id: collectionId,
      product: ANALYTICS_PRODUCT,
      product_path: ANALYTICS_PATH_CLEAN_UP_BUTTON,
    });
  };

  return (
    <div
      {...getPoptipAnchorProps(<div className={styles.refreshButtonPoptip}>{poptipText}</div>)}
      data-testid={WRAPPER_TEST_ID}
    >
      <Button disabled={shouldDisable} onClick={onClick} variant="enclosed--highEmph" size="md">
        {REFRESH_BUTTON_TEXT}
      </Button>
      <Poptip {...poptipProps} zIndex="var(--cx-zIndex-9)" alignment="top" />
    </div>
  );
};
