import React from 'react';

import {Poptip, usePoptip} from '@uc/cx.react';

import {useActionBarItems, GROUP_ID, useAction} from '@src/components/ActionBar/logic';
import {ActionBarProps} from '@src/components/ActionBar/types';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useSelectedIds} from '@src/store';

export const PrintOptions = (props: ActionBarProps) => {
  const {mode} = props;
  const {bar} = useActionBarItems({mode});
  const items = bar[GROUP_ID.PRINT];
  const onClick = useAction(props);
  const selected = useSelectedIds();
  const isDisabled = selected.length > 8;
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  if (items.length === 0) {
    return null;
  }

  return (
    <ButtonGroup
      {...getPoptipAnchorProps(
        'Up to 8 listings can be selected to access multi listing print tools',
        'top'
      )}
    >
      <Dropdown
        variant="enclosed--highEmph"
        size="sm"
        iconLeft="Printer"
        iconRight="ChevronDown"
        labelIsHidden
        label="Print"
        items={items}
        alignment="bottomLeft"
        onItemSelect={(id, selected) => {
          selected && onClick(id);
        }}
        disabled={isDisabled}
      />
      {isDisabled && <Poptip {...poptipProps} style={{maxWidth: 'none'}} />}
    </ButtonGroup>
  );
};
