import {CollectionListing} from '@src/types';

export const getListingIds = (collectionListings: CollectionListing[] | undefined) => {
  if (!collectionListings) {
    return [];
  }

  return collectionListings.map(
    (collectionListing) =>
      collectionListing.listingId || collectionListing.listingData?.listingIdSHA || ''
  );
};
