import {COLLECTION_LISTINGS_STATUS_FILTER_OPTIONS} from '@src/constants/filter';
import {CollectionListingsStatusFilter} from '@src/types';
import {getSelectedListingStatusFilterIds} from '@src/utils/getSelectedListingStatusFilterIds';

// eslint-disable-next-line @typescript-eslint/no-type-alias
type IParams = {
  collectionListingsStatusFilter?: CollectionListingsStatusFilter[];
};

// eslint-disable-next-line @typescript-eslint/no-type-alias
type StatusParams = {
  collectionListingsStatusFilters?: CollectionListingsStatusFilter[];
};

export const getCollectionListingsStatusFilterParams = (
  filterStr: string | undefined
): StatusParams => {
  if (filterStr === undefined || filterStr.length === 0) {
    return {collectionListingsStatusFilters: undefined};
  }

  const filters = getSelectedListingStatusFilterIds(filterStr);
  const filterParamsArr: StatusParams[] = filters.map(
    (filter) => COLLECTION_LISTINGS_STATUS_FILTER_OPTIONS[filter]
  );
  const paramsObj = {
    collectionListingsStatusFilters: filterParamsArr
      .filter((f) => f['collectionListingsStatusFilters'] !== undefined)
      .map((f) => f['collectionListingsStatusFilters'])
      .flat() as CollectionListingsStatusFilter[],
  };

  return paramsObj;
};
