import React, {useCallback, useEffect} from 'react';

import {McTemplates} from '@uc/action--create-mc-document.react';
import loadModal from '@uc/modal-loader';

import {COMPOSER_MODAL_URI, SHARE_LISTINGS_MODAL_URI} from '@src/constants';
import {useExportListings} from '@src/hooks/useExportListings';
import {usePrintListings} from '@src/hooks/usePrintListings';
import {CollectionListing} from '@src/types';

interface ListingDetailsProps {
  listing: CollectionListing;
  agentId?: string;
  onDismiss: () => void;
}

interface EventType {
  data: {
    iFrameListingPage: {action: string; composerParams: Record<string, unknown>; version: string};
  };
}

export const getIframeTestId = (listingId: string | undefined) => `listing-iframe-${listingId}`;

export const ListingDetails = ({listing, agentId, onDismiss}: ListingDetailsProps) => {
  const exportListings = useExportListings(listing.listingId ? [listing.listingId] : []);
  const printListings = usePrintListings({
    listings: listing && [listing],
    targetTemplate: McTemplates.ONE_SHEET,
  });
  const handleIframeTask = useCallback(
    (event: EventType): void => {
      if (event.data && event.data.iFrameListingPage) {
        const iFrameListingPage = event.data.iFrameListingPage;

        if (iFrameListingPage.action === '.email') {
          loadModal(SHARE_LISTINGS_MODAL_URI, {
            listings: [listing],
            sourceApp: 'Collections',
          }).catch(() => {
            // the modal promise rejects if the modal is closed, but we don't care about logging that at
            // this time. Catch it to prevent a JS error
          });
        } else if (iFrameListingPage.action === '.compose-email') {
          const {composerParams, version} = iFrameListingPage;

          loadModal(`${COMPOSER_MODAL_URI}/${version}`, {
            ...composerParams,
            analytics: {
              product: 'Collections',
              productPath: 'Listing iFrame',
            },
          });
        } else if (iFrameListingPage.action === '.download') {
          exportListings();
        } else if (iFrameListingPage.action === '.printTemplateBuilder') {
          printListings();
        }

        onDismiss();
      }
    },
    [exportListings, listing, onDismiss, printListings]
  );

  useEffect(() => {
    window.addEventListener('message', handleIframeTask);

    return () => {
      window.removeEventListener('message', handleIframeTask);
    };
  }, [handleIframeTask]);

  const listingIframeURL = (link: string): string => {
    let iframeUrl = link + 'view_iframe?hide_contacts=true&hide_save_action_btn=true';

    // If a consumer is viewing the listing and has a referring agent cookie set, pass that
    // info along to the iFrame.
    if (agentId) {
      iframeUrl += `?agent_id${agentId}`;
    }

    return iframeUrl;
  };

  return listing?.listingData?.pageLink ? (
    <iframe
      title="Listing page"
      src={listingIframeURL(listing.listingData.pageLink)}
      data-testid={getIframeTestId(listing.listingId)}
    />
  ) : (
    <></>
  );
};
