import {useQuery} from 'react-query';

import {ApiError, getBoard} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetBoardResponse} from '@src/types';

export const useBoard = ({collectionId}: {collectionId: string}) => {
  return useQuery<GetBoardResponse, ApiError>([API_KEY.GET_BOARD, collectionId], () =>
    getBoard({collectionId})
  );
};
