import {useMutation, useQueryClient} from 'react-query';

import {favoriteListings} from '@src/api';
import {getToastMessage} from '@src/components/ActionBar/logic';
import {ACTION_ID} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {
  useCollectionListingPaginatedQueryKey,
  UseGetCollectionListingsPaginatedResponse,
} from '@src/hooks/useGetCollectionListingsPaginated';
import {useDispatch, setToast} from '@src/store';
import {CollectionListingReviewStage} from '@src/types';

import {useIsInClientCommentModal} from './useIsInClientCommentModal';

export const useFavoriteListings = (
  {
    showSuccessToast,
  }: {
    showSuccessToast: boolean;
  } = {showSuccessToast: true}
) => {
  const client = useQueryClient();
  const dispatch = useDispatch();
  const isInClientCommentModal = useIsInClientCommentModal();
  const collectionListingsQueryKey = useCollectionListingPaginatedQueryKey();

  return useMutation(
    async ({collectionId, listingIds}: {collectionId: string; listingIds: string[]}) => {
      const toastMessage = getToastMessage({
        actionId: ACTION_ID.FAVORITE,
        listingsLength: listingIds.length,
      });

      try {
        const result = await favoriteListings({collectionId, listingIds});

        if (showSuccessToast) {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'success',
              message: toastMessage?.success,
            })
          );
        }

        // Don't invalidate queries for client comment modal because of cache updates
        if (!isInClientCommentModal) {
          client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_PAGINATED);
        }

        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_FACETS);
        client.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_SUMMARY);

        return result;
      } catch (e) {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: toastMessage?.error,
          })
        );
        return;
      }
    },
    {
      // Update the paginatedListings cache to mark listing as favorite
      onSuccess: (_, {listingIds}) => {
        if (isInClientCommentModal) {
          const previousListings = client.getQueryData(
            collectionListingsQueryKey
          ) as UseGetCollectionListingsPaginatedResponse;

          previousListings?.currentPageListings?.find((listing) => {
            if (listingIds.includes(listing.listingId || '')) {
              if (listing.reviewStage) {
                listing.reviewStage.currentStage = CollectionListingReviewStage.FAVORITE;
              } else {
                listing.reviewStage = {currentStage: CollectionListingReviewStage.FAVORITE};
              }
            }
          });

          client.setQueryData(collectionListingsQueryKey, previousListings);
        }
      },
    }
  );
};
