const MS_IN_MINUTE = 1000 * 60;

export const MS_IN_DAY = MS_IN_MINUTE * 60 * 24;

const MONTHS = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

export const areTimesWithinXMinutes = ({
  times,
  minutes,
}: {
  times: [number, number];
  minutes: number;
}) => {
  return Math.abs(times[0] - times[1]) < minutes * MS_IN_MINUTE;
};

export const getDisplayedTime = (timestamp: number) => {
  const datetime = new Date(timestamp);

  return datetime.toLocaleString('en-US', {hour: 'numeric', minute: '2-digit', hour12: true});
};

export const getDisplayedDateString = (timestamp: number) => {
  const todayTime = Date.now();
  const yesterdayTime = Date.now() - MS_IN_DAY;

  if (getUniqueDateString(timestamp) === getUniqueDateString(todayTime)) {
    return 'Today';
  } else if (getUniqueDateString(timestamp) === getUniqueDateString(yesterdayTime)) {
    return 'Yesterday';
  } else {
    const date = new Date(timestamp);

    return `${MONTHS[date.getMonth()]} ${date.getDate()}`;
  }
};

export const getUniqueDateString = (timestamp: number) => {
  const date = new Date(timestamp);

  return `${date.getMonth()}_${date.getDate()}_${date.getFullYear()}`;
};
