import {BBox} from '@turf/helpers';
import {ContainerSize, toClusterFeature} from '@uc-maps/geospatial';
import {useMemo, useState, useEffect} from 'react';

import {SearchQuery} from '@uc/search-query';

import {getMapSearchQuery} from '@src/components/NewMap/MapUtils';
import {useDebouncedValue} from '@src/hooks/useDebouncedValue';
import {useLoadMapSearch} from '@src/hooks/useLoadMapSearch';
import {RelationCluster, MapSearchResultsType} from '@src/types';

export function useMapData({
  searchResults,
  mapOptions,
  searchQuery,
}: {
  searchResults: MapSearchResultsType;
  mapOptions: {bbox: BBox; containerSize: ContainerSize};
  searchQuery?: SearchQuery;
}) {
  const mapSearchQuery = useMemo(
    () => getMapSearchQuery({searchResults, mapOptions, searchQuery}),
    [searchResults, mapOptions, searchQuery]
  );
  // stringify the mapSearchQuery to prevent get new value in every render after call useDebouncedValue
  const debouncedMapSearchQueryString = useDebouncedValue(JSON.stringify(mapSearchQuery), 500);
  const {data, isLoading} = useLoadMapSearch({
    enabled: searchResults.length > 0,
    mapSearchQuery: JSON.parse(debouncedMapSearchQueryString),
  });
  const [relationClusters, setRelationClusters] = useState<RelationCluster[]>([]);

  useEffect(() => {
    if (!isLoading) {
      setRelationClusters(data?.relationClusters ?? []);
    }
  }, [data, isLoading]);

  return relationClusters.map(toClusterFeature);
}
