import React from 'react';

import {Poptip, Avatar, usePoptip} from '@uc/cx.react';

import {useIsAgent} from '@src/store';
import {CollectionSharedCollaborator} from '@src/types';

import {getTruncatedAvatars} from './getTruncatedAvatars';

export const AvatarsWrapper = ({
  collectionOwnerAndcollaborators = [],
  handleOnClickInvite,
}: {
  collectionOwnerAndcollaborators: CollectionSharedCollaborator[];
  handleOnClickInvite: () => void;
}) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const isAgent = useIsAgent();
  const handleClick = (collaborator: CollectionSharedCollaborator) => {
    if (isAgent && collaborator.contact?.contactId) {
      window.location.href = '/app/contacts/' + collaborator.contact?.contactId;
    } else {
      handleOnClickInvite();
    }
  };
  const collaboratorsAvatars = collectionOwnerAndcollaborators
    .filter((collaborator) => collaborator.userId && collaborator.person)
    .map((collaborator) => {
      return (
        <Avatar
          key={collaborator.userId}
          size="lg"
          image={collaborator.person?.imagePath}
          firstName={collaborator.person?.firstName}
          lastName={collaborator.person?.lastName}
          className="collection-header-avatar"
          onClick={() => handleClick(collaborator)}
          data-testid="avatar-contacts"
          {...getPoptipAnchorProps(collaborator.person?.displayName, 'bottom')}
        />
      );
    });
  const truncatedAvatars = getTruncatedAvatars({collaboratorsAvatars, handleOnClickInvite});

  return (
    <div className="avatars-wrapper">
      <Poptip {...poptipProps} />
      {truncatedAvatars}
    </div>
  );
};
