import {COLLECTION_MODE, CollectionMode} from '@src/constants';
import {FilterId} from '@src/constants/filter';
import {CollectionListingsFilter} from '@src/types';

export const getCollectionListingFilterParams = ({
  collectionMode,
  filterId,
}: {
  collectionMode: CollectionMode;
  filterId?: FilterId;
}): CollectionListingsFilter | undefined => {
  // Note: COLLECTION_MODE.SHARED is handled at the end of the function
  // becuase the filterId should have higher priority/specificity.
  switch (collectionMode) {
    // Consumer Modes
    case COLLECTION_MODE.MATCHES:
      return CollectionListingsFilter.RECOMMEDED;
    case COLLECTION_MODE.FAVORITES:
      return CollectionListingsFilter.FAVORITED;
    case COLLECTION_MODE.NOT_INTERESTED:
      return CollectionListingsFilter.NOT_INTERESTED;
    // Agent Modes
    case COLLECTION_MODE.TRASH:
      return CollectionListingsFilter.TRASHED;
  }

  switch (filterId) {
    case 'all':
      return CollectionListingsFilter.ALL_SHARED;
    case 'pending':
      return CollectionListingsFilter.PENDING_CLIENT_ACTION;
    case 'interested':
      return CollectionListingsFilter.FAVORITED;
    case 'hasComments':
      return CollectionListingsFilter.HAS_COMMENTS;
    case 'notInterested':
      return CollectionListingsFilter.NOT_INTERESTED;
    case 'wantToSee':
      return CollectionListingsFilter.SHOWING_REQUESTED;
  }

  if (collectionMode === COLLECTION_MODE.SHARED) {
    return CollectionListingsFilter.ALL_SHARED;
  }

  return undefined;
};
