import React, {useCallback, useEffect, useRef, useState} from 'react';
// eslint-disable-next-line no-restricted-imports
import {useQueryClient} from 'react-query';

import {collectionCleanupBulkTrash} from '@uc/analytics-definitions';
import {
  Button,
  Icon,
  Modal,
  ModalProps,
  Poptip,
  Radio,
  Row,
  RowDivider,
  useModal,
  usePoptip,
} from '@uc/cx.react';

import {
  TRASH_LISTINGS_OPTION,
  TRASH_LISTINGS_MODAL_TRIGGER,
  TrashListingsOption,
  TRASH_LISTINGS_CLOSE_METHOD,
  TrashListingsCloseMethod,
} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {useTrashListings, useUpdateCurrentUrl} from '@src/hooks';
import {useCurrentUrlData} from '@src/hooks/url';
import {useObsoleteSavedSearchListings} from '@src/hooks/useObsoleteSavedSearchListings';
import {getTrashListingsModalTrigger, TrashListingsModalTrigger, useSelector} from '@src/store';
import {isTrashListingsModalEnabled} from '@src/utils/optyUtils';

import './TrashListingsModal.pcss';

interface ObsoleteListings {
  active: string[];
  inactive: string[];
}

export const ANALYTICS_PRODUCT = 'collections';
export const ANALYTICS_PRODUCT_PATH = 'trash_listings_modal';
export const CONTAINER_TEST_ID = 'collections-trash-listings-modal';
export const POPTIP_TEST_ID = 'trash-listings-activity-poptip';
export const SUBHEADER_TEST_ID = 'trash-listings-activity-subheader';
export const FOOTER_TEST_ID = 'trash-listings-footer';
export const CAP_COUNT_NOTE_TEST_ID = 'trash-listings-note';

const formatPlural = (word: string, array: unknown[]) => {
  if (array.length === 1) {
    return word;
  } else {
    return `${word}s`;
  }
};

export const capNumber = (n: number, limit = 500) => {
  if (n <= limit) {
    return `${n}`;
  } else {
    return `${limit}+`;
  }
};

const ActivityPoptip = () => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  return (
    <span className="activity-poptip">
      <Icon.CircleInfo {...getPoptipAnchorProps(POPTIP_TEXT, 'top')} data-testid={POPTIP_TEST_ID} />
      <Poptip {...poptipProps} />
    </span>
  );
};

export const getInactiveOptionText = (listings: ObsoleteListings) => {
  return (
    <div>
      Keep {capNumber(listings.active.length)} {formatPlural('listing', listings.active)} with
      activity <ActivityPoptip /> and move {capNumber(listings.inactive.length)}{' '}
      {formatPlural('other', listings.inactive)} to Trash
    </div>
  );
};

export const getOptionTestId = (option: string) => `trash-listings-option-${option}`;

export const getOptionLabelTestId = (option: string) => `trash-listings-option-label-${option}`;

export const getAllOptionText = (allListings: string[]) =>
  `Move ${capNumber(allListings.length)} ${formatPlural('listing', allListings)} to Trash`;

export const NONE_OPTION_TEXT = 'Do not move any listings to Trash';

export const getOptionId = (value: string) => `trash-listings-option-${value}`;

export const POPTIP_TEXT = 'Favorites, comments, tour requests';

export const CLEAN_UP_HEADER_TEXT = 'Clean up collection';

export const NO_OBSOLETE_MESSAGE_TEXT =
  'All listings in this collection match your search criteria. No listings were moved to Trash.';

export const NO_OBSOLETE_CONFIRMATION_TEXT = 'Got it';

export const POST_SAVE_HEADER_TEXT = 'Your saved search has been updated';

export const CAP_COUNT_NOTE_TEXT = `Only 500 listings can live in a collection for your client to review at a time.
If your trashed listing count is 500+, all listings that do not match the new search
are being trashed, not just those that were visible.`;

export const OTHER_SEARCHES_NOTE_TEXT =
  'Listings added manually and matches from other saved searches will not be affected.';

export const BASIC_NOTE_TEXT = 'Listings added manually will not be affected.';

interface ModalBodyProps {
  obsoleteListings: ObsoleteListings;
  selectOption: (option: TrashListingsOption) => void;
  selectedOption: TrashListingsOption;
  onFirstRender: () => void;
  trigger?: TrashListingsModalTrigger;
}

export const ModalBody: React.FC<ModalBodyProps> = ({
  obsoleteListings,
  selectedOption,
  selectOption,
  trigger,
  onFirstRender,
}) => {
  useEffect(() => {
    onFirstRender();
  }, [onFirstRender]);
  // Omit copy about other saved searches for clean up trigger because
  // we don't support multiple saved searches in this action
  const noteText =
    trigger === TRASH_LISTINGS_MODAL_TRIGGER.cleanUp ? BASIC_NOTE_TEXT : OTHER_SEARCHES_NOTE_TEXT;
  const allListings = obsoleteListings.active.concat(obsoleteListings.inactive);
  let trashOptions = [
    {
      label: getInactiveOptionText(obsoleteListings),
      value: TRASH_LISTINGS_OPTION.inactive,
      toggle: () => {
        selectOption(TRASH_LISTINGS_OPTION.inactive);
      },
    },
    {
      label: getAllOptionText(allListings),
      value: TRASH_LISTINGS_OPTION.all,
      toggle: () => {
        selectOption(TRASH_LISTINGS_OPTION.all);
      },
    },
    {
      label: NONE_OPTION_TEXT,
      value: TRASH_LISTINGS_OPTION.none,
      toggle: () => {
        selectOption(TRASH_LISTINGS_OPTION.none);
      },
    },
  ];
  const hasActiveListings = obsoleteListings.active.length > 0;
  const hasInactiveListings = obsoleteListings.inactive.length > 0;
  const hasCappedListingCount = allListings.length > 500;
  const cappedAllListings = capNumber(allListings.length);
  const cappedActiveListings = capNumber(obsoleteListings.active.length);

  if (!hasActiveListings || !hasInactiveListings) {
    trashOptions = trashOptions.filter((o) => o.value !== TRASH_LISTINGS_OPTION.inactive);
  }

  return (
    <div>
      <div
        className="trash-listings-modal-subheader"
        role="complementary"
        data-testid={SUBHEADER_TEST_ID}
      >
        <div>
          {cappedAllListings} listings no longer {allListings.length === 1 ? 'matches' : 'match'}{' '}
          your search criteria.
        </div>
        {(!hasActiveListings || !hasInactiveListings) && (
          <div className="trash-listings-modal-subheader-note">
            {cappedActiveListings} of {cappedAllListings}{' '}
            {formatPlural('listing', obsoleteListings.active)}{' '}
            {obsoleteListings.active.length === 1 ? 'has' : 'have'} activity <ActivityPoptip />
          </div>
        )}
      </div>
      <div
        role="group"
        aria-label="Clear Listings Options"
        className="trash-listings-modal-options"
      >
        {trashOptions.map((option, i) => (
          <div key={option.value}>
            <Row variant="input" key={option.value} className="checkbox-card-row">
              <Row.Accessory>
                <Radio
                  name="trash-listings-modal-option"
                  id={getOptionId(option.value)}
                  checked={selectedOption === option.value}
                  onChange={option.toggle}
                  data-testid={getOptionTestId(option.value)}
                />
              </Row.Accessory>
              <Row.Label
                data-testid={getOptionLabelTestId(option.value)}
                className="trash-listings-modal-option-label"
                htmlFor={getOptionId(option.value)}
              >
                {option.label}
              </Row.Label>
            </Row>
            {i < trashOptions.length - 1 && <RowDivider variant="fullBleed" />}
          </div>
        ))}
      </div>
      <div className="trash-listings-modal-note" role="note">
        {noteText}
        <br />
        Trashed listings are completely removed from the collection for your clients.
        {hasCappedListingCount && (
          <div data-testid={CAP_COUNT_NOTE_TEST_ID}>
            <br />
            {CAP_COUNT_NOTE_TEXT}
          </div>
        )}
      </div>
    </div>
  );
};

interface NoObsoleteListingsModalProps {
  modalProps: ModalProps;
  closeModal: () => void;
}

export const NoObsoleteListingsModal: React.FC<NoObsoleteListingsModalProps> = ({
  modalProps,
  closeModal,
}) => (
  <Modal {...modalProps} size="sm" className="trash-listings-modal" data-testid={CONTAINER_TEST_ID}>
    <Modal.Header
      closeButtonProps={{
        onClick: closeModal,
      }}
    >
      {CLEAN_UP_HEADER_TEXT}
    </Modal.Header>
    <Modal.Body className="trash-listings-modal-body">{NO_OBSOLETE_MESSAGE_TEXT}</Modal.Body>
    <Modal.Footer data-testid={FOOTER_TEST_ID}>
      <Button onClick={closeModal}>{NO_OBSOLETE_CONFIRMATION_TEXT}</Button>
    </Modal.Footer>
  </Modal>
);

export const TrashListingsModal = () => {
  const {mutate: trashListings} = useTrashListings();
  const queryClient = useQueryClient();
  const {collectionId, modalId} = useCurrentUrlData();
  // Use cache key to invalidate cache when the component is remounted
  const setParams = useUpdateCurrentUrl();
  const {isLoading, data} = useObsoleteSavedSearchListings();
  const [selectedOption, selectOption] = useState<TrashListingsOption | null>(null);
  const trigger = useSelector(getTrashListingsModalTrigger);
  const [haveOptionsBeenShown, setHaveOptionsBeenShown] = useState(false);
  const [haveQueriesBeenRefetched, setHaveQueriesBeenRefetched] = useState(false);

  useEffect(() => {
    // We want to ensure queries are refetched every time component mounts
    if (!isLoading) {
      queryClient.resetQueries(API_KEY.GET_OBSOLETE_SAVED_SEARCH_LISTINGS);
    }

    setHaveQueriesBeenRefetched(true);
    // We only want this check to run once on mount, even if isLoading changes
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // We are using a ref here instead of state because
  //   1. modal component has not clickaway callback, so we need to use onIsOpenChange to track analytics
  //   2. onIsOpenChange runs immediately after closeModal, before state changes can take place
  //   3. onIsOpenChange fires more times than expected, ref ensures duplicate analytics don't send (see tests)
  const haveAnalyticsBeenTracked = useRef(false);
  const {modalProps, closeModal} = useModal({
    initialIsOpen: true,
    onIsOpenChange: (isOpen) => {
      if (!isOpen) {
        // This is a hack to get around the fact that we can't bind an event to the click away action
        // If analytics have not been tracked another away, assume the modal was closed by click-away
        if (haveOptionsBeenShown && !haveAnalyticsBeenTracked.current) {
          trackAnalytics({
            option: TRASH_LISTINGS_OPTION.none,
            closeMethod: TRASH_LISTINGS_CLOSE_METHOD.clickAway,
          });
        }

        if (modalId) {
          setParams({
            modalId: undefined,
          });
        }
      }
    },
  });
  const onFirstBodyRender = useCallback(() => {
    setHaveOptionsBeenShown(true);
  }, []);
  const obsoleteListings = {
    active: data?.activeObsoleteListingIds || [],
    inactive: data?.inactiveObsoleteListingIds || [],
  };
  let defaultOption: TrashListingsOption = TRASH_LISTINGS_OPTION.inactive;

  if (obsoleteListings.active.length === 0) {
    defaultOption = TRASH_LISTINGS_OPTION.all;
  } else if (obsoleteListings.inactive.length === 0) {
    defaultOption = TRASH_LISTINGS_OPTION.none;
  }

  const selectionOrDefault = selectedOption || defaultOption;
  const isFeatureActive = isTrashListingsModalEnabled();
  const shouldShowWhenEmptyList = trigger === TRASH_LISTINGS_MODAL_TRIGGER.cleanUp;
  const isEmptyList =
    !obsoleteListings || obsoleteListings.active.length + obsoleteListings.inactive.length === 0;

  useEffect(() => {
    const shouldCloseDueToEmptyList =
      !isLoading && modalId && isEmptyList && !shouldShowWhenEmptyList;

    if (!isFeatureActive || shouldCloseDueToEmptyList) {
      closeModal();
    }
  });

  const trackAnalytics = (params: {
    option: TrashListingsOption;
    closeMethod: TrashListingsCloseMethod;
  }) => {
    const {option, closeMethod} = params;

    haveAnalyticsBeenTracked.current = true;

    collectionCleanupBulkTrash({
      collection_id: collectionId,
      product: ANALYTICS_PRODUCT,
      product_path: ANALYTICS_PRODUCT_PATH,
      trash_option: option,
      agent_client_activity:
        option === TRASH_LISTINGS_OPTION.all ? obsoleteListings.active.length > 0 : undefined,
      triggering_action: trigger || TRASH_LISTINGS_MODAL_TRIGGER.postSave,
      close_method: closeMethod,
    });
  };

  if (!isFeatureActive || isLoading || !haveQueriesBeenRefetched) {
    return null;
  }

  if (isEmptyList) {
    return shouldShowWhenEmptyList ? (
      <NoObsoleteListingsModal modalProps={modalProps} closeModal={closeModal} />
    ) : null;
  }

  const allListings = [...obsoleteListings.active, ...obsoleteListings.inactive];
  const listingsToTrash: string[] = {
    [TRASH_LISTINGS_OPTION.inactive]: obsoleteListings.inactive,
    [TRASH_LISTINGS_OPTION.all]: allListings,
    [TRASH_LISTINGS_OPTION.none]: [],
  }[selectionOrDefault];
  const headerCopy =
    trigger === TRASH_LISTINGS_MODAL_TRIGGER.cleanUp ? CLEAN_UP_HEADER_TEXT : POST_SAVE_HEADER_TEXT;
  const onSubmit = () => {
    if (listingsToTrash.length > 0) {
      trashListings({
        collectionId,
        listingIds: listingsToTrash,
      });
    }

    trackAnalytics({
      closeMethod: TRASH_LISTINGS_CLOSE_METHOD.submit,
      option: selectionOrDefault,
    });

    closeModal();
  };
  const onClickX = () => {
    trackAnalytics({
      closeMethod: TRASH_LISTINGS_CLOSE_METHOD.clickX,
      option: TRASH_LISTINGS_OPTION.none,
    });

    closeModal();
  };

  return (
    <Modal
      {...modalProps}
      size="lg"
      className="trash-listings-modal"
      data-testid={CONTAINER_TEST_ID}
    >
      <Modal.Header
        closeButtonProps={{
          onClick: onClickX,
        }}
      >
        {headerCopy}
      </Modal.Header>
      <Modal.Body className="trash-listings-modal-body">
        <ModalBody
          obsoleteListings={obsoleteListings}
          selectOption={selectOption}
          selectedOption={selectionOrDefault}
          trigger={trigger}
          onFirstRender={onFirstBodyRender}
        />
      </Modal.Body>
      <Modal.Footer data-testid={FOOTER_TEST_ID}>
        <Button onClick={onSubmit}>
          {selectionOrDefault === TRASH_LISTINGS_OPTION.none
            ? 'Done'
            : `Move ${capNumber(listingsToTrash.length)} ${formatPlural(
                'listing',
                listingsToTrash
              )} to Trash`}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
