/* eslint-disable @typescript-eslint/no-type-alias */
import {Location} from 'history';
import {matchPath} from 'react-router-dom';

import {
  CollectionMode,
  COLLECTION_MODE,
  DEFAULT_PAGINATION,
  ModalId,
  COLLECTION_MODE_TO_TAB,
  MODAL_URL_IDS,
  MODAL_URL_TO_ID,
  CollectionTab,
  CommentsModalTab,
  EMPTY_PAGE_PATH,
  ERROR_PAGE_PATH,
  ROOT_PAGE_PATH,
  AGENT_COLLECTION_MODE,
  CONSUMER_COLLECTION_MODE,
  AgentCollectionTab,
  ConsumerCollectionTab,
  COLLECTION_TAB_TO_MODE,
  AgentCollectionMode,
  ConsumerCollectionMode,
} from '@src/constants';
import {FilterId, FILTER_KEYS} from '@src/constants/filter';
import {SortId, SORT_OPTIONS} from '@src/constants/sort';
import {CollectionUrlData, LocationState} from '@src/store';

function toInt(i: string | null | number | undefined) {
  if (i == null) return undefined;
  const parsed = typeof i === 'number' ? i : parseInt(i);

  return isNaN(parsed) ? undefined : parsed;
}

function toBoolean(i: string | null) {
  if (i === 'true') return true;
  return false;
}

function toSearchString(params: {
  source?: string;
  page: number;
  pageSize: number;
  sort?: SortId;
  savedSearchId?: string;
  filter?: FilterId;
  filterByOpenHouses?: boolean;
  filterByLikelyToSell?: boolean;
  filterByListingStatus?: string;
  listingIds?: string;
  currentListingIndex?: number;
  address?: string;
  enableModalPagination?: boolean;
}): string {
  const {source, ...paramsExceptSource} = params;
  const entriesExceptSource = Object.entries(paramsExceptSource).sort();
  const entries: [string, unknown][] = [['source', source], ...entriesExceptSource];
  let filteredEntries = omitUndefined(entries);

  filteredEntries = omitFalsy(filteredEntries, ['enableModalPagination']);

  return '?' + filteredEntries.map(([key, value]) => `${key}=${value}`).join('&');
}

function omitUndefined(entries: [key: string, value: unknown][]) {
  return entries.filter(function ([, value]) {
    return value !== undefined;
  });
}

function omitFalsy(
  entries: [key: string, value: unknown][],
  matchKeys: (keyof CollectionUrlData)[]
) {
  return entries.filter(function ([key, value]) {
    return value || key in matchKeys;
  });
}

function getValidFilter(str: string | null): FilterId {
  if (!str) return 'all';
  return FILTER_KEYS.find((id) => id === str) || 'all';
}

const SORT_KEYS = Object.keys(SORT_OPTIONS) as SortId[];

function getValidSort(str: string | null): SortId | undefined {
  if (!str) return 'time_added';
  return SORT_KEYS.find((id) => id === str) || 'time_added';
}

// Route will allow any alphanumeric in id (to avoid less helpful generic route error),
// but this is verified with more strict character requirements later
const COLLECTION_ID = ':collectionId([0-9a-zA-Z]+)';
const TAB = `:tab(${Object.values(COLLECTION_MODE_TO_TAB).join('|')})`;
const MODAL = `:modalId(${Object.values(MODAL_URL_IDS).join('|')})`;
// listingId format is defined by listing-services-team
// https://github.com/UrbanCompass/urbancompass/blob/master/src/java/com/urbancompass/id_generation_64_bits/IdArbiter.java#L118
const LISTING_ID = ':listingId([0-9a-f]+)';
const NOT_MATCHED_URL = ':unrecognized(.+)';
const ROUTE_MATCH = [
  // empty page for consumer view
  `${EMPTY_PAGE_PATH}`,
  `${ERROR_PAGE_PATH}`,
  `${ROOT_PAGE_PATH}`,
  // board ID followed by tab, with modal option and listingId
  `/${COLLECTION_ID}/${TAB}/${MODAL}?/${LISTING_ID}?`,
  // board ID followed by optional modal
  `/${COLLECTION_ID}/${MODAL}?/${LISTING_ID}?`,
  `/${COLLECTION_ID}/${NOT_MATCHED_URL}`,
];

interface PathMatch {
  collectionId?: string;
  tab?: CollectionTab;
  modalId?: ModalId;
  listingId?: string;
}

const getCollectionMode = ({
  isAgent,
  tab,
}: {
  isAgent: boolean;
  tab?: AgentCollectionTab | ConsumerCollectionTab;
}) => {
  const collectionMode = tab && COLLECTION_TAB_TO_MODE[tab];

  if (isAgent && collectionMode) {
    return Object.values(AGENT_COLLECTION_MODE).indexOf(collectionMode as AgentCollectionMode) > -1
      ? collectionMode
      : AGENT_COLLECTION_MODE.SHARED;
  }

  if (!isAgent && collectionMode) {
    return Object.values(CONSUMER_COLLECTION_MODE).indexOf(
      collectionMode as ConsumerCollectionMode
    ) > -1
      ? collectionMode
      : CONSUMER_COLLECTION_MODE.FAVORITES;
  }

  return isAgent ? AGENT_COLLECTION_MODE.SHARED : CONSUMER_COLLECTION_MODE.FAVORITES;
};

export function getCollectionUrlData(
  {
    search,
    pathname,
    locationState,
  }: {
    pathname: string;
    search: string;
    locationState: LocationState;
  },
  {
    isAgent = false,
    pageSizesByCollectionId = {},
    source,
  }: {
    isAgent?: boolean;
    pageSizesByCollectionId?: Record<string, number | undefined>;
    source?: string;
  } = {}
): CollectionUrlData | undefined {
  const match = matchPath<PathMatch>(pathname, {path: ROUTE_MATCH, exact: true});

  if (!match) {
    return;
  }

  if (
    match.path === EMPTY_PAGE_PATH ||
    match.path === ERROR_PAGE_PATH ||
    // eslint-disable-next-line @typescript-eslint/no-extra-parens
    (!isAgent && match.path === ROOT_PAGE_PATH)
  ) {
    return {
      path: match.path,
      page: DEFAULT_PAGINATION.currentPage,
      pageSize: DEFAULT_PAGINATION.pageSize,
      collectionId: '',
      collectionMode: COLLECTION_MODE.FAVORITES,
    };
  } else if (isAgent && match.path === ROOT_PAGE_PATH) {
    return {
      path: match.path,
      page: DEFAULT_PAGINATION.currentPage,
      pageSize: DEFAULT_PAGINATION.pageSize,
      collectionId: '',
      collectionMode: COLLECTION_MODE.SHARED,
    };
  }

  if (match.params.collectionId) {
    const _s = new URLSearchParams(search);
    const {collectionId, tab, modalId, listingId} = match.params;
    const collectionMode = getCollectionMode({isAgent, tab});
    // if without pageSize in url, try get the page stored in redux by the user last set
    const pageSize =
      toInt(_s.get('pageSize')) ||
      pageSizesByCollectionId[collectionId] ||
      DEFAULT_PAGINATION.pageSize;

    return {
      collectionId,
      modalId: modalId ? MODAL_URL_TO_ID[modalId] : undefined,
      collectionMode,
      listingId,
      pageSize,
      listingIds:
        _s.get('listingIds')?.split(',') || _s.get('listingIdSHAs')?.split(',') || undefined,
      currentListingIndex: toInt(_s.get('currentListingIndex')) || undefined,
      page: toInt(_s.get('page') || 1) || DEFAULT_PAGINATION.currentPage,
      sortId: getValidSort(_s.get('sort')),
      savedSearchId: _s.get('savedSearchId') || undefined,
      filterId: getValidFilter(_s.get('filter')),
      filterByOpenHouses: toBoolean(_s.get('filterByOpenHouses')) || undefined,
      filterByLikelyToSell: toBoolean(_s.get('filterByLikelyToSell')) || undefined,
      filterByListingStatus: _s.get('filterByListingStatus') || undefined,
      selectedCommentsModalTab: locationState?.selectedCommentsModalTab || undefined,
      address: _s.get('address') || undefined,
      source,
      enableModalPagination: toBoolean(_s.get('enableModalPagination')) || undefined,
    };
  }

  return;
}

export function toCollectionUrl({
  collectionId,
  modalId,
  collectionMode,
  page,
  pageSize,
  sortId,
  savedSearchId,
  filterId,
  filterByOpenHouses,
  filterByLikelyToSell,
  filterByListingStatus,
  listingId,
  listingIds,
  currentListingIndex,
  selectedCommentsModalTab,
  address,
  source,
  enableModalPagination,
}: {
  collectionId: string;
  modalId?: ModalId;
  collectionMode: CollectionMode;
  page?: number;
  pageSize?: number;
  sortId?: SortId;
  savedSearchId?: string;
  filterId?: FilterId;
  filterByOpenHouses?: boolean;
  filterByLikelyToSell?: boolean;
  filterByListingStatus?: string;
  listingId?: string;
  listingIds?: string[];
  currentListingIndex?: number;
  selectedCommentsModalTab?: CommentsModalTab;
  address?: string;
  source?: string;
  enableModalPagination?: boolean;
}): Location<LocationState> {
  const path = [collectionId];

  if (collectionMode !== COLLECTION_MODE.SHARED && collectionMode !== COLLECTION_MODE.FAVORITES) {
    path.push(COLLECTION_MODE_TO_TAB[collectionMode]);
  }

  if (modalId) {
    path.push(modalId);
  }

  if (listingId) {
    path.push(listingId);
  }

  return {
    pathname: '/' + path.join('/'),
    search: toSearchString({
      source,
      page: page || DEFAULT_PAGINATION.currentPage,
      pageSize: pageSize || DEFAULT_PAGINATION.pageSize,
      filter: collectionMode === COLLECTION_MODE.SHARED ? filterId || 'all' : undefined,
      filterByOpenHouses: filterByOpenHouses === true ? filterByOpenHouses : undefined,
      filterByLikelyToSell: filterByLikelyToSell === true ? filterByLikelyToSell : undefined,
      filterByListingStatus: filterByListingStatus || undefined,
      sort: sortId || 'time_added',
      savedSearchId,
      listingIds: listingIds ? listingIds.join(',') : undefined,
      currentListingIndex: currentListingIndex || undefined,
      address: address ? address : undefined,
      enableModalPagination: enableModalPagination === true ? enableModalPagination : false,
    }),
    state: {selectedCommentsModalTab},
    hash: '',
  };
}
