import React from 'react';

import {CommentsModal} from '@src/components/CommentsModal';
import {DiscussionModal} from '@src/components/DiscussionModal';
import {GoogleSheetCmaModal} from '@src/components/GoogleSheetCmaModal';
import {AgentShareModal} from '@src/components/InviteModal/AgentShareModal';
import {ConsumerInviteModal} from '@src/components/InviteModal/ConsumerInviteModal';
import {MarkVisitedModal} from '@src/components/MarkVisitedModal';
import {ReviewModal} from '@src/components/ReviewModal';
import {SettingsModal} from '@src/components/SettingsModal';
import {TrashListingsModal} from '@src/components/TrashListingsModal/TrashListingsModal';
import {MODAL_URL_IDS} from '@src/constants';
import {useSelector, getCurrentNav, useIsAgent} from '@src/store';

export const ModalController = () => {
  const params = useSelector(getCurrentNav);
  const isAgent = useIsAgent();

  if (!params?.collectionId) {
    return null;
  }

  const {collectionId, modalId} = params;

  switch (modalId) {
    case MODAL_URL_IDS.settings:
      return <SettingsModal collectionId={collectionId} />;
    case MODAL_URL_IDS.discussion:
      return <DiscussionModal />;
    case MODAL_URL_IDS.invite:
      return isAgent ? <AgentShareModal /> : <ConsumerInviteModal />;
    case MODAL_URL_IDS.listings:
      return <ReviewModal />;
    case MODAL_URL_IDS.comment:
    case MODAL_URL_IDS.listing:
      return <CommentsModal />;
    case MODAL_URL_IDS.markVisited:
      return <MarkVisitedModal collectionId={collectionId} />;
    case MODAL_URL_IDS.export:
      return <GoogleSheetCmaModal />;
    case MODAL_URL_IDS.trashListings:
      return <TrashListingsModal />;
    default:
      return null;
  }
};
