/* eslint-disable @typescript-eslint/no-extra-parens */
import Cookies from 'js-cookie';
import React, {useState, useEffect, useMemo} from 'react';

import formatListingUrl from '@uc/format-listing-url';
import loadModal from '@uc/modal-loader';

import {Loading} from '@src/components/Loading';
import {CollectionMode, CONFIRMATION_MODAL_URI, LISTING_COMMENTS_VIEWS_TAB} from '@src/constants';
import {
  useGetCollectionListingByListingId,
  useGetCollectionListingsPaginated,
  useShareListings,
} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useAddComment} from '@src/hooks/useAddComment';
import {useDeleteComment} from '@src/hooks/useDeleteComment';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';
import {useFavoriteListings} from '@src/hooks/useFavoriteListings';
import {useNotInterestedListings} from '@src/hooks/useNotInterestedListings';
import {usePageItemNavigation} from '@src/hooks/usePageItemNavigation';
import {useReadListingComment} from '@src/hooks/useReadListingComment';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {useTrashListings} from '@src/hooks/useTrashListings';
import {
  useIsAgent,
  useCurrentUser,
  getDeliverableId,
  useSelector,
  useDispatch,
  getReferringListingPath,
  clearReferringListingPathAndDeliverableId,
  getListingCount,
} from '@src/store';
import {
  Comment as CommentType,
  Collection,
  ListingCommentsModalResult,
  UnreadListingUpdates,
} from '@src/types';
import {notEmpty} from '@src/utils';
import {setListingFrameTrackParams} from '@src/utils/setListingFrameTrackParams';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import {agentLeadTokenKey} from '../constants';
import {CommentSection} from './CommentSection/CommentSection';
import {AgentFooter} from './Footer/AgentFooter';
import {ClientFooter} from './Footer/ClientFooter';
import {ModalHeader} from './Header/ModalHeader';
import {ModalSecondaryHeader} from './Header/ModalSecondaryHeader';
import {ListingDetails} from './ListingDetails/ListingDetails';

import './ListingCommentsViewsModal.pcss';

const useModalNavigation = ({
  listingIds,
  areListingsLoading,
  closeModal,
}: {
  listingIds: string[] | undefined;
  areListingsLoading: boolean;
  closeModal: () => void;
}) => {
  const {
    page: currentPage,
    pageSize,
    enableModalPagination,
    listingId: urlListingId,
    listingIds: urlListingIds,
  } = useCurrentUrlData();
  const setParams = useUpdateCurrentUrl();
  const totalListings = useSelector(getListingCount);
  let activeId = undefined;

  if (urlListingId) {
    activeId = urlListingId;
  } else if (urlListingIds && urlListingIds.length > 0) {
    activeId = urlListingIds[0];
  }

  return usePageItemNavigation({
    currentPage,
    pageSize,
    isDisabled: !enableModalPagination,
    pageIds: listingIds,
    activeId,
    totalItems: totalListings,
    isPageLoading: areListingsLoading,
    goToNextPage: () => {
      setParams({page: currentPage + 1});
    },
    goToPreviousPage: () => {
      setParams({page: currentPage - 1});
    },
    setActiveId: (id) => {
      setParams({listingIds: [id]});
    },
    closeNavigation: closeModal,
  });
};

export const ListingCommentsViewsModal = ({
  collection,
  collectionMode,
  hasCollaborators,
  onDismiss,
}: {
  collection: Collection;
  collectionMode: CollectionMode;
  hasCollaborators: boolean;
  onDismiss: (result?: ListingCommentsModalResult) => void;
}) => {
  const {collectionId = ''} = collection;
  const {collectionListings, isLoading: areListingsLoading} = useGetCollectionListingsPaginated();
  const listingIds: string[] = collectionListings.reduce((ids, listing) => {
    return ids.concat(listing.listingId ? [listing.listingId] : []);
  }, [] as string[]);
  const {
    activeIndex,
    activeId: listingId,
    paginationTotal,
    onNext,
    onPrevious,
    shouldDisableNext,
    shouldDisablePrevious,
    isPageLoading,
  } = useModalNavigation({
    listingIds,
    areListingsLoading,
    closeModal: onDismiss,
  });
  const {collectionListing: fetchedCollectionListing, isLoading: isFetchingListing} =
    useGetCollectionListingByListingId({listingId});
  // Check if listing is already on the current page, to avoid additional latency in showing the listing
  const pageCollectionListing = useMemo(
    () => collectionListings.find((collectionListing) => collectionListing.listingId === listingId),
    [collectionListings, listingId]
  );
  const collectionListing = useMemo(() => {
    if (pageCollectionListing) {
      return pageCollectionListing;
    } else if (fetchedCollectionListing) {
      return fetchedCollectionListing;
    } else {
      return {};
    }
  }, [pageCollectionListing, fetchedCollectionListing]);
  const isWaitingForFetchedListing = !pageCollectionListing && isFetchingListing;
  const deliverable_id = useSelector(getDeliverableId);
  const referringListingPath = useSelector(getReferringListingPath);
  const dispatch = useDispatch();
  const {selectedCommentsModalTab} = useCurrentUrlData();
  const reviewStage = collectionListing?.reviewStage?.currentStage || null;
  const readListingComment = useReadListingComment();
  const {mutateAsync: favoriteMutate} = useFavoriteListings();
  const {mutateAsync: notInterestedMutate} = useNotInterestedListings();
  const {mutateAsync: trashListingMutate} = useTrashListings();
  const {mutateAsync: deleteCommentMutate} = useDeleteComment();
  const {mutateAsync: addCommentMutate} = useAddComment();
  const {mutateAsync: shareListingMutate} = useShareListings();
  const currentUser = useCurrentUser();
  const isAgent = useIsAgent();
  const {isDesktop, isMobile, isTablet} = useDeviceWidth();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(
    String(collectionId)
  );
  const agentId = (!isAgent && Cookies.get(agentLeadTokenKey)) || undefined;
  const comments = collectionListing.comments?.slice() || [];
  const [currentSelectedTab, updateCurrentSelectedTab] = useState<string>(
    isDesktop
      ? LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL
      : selectedCommentsModalTab || LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL
  );
  const useMobileLayout = useMemo(() => isMobile || isTablet, [isMobile, isTablet]);
  const onViewFullListingDetails = () => {
    const queryParams = {
      origin_type: 'ListingCommentsModal',
      origin: 'Collection',
      agentId: '',
    };

    if (agentId) {
      // If a consumer is viewing the listing and has a referring agent cookie set, pass that
      // info along to the formatListingUrl function.
      queryParams.agentId = agentId;
    }

    if (collectionListing?.listingData) {
      const url = formatListingUrl(collectionListing.listingData, queryParams);

      window.open(url || undefined, '_blank');
    }
  };
  const onMarkAsNotInterested = () => {
    if (collectionId && collectionListing.listingId) {
      return notInterestedMutate({
        collectionId,
        listingIds: [collectionListing.listingId],
      });
    }

    return;
  };
  const onFavoriteListing = () => {
    if (collectionId && collectionListing.listingId) {
      return favoriteMutate({
        collectionId,
        listingIds: [collectionListing.listingId],
      });
    }

    return;
  };
  const onShareListing = () => {
    if (collectionId && collectionListing.listingId) {
      trackCollectionListingFlowAction({
        actionName: 'Share',
        productPath: PRODUCT_PATH.LISTING_PAGE,
      });
      shareListingMutate({
        collectionId,
        listingIds: [collectionListing.listingId],
      });
    }
  };
  const onTrashListing = () => {
    if (collectionId && collectionListing.listingId) {
      trackCollectionListingFlowAction({
        actionName: 'Trash',
        productPath: PRODUCT_PATH.LISTING_PAGE,
      });
      trashListingMutate({
        collectionId,
        listingIds: [collectionListing.listingId],
      });
    }
  };
  const onPostComment = (content: string) => {
    if (collectionId && collectionListing.listingId) {
      return addCommentMutate({
        collectionId,
        listingId: collectionListing.listingId,
        content,
      }).then((result) => {
        trackCollectionListingFlowAction({
          actionName: 'Comment',
          productPath: PRODUCT_PATH.LISTING_MODAL,
          collectionMode,
        });
        return result;
      });
    }

    return;
  };
  const onDelete = (comment: CommentType) => {
    return loadModal(CONFIRMATION_MODAL_URI, {
      hdText: 'Delete Comment',
      bdText: `Are you sure you want to delete this comment? This can't be undone.`,
      yesText: 'Yes, Delete',
      noText: 'Cancel',
    }).then(() => {
      if (collectionId && comment.commentId && collectionListing.listingId) {
        deleteCommentMutate({
          collectionId,
          commentId: comment.commentId,
          listingId: collectionListing.listingId,
        })
          .then(() => {
            if (collectionListing) {
              trackCollectionListingFlowAction({
                actionName: 'Comment Removed',
                productPath: PRODUCT_PATH.LISTING_MODAL,
                collectionMode,
              });
            }

            return 'deleted';
          })
          .catch(() => {
            /* no-op */
          });
      }

      return;
    });
  };
  const onCloseComments = () => {
    updateCurrentSelectedTab(LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL);
  };

  useEffect(() => {
    if (
      !isPageLoading &&
      notEmpty(collectionId) &&
      collectionListing.listingId &&
      notEmpty(collectionListing.listingId)
    ) {
      dispatch(clearReferringListingPathAndDeliverableId());
      setListingFrameTrackParams(collectionListing.listingId, {
        product: 'collections',
        collection_id: collectionId,
        referring_product: 'direct',
        referring_product_path: referringListingPath,
        deliverable_id,
      });

      if (!useMobileLayout || selectedCommentsModalTab === LISTING_COMMENTS_VIEWS_TAB.COMMENTS) {
        readListingComment.mutate({
          collectionId,
          listingId: collectionListing.listingId,
        });
      }
    }
    /* we only track each time when the current listingId changed */
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [collectionListing.listingId, isPageLoading]);

  const hasUnreadComments = !!(
    collectionListing.updatesUnreadByCurrentUser &&
    collectionListing.updatesUnreadByCurrentUser.includes(UnreadListingUpdates.RECENT_COMMENTS)
  );
  const toggleCommentsView = () => {
    if (hasUnreadComments && currentSelectedTab === LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL) {
      readListingComment.mutate({
        collectionId,
        listingId: collectionListing.listingId || '',
      });
    }

    updateCurrentSelectedTab(
      currentSelectedTab === LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL
        ? LISTING_COMMENTS_VIEWS_TAB.COMMENTS
        : LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL
    );
  };
  const shouldShowWantToSee = !!(collectionId && collectionListing && hasCollaborators && !isAgent);

  return (
    <div
      className={`modal-content uc-listingCommentsViews ${
        useMobileLayout ? '' : 'uc-listingCommentsViews--lg'
      }`}
    >
      {(!useMobileLayout || currentSelectedTab === LISTING_COMMENTS_VIEWS_TAB.LISTING_DETAIL) && (
        <>
          <ModalHeader
            isAgent={isAgent}
            isMobile={useMobileLayout}
            onViewFullListingDetails={onViewFullListingDetails}
            onCloseModal={onDismiss}
            onTrashListing={onTrashListing}
            reviewStage={reviewStage}
            currentIndex={activeIndex}
            totalListings={paginationTotal}
            onNext={onNext}
            onPrevious={onPrevious}
            shouldDisableNext={shouldDisableNext}
            shouldDisablePrevious={shouldDisablePrevious}
            paginationLoading={isPageLoading}
            collection={collection}
            collectionMode={collectionMode}
            viewsCount={collectionListing.totalClientViews || 0}
          />
          <div
            className="uc-listingCommentsViews-column uc-listingCommentsViews-column--left"
            data-testid="listingViewContainer"
          >
            {!useMobileLayout && !isAgent && (
              <ModalSecondaryHeader
                isMobile={useMobileLayout}
                collectionId={collectionId || ''}
                onFavoriteListing={onFavoriteListing}
                onMarkAsNotInterested={onMarkAsNotInterested}
                collectionListing={collectionListing}
                shouldShowWantToSee={shouldShowWantToSee}
              />
            )}

            {(areListingsLoading || isWaitingForFetchedListing) && (
              <div className="uc-listingCommentsViews-listingLoadingWrapper">
                <Loading />
              </div>
            )}

            {collectionListing && (
              <ListingDetails listing={collectionListing} agentId={agentId} onDismiss={onDismiss} />
            )}
            {useMobileLayout && isAgent && (
              <AgentFooter
                onTrashListing={onTrashListing}
                reviewStage={reviewStage}
                viewsCount={collectionListing.totalClientViews || 0}
                toggleCommentsView={toggleCommentsView}
                hasUnreadComments={hasUnreadComments}
                hasComments={comments.length > 0}
              />
            )}
            {useMobileLayout && !isAgent && (
              <ClientFooter
                isMobile={true}
                collectionId={collectionId || ''}
                onFavoriteListing={onFavoriteListing}
                onMarkAsNotInterested={onMarkAsNotInterested}
                toggleCommentsView={toggleCommentsView}
                collectionListing={collectionListing}
                shouldShowWantToSee={shouldShowWantToSee}
                hasUnreadComments={hasUnreadComments}
                hasComments={comments.length > 0}
              />
            )}
          </div>
        </>
      )}
      {((!isMobile && currentSelectedTab !== LISTING_COMMENTS_VIEWS_TAB.CLIENT_VIEWS) ||
        currentSelectedTab === LISTING_COMMENTS_VIEWS_TAB.COMMENTS) && (
        <div
          className={`uc-listingCommentsViews-column uc-listingCommentsViews-column--right is-active`}
          data-testid="commentsViewContainer"
        >
          {((!isMobile && currentSelectedTab !== LISTING_COMMENTS_VIEWS_TAB.CLIENT_VIEWS) ||
            currentSelectedTab === LISTING_COMMENTS_VIEWS_TAB.COMMENTS) && (
            <CommentSection
              comments={comments}
              currentUser={currentUser}
              onCommentDelete={onDelete}
              onPostComment={onPostComment}
              onDone={onDismiss}
              onShowListingDetails={onCloseComments}
              onShareListing={onShareListing}
              reviewStage={reviewStage}
              processedListingCard={collectionListing.listingCardData}
            />
          )}
        </div>
      )}
    </div>
  );
};
