import React, {useEffect, useState} from 'react';

import {ltsBannerClosed, ltsStatusUpdated} from '@uc/analytics-definitions';
import {Button} from '@uc/cx.react';

import {LearnMore} from '@src/components/LikelyToSell/LearnMore';
import {COLLECTION_MODE} from '@src/constants';
import {useUpdateCurrentUrl, useUpdateLtsSetting} from '@src/hooks';
import {useCurrentUrlData} from '@src/hooks/url';
import {setShowLTSBanner, useStore, useShowLTSBanner, setToast} from '@src/store';

import styles from './LikelyToSellBanner.module.pcss';

export const LikelyToSellBanner = () => {
  const {collectionId} = useCurrentUrlData();
  const store = useStore();
  const setParams = useUpdateCurrentUrl();
  const showBanner = useShowLTSBanner();
  const updateLtsSetting = useUpdateLtsSetting().mutateAsync;
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const handleCloseBannerButton = () => {
    store.dispatch(setShowLTSBanner(false));
    ltsBannerClosed({
      product: 'collections',
      collection_id: collectionId,
    });
  };
  const handleModalCloseButtonClick = () => {
    setIsOpen(false);
  };
  const handleTurnOffButtonClick = () => {
    updateLtsSetting({collectionId, hideLikelyToSell: true})
      .then(() => {
        setParams({collectionMode: COLLECTION_MODE.SHARED});
      })
      .then(() => {
        ltsStatusUpdated({
          product: 'collections',
          collection_id: collectionId,
          status: false,
        });
      })
      .catch(() => {
        store.dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: 'Something went wrong. Please refresh and try again.',
          })
        );
      });
  };

  useEffect(() => {
    store.dispatch(setShowLTSBanner(showBanner));
  }, [showBanner, store]);

  return (
    <>
      {showBanner && (
        <div className={styles.LTSBanner} data-testid="lts-banner-test-id">
          <div className={styles.LTSBannerIconWrapper} data-tn="suggestion-icon">
            <svg className={styles.LTSBannerIcon}>
              <use xlinkHref="#cx-icon-lightbulb_24x24" />
            </svg>
          </div>
          <div className={styles.LTSBannerTextWrapper}>
            <div className={styles.LTSBannerText}>
              <span className={styles.LTSBannerTextBold}>Likely to Sell enabled.</span>
              &nbsp;We have found properties that match this collection and have an increased
              likelihood of selling. If you do not review these properties within 14 days, we may
              expire them.
            </div>
            <Button
              className={styles.LTSBannerButton}
              data-testid="learnMoreButton"
              onClick={() => setIsOpen(true)}
            >
              Learn More
            </Button>
            <Button
              className={styles.LTSBannerButton}
              variant="naked"
              data-testid="ltsTurnOffButton"
              onClick={handleTurnOffButtonClick}
            >
              Turn Off
            </Button>
          </div>
          <Button
            className={styles.LTSBannerCloseButton}
            variant="naked--lowEmph"
            onClick={handleCloseBannerButton}
            data-testid="closeLTSBannerButton"
          >
            <svg className="cx-btn-icon">
              <use xlinkHref="#cx-icon-x_16x16" />
            </svg>
          </Button>
          <LearnMore isOpen={isOpen} handleCloseButtonClick={handleModalCloseButtonClick} />
        </div>
      )}
    </>
  );
};
