import {useMutation, useQueryClient} from 'react-query';

import {archiveListings} from '@src/api';
import {useDispatch, setToast} from '@src/store';

export const useArchiveListings = () => {
  const client = useQueryClient();
  const dispatch = useDispatch();

  return useMutation(
    async ({collectionId, listingIds}: {collectionId: string; listingIds: string[]}) => {
      try {
        const result = await archiveListings({collectionId: collectionId, listingIds});

        dispatch(
          setToast({
            isOpen: true,
            variant: 'success',
            message: 'Marked as Not Interested',
          })
        );

        await client.invalidateQueries();
        return result;
      } catch (e) {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: 'Unable to mark listings as Not Interested',
          })
        );
        return;
      }
    }
  );
};
