import React from 'react';

import {Button, usePoptip, Poptip, IconProps} from '@uc/cx.react';

import {useNotInterestedListings} from '@src/hooks/useNotInterestedListings';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {clearSelectedListings, store} from '@src/store';
import {getNotInterestedIcon} from '@src/utils/getNotInterestedIcon';
import {isNotInterestedIconWebAbTestEnabled} from '@src/utils/optyUtils';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import {ConsumerOptionGroupProps} from '../types';
import styles from './OptionsGroupConsumer.module.pcss';

interface NotInterestedButtonProps extends ConsumerOptionGroupProps {
  toolTip: string;
  showSuccessToast: boolean;
  icon: IconProps['type'];
}

export const NotInterestedButton = (props: NotInterestedButtonProps) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const {mutateAsync: notInterestedMutate, isLoading} = useNotInterestedListings({
    showSuccessToast: props.showSuccessToast,
  });
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(props.collectionId);
  const notInterested = () => {
    if (isLoading) return;

    trackCollectionListingFlowAction({
      actionName: 'Not Interested',
      productPath: PRODUCT_PATH.ACTION_BAR,
      ab_test: isNotInterestedIconWebAbTestEnabled(),
      ab_test_version: getNotInterestedIcon(),
    });
    notInterestedMutate({
      collectionId: props.collectionId,
      listingIds: props.listingIds,
    }).then(() => {
      store.dispatch(clearSelectedListings());
    });
  };

  return (
    <>
      <Button
        className={styles.notInterestedButton}
        variant="enclosed"
        size="sm"
        icon={props.icon}
        {...getPoptipAnchorProps(props.toolTip, 'bottom')}
        onClick={notInterested}
        aria-label={props.toolTip}
      />
      <Poptip {...poptipProps} />
    </>
  );
};
