import {useMemo} from 'react';

import {getSelectedListings, useSelector} from '@src/store';
import {CollectionListing} from '@src/types';
import {notEmpty} from '@src/utils/notEmpty';

function toIdSet(listings: Readonly<CollectionListing[]>) {
  return new Set(listings.map(({listingId}) => listingId).filter(notEmpty));
}

export const useSelectedInfo = (listingsOnPage: Readonly<CollectionListing[]>) => {
  const selected = useSelector(getSelectedListings);

  return useMemo(() => {
    const selectedIds = toIdSet(selected);
    const pageIds = Array.from(toIdSet(listingsOnPage));
    const isPartialPageSelected = pageIds.length > 0 && pageIds.some((id) => selectedIds.has(id));
    const isWholePageSelected = pageIds.length > 0 && pageIds.every((id) => selectedIds.has(id));

    return {isWholePageSelected, isPartialPageSelected};
  }, [listingsOnPage, selected]);
};
