import {FilterId, FILTER_KEYS} from '@src/constants/filter';

export const FILTER_OPTION_LABELS: Record<FilterId, string> = {
  all: 'All',
  pending: 'Pending client action',
  interested: 'Favorites',
  hasComments: 'Comments',
  notInterested: 'Not interested',
  wantToSee: 'Tour requested',
};

export function getFilterOptions() {
  return FILTER_KEYS.map((id: FilterId) => {
    return {id, label: FILTER_OPTION_LABELS[id]};
  });
}
