import {useQuery} from 'react-query';

import {ApiError, getCollectionListingsFacets} from '@src/api';
import {CollectionMode} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {FilterId} from '@src/constants/filter';
import {GetCollectionListingsFacetsResponse} from '@src/types';

export const useCollectionListingsFacets = ({
  collectionId,
  collectionMode,
  filterId,
}: {
  collectionId: string;
  collectionMode: CollectionMode;
  filterId?: FilterId;
}) => {
  return useQuery<GetCollectionListingsFacetsResponse, ApiError>(
    [API_KEY.GET_COLLECTION_LISTINGS_FACETS, collectionId, collectionMode, filterId],
    () =>
      getCollectionListingsFacets({
        collectionId,
        collectionMode,
        filterId,
      })
  );
};
