import React from 'react';

import {Button, Modal} from '@uc/cx.react';

export const LearnMore = ({
  isOpen,
  handleCloseButtonClick,
}: {
  isOpen: boolean;
  handleCloseButtonClick: () => void;
}) => {
  const handleFAQButtonClick = () => {
    window.open('https://compasssupport.zendesk.com/hc/en-us/articles/4409417535507', '_blank');
  };

  return (
    <Modal size="lg" isOpen={isOpen}>
      <Modal.Header closeButtonProps={{onClick: handleCloseButtonClick}}>
        How it works?
      </Modal.Header>
      <Modal.Body>
        <div data-testid="learnMoreText">
          “Likely to Sell” predicts properties that have a higher likelihood of selling within the
          next 12 months.
          <ul>
            <li>If you do not review these properties within 14 days, we may expire them.</li>
            <li>A maximum of 5 agents will have access to a single property.</li>
            <li>
              If an agent adds a homeowner to their contacts, it will be removed from other agents’
              collections the following day.
            </li>
            <li>
              If changes are made to your search criteria, your likely to sell recommendations may
              be affected the following day.
            </li>
          </ul>
          Do you want to learn more? Read our FAQs or contact your Agent Experience Manager.
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={handleFAQButtonClick} data-testid="learnMoreFAQButton">
          Read FAQs
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
