import React, {useCallback, useMemo} from 'react';

import {GoogleSheetCmaModal as Modal} from '@src/components/GoogleSheetCma';
import {useDefaultExportParams} from '@src/hooks';
import {
  useSelectedIds,
  useSelectedListings,
  useCurrentColumns,
  useDispatch,
  getCurrentNav,
  setCurrentNav,
  useSelector,
} from '@src/store';
import {ProcessedListingType} from '@src/types';

export const GoogleSheetCmaModal = () => {
  const dispatch = useDispatch();
  const params = useSelector(getCurrentNav);
  const {selectedProcessedListings} = useSelectedListings();
  const selectedIds = useSelectedIds();
  const {listingIds, geographies} = useDefaultExportParams(selectedIds);
  const {currentShownColumnIds, currentColumns} = useCurrentColumns({
    isCollectionShared: false,
  });
  const handleClose = useCallback(() => {
    if (params) {
      dispatch(setCurrentNav({...params, modalId: undefined}));
    }
  }, [dispatch, params]);
  const isSales = useMemo(() => {
    return selectedProcessedListings.every(
      (listing) => listing.listingType === ProcessedListingType.SALE
    );
  }, [selectedProcessedListings]);

  return (
    <Modal
      actionBarData={{
        listingIds,
        geographies,
        isSales,
        defaultColumnIds: currentShownColumnIds,
        allColumns: currentColumns,
      }}
      onClose={handleClose}
    />
  );
};
