import React from 'react';

import {isClientHomeRootEnabled} from '@src/utils/optyUtils';

import styles from './Layout.module.pcss';

export interface LayoutProps {
  header?: React.ReactNode;
  nav?: React.ReactNode;
  children: React.ReactNode;
}

export const Layout = ({header, nav, children}: LayoutProps) => {
  return (
    <>
      <div className={`${styles.wrapper} ${nav ? styles.withNav : styles.withoutNav}`}>
        {nav && (
          <div data-testid="layout-nav" className={styles.nav}>
            {nav}
          </div>
        )}
        {header && (
          <div
            data-testid="layout-header"
            className={`${styles.header} ${
              isClientHomeRootEnabled() ? styles['clientDashboardLayout-header'] : ''
            }`}
          >
            {header}
          </div>
        )}
        <div data-testid="layout-content" className={styles.content}>
          {children}
        </div>
      </div>
    </>
  );
};
