import React from 'react';

import {Button, Poptip, usePoptip} from '@uc/cx.react';

import {useCurrentUrlData} from '@src/hooks/url';
import {useCurrentUser} from '@src/store';
import {CollectionLinkedSearch} from '@src/types';

import {editSearchCriteria} from './linkedSearchesUtils';

export const SingleLinkedSearch = ({
  savedSearch,
  isAgent,
}: {
  savedSearch: CollectionLinkedSearch;
  isAgent: boolean;
}) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const currentUserId = useCurrentUser().personId || '';
  const {collectionId} = useCurrentUrlData();

  return (
    <>
      <div
        className="collection-linkedSearches-summary textIntent-caption2 u-ellipsis"
        {...getPoptipAnchorProps(<>{savedSearch.summary}</>, 'bottomLeft')}
      >
        {savedSearch.summary}
      </div>
      <Poptip {...poptipProps} zIndex="var(--cx-zIndex-10)" />
      <Button
        className="collection-linkedSearhes-editingBtn"
        size="xs"
        variant="naked"
        onClick={() => editSearchCriteria({isAgent, savedSearch, collectionId, currentUserId})}
      >
        Edit Criteria
      </Button>
    </>
  );
};
