import {useQuery} from 'react-query';

import {ApiError, getCollection, getBoard, acceptInvitation} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetCollectionResponse} from '@src/types';
import {isClientHomeInvitationEnabled} from '@src/utils/optyUtils';

export const useGetCollection = ({collectionId}: {collectionId: string}) => {
  const keyArray = isClientHomeInvitationEnabled()
    ? ['acceptInvitation', collectionId]
    : ['getBoard', collectionId];
  const callback = isClientHomeInvitationEnabled()
    ? () => {
        acceptInvitation({collectionId, isMobileApp: false});
      }
    : () => getBoard({collectionId});

  useQuery(keyArray, callback);

  return useQuery<GetCollectionResponse, ApiError>([API_KEY.GET_COLLECTION, collectionId], () =>
    getCollection({collectionId})
  );
};
