import React from 'react';

import {Avatar} from '@uc/cx.react';

export const getTruncatedAvatars = ({
  collaboratorsAvatars,
  handleOnClickInvite,
}: {
  collaboratorsAvatars: React.ReactElement[];
  handleOnClickInvite?: () => void;
}) => {
  let truncatedAvatars;

  if (collaboratorsAvatars.length > 4) {
    const remainingAvatarsLengthStr = (collaboratorsAvatars.length - 4).toString();

    truncatedAvatars = collaboratorsAvatars.slice(0, 4);
    truncatedAvatars.push(
      <Avatar
        size="lg"
        className="collection-header-avatar"
        data-count={remainingAvatarsLengthStr}
        key={remainingAvatarsLengthStr}
        firstName="+"
        lastName={remainingAvatarsLengthStr}
        onClick={handleOnClickInvite ? handleOnClickInvite : undefined}
      />
    );
  } else {
    truncatedAvatars = collaboratorsAvatars;
  }

  return truncatedAvatars;
};
