import {MODAL_URL_IDS} from '@src/constants';
import {useIsAgent} from '@src/store';

import {useCurrentUrlData} from './url';

export const useIsInClientCommentModal = () => {
  const isAgent = useIsAgent();
  const {modalId} = useCurrentUrlData();

  return !isAgent && (modalId === MODAL_URL_IDS.comment || modalId === MODAL_URL_IDS.listing);
};
