import {useQuery} from 'react-query';

import {getObsoleteSavedSearchListings} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {isTrashListingsModalEnabled} from '@src/utils/optyUtils';

import {useCurrentUrlData} from './url';

export interface ObsoleteListings {
  active: string[];
  inactive: string[];
}

export const useObsoleteSavedSearchListings = () => {
  const {collectionId, savedSearchId} = useCurrentUrlData();

  return useQuery(
    [API_KEY.GET_OBSOLETE_SAVED_SEARCH_LISTINGS, savedSearchId, collectionId],
    async () => {
      if (savedSearchId && isTrashListingsModalEnabled()) {
        return getObsoleteSavedSearchListings({
          collectionId: collectionId,
          savedSearchId: savedSearchId,
        });
      } else {
        return null;
      }
    }
  );
};
