export class ApiError extends Error {
  constructor(public message: string, public code: number) {
    super(message);
  }
}

export async function handleError(originalError: Error & {response?: Response}): Promise<never> {
  if (originalError.response) {
    const {code, error} = await originalError.response?.json().catch(() => {
      return {error: originalError.message};
    });

    throw new ApiError(error, code);
  }

  throw originalError;
}

