export const getReviewStageIntersections = (
  reviewStage: CollectionListingReviewStage[] | undefined,
  filteredReviewStage: CollectionListingReviewStage[] | undefined
) => {
  if (reviewStage && filteredReviewStage) {
    const intersection = reviewStage.filter((stage) => {
      return filteredReviewStage.some((fStage) => fStage === stage);
    });

    return intersection.length > 0 ? intersection : reviewStage;
  }

  return filteredReviewStage || reviewStage || [CollectionListingReviewStage.FAVORITE];
};

export function getCollectionListingsPaginated({
  collectionId,
  collectionMode,
  limit = 24,
  skip = 0,
  sortId,
  filterId,
  shouldFilterByOpenHouses,
  shouldFilterByLikelyToSell,
  listingStatusFilter,
  address,
}: {
  collectionId: string;
  collectionMode: CollectionMode;
  limit?: number;
  skip?: number;
  sortId?: SortId;
  filterId?: FilterId;
  shouldFilterByOpenHouses?: boolean;
  shouldFilterByLikelyToSell?: boolean;
  listingStatusFilter?: string;
  address?: string;
}): Promise<GetCollectionListingsPaginatedResponse> {
  const {collectionSortOrder, sortOrder} = getCollectionListingSortOrder(sortId);
  const listingsFilter = getCollectionListingFilterParams({
    collectionMode,
    filterId,
  });
  const {collectionListingsStatusFilters} =
    getCollectionListingsStatusFilterParams(listingStatusFilter);

  return collectionsApi_getCollectionListingsPaginated({
    collectionId,
    pagination: {skip, limit},
    query: {
      listingsFilter,
      sort: {collectionListingsSortOrder: collectionSortOrder, listingSearchSortOrder: sortOrder},
      searchCriteria: {
        statusFilters: collectionListingsStatusFilters,
        textForSearchByAddress: address,
        openHouse: shouldFilterByOpenHouses,
      },
      likelyToSell: shouldFilterByLikelyToSell,
      enrichments: [
        CollectionListingsEnrichment.VIEWED_BY,
        CollectionListingsEnrichment.FAVORITED_BY,
        CollectionListingsEnrichment.NOT_INTERESTED_BY,
      ],
    },
  }).catch(handleError);
}

