export function getBoard({collectionId}: {collectionId: string}): Promise<GetBoardResponse> {
  let markUserActive = false;

  if (!MARKED_USER_ACTIVE.has(collectionId)) {
    markUserActive = true;
    MARKED_USER_ACTIVE.add(collectionId);
  }

  return boardsApi_getBoard(
    collectionId,
    {
      listingQuery: {
        expandListings: false,
        limit: 1,
        skip: 0,
      },
      markUserActive: markUserActive,
    },
    {
      headers: {
        'Cache-Control': 'must-revalidate, no-store, no-cache, private',
      },
    }
  ).catch(handleError);
}

