export function getCollectionSearchResults({
  collectionId,
  page = DEFAULT_PAGINATION.currentPage,
  pageSize = 0, // If pageSize is zero no listings will be included in response
  savedSearchId,
  sortId,
  address,
}: {
  collectionId: string;
  page?: number;
  pageSize?: number;
  savedSearchId?: string;
  sortId?: SortId;
  address?: string;
}): Promise<GetCollectionSearchResultsResponse> {
  let skip, limit;
  const {sortOrder} = getSortOrder(sortId);

  if (page && pageSize) {
    const paginationParams = getPaginationParams({page, pageSize});

    skip = paginationParams.skip;
    limit = paginationParams.limit;
  }

  return collectionsApi_getCollectionSearchResults({
    collectionId,
    limit,
    skip,
    savedSearchId,
    includeNewMatchesOnPage: true,
    sortOrder,
    address,
  }).catch(handleError);
}

