import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {HeaderConfig} from '@src/types';

import {
  select,
  image,
  share,
  visibleIndicator,
  trash,
  notInterested,
  showingRequested,
  requestShowing,
  address,
  buildingName,
  bedrooms,
  monthlyFees,
  monthlyFeesAndTaxes,
  dom,
  fullBa,
  hba,
  legalRoomCount,
  listedDate,
  listingType,
  neighborhood,
  status,
  likelyToSell,
  potentialSeller,
  city,
  price,
  perSquareFoot,
  totalBathrooms,
  lotSizeInSqft,
  taxesPerYear,
  condoCoopFees,
  propertyType,
  dateAvailable,
  incentives,
  transactionType,
  addedBy,
  timeAdded,
  updatedDate,
  listedPrice,
  contractDate,
  soldPrice,
  soldDate,
  openHouses,
  comment,
  geo,
  state,
  managementCompany,
  zipCode,
  squareFeet,
  propertySubtype,
  outdoorSpace,
  mlsId,
  newBadge,
  yearBuilt,
  monthlyTaxes,
  monthlyHoaFees,
  monthlyMaintenance,
} from '../components/ListingTable/listingTableColumns';

export const getTableColumnDefs = ({
  isAgent,
  mode,
  isCollectionShared,
  hasLikelyToSell = false,
}: {
  isAgent: Readonly<boolean>;
  mode: Readonly<CollectionMode>;
  isCollectionShared: Readonly<boolean>;
  hasLikelyToSell?: Readonly<boolean>;
}): Readonly<HeaderConfig[]> => {
  // Note: Column indexes are given to easily see where columns are inserted.
  // Please update them if you change this array of HeaderConfigs.
  const columnDefs = [
    select, // 0
    image, // 1
    address, // 2
    neighborhood, // 3
    city, // 4
    buildingName, // 5
    state, // 6
    zipCode, // 7
    price, // 8
    legalRoomCount, // 9
    bedrooms, // 10
    totalBathrooms, // 11
    fullBa, // 12
    hba, // 13
    dom, // 14
    lotSizeInSqft, // 15
    squareFeet, // 16
    perSquareFoot, // 17
    yearBuilt, // 18
    outdoorSpace, // 19
    taxesPerYear, // 20
    monthlyFeesAndTaxes, // 21
    monthlyTaxes, // 22
    monthlyFees, // 23
    monthlyHoaFees, // 24
    condoCoopFees, // 25
    monthlyMaintenance, // 26
    propertyType, // 27
    propertySubtype, // 28
    listingType, // 29
    status, // 30
    comment, // 31
    dateAvailable, // 32
    incentives, // 33
    addedBy, // 34
    timeAdded, // 35
    updatedDate, // 36
    listedDate, // 37
    listedPrice, // 38
    contractDate, // 39
    soldPrice, // 40
    soldDate, // 41
    openHouses, // 42
    geo, // 43
    managementCompany, // 44
    mlsId, // 45
  ];
  const isUnsharedScreen = mode === COLLECTION_MODE.UNSHARED;
  const isTrashScreen = mode === COLLECTION_MODE.TRASH;
  const isSharedScreen = mode === COLLECTION_MODE.SHARED;
  const isMatchesScreen = mode === COLLECTION_MODE.MATCHES;
  const isFavoritesScreen = mode === COLLECTION_MODE.FAVORITES;
  const isNotInterestedScreen = mode === COLLECTION_MODE.NOT_INTERESTED;
  const showShare = isUnsharedScreen || isTrashScreen;
  let offsetCounter = 0;
  const spliceColumn = (index: number, column: HeaderConfig) => {
    columnDefs.splice(index + offsetCounter, 0, column);
    offsetCounter++;
  };

  // Note: Columns should be inserted in order from lowest index
  // to highest index so that relative-indexing can be maintained.
  if (isAgent) {
    if (isUnsharedScreen || isSharedScreen) {
      spliceColumn(2, newBadge);
    }

    if (showShare) {
      spliceColumn(2, share);
    }

    if (isCollectionShared && isSharedScreen) {
      spliceColumn(20, showingRequested);
    }

    if (!isUnsharedScreen && !isTrashScreen) {
      spliceColumn(20, visibleIndicator);
    }

    if (isUnsharedScreen || isSharedScreen) {
      spliceColumn(32, trash);
    }

    spliceColumn(34, transactionType);

    if ((isSharedScreen || isTrashScreen) && hasLikelyToSell) {
      columnDefs.push(likelyToSell);
    }
  } else {
    if (isCollectionShared) {
      spliceColumn(20, requestShowing);
    }

    if (isMatchesScreen || isFavoritesScreen || isNotInterestedScreen) {
      spliceColumn(21, notInterested);

      if (hasLikelyToSell) {
        columnDefs.push(potentialSeller);
      }
    }
  }

  return columnDefs;
};
