import {useEffect} from 'react';

import {getScrollId, useSelector} from '@src/store';

interface ScrollToTopParams {
  target?: HTMLElement;
}

export function getScrollParent(node?: HTMLElement): null | HTMLElement {
  if (!node) {
    return null;
  }

  if (node.scrollHeight > node.clientHeight) {
    return node;
  } else {
    return getScrollParent(node.parentNode as HTMLElement);
  }
}

export const useScrollToTop = ({target}: ScrollToTopParams) => {
  const scrollId = useSelector(getScrollId);

  useEffect(() => {
    const scrollable = getScrollParent(target);

    scrollable?.scrollTo({
      top: 0,
    });
  }, [scrollId, target]);
};
