import React, {useEffect, useState} from 'react';

import {COLLECTION_MODE} from '@src/constants';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetProcessedListings} from '@src/hooks/useGetProcessedListings';
import {useShareListings} from '@src/hooks/useShareListings';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {useTrashListings} from '@src/hooks/useTrashListings';
import {
  getCurrentUser,
  getDeliverableId,
  useSelector,
  useDispatch,
  getReferringListingPath,
  clearReferringListingPathAndDeliverableId,
  useNavListingIds,
  useNavTabCounts,
} from '@src/store';
import {ProcessedListing, CollectionListing} from '@src/types';
import {setListingFrameTrackParams} from '@src/utils/setListingFrameTrackParams';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import {ModalHeader} from '../components/header';
import {ListingDetails} from '../components/ListingDetails/ListingDetails';
import {ModalProps} from '../modal';
import {ReviewOptionsContainer} from './Options.container';

import './styles.pcss';

interface IProps extends ModalProps {
  onClose: () => void;
  onSubmit: (result: IModalResult) => void;
}

export interface IModalResult {
  iframeListingAction: string;
  listing: ProcessedListing;
}

export const ReviewListingsContainer: React.FC<IProps> = ({
  onClose,
  onSubmit,
  collectionId,
  currentListingIndex,
}: IProps) => {
  const dispatch = useDispatch();
  const {page: currentPage, pageSize, listingIds: currentUrlListingIds} = useCurrentUrlData();
  const isMoreThanOneIdInCurrentUrlListingIds = Boolean(
    currentUrlListingIds && currentUrlListingIds.length > 1
  );
  const currentListingIndexAcrossAllPages = (currentPage - 1) * pageSize + currentListingIndex; // currentListingIndex is the index of the current listing in current page
  const setParams = useUpdateCurrentUrl();
  const deliverable_id = useSelector(getDeliverableId);
  const {mutateAsync: shareListingAsync, isLoading: isShareListingLoading} = useShareListings();
  const {mutateAsync: trashListingAsync, isLoading: isTrashListingLoading} = useTrashListings();
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const [currentPageListingIndex, setCurrentPageListingIndex] =
    React.useState<number>(currentListingIndex);
  const currentNavListingIds = useNavListingIds();
  const [currentPageListingIds, setCurrentPageListingIds] = React.useState<string[]>(
    currentNavListingIds || []
  );
  const [resultResponse, updateResultResponse] = useState<IModalResult>({
    iframeListingAction: '',
    listing: {},
  });
  const [comment, setComment] = React.useState<string>('');
  const [shared, setShared] = React.useState<boolean>(false);
  const [trashed, setTrashed] = React.useState<boolean>(false);
  const listingId = currentPageListingIds[currentListingIndex];
  const {data} = useGetProcessedListings(
    currentPageListingIds && currentListingIndex >= 0 && currentPageListingIds[currentListingIndex]
      ? [listingId]
      : undefined
  );
  const processedListing =
    data?.listings && data?.listings?.length > 0 ? data.listings[0] : undefined;
  const [currentListing, setCurrentListing] = React.useState<ProcessedListing>(
    processedListing || {}
  );
  const navTabCounts = useNavTabCounts();
  const [totalListings, setTotalListings] = React.useState<number>(
    isMoreThanOneIdInCurrentUrlListingIds && navTabCounts?.unshared ? navTabCounts?.unshared : 1
  );
  const onClickIframeAction = (iframeListingAction: string) => {
    updateResultResponse(Object.assign(resultResponse, {iframeListingAction}));
    onSubmit(resultResponse);
    onClose();
  };
  const currentUser = useSelector(getCurrentUser);
  const referringListingPath = useSelector(getReferringListingPath);

  useEffect(() => {
    if (isMoreThanOneIdInCurrentUrlListingIds && navTabCounts?.unshared)
      setTotalListings(navTabCounts?.unshared);
  }, [isMoreThanOneIdInCurrentUrlListingIds, navTabCounts?.unshared]);

  useEffect(() => {
    if (currentUrlListingIds && currentUrlListingIds?.length) {
      setCurrentPageListingIds(currentUrlListingIds);
    }

    if (
      isMoreThanOneIdInCurrentUrlListingIds &&
      currentNavListingIds &&
      currentPageListingIndex < currentNavListingIds.length
    ) {
      setParams({
        listingIds: currentNavListingIds,
        currentListingIndex: currentPageListingIndex,
      });
    }
  }, [
    currentNavListingIds,
    currentPageListingIndex,
    currentUrlListingIds,
    isMoreThanOneIdInCurrentUrlListingIds,
    setParams,
  ]);

  useEffect(() => {
    setCurrentListing(processedListing || {});
    setComment('');
  }, [processedListing]);

  useEffect(() => {
    if (processedListing) {
      dispatch(clearReferringListingPathAndDeliverableId());
      listingId &&
        setListingFrameTrackParams(listingId, {
          product: 'collections',
          collection_id: collectionId,
          referring_product: 'direct',
          referring_product_path: referringListingPath,
          deliverable_id,
          availability: processedListing.localizedStatus,
        });
    }
    /* we only track each time when the current listingId changed */
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [listingId]);

  const handleNavigator = (updatedIndex: number) => {
    if (updatedIndex + 1 > currentPageListingIds.length) {
      setParams({
        page: currentPage + 1,
        currentListingIndex: 0,
      });
      setCurrentPageListingIndex(0);
    } else if (updatedIndex < 0 && currentPageListingIds.length == pageSize) {
      setParams({
        page: currentPage - 1,
        currentListingIndex: pageSize - 1,
      });
      setCurrentPageListingIndex(pageSize - 1);
    } else if (updatedIndex < 0 && currentPageListingIds.length < pageSize) {
      /* when navigating from the first listing of the last page to the previous one AND
       when the last page has fewer listings than previous page, before the previous page listigIds load up,
       we need to make sure the modal stay open, so the currentPageListingIndex needs to wait for the new listingIds update,
       so `currentListingIndex` will be set in the useEffect
      */
      setParams({
        page: currentPage - 1,
      });
      setCurrentPageListingIndex(pageSize - 1);
    } else {
      setParams({currentListingIndex: updatedIndex});
      setCurrentPageListingIndex(updatedIndex);
    }
  };
  const handleOnShareListing = ({comment}: {comment: string}) => {
    setShared(true);
    if (isShareListingLoading) return Promise.reject();

    trackCollectionListingFlowAction({
      actionName: 'Share',
      productPath: PRODUCT_PATH.LISTING_PAGE,
    });
    return shareListingAsync({
      collectionId: collectionId,
      listingIds: [listingId || ''],
      comment,
    })
      .then(() => {
        setShared(false);

        if (
          currentListingIndex + 1 < currentPageListingIds.length ||
          currentListingIndexAcrossAllPages + 1 < totalListings
        ) {
          setTotalListings(totalListings - 1);
        } else if (
          currentPageListingIds.length === 1 &&
          currentListingIndexAcrossAllPages + 1 === totalListings &&
          currentPage > 1
        ) {
          setParams({page: currentPage - 1, modalId: undefined});
        } else if (navTabCounts?.unshared === 1) {
          setParams({collectionMode: COLLECTION_MODE.SHARED, modalId: undefined});
        } else {
          onClose();
        }
      })
      .catch(() => setShared(false));
  };
  const handleOnTrashListing = ({comment}: {comment: string}) => {
    setTrashed(true);
    if (isTrashListingLoading) return Promise.reject();

    trackCollectionListingFlowAction({
      actionName: 'Trash',
      productPath: PRODUCT_PATH.LISTING_PAGE,
    });
    return trashListingAsync({
      collectionId: collectionId,
      listingIds: [listingId || ''],
      comment,
      customizedToastMessage: `${
        currentListing?.location?.prettyAddress || '1 listing'
      } has been moved to the trash`,
    })
      .then(() => {
        setTrashed(false);

        if (
          currentListingIndex + 1 < currentPageListingIds.length ||
          currentListingIndexAcrossAllPages + 1 < totalListings
        ) {
          setTotalListings(totalListings - 1);
        } else if (
          currentPageListingIds.length === 1 &&
          currentListingIndexAcrossAllPages + 1 === totalListings &&
          currentPage > 1
        ) {
          setParams({page: currentPage - 1, modalId: undefined});
        } else if (navTabCounts?.unshared === 1) {
          setParams({collectionMode: COLLECTION_MODE.SHARED, modalId: undefined});
        } else {
          onClose();
        }
      })
      .catch(() => setTrashed(false));
  };

  return currentListing ? (
    <div className="uc-workspaceBoardReviewListingsModal-container">
      <section className="uc-workspaceBoardReviewListingsModal-listing is-iframesRendered">
        <ListingDetails
          listing={{listingData: currentListing} as CollectionListing}
          onClickIframeAction={onClickIframeAction}
          currentUser={currentUser}
          collectionId={collectionId}
        />
      </section>
      <ModalHeader
        processedListing={currentListing}
        close={onClose}
        currentListingIndexAcrossAllPages={currentListingIndexAcrossAllPages}
        currentPageListingIndex={currentPageListingIndex}
        totalListings={totalListings}
        handleNavigator={handleNavigator}
      />
      <ReviewOptionsContainer
        onShareListing={handleOnShareListing}
        onTrashListing={handleOnTrashListing}
        shared={shared}
        trashed={trashed}
        comment={comment}
        setComment={setComment}
      />
    </div>
  ) : (
    <></>
  );
};
