import React from 'react';

import {Modal, useModal} from '@uc/cx.react';

import {useRefreshGetCollectionQueryCache} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useIsCollectionShared} from '@src/hooks/useIsCollectionShared';
import {ListingCommentsViewsModal} from '@src/modals/listing-comments-views/modal';

import './CommentsModal.pcss';

export const CommentsModal = () => {
  const {collectionId, modalId, collectionMode} = useCurrentUrlData();
  const setParams = useUpdateCurrentUrl();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();
  const onClose = () => {
    if (modalId) {
      setParams({
        listingId: undefined,
        listingIds: undefined,
        modalId: undefined,
        enableModalPagination: undefined,
      });
    }

    refreshGetCollectionQueryCache();
  };
  const {modalProps} = useModal({initialIsOpen: true, onIsOpenChange: onClose});
  const {data: collectionData} = useGetCollection({collectionId});
  const collection = collectionData?.collection;
  const {isCollectionShared: hasCollaborators} = useIsCollectionShared({
    collectionId,
  });

  return (
    <>
      {collection && (
        <Modal {...modalProps} size="lg" className="commentsModal">
          <ListingCommentsViewsModal
            collection={collection}
            hasCollaborators={hasCollaborators}
            onDismiss={onClose}
            collectionMode={collectionMode}
          />
        </Modal>
      )}
    </>
  );
};
