import React from 'react';

import {Icon} from '@uc/cx.react';

import {ErrorContent} from '@src/components/BoardLayout/ErrorContent';
import {CollectionNotFound} from '@src/components/EmptyStates/CollectionNotFound';
import {API_ERROR_CODE} from '@src/constants';
import {useCurrentUser} from '@src/store';
import {ApiError} from '@src/types';

import styles from './CollectionLoadError.module.pcss';

export const CollectionLoadError = ({error}: {error: ApiError}) => {
  const currentUser = useCurrentUser();
  const {code} = error;

  switch (code) {
    case API_ERROR_CODE.FORBIDDEN: {
      return (
        <ErrorTemplate
          header="You do not have access to this collection"
          body={`
      You are logged in as ${currentUser.email}. To access
      this collection, ask to be invited with this email address.
      `}
        />
      );
    }

    case API_ERROR_CODE.GONE: {
      return (
        <ErrorTemplate
          header="This item is no longer available"
          body="Looks like this item doesn’t exist or might’ve been deleted."
        >
          <Icon.HomeInSquares className={styles.errorIllustrationTop} />
        </ErrorTemplate>
      );
    }

    case API_ERROR_CODE.NOT_FOUND: {
      return (
        <div className={styles.error}>
          <CollectionNotFound />
        </div>
      );
    }

    default: {
      return (
        <div className={styles.error}>
          <ErrorContent error={error} />
        </div>
      );
    }
  }
};

interface ErrorTemplateProps {
  header: string;
  body: string;
  children?: React.ReactChild;
}
const ErrorTemplate = ({header, body, children}: ErrorTemplateProps) => (
  <div className={styles.error}>
    <div className={styles.errorContent}>
      <div className={styles.errorContentWrapper}>
        {children}
        <h1 className={`${styles.errorTitle} textIntent-display1`}>{header}</h1>
        <p className="textIntent-body">{body}</p>
      </div>
    </div>
  </div>
);
