import React from 'react';

import {UcLcCommentFooterAction, UcLcFooterAction} from '@uc/listing-card-components.react';

import {LISTING_COMMENTS_VIEWS_TAB, MODAL_URL_IDS} from '@src/constants';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useRequestShowing} from '@src/hooks/useRequestShowing';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {useTrackCommentModalInitiated} from '@src/hooks/useTrackCommentModalInitiated';
import {CollectionListing, UnreadListingUpdates, ShowingStatus, ListingShowing} from '@src/types';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import styles from './CardFooter.module.pcss';

export interface CardFooterProps {
  collectionListing: CollectionListing;
  collectionId: string;
  isCollectionShared: boolean;
  clearNewMatchBadge?: () => void;
}

function shouldShowTourRequestButton(tourInfo: ListingShowing | undefined) {
  if (!tourInfo || tourInfo.status === undefined) {
    return true;
  }

  if (
    tourInfo.status === ShowingStatus.SCHEDULED &&
    tourInfo.tourDate &&
    tourInfo.tourDate < +new Date()
  ) {
    return true;
  }

  return [ShowingStatus.REQUEST_CANCELLED, ShowingStatus.SEEN].includes(tourInfo.status);
}

export const CardFooter = ({
  collectionListing,
  collectionId,
  isCollectionShared,
  clearNewMatchBadge,
}: CardFooterProps) => {
  const setParams = useUpdateCurrentUrl();
  const {trackCommentModalInitiated} = useTrackCommentModalInitiated('grid view');
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);
  const {mutate, isLoading} = useRequestShowing();
  const {comments = [], updatesUnreadByCurrentUser, listingId, showing} = collectionListing;
  const commentsCount = comments.length;
  const hasUnreadComments = updatesUnreadByCurrentUser?.includes(
    UnreadListingUpdates.RECENT_COMMENTS
  );
  const canRequestTour = Boolean(collectionListing?.canRequestTour);
  const shouldDisableCancelRequestButton = showing?.status === ShowingStatus.SCHEDULED;
  const handleRequestShowing = () => {
    if (!isLoading) {
      clearNewMatchBadge?.();
      mutate({collectionListing, collectionId});
    }
  };

  return (
    <div slot="footer-actions">
      {isCollectionShared && (
        <>
          {shouldShowTourRequestButton(showing) ? (
            <UcLcFooterAction
              text="Request a tour"
              className={styles.requestTourBtn}
              onFooterActionClick={() => {
                trackCollectionListingFlowAction({
                  actionName: 'collections_showing_requested',
                  listingFlowAction: 'tour_requested',
                  productPath: PRODUCT_PATH.LISTING_CARD,
                });
                handleRequestShowing();
              }}
              disabled={!canRequestTour}
            />
          ) : (
            <UcLcFooterAction
              text="Cancel request"
              className={styles.requestTourBtn}
              onFooterActionClick={() => {
                trackCollectionListingFlowAction({
                  actionName: 'consumer_cancelled_tour_request',
                  listingFlowAction: 'tour_request_cancelled',
                  productPath: PRODUCT_PATH.LISTING_CARD,
                });
                handleRequestShowing();
              }}
              disabled={shouldDisableCancelRequestButton}
            />
          )}
        </>
      )}
      <UcLcCommentFooterAction
        numOfComments={commentsCount}
        hasUnreadComments={hasUnreadComments}
        onCommentClick={() => {
          setParams({
            modalId: MODAL_URL_IDS.comment,
            listingIds: [listingId as string],
            selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
            enableModalPagination: true,
          });
          trackCommentModalInitiated?.();
          clearNewMatchBadge?.();
        }}
      />
    </div>
  );
};
