import className from 'classnames';
import React, {useMemo} from 'react';

import {Icon, Flyout, OptionsMenu, useOptionsMenu} from '@uc/cx.react';
import {
  saleStatus as formatSalesStatus,
  rentalStatus as formatRentalStatus,
  shortTermRentalStatus as formatShortTermRentalStatus,
  listingType as formatListingType,
} from '@uc/listing-enum-to-text';

import {ListingType, EntityInfo} from '@src/types';

import {testSelectors} from './testSelectors';

import './ListingSelect.pcss';

interface ListingSelectOption {
  id: number;
  label: string;
}

interface ListingSelectProps {
  selectedIndex?: number;
  listings: EntityInfo[];
  showErrorMsg?: boolean;
  onChange?: (index: number) => void;
}

export const NO_SELECTION_INDEX = -1;

function formatToText<T>(value: T | undefined, formatter: (value: T) => string | null): string {
  // eslint-disable-next-line @typescript-eslint/no-extra-parens
  return (value !== undefined && formatter(value)) || '';
}

const formatStatus = (listing: EntityInfo): string => {
  switch (listing.listingType) {
    case ListingType.LISTING: {
      return formatToText(listing.rentalStatus, formatRentalStatus);
    }

    case ListingType.SALES: {
      return formatToText(listing.salesStatus, formatSalesStatus);
    }

    case ListingType.SHORT_TERM_RENTAL: {
      return formatToText(listing.shortTermRentalStatus, formatShortTermRentalStatus);
    }
  }

  return '';
};
const formatListing = (listing: EntityInfo, id: number): ListingSelectOption => {
  const listingType = formatToText(listing.listingType, formatListingType);
  const dateStr = formatToText(listing.lastStatusChangeDate, (date) =>
    new Date(date).toLocaleDateString('en-US')
  );

  return {id, label: `${listingType}, ${formatStatus(listing)} ${dateStr}`};
};
const formatListings = (listings: EntityInfo[]): ListingSelectOption[] =>
  listings.map((listing, index) => formatListing(listing, index));

export const ListingSelect = ({
  selectedIndex = NO_SELECTION_INDEX,
  listings,
  showErrorMsg = false,
  onChange = () => undefined,
}: ListingSelectProps) => {
  const items = useMemo(() => formatListings(listings), [listings]);
  const onItemSelect = (id: React.Key): void => {
    const index = parseInt(id.toString(), 10);

    if (!isNaN(index)) {
      onChange(index);
    }
  };
  const {anchorProps, menuProps, flyoutProps, getItemProps} = useOptionsMenu({
    items,
    onItemSelect,
    closeOnSelect: true,
  });

  return (
    <div className="cx-formElement googleSheetCmaModal-listingSelect">
      <div
        className="googleSheetCmaModal-listingSelectInput"
        data-testid={testSelectors.listingSelect}
        {...anchorProps}
      >
        <input
          className={className('cx-textField cx-textField--sm', {
            'googleSheetCmaModal-listingSelectInput-error': showErrorMsg,
          })}
          type="text"
          placeholder="Select a listing"
          value={items[selectedIndex]?.label || ''}
          readOnly
          data-testid={testSelectors.listingSelectInput}
        />
        <Icon.TriangleUpDown className="googleSheetCmaModal-triangleIcon" />
        {showErrorMsg && (
          <span
            className="googleSheetCmaModal-listingSelectInput-errorMsg"
            data-testid={testSelectors.listingErrMsg}
          >
            Please select a subject listing.
          </span>
        )}
      </div>
      <Flyout {...flyoutProps} data-testid={testSelectors.listingMenu}>
        <OptionsMenu {...menuProps}>
          {items.map((item, index) => (
            <OptionsMenu.Item
              key={listings[index].listingIdSHA}
              data-testid={testSelectors.listingOption(listings[index].listingIdSHA)}
              {...getItemProps(item)}
            >
              {item.label}
            </OptionsMenu.Item>
          ))}
        </OptionsMenu>
      </Flyout>
    </div>
  );
};
