import {getTime} from 'date-fns';
import React, {useState, useRef, useEffect} from 'react';

import {Button, Flyout, useFlyoutOnClick, usePoptipOnHover, Poptip, Icon} from '@uc/cx.react';
import Datepicker from '@uc/date-picker';
import {formatTourStatus} from '@uc/format-tour-status';

import {useRefreshBoardQueryCache} from '@src/hooks/useRefreshQueryCache';
import {useRemoveRequestedShowing} from '@src/hooks/useRemoveRequestedShowing';
import {useRequestShowing} from '@src/hooks/useRequestShowing';
import {useTrackCollectionListingFlowAction} from '@src/hooks/useTrackCollectionListingFlowAction';
import {CollectionListing, ShowingStatus} from '@src/types';
import {PRODUCT_PATH} from '@src/utils/trackCollectionListingFlowAction';

import {RequestTourTip} from '../RequestTourTip';
import {DatePicker as DatePickerComponent} from './DatePicker';
import './WantToSee.pcss';

export interface WantToSeeProps {
  isMobile?: boolean;
  collectionId: string;
  collectionListing: CollectionListing;
}

export const WantToSee = ({isMobile, collectionId, collectionListing}: WantToSeeProps) => {
  const refreshQueryCache = useRefreshBoardQueryCache();
  const {mutate: updateCollectionListingShowings} = useRequestShowing();
  const {mutate: removeCollectionListingShowings} = useRemoveRequestedShowing();
  const formattedTour = formatTourStatus(
    collectionListing?.showing?.status,
    collectionListing?.showing?.tourDate
  );
  const [date, setDate] = useState(collectionListing?.showing?.tourDate || getTime(Date.now()));
  const [seenDate, setSeenDate] = useState(
    formattedTour.tourStatus === ShowingStatus.SEEN && collectionListing?.showing?.tourDate
  );
  const [requested, setRequested] = useState(formattedTour.tourStatus === ShowingStatus.REQUESTED);
  const {getFlyoutAnchorProps, flyoutProps, dismissFlyout} = useFlyoutOnClick();
  const {poptipProps, getPoptipAnchorProps} = usePoptipOnHover();
  const datePicker = useRef<Datepicker | null>(null);
  const listingId = collectionListing?.listingId ?? '';
  const trackCollectionListingFlowAction = useTrackCollectionListingFlowAction(collectionId);

  async function toggleRequest(): Promise<void> {
    const newRequest = !requested;

    updateCollectionListingShowings(
      {
        collectionListing,
        collectionId: collectionId,
        showingStatus: newRequest ? ShowingStatus.REQUESTED : ShowingStatus.REQUEST_CANCELLED,
      },
      {
        onSuccess() {
          refreshQueryCache();
        },
      }
    );

    trackCollectionListingFlowAction({
      actionName: newRequest ? 'collections_showing_requested' : 'consumer_cancelled_tour_request',
      productPath: PRODUCT_PATH.LISTING_PAGE,
      listingFlowAction: newRequest ? 'tour_requested' : 'tour_request_cancelled',
    });

    setRequested(newRequest);
  }

  async function toggleSeen(): Promise<void> {
    trackCollectionListingFlowAction({
      actionName: 'Mark as Visited',
      productPath: PRODUCT_PATH.LISTING_PAGE,
    });

    if (date) {
      updateCollectionListingShowings({
        collectionListing,
        collectionId: collectionId,
        showingStatus: ShowingStatus.SEEN,
        tourDate: date,
      });
      setSeenDate(date);
      setRequested(false);
    } else {
      removeCollectionListingShowings({collectionId, listingId});
      setSeenDate(false);
    }

    dismissFlyout();
  }

  const canRequestTour = Boolean(collectionListing?.canRequestTour);

  useEffect(() => {
    setSeenDate(
      formattedTour.tourStatus === ShowingStatus.SEEN && collectionListing?.showing?.tourDate
    );
    setRequested(formattedTour.tourStatus === ShowingStatus.REQUESTED);
  }, [
    collectionListing?.showing?.status,
    collectionListing?.showing?.tourDate,
    formattedTour.tourStatus,
  ]);
  return (
    <div className="wantToSee-container" data-testid="wantToSee-container">
      {!seenDate &&
      (formattedTour.tourStatus === ShowingStatus.SCHEDULED ||
        formattedTour.tourStatus === ShowingStatus.PENDING_CONFIRMATION) ? (
        <>
          <div {...getPoptipAnchorProps(formattedTour.tourStatusText.toLowerCase(), 'top')}>
            <Button
              variant="enclosed"
              className="requestTourButton"
              size={isMobile ? 'sm' : 'md'}
              disabled
            >
              Cancel tour request
            </Button>
          </div>
          <Poptip {...poptipProps} />
        </>
      ) : (
        <RequestTourTip
          collectionListing={collectionListing}
          alignment={isMobile ? 'right' : 'bottom'}
        >
          <Button
            variant="enclosed"
            className="requestTourButton"
            size={isMobile ? 'sm' : 'md'}
            onClick={toggleRequest}
            disabled={!canRequestTour}
          >
            {requested ? 'Cancel tour request' : 'Request a tour'}
          </Button>
        </RequestTourTip>
      )}
      {!isMobile && (
        <Button
          variant="enclosed"
          {...getFlyoutAnchorProps('topRight')}
          className="wantToSee-button--normal"
          size="sm"
          data-fn="wantToSee-button-toggle"
          aria-label="Mark Listing as Visited"
        >
          <Icon.EllipsisHorizontal className="cx-btn-icon" />
        </Button>
      )}
      <Flyout {...flyoutProps}>
        <div className="wantToSee-datePicker">
          <p className="textIntent-title2 wantToSee-title">Mark Listing as Visited</p>
          <DatePickerComponent
            date={date}
            selectDate={(d) => {
              setDate(d);
            }}
            isOpen={flyoutProps.isOpen}
            datepickerRef={datePicker}
          />
          <div className="wantToSee-datePicker-buttons" data-testid="datePickerBtns">
            <Button variant="enclosed--highEmph" size="sm" onClick={dismissFlyout}>
              Cancel
            </Button>
            <Button variant="solid" size="sm" onClick={toggleSeen} data-fn="wantToSee-button-set">
              Done
            </Button>
          </div>
        </div>
      </Flyout>
    </div>
  );
};
