import React, {useRef, useState} from 'react';

import {Button, Flyout, Icon, FlyoutProps} from '@uc/cx.react';

import {LoadingSkeleton} from '@src/cx/LoadingSkeleton';
import {useReferrer, useSource} from '@src/store';
import {DealSummary} from '@src/types';

interface breadcrumbItem {
  name: string;
  url?: string;
  multiBreadcrumb?: Omit<breadcrumbItem, 'multiBreadcrumb'>[];
}

export const getBreadcrumbItems = ({
  collectionName,
  dealSummaries = [],
  referrer = '',
  source,
}: {
  collectionName: string;
  dealSummaries?: DealSummary[];
  referrer?: string;
  source?: string;
}) => {
  let breadcrumbsItems: breadcrumbItem[] = [];
  const isFromBT = referrer.indexOf('/app/lab') >= 0;
  const isFromAgentSearch = referrer.indexOf('/app/agent-search') >= 0;
  const dealSummariesLength = dealSummaries.filter(({id}) => id).length;

  if ((isFromBT || isFromAgentSearch || !referrer) && dealSummariesLength > 0 && source) {
    breadcrumbsItems = [
      {
        name: 'Business Tracker',
        url: '/app/lab',
      },
      generateDealBreadcrumb(dealSummaries),
      {
        name: collectionName,
      },
    ];
  } else {
    breadcrumbsItems = [
      {
        name: 'My Workspace',
        url: '/app/workspaces/#!/overview',
      },
      {
        name: 'Collections',
        url: '/app/workspaces/#!/collections',
      },
      {
        name: collectionName,
      },
    ];
  }

  return breadcrumbsItems;
};

export const Breadcrumbs = ({
  collectionName,
  dealSummaries,
  isAgent,
}: {
  collectionName: string;
  dealSummaries?: DealSummary[];
  isAgent: boolean;
}) => {
  const referrer = useReferrer() || '';
  const source = useSource();
  const breadcrumbsItems = getBreadcrumbItems({collectionName, dealSummaries, referrer, source});
  const anchorRef = useRef<HTMLButtonElement>(null);
  const [openFolder, setOpenFolder] = useState(false);
  const flyoutProps: FlyoutProps = {
    anchor: anchorRef,
    isOpen: openFolder,
    onDismiss: () => setOpenFolder(false),
    zIndex: 9999,
  };

  return (
    <>
      {isAgent && (
        <ul className="cx-breadcrumbs textIntent-caption2 header-breadcrumbs">
          {breadcrumbsItems.map((item: breadcrumbItem, i) => {
            if (item.url) {
              return (
                <li key={`${i}-${item.url}`}>
                  <a href={item.url} className="cx-textLink">
                    {item.name}
                  </a>
                </li>
              );
            }

            if (item.multiBreadcrumb?.length && item.multiBreadcrumb.length < 3) {
              return (
                <li key={`${i}-${item.name}`}>
                  {item.multiBreadcrumb.map((breadcrumb, index) => (
                    <div key={`${index}-${breadcrumb.url}`}>
                      <a href={breadcrumb.url} className="cx-textLink">
                        {breadcrumb.name}
                      </a>
                      {item.multiBreadcrumb?.length !== index + 1 && <span>, </span>}
                    </div>
                  ))}
                </li>
              );
            }

            if (item.multiBreadcrumb?.length && item.multiBreadcrumb.length >= 3) {
              return (
                <li key={`${i}-${item.name}`}>
                  <Button
                    className="breadcrumbs-folder"
                    ref={anchorRef}
                    onClick={() => setOpenFolder(!openFolder)}
                    variant="naked"
                    size="xs"
                    data-testid="breadcrumbsFolderButton"
                    id="breadcrumbsFolderButton"
                    aria-label="Breadcrumbs Folder Button"
                  >
                    <div className="folder-button">
                      Folder
                      <Icon.ChevronDown className="folder-icon" />
                    </div>
                  </Button>
                  <Flyout {...flyoutProps} style={{position: 'relative'}}>
                    {item.multiBreadcrumb.map((breadcrumb, index) => (
                      <p className="flyout-item" key={`${index}-${breadcrumb.url}`}>
                        <a className="flyout-link" href={breadcrumb.url}>
                          {breadcrumb.name}
                        </a>
                      </p>
                    ))}
                  </Flyout>
                </li>
              );
            }

            if (item.name === '') {
              return (
                <li key={i} className="breadcrumbs-loading">
                  <LoadingSkeleton />
                </li>
              );
            }

            return <li key={`${i}-${item.name}`}>{item.name}</li>;
          })}
        </ul>
      )}
    </>
  );
};

export function generateDealBreadcrumb(dealSummaries: DealSummary[]) {
  return dealSummaries
    .sort((dealA, dealB) => {
      return (dealA.name ?? '').localeCompare(dealB.name ?? '', undefined, {sensitivity: 'accent'});
    })
    .reduce(
      (result, next) => {
        if (!next.id || !next.name) return result;
        result.name = result.name ? `${result.name}, ${next.name}` : next.name;
        result.multiBreadcrumb?.push({
          name: next.name || '',
          url: `/app/lab/${next.id}`,
        });
        return result;
      },
      {name: '', multiBreadcrumb: []} as breadcrumbItem
    );
}
