/* eslint-disable no-restricted-imports */
import {History} from 'history';
import {createStore as reduxCreateStore, applyMiddleware, Store} from 'redux';

import {StorageAdapter} from '@src/lib/StorageAdapter';

import {Action} from './actions';
import {createHistory} from './createHistory';
import {createNavMiddleware} from './createNavMiddleware';
import {createPersistMiddleware} from './createPersistMiddleware';
import {createRootReducer} from './createRootReducer';
import {createStorage} from './createStorage';
import {loadState} from './lib';
import {RootState, LocationState} from './types';

// ** create the redux store
// exported for testing purposes
export function createStore(): {
  store: Store<RootState, Action>;
  history: History<LocationState>;
  storage: StorageAdapter<Partial<RootState>>;
} {
  const storage = createStorage();
  const history: History<LocationState> = createHistory();
  const preloadedState = loadState({storage, history});
  const store = reduxCreateStore(
    createRootReducer({history}),
    preloadedState,
    applyMiddleware(createPersistMiddleware(storage), createNavMiddleware(history))
  );

  return {store, history, storage};
}
