import {
  CollectionListingReviewStage,
  ShowingStatus,
  CollectionListingsStatusFilter,
} from '@src/types';

export const FILTER_OPTIONS = {
  // the order of the items below is the order of the filter pills showing up in UI
  all: {
    filteredReviewStage: [
      CollectionListingReviewStage.REVIEW,
      CollectionListingReviewStage.FAVORITE,
      CollectionListingReviewStage.NOT_INTERESTED,
    ] as CollectionListingReviewStage[],
  },
  pending: {
    hasClientComment: false,
    filteredReviewStage: [CollectionListingReviewStage.REVIEW] as CollectionListingReviewStage[],
  },
  interested: {
    filteredReviewStage: [CollectionListingReviewStage.FAVORITE] as CollectionListingReviewStage[],
  },
  hasComments: {
    hasComments: true,
    filteredReviewStage: [
      CollectionListingReviewStage.REVIEW,
      CollectionListingReviewStage.FAVORITE,
      CollectionListingReviewStage.NOT_INTERESTED,
    ] as CollectionListingReviewStage[],
  },
  notInterested: {
    filteredReviewStage: [
      CollectionListingReviewStage.NOT_INTERESTED,
    ] as CollectionListingReviewStage[],
  },
  wantToSee: {
    showingStatus: ShowingStatus.REQUESTED,
    filteredReviewStage: [
      CollectionListingReviewStage.REVIEW,
      CollectionListingReviewStage.FAVORITE,
      CollectionListingReviewStage.NOT_INTERESTED,
    ] as CollectionListingReviewStage[],
  },
} as const;

export type FilterId = keyof typeof FILTER_OPTIONS; //eslint-disable-line @typescript-eslint/no-type-alias

export const FILTER_KEYS = Object.keys(FILTER_OPTIONS) as FilterId[];

