import {GetCollectionUsersResponse, CollectionSharedCollaborator} from '@src/types';

export const getCollectionOwnerAndCollaborators = (
  collectionUsers?: GetCollectionUsersResponse
): CollectionSharedCollaborator[] => {
  if (!collectionUsers) {
    return [];
  }

  const nonBusinessTrackerCollaborators: CollectionSharedCollaborator[] =
    collectionUsers?.users?.map(({userId, person, contact}) => ({userId, person, contact})) || [];
  const businessTrackerCollaborators: CollectionSharedCollaborator[] =
    collectionUsers?.sharedCollaborators || [];
  const collectionOwnerAndCollaborators = [
    ...nonBusinessTrackerCollaborators,
    ...businessTrackerCollaborators,
  ];

  return collectionOwnerAndCollaborators;
};
