import React from 'react';

import {AgentCard} from '@src/components/AgentCard';
import {ConsumerCard} from '@src/components/ConsumerCard';
import {LikelyToSell} from '@src/components/LikelyToSell/LikelyToSell';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {CardLayout} from '@src/cx/CardLayout';
import {CollectionListing} from '@src/types';

function Card({
  mode,
  collectionListing,
  collectionId,
  isCollectionShared,
  isCardsLoading,
}: {
  mode: CollectionMode;
  collectionListing: CollectionListing;
  collectionId: string;
  isCollectionShared: boolean;
  isCardsLoading?: boolean;
}) {
  if (
    mode === COLLECTION_MODE.UNSHARED ||
    mode === COLLECTION_MODE.SHARED ||
    mode === COLLECTION_MODE.TRASH ||
    mode === COLLECTION_MODE.LIKELY_TO_SELL
  ) {
    return (
      <AgentCard
        collectionId={collectionId}
        mode={mode}
        collectionListing={collectionListing}
        isCardsLoading={isCardsLoading}
      />
    );
  }

  return (
    <ConsumerCard
      mode={mode}
      collectionListing={collectionListing}
      collectionId={collectionId}
      isCollectionShared={isCollectionShared}
    />
  );
}

export const ListingGrid = ({
  collectionId,
  mode,
  collectionListings,
  isCollectionShared,
  isCardsLoading,
}: {
  collectionId: string;
  mode: CollectionMode;
  collectionListings?: CollectionListing[];
  isCollectionShared: boolean;
  isCardsLoading?: boolean;
}) => {
  if (mode === COLLECTION_MODE.LIKELY_TO_SELL) {
    return <LikelyToSell />;
  }

  return (
    <CardLayout>
      {collectionListings?.map((collectionListing) => (
        <Card
          mode={mode}
          collectionListing={collectionListing}
          collectionId={collectionId}
          key={collectionId + collectionListing.listingId}
          isCollectionShared={isCollectionShared}
          isCardsLoading={isCardsLoading}
        />
      ))}
    </CardLayout>
  );
};
