import {useQuery} from 'react-query';

import {ApiError, getCollectionSearchResults} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {GetCollectionSearchResultsResponse} from '@src/types';
import {filterBadgesForSearchResult} from '@src/utils/filterBadges';

import {useCurrentUrlData} from './url';

export const useGetCollectionSearchResults = (collectionId: string) => {
  const {page, pageSize, savedSearchId, sortId, address} = useCurrentUrlData();

  return useQuery<GetCollectionSearchResultsResponse, ApiError>(
    [
      API_KEY.GET_COLLECTION_SEARCH_RESULTS,
      collectionId,
      page,
      pageSize,
      savedSearchId,
      sortId,
      address,
    ],
    async () => {
      const result = await getCollectionSearchResults({
        collectionId,
        page,
        pageSize,
        savedSearchId,
        sortId,
        address,
      });

      result?.listings?.forEach(filterBadgesForSearchResult);

      return result;
    }
  );
};
