import {useQuery} from 'react-query';

import {ApiError, getCollectionUsers} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {useIsAgent} from '@src/store';
import {GetCollectionUsersResponse} from '@src/types';

export const useGetCollectionUsers = ({collectionId}: {collectionId: string}) => {
  const isAgent = useIsAgent();

  return useQuery<GetCollectionUsersResponse, ApiError>(
    [API_KEY.GET_COLLECTION_USERS, collectionId, isAgent],
    () =>
      getCollectionUsers({
        collectionId,
        isAgent,
      })
  );
};
