import {RootState} from '../store/types';

export const getSelectListingToastConfig = () => {
  const maxCount = 500;
  const toastData: RootState['toast'] = {
    isOpen: true,
    variant: 'error',
    message: `Too many listings selected. Selection cannot exceed ${maxCount} listings.`,
  };

  return {
    maxCount,
    toastData,
  };
};
