import classnames from 'classnames';
import moment from 'moment';
import React, {useEffect, useState, useRef} from 'react';
// eslint-disable-next-line no-restricted-imports
import {useQueryClient} from 'react-query';

import {
  ltsAdded,
  ltsHomeownerAdded,
  ltsPropertyReportOpened,
  ltsSuggestedContactClicked,
  listingViewed,
} from '@uc/analytics-definitions';
import {ContactCard} from '@uc/crm-components.react';
import {Button, Card, Flyout, FlyoutProps, Icon, OptionsMenu, RowDivider} from '@uc/cx.react';
import ListingCard from '@uc/listing-card.react';

import {AddCommentModal} from '@src/components/LikelyToSell/AddCommentModal';
import {convertToTitleCase, getHomeowner} from '@src/components/LikelyToSell/utils';
import {LISTING_COMMENTS_VIEWS_TAB, MODAL_URL_IDS} from '@src/constants';
import {useAddHomeowner, useGetListingRealistUrl, useShareListings} from '@src/hooks';
import {useAddComment} from '@src/hooks/useAddComment';
import {CollectionUrlData, useDispatch, setReferringListingPath, setToast} from '@src/store';
import {
  Contact,
  RecommendationStrength,
  ProcessedListing,
  RecommendationMetadata,
  AddHomeownerToContactsRequest,
  AddHomeownerToContactsResponse,
  NeighborProximityType,
  PriceEstimate,
} from '@src/types';

import styles from './RecommendationCard.module.pcss';

export interface Recommendation {
  taskId?: string;
  homeOwner?: Contact;
  listingIdSHA?: string;
  listing?: ProcessedListing;
  description?: string;
  metaData?: RecommendationMetadata;
  createdAt?: number;
  isAdded?: boolean;
  score?: string;
}

export interface RecommendationCardProps {
  aiRecommendation: Recommendation;
  collectionId: string;
  collectionName: string;
  dismissButtonClick: (taskId: string | undefined, address: string | undefined) => void;
  setParams: (boardUrlData: Partial<CollectionUrlData>) => void;
  showNeighborInfo: boolean;
}

const getScoreDetails = (reco: Recommendation): {level: string; class: string} | null => {
  const strength = reco.metaData?.basicMetadata?.strength;

  // We need this because RecommendationStrength.HIGH equates to 0
  if (reco.score === undefined && strength === undefined) return null;
  if (reco.score === 'HIGH' || strength === RecommendationStrength.HIGH)
    return {level: 'HIGH', class: styles.RecCardScoreHigh};
  if (reco.score === 'MEDIUM' || strength === RecommendationStrength.MEDIUM)
    return {level: 'MED', class: styles.RecCardScoreMed};
  return {level: 'LOW', class: styles.RecCardScoreMed};
};

export const getPriceDetails = (reco: Recommendation): {min: string | null; max: string | null} => {
  const getPriceRange = (priceEstimate: PriceEstimate) => {
    if (priceEstimate.minValue && priceEstimate.maxValue) {
      return {
        min: priceFormatter.format(priceEstimate.minValue),
        max: priceFormatter.format(priceEstimate.maxValue),
      };
    }

    return {
      min: priceEstimate.pricePoint ? priceFormatter.format(priceEstimate.pricePoint) : null,
      max: null,
    };
  };

  if (reco.metaData?.ltsNetworkMetadata?.propertyInfo?.priceEstimate) {
    return getPriceRange(reco.metaData.ltsNetworkMetadata.propertyInfo.priceEstimate);
  }

  if (reco.metaData?.ltsNoConnectionMetadata?.propertyInfo?.priceEstimate) {
    return getPriceRange(reco.metaData.ltsNoConnectionMetadata.propertyInfo.priceEstimate);
  }

  if (reco.metaData?.ltsMetadata?.propertyInfo?.priceEstimate) {
    return getPriceRange(reco.metaData.ltsMetadata.propertyInfo.priceEstimate);
  }

  return {min: null, max: null};
};

export const priceFormatter = new Intl.NumberFormat('en-US', {
  style: 'currency',
  currency: 'USD',
  minimumFractionDigits: 0,
  maximumFractionDigits: 0,
});

export const RecommendationCard = ({
  aiRecommendation,
  collectionId,
  collectionName,
  dismissButtonClick,
  setParams,
  showNeighborInfo,
}: RecommendationCardProps) => {
  const {listing = {}} = aiRecommendation;
  const dispatch = useDispatch();
  const recCardRef = useRef<HTMLDivElement>(null);
  const ellipsisButtonRef = useRef<HTMLButtonElement>(null);
  const [useResponsiveDesign, setUseResponsiveDesign] = useState<boolean>(false);
  const [showFlyoutMenu, setShowFlyoutMenu] = useState<boolean>(false);
  const [addCommentModalIsOpen, setAddCommentModalIsOpen] = useState<boolean>(false);
  const [isContactRecommendation, setIsContactRecommendation] = useState<boolean>(
    aiRecommendation.metaData?.ltsMetadata?.homeowners !== undefined || false
  );
  const scoreDetails = getScoreDetails(aiRecommendation);
  const priceRange = getPriceDetails(aiRecommendation);

  if (
    isContactRecommendation &&
    aiRecommendation.metaData?.ltsMetadata?.homeowners &&
    aiRecommendation.metaData.ltsMetadata.homeowners.length > 0
  ) {
    aiRecommendation.homeOwner = aiRecommendation.metaData.ltsMetadata.homeowners[0];
  } else {
    aiRecommendation.homeOwner = getHomeowner(aiRecommendation);
  }

  const homeOwnerName = aiRecommendation.homeOwner
    ? `${aiRecommendation.homeOwner.firstName || ''} ${
        aiRecommendation.homeOwner.lastName || ''
      }`.trim()
    : '';
  const showHomeowner =
    aiRecommendation.homeOwner?.emailAddresses?.length ||
    aiRecommendation.homeOwner?.phoneNumbers?.length ||
    homeOwnerName;
  const fromNow = aiRecommendation.createdAt ? moment(aiRecommendation.createdAt).fromNow() : '';
  const getRealistUrl = useGetListingRealistUrl(aiRecommendation).mutateAsync;
  const addHomeownerApi = useAddHomeowner().mutateAsync;
  const addComment = useAddComment().mutateAsync;
  const addToCollection = useShareListings().mutate;
  const client = useQueryClient();
  const handleRealistClick = () => {
    getRealistUrl().then((realistUrl) => {
      if (realistUrl) {
        window.open(realistUrl, '_blank');
        ltsPropertyReportOpened({
          product: 'collections',
          collection_id: collectionId,
          task_id: aiRecommendation.taskId || '',
        });
      }
    });
  };
  const addHomeownerToContacts = () => {
    if (showHomeowner) {
      const request: AddHomeownerToContactsRequest = {
        contact: aiRecommendation.homeOwner,
        collectionName,
        taskId: aiRecommendation.taskId,
      };

      addHomeownerApi(request)
        .then((response: AddHomeownerToContactsResponse) => {
          setShowFlyoutMenu(false);

          if (response?.contactId) {
            ltsHomeownerAdded({
              product: 'collections',
              collection_id: collectionId,
              task_id: aiRecommendation.taskId || '',
              contact_id: response.contactId || '',
            });
            const identifier = homeOwnerName
              ? homeOwnerName
              : aiRecommendation.homeOwner?.emailAddresses
              ? aiRecommendation.homeOwner.emailAddresses[0].address
              : 'This contact';

            dispatch(
              setToast({
                isOpen: true,
                variant: 'success',
                message: `${identifier} has been added to your contacts with tag 'LTSinColl'`,
                action: 'View',
                onActionClick: () => {
                  window.open(`/app/contacts/${response?.contactId}`, '_blank');
                },
              })
            );
            setIsContactRecommendation(true);

            if (aiRecommendation.homeOwner) {
              aiRecommendation.homeOwner.id = response.contactId;
            }
          }
        })
        .catch(() => {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Something went wrong. Please refresh and try again.',
            })
          );
        });
    }
  };
  const viewHomeownerInContacts = () => {
    try {
      window.open(`/app/contacts/${aiRecommendation?.homeOwner?.id}`, '_blank');
    } catch (e) {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'error',
          message: 'Something went wrong. Please refresh and try again.',
        })
      );
    }
  };
  const openListingModal = () => {
    dispatch(setReferringListingPath('card'));
    setParams({
      modalId: MODAL_URL_IDS.comment,
      listingId: aiRecommendation.listingIdSHA,
      selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
      enableModalPagination: true,
    });
  };
  const handleAddToCollectionButton = () => {
    if (aiRecommendation.listingIdSHA && !aiRecommendation.isAdded) {
      aiRecommendation.isAdded = true;

      try {
        addToCollection({
          collectionId,
          listingIds: [aiRecommendation.listingIdSHA],
          toastParams: {
            isOpen: true,
            variant: 'success',
            duration: 5000,
            message: `${listing.location?.prettyAddress} has been shared`,
            action: 'View',
            onActionClick: openListingModal,
          },
          likelyToSell: true,
          doNotInvalidateQueries: true,
        });
        ltsAdded({
          product: 'collections',
          collection_id: collectionId,
          task_id: aiRecommendation.taskId || '',
        });
        setAddCommentModalIsOpen(true);
      } catch (e) {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: 'Something went wrong. Please refresh and try again.',
          })
        );
      }
    }
  };
  const handleAddingComment = (comment: string) => {
    addComment({
      collectionId,
      listingId: aiRecommendation.listingIdSHA,
      content: comment,
    }).then(() => {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'success',
          message: 'Your comment has been added',
          action: 'View',
          onActionClick: openListingModal,
        })
      );
    });
    handleACMCloseButton();
  };
  const handleACMCloseButton = async () => {
    setAddCommentModalIsOpen(false);
    await client.invalidateQueries();
  };
  const handleContactCardClick = () => {
    ltsSuggestedContactClicked({
      product: 'collections',
      collection_id: collectionId,
      task_id: aiRecommendation.taskId || '',
    });
  };
  const handleListingCardClick = () => {
    listingViewed({
      listing_id: aiRecommendation.listingIdSHA || '',
      product: 'collections',
      referring_product: 'collections',
      referring_product_path: 'likely_to_sell_agent',
      collection_id: collectionId,
      deliverable_id: aiRecommendation.taskId,
      availability: listing.localizedStatus || '',
    });
    window.open(`${listing.pageLink}`, '_blank');
  };
  const getNeighborInfo = () => {
    const neighbor = aiRecommendation.metaData?.ltsNetworkMetadata?.neighbors?.length
      ? aiRecommendation.metaData.ltsNetworkMetadata.neighbors[0]
      : undefined;
    const name = neighbor?.contact
      ? convertToTitleCase(
          `${neighbor.contact.firstName || ''} ${neighbor.contact.lastName || ''}`.trim()
        )
      : '';

    if (name && neighbor?.contact?.id) {
      const nameLink = (
        <a
          href={`/app/contacts/${neighbor.contact.id}`}
          className={styles.RecCardBlueLink}
          target="_blank"
          rel="noreferrer"
        >
          {name}
        </a>
      );
      let relationText = '';

      switch (neighbor.type) {
        case NeighborProximityType.BUILDING:
          relationText = 'lives in the same building';
          break;
        case NeighborProximityType.NEARBY:
          relationText = 'lives nearby';
          break;
        case NeighborProximityType.STREET:
          relationText = 'lives on the same street';
          break;
      }

      const propertyAddress =
        aiRecommendation.metaData?.ltsNetworkMetadata?.propertyInfo?.address || '';

      return (
        <div className={styles.RecCardInfo}>
          <Icon.User />
          <span className={styles.RecCardInfoLineContainer}>
            <span className={styles.RecCardInfoLine} data-testid="recCardNeighborInfo">
              <>
                {'Your contact'}
                {nameLink}
                {relationText}
                {propertyAddress && ` at ${propertyAddress}`}
              </>
            </span>
          </span>
        </div>
      );
    }

    return null;
  };
  const getHomeOwnerInfo = () => {
    if (!showHomeowner) {
      return null;
    }

    if (isContactRecommendation) {
      return (
        <div className={styles.RecCardInfo}>
          <Icon.User />
          <span className={styles.RecCardInfoLineContainer} data-testid="recCardHomeowner">
            <span className={styles.RecCardInfoLine}>Homeowner is your contact:</span>
            <span
              aria-hidden="true"
              className={styles.RecCardGreyLink}
              onClick={viewHomeownerInContacts}
            >
              {homeOwnerName}
            </span>
          </span>
        </div>
      );
    }

    const anchor = (
      <div
        className={styles.RecCardGreyLink}
        data-testid="recCardContactCardAnchor"
        onClick={handleContactCardClick}
        onKeyDown={handleContactCardClick}
        role="button"
        tabIndex={0}
      >
        Suggested contact information
      </div>
    );

    return (
      <div className={styles.RecCardInfo}>
        <Icon.User />
        <span className={styles.RecCardInfoLineContainer} data-testid="recCardHomeowner">
          <span className={styles.RecCardInfoLine}>{`Homeowner: ${homeOwnerName}`}</span>
          {(aiRecommendation.homeOwner?.emailAddresses?.length ||
            aiRecommendation.homeOwner?.phoneNumbers?.length) && (
            <ContactCard
              contact={aiRecommendation.homeOwner}
              innerButtonText="Add to contacts"
              contactSource="Contact information via third party"
              anchor={anchor}
              popoverProps={{children: 'div', alignment: 'bottom'}}
              innerButtonOnClick={addHomeownerToContacts}
            />
          )}
        </span>
      </div>
    );
  };
  const getOtherActionButtons = () => {
    const dismissButton = (
      <Button
        icon="Prohibited"
        variant="naked--lowEmph"
        size="sm"
        className={styles.RecCardDismissButton}
        onClick={() => dismissButtonClick(aiRecommendation.taskId, listing.location?.prettyAddress)}
        data-testid="recCardDismissButton"
      >
        Dismiss
      </Button>
    );

    if (showHomeowner && useResponsiveDesign) {
      const flyoutProps: FlyoutProps = {
        anchor: ellipsisButtonRef,
        isOpen: showFlyoutMenu,
        onDismiss: () => setShowFlyoutMenu(false),
      };

      return (
        <>
          {dismissButton}
          <Button
            variant="naked--lowEmph"
            icon="HorizontalEllipsisStroke"
            ref={ellipsisButtonRef}
            onClick={() => setShowFlyoutMenu(!showFlyoutMenu)}
            className={styles.RecCardEllipsisButton}
            data-testid="recCardEllipsisButton"
          />
          <Flyout {...flyoutProps} style={{width: '240px'}}>
            <OptionsMenu>
              <OptionsMenu.Item
                onClick={isContactRecommendation ? viewHomeownerInContacts : addHomeownerToContacts}
                data-testid="recCardAddHomeownerButton"
              >
                {isContactRecommendation
                  ? 'View Homeowner in Contacts'
                  : 'Add Homeowner to Contacts'}
              </OptionsMenu.Item>
            </OptionsMenu>
          </Flyout>
        </>
      );
    }

    return (
      <>
        {showHomeowner && (
          <Button
            variant="enclosed"
            size="sm"
            onClick={isContactRecommendation ? viewHomeownerInContacts : addHomeownerToContacts}
            data-testid="recCardAddHomeownerButton"
          >
            {isContactRecommendation ? 'View Homeowner in Contacts' : 'Add Homeowner to Contacts'}
          </Button>
        )}
        {dismissButton}
      </>
    );
  };

  useEffect(() => {
    // The numbers are the sum of the width of the 3 buttons (Add(ed) to Collection, Dismiss,
    // Add Homeowner to Collection)
    const limit = aiRecommendation.isAdded ? 554 : 550;

    if (recCardRef.current?.offsetWidth && recCardRef.current.offsetWidth < limit) {
      setUseResponsiveDesign(true);
    } else {
      setUseResponsiveDesign(false);
    }
  }, [aiRecommendation.isAdded, recCardRef.current?.offsetWidth]);

  return (
    <Card className={styles.RecCard} data-testid="recCardId" ref={recCardRef}>
      <div className={styles.RecCardHeader}>
        <span className={styles.RecCardTitle}>{`${listing.location?.prettyAddress}`}</span>
        <span className={styles.RecCardPriority}>
          <span className={styles.RecCardFromNow} data-testid="recCardFromNowId">
            {fromNow}
          </span>
          {scoreDetails && (
            <span
              className={classnames(styles.RecCardScore, scoreDetails.class)}
              data-testid="recCardScoreId"
            >
              {scoreDetails.level}
            </span>
          )}
        </span>
      </div>
      <Card.Body className={styles.RecCardBody}>
        <div className={styles.RecCardInfo}>
          <Icon.BarChart />
          <span className={styles.RecCardInfoLine}>{aiRecommendation.description}</span>
        </div>
        {getHomeOwnerInfo()}
        {showNeighborInfo ? getNeighborInfo() : null}
        {priceRange.min && (
          <div className={styles.RecCardInfo}>
            <Icon.Tag />
            <span className={styles.RecCardInfoLineContainer}>
              <span className={styles.RecCardInfoLine}>
                {`RealtyTrac estimated ${
                  priceRange.max
                    ? `range: ${priceRange.min} - ${priceRange.max}`
                    : `value: ${priceRange.min}`
                }`}
              </span>
            </span>
          </div>
        )}
        <div className={styles.RecCardInfo}>
          <Icon.CoreLogicColor />
          <span className={styles.RecCardInfoLineContainer}>
            <span className={styles.RecCardInfoLine}>Realist Property Report:</span>
            <span
              aria-hidden="true"
              className={styles.RecCardGreyLink}
              onClick={handleRealistClick}
            >
              View Report
            </span>
          </span>
        </div>
        {aiRecommendation.listing && (
          <div className={styles.RecCardListing}>
            <ListingCard
              listing={listing}
              openUrlInNewTab={true}
              size="xs"
              isCheckable={false}
              onListingClick={handleListingCardClick}
            />
          </div>
        )}
      </Card.Body>
      <RowDivider variant="fullBleed" />
      <div className={styles.RecCardFooter}>
        <Button
          icon="Star"
          size="sm"
          className={styles.RecCardAddButton}
          onClick={handleAddToCollectionButton}
          data-testid="recCardAddToCollectionButton"
          disabled={aiRecommendation.isAdded}
        >
          {aiRecommendation.isAdded ? 'Added to Collection' : 'Add to Collection'}
        </Button>
        <AddCommentModal
          isOpen={addCommentModalIsOpen}
          handleCloseButtonClick={handleACMCloseButton}
          handleAddingComment={handleAddingComment}
        />
        {getOtherActionButtons()}
      </div>
    </Card>
  );
};
