import React from 'react';

import {Button, Empty} from '@uc/cx.react';

import {useUpdateCurrentUrl} from '@src/hooks/url';

import styles from './EmptyState.module.pcss';

export const EmptySearchResults = () => {
  const setParams = useUpdateCurrentUrl();

  return (
    <div className={styles.emptyState} data-testid="empty-state">
      <Empty />
      <p className="cx-text-header4"> No Results </p>
      <p className="cx-text-body1Regular" style={{textAlign: 'center', marginTop: '-15px'}}>
        Clear search to see more results
      </p>
      <Button
        variant="enclosed--highEmph"
        onClick={() => setParams({address: ''})}
        style={{marginTop: '20px'}}
      >
        Clear Search
      </Button>
    </div>
  );
};
