import React from 'react';

import {Button, Flyout, OptionsMenu, Poptip, useOptionsMenu, usePoptip} from '@uc/cx.react';

import {CollectionMode} from '@src/constants';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useCurrentUrlData} from '@src/hooks/url';
import {useCurrentUser} from '@src/store';
import {CollectionLinkedSearch} from '@src/types';

import {editSearchCriteria} from './linkedSearchesUtils';

const Summary = ({
  mode,
  currentSavedSearchId,
  savedSearch,
}: {
  mode: CollectionMode;
  currentSavedSearchId: string;
  savedSearch: CollectionLinkedSearch;
}) => {
  const setParams = useUpdateCurrentUrl();
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  return (
    <>
      <div
        className={`collection-linkedSearches-summary textIntent-caption2 u-ellipsis ${
          mode === 'UNSHARED' && savedSearch.id === currentSavedSearchId
            ? 'collection-linkedSearches-currentSearchCriteria'
            : ''
        }`}
        {...getPoptipAnchorProps(<>{savedSearch.summary}</>, 'bottomLeft')}
      >
        <button
          className="collection-button-no-style"
          onClick={() => {
            setParams({savedSearchId: savedSearch.id});
          }}
        >
          {savedSearch.summary}
        </button>
      </div>
      <Poptip {...poptipProps} zIndex="var(--cx-zIndex-10)" />
    </>
  );
};
const EditOptions = ({
  savedSearch,
  isAgent,
  currentSavedSearchId,
  deleteSearchCriteria,
}: {
  savedSearch: CollectionLinkedSearch;
  isAgent: boolean;
  currentSavedSearchId: string;
  deleteSearchCriteria: ({
    savedSearch,
    currentSavedSearchId,
  }: {
    savedSearch: CollectionLinkedSearch;
    currentSavedSearchId: string;
  }) => void;
}) => {
  const currentUser = useCurrentUser();
  const currentUserId = useCurrentUser().personId || '';
  const {collectionId} = useCurrentUrlData();

  return (
    <div className="collection-linkedSearhes-savedSearchActions">
      <Button
        className="collection-linkedSearhes-editingBtn"
        size="xs"
        variant="naked"
        onClick={() => editSearchCriteria({isAgent, savedSearch, collectionId, currentUserId})}
      >
        Edit Criteria
      </Button>
      {currentUser._id === savedSearch.ownerId && (
        <Button
          className="collection-linkedSearhes-editingBtn collection-linkedSearches-deletingBtn"
          size="xs"
          variant="naked"
          onClick={() => deleteSearchCriteria({savedSearch, currentSavedSearchId})}
        >
          Delete
        </Button>
      )}
    </div>
  );
};

export const MultipleLinkedSearches = ({
  mode,
  currentSavedSearchId,
  isAgent,
  savedSearches,
  deleteSearchCriteria,
}: {
  mode: CollectionMode;
  currentSavedSearchId: string;
  isAgent: boolean;
  savedSearches: CollectionLinkedSearch[];
  deleteSearchCriteria: ({
    savedSearch,
    currentSavedSearchId,
  }: {
    savedSearch: CollectionLinkedSearch;
    currentSavedSearchId: string;
  }) => void;
}) => {
  const {anchorProps, flyoutProps, getItemProps, menuProps} = useOptionsMenu({
    allowMultiple: false,
    closeOnSelect: true,
  });

  return (
    <div className="collection-linkedSearhes-multile">
      <Button variant="naked--lowEmph" icon="ChevronDown" iconPosition="right" {...anchorProps}>
        {savedSearches.length} search criteria
      </Button>
      <Flyout
        {...flyoutProps}
        zIndex="var(--cx-zIndex-10)"
        isOpen={flyoutProps.isOpen}
        className="collection-linkedSearches-flyout"
      >
        <OptionsMenu
          {...menuProps}
          className="collection-linkedSearches-container"
          aria-label="Linked saved searches"
        >
          {savedSearches.map(
            (ss, index): JSX.Element => (
              <OptionsMenu.Item
                key={index}
                {...getItemProps({id: index, item: ss})}
                className="collection-linkedSearhes-option"
              >
                <Summary mode={mode} currentSavedSearchId={currentSavedSearchId} savedSearch={ss} />
                <EditOptions
                  savedSearch={ss}
                  isAgent={isAgent}
                  currentSavedSearchId={currentSavedSearchId}
                  deleteSearchCriteria={deleteSearchCriteria}
                />
              </OptionsMenu.Item>
            )
          )}
        </OptionsMenu>
      </Flyout>
    </div>
  );
};
