/* eslint-disable no-restricted-imports */
import {createBrowserHistory, createMemoryHistory, History} from 'history';

import {ROOT_PATH} from '@src/constants';
import {LocationState} from '@src/store/types';

export const createHistory = (): History<LocationState> => {
  const isTest = process.env.NODE_ENV === 'test';
  const history: History<LocationState> = isTest
    ? createMemoryHistory({initialEntries: ['/']})
    : createBrowserHistory({basename: ROOT_PATH});

  return history;
};
