import {useCallback} from 'react';

import {toggleSelectedListing, useDispatch, useSelectedIds, setToast} from '@src/store';
import {CollectionListing} from '@src/types';
import {getSelectListingToastConfig} from '@src/utils/getSelectListingToastConfig';

export const useToggleListing = () => {
  const dispatch = useDispatch();
  const selected = useSelectedIds();
  const toggle = useCallback(
    ({
      isSelected,
      collectionListing,
    }: {
      isSelected: boolean;
      collectionListing: CollectionListing;
    }) => {
      const {maxCount, toastData} = getSelectListingToastConfig();

      if (selected.length >= maxCount && !isSelected) {
        dispatch(setToast(toastData));
        return;
      }

      collectionListing?.listingData && dispatch(toggleSelectedListing(collectionListing));
    },
    [selected, dispatch]
  );

  return toggle;
};
