import {useMutation} from 'react-query';

import {getShowingTimeLinkInfo} from '@src/api';
import {useDispatch, setShowingTimeInfoModel} from '@src/store';
import {ShowingTimeErrorCode, ShowingTimeInfoModel} from '@src/types';

export function getShowingTimeMenuStatus(errorCode: ShowingTimeErrorCode): ShowingTimeInfoModel {
  let message = '';
  let isDisable = false;
  let isHidden = false;

  switch (errorCode) {
    case ShowingTimeErrorCode.MISSING_MLS_MEMBERSHIP:
      isHidden = true;
      break;

    case ShowingTimeErrorCode.INVALID_MLS_MEMBERSHIP:
      isDisable = true;
      message =
        'We’ve encountered an error with your MLS membership, please contact agentexperience@compass.com for assistance updating your membership details.';
      break;
    case ShowingTimeErrorCode.INVALID_LISTING:
      isHidden = true;
      break;
    case ShowingTimeErrorCode.UNSUPPORTED_LISTING_STATUS:
      isHidden = true;
      break;
    case ShowingTimeErrorCode.NOT_MLS_LISTING:
      isDisable = true;
      message =
        'ShowingTime can’t schedule one or more of the listings selected because it’s not from an MLS. Contact the listing agent to schedule a showing.';
      break;
    case ShowingTimeErrorCode.UNSUPPORTED_IN_SHOWINGTIME:
      isDisable = true;
      message =
        'One or more of the listings you selected can not be scheduled with ShowingTime please contact the listing agent directly to schedule a showing.';
      break;
    case ShowingTimeErrorCode.OUT_OF_COVERAGE_GEO:
      isHidden = true;
      break;

    case ShowingTimeErrorCode.UNKNOWN_FEED:
      isDisable = true;
      message =
        'ShowingTime can’t schedule one or more of the listings selected because it doesn’t support this region. Contact the listing agent to schedule a showing';
      break;
  }

  return {
    message,
    isDisable,
    isHidden,
  };
}

export const useGetShowingTimeLinkInfo = () => {
  const dispatch = useDispatch();

  return useMutation(async (listingIds: string[]) => {
    try {
      const result = await getShowingTimeLinkInfo({
        listingIds,
      });
      let showingTimeStatus;

      if (!!result && result?.coverageErrors && result?.coverageErrors.length > 0) {
        const errorCode = result?.coverageErrors?.[0].code || 0;

        showingTimeStatus = getShowingTimeMenuStatus(errorCode);
      } else {
        showingTimeStatus = {
          message: '',
          isDisable: false,
          isHidden: false,
        };
      }

      dispatch(
        setShowingTimeInfoModel({
          data: result,
          ...showingTimeStatus,
        })
      );

      return <ShowingTimeInfoModel>{
        data: result,
        ...showingTimeStatus,
      };
    } catch (e) {
      return {
        message: '',
        isDisable: false,
        isHidden: true,
      };
      // storing my error onto Sentry
    }
  });
};
