import {
  generateLocalStoragePath,
  LISTING_PARAMETERS_LOCAL_STORAGE_PATH,
  setParamsToLocalStorage,
} from '@uc/local-storage-parameters-tracking';

import {notEmpty} from '.';

export function setListingFrameTrackParams(
  listingId: string,
  params: {
    product: 'collections';
    collection_id: string;
    referring_product: 'direct';
    deliverable_id?: string;
    availability?: string;
    referring_product_path?: string;
  }
) {
  const localStoragePath = generateLocalStoragePath(
    LISTING_PARAMETERS_LOCAL_STORAGE_PATH,
    listingId
  );
  const cleaned = Object.entries(params).reduce<Record<string, unknown>>((prev, [key, value]) => {
    if (notEmpty(value)) {
      prev[key] = value;
    }

    return prev;
  }, {});

  setParamsToLocalStorage(cleaned, localStoragePath);
}
