import classnames from 'classnames';
import React from 'react';

import {Poptip} from '@uc/cx.react';
import {salesUrl} from '@uc/saved-search.react/dist/esm/urls';

import {useEllipsisPoptip} from '@src/hooks/useEllipsisPoptip';
import {SavedSearchMetadata} from '@src/types';

import styles from './SavedSearchMenu.module.pcss';

const searchSalesUrl = salesUrl(false);
const getSavedSearchUrl = (savedSearchId?: string) =>
  savedSearchId ? `/workspace/#/saved-search/${savedSearchId}` : '/workspace/#/saved-searches';

export interface SavedSearchMenuData {
  savedSearches: SavedSearchMetadata[];
}

export const SavedSearchMenu = ({data}: {data?: SavedSearchMenuData}) => {
  const {poptipProps, getPoptipAnchorProps} = useEllipsisPoptip();

  return (
    <React.Fragment>
      <ul className="cx-sideMenu-section">
        <li className="cx-sideMenu-sectionTitle">Saved Searches</li>
        {data &&
          data.savedSearches.map((savedSearch) => (
            <li key={savedSearch._id}>
              <a
                className={classnames(styles.menuItem, 'cx-sideMenu-item', 'u-ellipsis', {
                  'cx-indicator': savedSearch.badgeCount,
                })}
                data-indicator={savedSearch.badgeCount}
                {...getPoptipAnchorProps(savedSearch.name, 'top')}
                href={getSavedSearchUrl(savedSearch._id)}
              >
                {savedSearch.name}
              </a>
            </li>
          ))}
        <li>
          <a className={`${styles.newLink} cx-sideMenu-sectionAction`} href={searchSalesUrl}>
            Create New
          </a>
        </li>
        <Poptip {...poptipProps} />
      </ul>
      <div className="cx-sideMenu-divider"></div>
    </React.Fragment>
  );
};
