import React, {useState, useRef, useEffect} from 'react';

import {Poptip, PoptipProps} from '@uc/cx.react';

import {keys, validateInput} from '@src/utils';

const ErrorMessage = () => {
  return (
    <p className="textIntent-caption2" data-testid="header-title-error">
      {`Collection name can only contain these special characters: $ ! < > - _ ' , .`}
    </p>
  );
};

export const HeaderTitle = ({
  collectionName,
  onSaveCollectionName,
}: {
  collectionName: string;
  onSaveCollectionName: (newName: string) => void;
}) => {
  const [isTitleTipOpen, setIsTitleTipOpen] = useState(false);
  const anchorRefOfTip = useRef<HTMLHeadingElement>(null);
  const [isInputValid, setIsInputValid] = useState<boolean>(true);
  const [isEditMode, setIsEditMode] = useState<boolean>(false);
  const [headerTitle, setHeaderTitle] = useState<string>(collectionName || '');
  const [inputTitle, setInputTitle] = useState<string>(collectionName || '');
  const inputRef = React.createRef<HTMLInputElement>();

  useEffect(() => {
    if (isEditMode) {
      setIsTitleTipOpen(false);

      if (inputRef.current) {
        inputRef.current.focus();
      }
    }
  }, [isEditMode, inputRef]);

  useEffect(() => {
    setHeaderTitle(collectionName || '');
    setInputTitle(collectionName || '');
  }, [collectionName]);

  const handleTitleChange = (e: React.FormEvent<HTMLFormElement>) => {
    const collectionName = inputTitle.trim();

    if (collectionName.length && isInputValid) {
      setIsEditMode(false);
      onSaveCollectionName(collectionName);
      setHeaderTitle(collectionName);
    } else {
      e.preventDefault();
    }
  };
  const cancelEdit = () => {
    setIsEditMode(false);
    setIsInputValid(true);
    setInputTitle(headerTitle);
  };
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === keys.ESCAPE) {
      cancelEdit();
    }
  };
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const input = e.target.value;

    setIsInputValid(validateInput(input));
    setInputTitle(input);
  };
  const displayMode = () => {
    const poptipProps: PoptipProps = {
      anchor: anchorRefOfTip,
      alignment: 'bottom',
      children: isInputValid ? headerTitle : collectionName,
      isOpen: isTitleTipOpen,
    };

    return (
      <>
        <h1
          className="u-ellipsis collection-header-titleEditing textIntent-headline1"
          ref={anchorRefOfTip}
          onMouseEnter={() => setIsTitleTipOpen(true)}
          onMouseLeave={() => setIsTitleTipOpen(false)}
        >
          <button
            className="collection-button-no-style u-ellipsis"
            data-testid="header-title-display"
            onClick={() => setIsEditMode(true)}
          >
            {isInputValid ? headerTitle : collectionName}
          </button>
        </h1>
        <Poptip {...poptipProps} />
      </>
    );
  };
  const editMode = () => {
    return (
      <div className="u-ellipsis">
        <form
          className="collection-header-titleEditing"
          onSubmit={handleTitleChange}
          onBlur={cancelEdit}
          data-testid="header-title-form"
        >
          <input
            ref={inputRef}
            className="collection-header-titleInput"
            type="text"
            value={inputTitle}
            aria-label="title input"
            data-testid="header-title-input"
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
          />
        </form>
        {!isInputValid && <ErrorMessage />}
      </div>
    );
  };

  return <>{isEditMode ? editMode() : displayMode()}</>;
};
