import {useMemo} from 'react';

import {useDispatch, setToast, useCurrentColumns, useGeoId} from '@src/store';
import {ExportListingsToGsheetRequest, ColumnDefinition} from '@src/types';

import {useCreateGoogleSheetFromListingIds} from './useCreateGoogleSheetFromListingIds';

const advancedOptions = {
  applyClientFormatting: false,
  groupByStatus: false,
  includeSummaryStatistics: false,
};

export const useExportParams = (listingIds: string[]): ExportListingsToGsheetRequest => {
  const geoId = useGeoId();
  const {currentColumns} = useCurrentColumns({
    isCollectionShared: false,
  });
  const allColumnsToExport = useMemo(() => {
    return currentColumns
      .map((column) => column.exportDefinition)
      .filter(
        (exportDefinition): exportDefinition is ColumnDefinition => exportDefinition !== undefined
      );
  }, [currentColumns]);

  return useMemo(
    () => ({
      listingIds: listingIds || [],
      advancedOptions,
      columnsToExport: allColumnsToExport,
      localToUTCTimeOffset: new Date().getTimezoneOffset(),
      geographies: [geoId],
    }),
    [listingIds, allColumnsToExport, geoId]
  );
};

export const useExportListings = (listingIds: string[]) => {
  const defaultExportParams = useExportParams(listingIds);
  const createGoogleSheetFromListingIdsAsync = useCreateGoogleSheetFromListingIds().mutateAsync;
  const dispatch = useDispatch();

  return useMemo(
    () => () => {
      if (listingIds.length === 0) {
        return;
      }

      dispatch(
        setToast({
          isOpen: true,
          variant: 'success',
          message: 'Downloading listings to Google Sheets',
          duration: 0,
        })
      );
      return createGoogleSheetFromListingIdsAsync(defaultExportParams)
        .then((response) => {
          const {spreadsheetName, spreadsheetUrl} = response;

          dispatch(
            setToast({
              isOpen: true,
              variant: 'success',
              message: `Done downloading: ${spreadsheetName}`,
              duration: 2000,
            })
          );
          setTimeout(() => {
            window.open(spreadsheetUrl);
          }, 2000);
        })
        .catch(() => {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message:
                'We are unable to complete your request - please try again or contact support',
              duration: 2000,
            })
          );
        });
    },
    [createGoogleSheetFromListingIdsAsync, defaultExportParams, dispatch, listingIds]
  );
};
