import React from 'react';

import {Modal, useModal} from '@uc/cx.react';

import {useExportListings, useRefreshGetCollectionQueryCache} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {ReviewListingsContainer} from '@src/modals/review-listings/modal';
import {ProcessedListing} from '@src/types';

import './ReviewModal.pcss';

const ReviewModalInner = ({
  listingId,
  currentListingIndex,
  collectionId,
}: {
  collectionId: string;
  currentListingIndex: number;
  listingId: string;
}) => {
  const {modalId} = useCurrentUrlData();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();
  const onClose = () => {
    if (modalId) {
      setParams({listingIds: undefined, modalId: undefined, currentListingIndex: undefined});
    }

    refreshGetCollectionQueryCache();
  };
  const {modalProps} = useModal({initialIsOpen: true, onIsOpenChange: onClose});
  const setParams = useUpdateCurrentUrl();
  const exportListings = useExportListings([listingId]);
  const onResolve = (result?: {iframeListingAction?: string; listing?: ProcessedListing}) => {
    if (result?.iframeListingAction === '.download') {
      exportListings();
      onClose();
    } else {
      onClose();
    }
  };

  return (
    <Modal {...modalProps} size="lg" className="reviewModal">
      <ReviewListingsContainer
        onClose={onClose}
        onSubmit={onResolve}
        collectionId={collectionId}
        currentListingIndex={currentListingIndex}
      />
    </Modal>
  );
};

export const ReviewModal = () => {
  const urlData = useCurrentUrlData();
  const {collectionId, listingIds, currentListingIndex} = urlData;
  const listingId =
    listingIds && currentListingIndex && currentListingIndex >= 0
      ? listingIds[currentListingIndex]
      : listingIds
      ? listingIds[0]
      : undefined;

  return (
    <>
      {listingId && (
        <ReviewModalInner
          collectionId={collectionId}
          listingId={listingId}
          currentListingIndex={currentListingIndex || 0}
        />
      )}
    </>
  );
};
