import {useMemo} from 'react';

import {CollectionMode, ModalId} from '@src/constants';
import {useSavedPageSize} from '@src/store';
import {toCollectionUrl} from '@src/utils/url';

export const useBoardLink = ({
  collectionId,
  modalId,
  mode,
  savedSearchId,
  source,
}: {
  collectionId: string;
  mode: CollectionMode;
  modalId?: ModalId;
  savedSearchId?: string;
  source?: string;
}) => {
  // get the currently saved page size if present
  const pageSize = useSavedPageSize(collectionId);

  return useMemo(
    () =>
      toCollectionUrl({
        collectionMode: mode,
        collectionId,
        modalId,
        pageSize,
        savedSearchId,
        source,
      }),
    [mode, collectionId, modalId, pageSize, savedSearchId, source]
  );
};
