import {Location} from 'history';
import {useMemo} from 'react';
import {useHistory} from 'react-router-dom';

import {EMPTY_PAGE_PATH} from '@src/constants';
import {
  setPageSize,
  useDispatch,
  useSelector,
  getCurrentNav,
  CollectionUrlData,
  LocationState,
  setSource,
} from '@src/store';
import {toCollectionUrl} from '@src/utils/url';

export const useCurrentUrlData = () => {
  const data = useSelector(getCurrentNav);

  if (!data) {
    throw new Error('Route mismatch error');
  }

  return data;
};

function hasLocationChanged(
  prev: Location<LocationState | undefined>,
  next: Location<LocationState | undefined>
) {
  if (prev.pathname !== next.pathname) return true;
  if (prev.search !== next.search) return true;
  if (prev.hash !== next.hash) return true;
  if (prev.state?.selectedCommentsModalTab !== next.state?.selectedCommentsModalTab) return true;
  return false;
}

export const useUpdateCurrentUrl = (): ((next: Partial<CollectionUrlData>) => void) => {
  const current = useCurrentUrlData();
  const history = useHistory<LocationState>();
  const dispatch = useDispatch();

  /**
   * Update the current URL params with the passed values.
   */
  return useMemo(() => {
    return (update: Partial<CollectionUrlData>) => {
      update = {...update};

      if (!current) {
        return;
      }

      if (update.path === EMPTY_PAGE_PATH) {
        history.push(EMPTY_PAGE_PATH);
        return;
      }

      const {
        pageSize,
        savedSearchId,
        sortId,
        filterId,
        filterByOpenHouses,
        filterByListingStatus,
        filterByLikelyToSell,
        collectionId,
        address,
        collectionMode,
      } = update;

      if (collectionId && collectionId !== current.collectionId) {
        history.push(`/${collectionId}`);
        return;
      } else if (collectionMode && collectionMode !== current.collectionMode) {
        update.address = undefined;
      }

      if (
        /* eslint-disable @typescript-eslint/no-extra-parens */
        (pageSize && pageSize !== current.pageSize) ||
        (savedSearchId && savedSearchId !== current.savedSearchId) ||
        (sortId && sortId !== current.sortId) ||
        (filterId && filterId !== current.filterId) ||
        (typeof filterByOpenHouses === 'boolean' &&
          filterByOpenHouses !== current.filterByOpenHouses) ||
        (typeof filterByLikelyToSell === 'boolean' &&
          filterByLikelyToSell !== current.filterByLikelyToSell) ||
        (address && address !== current.address) ||
        (filterByListingStatus && filterByListingStatus !== current.filterByListingStatus)
        /* eslint-enable @typescript-eslint/no-extra-parens */
      ) {
        update.page = 1;
      }

      const next = {
        ...current,
        ...update,
      };
      const location = toCollectionUrl(next);

      if (hasLocationChanged(history.location, location)) {
        if (current.source !== next.source) {
          dispatch(setSource(next.source));
        }

        history.push(location);

        if (current.pageSize !== next.pageSize) {
          dispatch(setPageSize({collectionId: next.collectionId, pageSize: next.pageSize}));
        }
      }
    };
  }, [current, dispatch, history]);
};
