import React from 'react';

export interface ActionButtonProps {
  isPrimary: boolean;
  size: 'sm' | 'md' | 'lg';
  action(): void;
  label: string;
  icon?: JSX.Element;
}

export const ActionButton: React.FC<ActionButtonProps> = (props: ActionButtonProps) => {
  const {isPrimary, size = 'sm', action, label, icon} = props;
  const buttonStyle = isPrimary ? `cx-solidBtn` : `cx-enclosedBtn`;
  const btnSize = `buttonStyle-${size}`;
  const btnClassNames = [buttonStyle, btnSize];

  return (
    <button
      className={`uc-workspaceBoardReviewListingsModal-reviewBtn ${btnClassNames.join(' ')}`}
      onClick={action}
    >
      {icon} <span style={{paddingLeft: '5px'}}>{label}</span>
    </button>
  );
};
