import React, {useEffect} from 'react';

import {useActionBarItems, GROUP_ID, useAction} from '@src/components/ActionBar/logic';
import {ActionBarProps} from '@src/components/ActionBar/types';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {Dropdown} from '@src/cx/Dropdown';
import {useGetShowingTimeLinkInfo} from '@src/hooks/useGetShowingTimeLinkInfo';
import {useSelectedIds, useGetShowingTimeData} from '@src/store';

export const TourOptions = (props: ActionBarProps) => {
  const selected = useSelectedIds();
  const getShowingTimeInfoAsync = useGetShowingTimeLinkInfo().mutateAsync;
  const showingInfo = useGetShowingTimeData();
  const {bar} = useActionBarItems({mode: props.mode}, showingInfo);
  const items = bar[GROUP_ID.TOUR];
  const onClick = useAction(props);

  useEffect(() => {
    if (selected.length > 0) {
      getShowingTimeInfoAsync(selected || []);
    }
  }, [selected, getShowingTimeInfoAsync]);

  if (items.length === 0) {
    return null;
  }

  const showingTimeAvailable = !showingInfo?.isHidden;

  return (
    <ButtonGroup>
      <Dropdown
        variant="enclosed--highEmph"
        labelIsHidden={props.size < 4}
        iconLeft={showingTimeAvailable ? 'ShowingTimeClear' : 'MapRoute'}
        iconRight="ChevronDown"
        size="sm"
        label="Tour Options"
        items={items}
        alignment="bottomLeft"
        onItemSelect={onClick}
        data-testid="tourOptionsDropdown"
      />
    </ButtonGroup>
  );
};
