// Note: Exported for testing only
export const formatMonthlyPrice = (charge?: Charge | null): string | undefined => {
  if (!charge?.chargeAmount) {
    return;
  }

  let chargeMultiplier = 1;

  switch (charge?.paymentFrequentType) {
    case FeePaymentFrequentType.ANNUALLY:
      chargeMultiplier = 1 / 12;
      break;
    case FeePaymentFrequentType.SEMI_ANNUALLY:
      chargeMultiplier = 1 / 6;
      break;
    case FeePaymentFrequentType.QUARTERLY:
      chargeMultiplier = 1 / 3;
      break;
    case FeePaymentFrequentType.SEMI_MONTHLY:
      chargeMultiplier = 2;
      break;
  }

  const chargeAmountPerMonth = charge.chargeAmount * chargeMultiplier;

  return formatPrice(chargeAmountPerMonth);
};

