const ListingImage = ({listingData}: {listingData?: ProcessedListing}) => {
  const [src, setSrc] = useState(listingData?.media?.[0]?.thumbnailUrl ?? listingImagePlaceholder);

  return (
    <img
      src={src}
      alt="Listing"
      style={{width: '80px', height: '80px'}}
      onError={() => setSrc(listingImagePlaceholder)}
    />
  );
};
