const visibleIndicator: HeaderConfig = {
  id: COLUMN_ID.CLIENT_ACTION,
  label: 'Client Action',
  canDrag: true,
  width: '120px',
  render: function visibleIndicatorRender(collectionListing: CollectionListing) {
    const reviewStage = collectionListing?.reviewStage?.currentStage;
    const notInterested = reviewStage === CollectionListingReviewStage.NOT_INTERESTED;
    const interested = reviewStage === CollectionListingReviewStage.FAVORITE;

    if (notInterested) {
      const IconComponent = Icon[getNotInterestedIcon()];

      return (
        <div className={styles.iconNotInterestedWrap} data-testid="notInterestedIconIndicator">
          <IconComponent className={styles.iconNotInterested} />
        </div>
      );
    }

    if (interested) {
      return (
        <div className={styles.iconStarWrap} data-testid="starIconIndicator">
          <Icon.Star className={styles.iconStar} />
        </div>
      );
    }

    return null;
  },
};
