const OpenHousesRenderComponent = ({listingData}: {listingData: ProcessedListing}) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  if (listingData && hasOpenHouse(listingData)) {
    const formatted = formatAllOpenHouses(listingData) || [];
    const tooltipContent = formatted.reduce<React.ReactNode[]>(
      (acc, current) => acc.concat(current, <br key={current} />),
      []
    );

    return (
      <>
        <div
          className={`u-ellipsis ${styles.openHousesContent}`}
          {...getPoptipAnchorProps(<>{tooltipContent}</>, 'top')}
          data-testid="openHousesContent"
        >
          {formatFirstOpenHouse(listingData)}
        </div>
        <Poptip {...poptipProps} />
      </>
    );
  }

  return null;
};
