const CommentsButton = ({collectionListing}: {collectionListing: CollectionListing}) => {
  const dispatch = useDispatch();
  const {collectionMode: mode} = useCurrentUrlData();
  const listingIds = useNavListingIds();
  const setParams = useUpdateCurrentUrl();
  const {trackCommentModalInitiated} = useTrackCommentModalInitiated('list view');
  const numComments = collectionListing.comments?.length || 0;
  const hasUnreadComments = Boolean(
    collectionListing.updatesUnreadByCurrentUser?.includes(UnreadListingUpdates.RECENT_COMMENTS)
  );
  const numCommentsText = numComments > 0 ? ` ${numComments}` : '';
  const indicatorClassName = hasUnreadComments ? ' cx-indicator cx-indicator--new' : '';
  const modalParams =
    mode === COLLECTION_MODE.UNSHARED
      ? {
          modalId: MODAL_URL_IDS.listings,
          listingIds: listingIds,
          selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
          currentListingIndex: collectionListing.listingId
            ? listingIds?.indexOf(collectionListing?.listingId)
            : undefined,
        }
      : {
          modalId: MODAL_URL_IDS.comment,
          listingIds: collectionListing.listingId ? [collectionListing.listingId] : undefined,
          selectedCommentsModalTab: LISTING_COMMENTS_VIEWS_TAB.COMMENTS,
          enableModalPagination: true,
        };

  return (
    <Button
      data-testid="commentButton"
      variant="naked"
      onClick={(e) => {
        e.stopPropagation();
        dispatch(setReferringListingPath('table'));
        setParams(modalParams);
        trackCommentModalInitiated();
      }}
    >
      <span className={'cx-icon cx-btn-icon'.concat(indicatorClassName)} data-testid="commentIcon">
        <svg width={16} height={16} viewBox="0 0 16 16">
          <use xlinkHref="#cx-icon-talkBubble_16x16"></use>
        </svg>
      </span>
      <span>{numCommentsText}</span>
    </Button>
  );
};
