const OutdoorSpaceRenderComponent = ({listingData}: {listingData: ProcessedListing}) => {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const outdoorSpace = listingData?.detailedInfo?.outdoorSpace || [];
  const hasOutdoorSpace = outdoorSpace.length > 0;

  if (hasOutdoorSpace) {
    const tooltipContent = outdoorSpace.reduce<React.ReactNode[]>(
      (acc, current) => acc.concat(current, <br />),
      []
    );

    return (
      <>
        <div
          className="u-ellipsis"
          {...getPoptipAnchorProps(<>{tooltipContent}</>, 'top')}
          data-testid="outdoorSpaceContent"
        >
          {outdoorSpace.join(', ')}
        </div>
        <Poptip {...poptipProps} />
      </>
    );
  }

  return null;
};
