import React from 'react';

import {useDeviceWidth} from '@src/hooks';

// eslint-disable-next-line @typescript-eslint/no-type-alias
type DeviceWidthProps = ReturnType<typeof useDeviceWidth>;

const shouldRender = (a: DeviceWidthProps, b: Partial<DeviceWidthProps>) => {
  const keys = Object.keys(a) as Array<keyof typeof a>;

  for (const key of keys) {
    if (a[key] === b[key]) {
      return true;
    }
  }

  return false;
};

export const Breakpoint = (props: Partial<DeviceWidthProps> & {children: React.ReactNode}) => {
  const {children, ...rest} = props;
  const width = useDeviceWidth();
  const show = shouldRender(width, rest);

  return <>{show ? children : null}</>;
};
