/* eslint-disable react/no-unescaped-entities */
import React, {useEffect} from 'react';

import {
  collectionCreated as logCollectionCreated,
  collectionCreateInitiated as logCollectionCreateInitiated,
} from '@uc/analytics-definitions';
import {Button} from '@uc/cx.react';
import loadModal from '@uc/modal-loader';

import {Loading} from '@src/components/Loading';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useCollectionMenuData} from '@src/hooks/useCollectionMenuData';
import {useCreateBoard} from '@src/hooks/useCreateBoard';
import {useDispatch, setToast} from '@src/store';
import {getResolvedAssetVersion} from '@src/utils/getResolvedAssetVersion';
import {isValidCollectionId} from '@src/utils/isValidCollectionId';

import styles from './Content.module.pcss';

const EMPTY_CONTENT_PRODUCT_PATH = 'Empty Content Page';

export const EmptyContent = () => {
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const createBoardAsync = useCreateBoard().mutateAsync;
  const data = useCollectionMenuData();
  const nextCollection = data?.boards?.[0];

  useEffect(() => {
    if (data.isLoading) {
      return;
    }

    if (nextCollection?.id && isValidCollectionId(nextCollection?.id)) {
      setParams({collectionId: nextCollection.id});
    }
  }, [data.isLoading, nextCollection, setParams]);

  // If there is a next collection, then we are redirecting
  // to a new board, so don't render the empty content UI.
  if (data.isLoading || nextCollection) {
    return <Loading />;
  }

  const handleCreateNew = () => {
    logCollectionCreateInitiated({
      product: 'collections',
      product_path: EMPTY_CONTENT_PRODUCT_PATH,
    });
    const modalVersion = getResolvedAssetVersion('modal--create-collection.react', 'latest');

    loadModal<{isPublic: boolean}, {boardName: string}>(`create-collection.react/${modalVersion}`, {
      isPublic: false,
    })
      .then(({boardName}) => {
        return createBoardAsync({boardName});
      })
      .then(({board = {}}) => {
        return board;
      })
      .then(async (collection) => {
        logCollectionCreated({
          collection_id: collection.id || '',
          product: 'collections',
          product_path: EMPTY_CONTENT_PRODUCT_PATH,
        });
        setParams({
          collectionId: collection.id,
        });
      })
      .catch((err) => {
        if (err) {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Collection creation failed. Please try again.',
            })
          );
        }
      });
  };

  return (
    <div className={styles.placeholderWrapper}>
      <h1 className="textIntent-display1">No Collections</h1>
      <p className="textIntent-body">You don't have any collections.</p>
      <Button
        onClick={handleCreateNew}
        variant="solid"
        size="sm"
        aria-label="Create a Collection"
        data-testid="createCollectionButton"
      >
        Create a Collection
      </Button>
    </div>
  );
};
