import React from 'react';

import {SearchQuery} from '@uc/search-query';

import {Map} from '@src/components/Map';
import {NewMap} from '@src/components/NewMap';
import {useLoadMapSearch} from '@src/hooks/useLoadMapSearch';
import {ProcessedListing, CollectionListing, MapSearchResultsType} from '@src/types';
import {isNewMapComponentEnabled} from '@src/utils/optyUtils';

import styles from './Content.module.pcss';

export const UnsharedTabMapWrapper = ({
  isAgent,
  onListingClick,
  searchQuery,
}: {
  isAgent: boolean;
  onListingClick: ({listing}: {listing: ProcessedListing}) => void;
  searchQuery?: SearchQuery;
}) => {
  const mapSearchQuery = {
    // eslint-disable-next-line @typescript-eslint/no-extra-parens
    ...(searchQuery ?? {}),
    rawQuery: '',
    debugLevel: 0,
  };
  const {data, isLoading} = useLoadMapSearch({mapSearchQuery});

  if (isLoading) {
    return null;
  }

  return (
    <MapComponent
      searchResults={data?.relationClusters ?? []}
      searchQuery={searchQuery}
      isAgent={isAgent}
      onListingClick={onListingClick}
    />
  );
};

export const DefaultMapWrapper = ({
  isAgent,
  listings,
  onListingClick,
}: {
  isAgent: boolean;
  listings?: CollectionListing[];
  onListingClick: ({listing}: {listing: ProcessedListing}) => void;
}) => {
  const processedListings: ProcessedListing[] = (
    listings?.map((listing) => listing.listingData) || []
  ).filter((processedListing): processedListing is ProcessedListing => Boolean(processedListing));

  return (
    <MapComponent
      searchResults={processedListings}
      isAgent={isAgent}
      onListingClick={onListingClick}
    />
  );
};

export const MapComponent = ({
  searchResults,
  isAgent,
  onListingClick,
  searchQuery,
}: {
  searchResults?: MapSearchResultsType;
  isAgent: boolean;
  onListingClick: ({listing}: {listing: ProcessedListing}) => void;
  searchQuery?: SearchQuery;
}) => {
  const getMapComponent = (): JSX.Element => {
    if (!searchResults || searchResults.length === 0) {
      return <span>no listings to show on map</span>;
    }

    const props = {
      isAgent,
      searchQuery,
      searchResults,
      onListingClick,
    };

    if (isNewMapComponentEnabled()) {
      return <NewMap {...props} />;
    }

    return <Map {...props} />;
  };

  return <div className={styles.map}>{getMapComponent()}</div>;
};
