import {Location} from 'history';
import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';

import {BoardLayout, EmptyBoardLayout, NotFoundBoardLayout} from '@src/components/BoardLayout';
import {Loading} from '@src/components/Loading';
import {EMPTY_PAGE_PATH, ERROR_PAGE_PATH, ROOT_PAGE_PATH} from '@src/constants';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useCollectionMenuData} from '@src/hooks/useCollectionMenuData';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {
  CollectionUrlData,
  useDispatch,
  setAppReferrer,
  useReferrer,
  useSource,
  store,
} from '@src/store';
import {Collection} from '@src/types';
import {isValidCollectionId} from '@src/utils/isValidCollectionId';
import {toCollectionUrl} from '@src/utils/url';

export const checkShouldUpdateUrl = ({
  collection,
  source,
  referrer,
  updateCurrentUrl,
  dispatch,
}: {
  collection: Collection;
  source: string | undefined;
  referrer: string;
  updateCurrentUrl: (next: Partial<CollectionUrlData>) => void;
  dispatch: typeof store.dispatch;
}) => {
  const dealSummaries = collection?.dealSummaries;
  const dealSummariesLength = dealSummaries && dealSummaries.filter(({id}) => id).length;

  if (!dealSummariesLength) {
    source && updateCurrentUrl({source: undefined});

    dispatch(setAppReferrer('/workspace'));
  } else if (!referrer && dealSummariesLength && !source) {
    updateCurrentUrl({source: 'deals'});
  }
};

const checkIsRootPath = ({location}: {location: Location<unknown>}) => {
  const {pathname} = location;

  return pathname === ROOT_PAGE_PATH;
};
const RootPathRoute = () => {
  const data = useCollectionMenuData();
  const nextCollection = data?.boards?.[0];
  const updateCurrentUrl = useUpdateCurrentUrl();

  useEffect(() => {
    if (data.isLoading) {
      return;
    }

    if (nextCollection?.id && isValidCollectionId(nextCollection?.id)) {
      updateCurrentUrl({collectionId: nextCollection?.id});
    }
  }, [data.isLoading, nextCollection, updateCurrentUrl]);

  if (data.isLoading) {
    return <Loading />;
  }

  return <EmptyBoardLayout />;
};

export const BoardPage = () => {
  const location = useLocation();
  const isRootPath = checkIsRootPath({location});
  const {search, pathname} = location;
  const dispatch = useDispatch();
  const updateCurrentUrl = useUpdateCurrentUrl();
  const referrer = useReferrer() || '';
  const history = useHistory();
  const urlParams = useCurrentUrlData();
  const {data} = useGetCollection({collectionId: urlParams.collectionId});
  const source = useSource();

  data &&
    data.collection &&
    checkShouldUpdateUrl({
      collection: data.collection,
      source,
      referrer,
      updateCurrentUrl,
      dispatch,
    });

  useEffect(() => {
    dispatch(setAppReferrer(document.referrer || referrer));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (!urlParams) {
      return;
    }

    const next = toCollectionUrl(urlParams);

    // eslint-disable-next-line @typescript-eslint/no-extra-parens
    if (
      urlParams.path !== EMPTY_PAGE_PATH &&
      !isRootPath &&
      (search !== next.search || pathname !== next.pathname)
    ) {
      // use replace to avoid the '/BOARD_ID' in History stack
      history.replace(next);
    }
    // We only want the update to search string to occur
    // on first mount of the BoardPage component. The rest of our
    // URL updates should enforce adding the appropriate search
    // string into the URL.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!urlParams) {
    return null;
  }

  if (isRootPath) {
    return <RootPathRoute />;
  }

  if (urlParams.path === EMPTY_PAGE_PATH) {
    return <EmptyBoardLayout />;
  }

  if (urlParams.path === ERROR_PAGE_PATH) {
    throw new Error();
  }

  if (!isValidCollectionId(urlParams.collectionId)) {
    return <NotFoundBoardLayout />;
  }

  return <BoardLayout mode={urlParams.collectionMode} collectionId={urlParams.collectionId} />;
};
