import React from 'react';

import {Button} from '@uc/cx.react';

import {CollectionListingReviewStage} from '@src/types';

import './Footer.pcss';
import {ToggleChatButton} from './ToggleChatButton';

export const AgentFooter = ({
  onTrashListing,
  viewsCount,
  toggleCommentsView,
  hasComments,
  hasUnreadComments,
  reviewStage,
}: {
  onTrashListing: () => void | null;
  viewsCount: number;
  toggleCommentsView: () => void;
  hasComments: boolean;
  hasUnreadComments: boolean;
  reviewStage: CollectionListingReviewStage | null;
}) => {
  const enableTrash = reviewStage !== CollectionListingReviewStage.TRASH;

  return (
    <div className="AgentFooter" data-testid="agentFooterContainer">
      {enableTrash ? (
        <Button
          variant="enclosed"
          icon="Trash"
          size="sm"
          className="AgentFooter-TrashButton"
          onClick={() => onTrashListing()}
        >
          Trash
        </Button>
      ) : (
        <div></div>
      )}
      <div className="AgentFooter-RightActionGroup">
        <div>{`${viewsCount} views`}</div>
        <ToggleChatButton
          hasComments={hasComments}
          hasUnreadComments={hasUnreadComments}
          toggleCommentsView={toggleCommentsView}
        />
      </div>
    </div>
  );
};

AgentFooter.defaultProps = {
  onTrashListing: () => undefined,
  enableTrashListing: true,
  viewsCount: 0,
  toggleCommentsView: () => undefined,
  hasComments: false,
  hasUnreadComments: false,
};
