import {useQuery} from 'react-query';

import {ApiError, getAIRecommendations} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {useIsAgent} from '@src/store';
import {
  GetTasksRequest,
  GetTasksResponse,
  ResourceType,
  TaskSortableFields,
  TaskType,
  WorkflowEnrichOptions,
} from '@src/types';
import {isAIBuyerMatchingInCollectionsEnabled} from '@src/utils/optyUtils';

export const createTasksRequest = ({
  numOfItems,
  skip,
  collectionId,
  getDeletedTasks,
}: {
  numOfItems: number;
  skip: number;
  collectionId: string;
  getDeletedTasks: boolean;
}): GetTasksRequest => {
  return {
    sort: [{orderAsc: false, orderBy: TaskSortableFields.CREATED_AT}],
    enrichOptions: [WorkflowEnrichOptions.ENROLLMENT_ACTIONS],
    pagination: {limit: numOfItems, skip},
    filterQuery: {
      v2: true,
      primaryResourceTypes: [ResourceType.LISTING],
      attachmentTypes: [ResourceType.COLLECTION, ResourceType.LIKELIHOOD_TO_SELL],
      completed: getDeletedTasks,
      types: [TaskType.LTS_IN_COLLECTIONS],
      attachmentIds: [collectionId],
    },
  };
};

export const useAIRecommendations = ({
  collectionId,
  numOfItems,
  skip = 0,
  getDeletedTasks = false,
}: {
  collectionId: string;
  numOfItems: number;
  skip?: number;
  getDeletedTasks?: boolean;
}) => {
  const isAgent = useIsAgent();
  const request = createTasksRequest({numOfItems, skip, collectionId, getDeletedTasks});

  return useQuery<GetTasksResponse, ApiError>(
    [API_KEY.GET_AI_RECOMMENDATIONS, collectionId, numOfItems, skip, getDeletedTasks],
    () => {
      return isAgent && isAIBuyerMatchingInCollectionsEnabled()
        ? getAIRecommendations(request)
        : {totalResults: 0, taskV2s: []};
    }
  );
};
