import classnames from 'classnames';
import React, {useEffect, useMemo} from 'react';

import {collectionViewed, savedSearchViewed} from '@uc/analytics-definitions';
import {Pagination} from '@uc/cx.react';

import {ActionBar} from '@src/components/ActionBar';
import {MODES as AGENT_CARD_MODES} from '@src/components/AgentCard';
import {MODES as CONSUMER_CARD_MODES} from '@src/components/ConsumerCard';
import {EmptyState} from '@src/components/EmptyStates';
import {Filters} from '@src/components/Filters';
import {InfoBox} from '@src/components/InfoBox';
import {LikelyToSellBanner} from '@src/components/LikelyToSell';
import {ListingGrid} from '@src/components/ListingGrid';
import {ListingTable} from '@src/components/ListingTable';
import {Tabs} from '@src/components/Tabs';
import {
  CollectionMode,
  COLLECTION_MODE,
  SIDE_PANEL_IDS,
  AgentCollectionMode,
  ConsumerCollectionMode,
} from '@src/constants';
import {useDeviceWidth, useShowMap} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useGetCollectionListingsPaginated} from '@src/hooks/useGetCollectionListingsPaginated';
import {useGetCollectionSearchResults} from '@src/hooks/useGetCollectionSearchResults';
import {useIsCollectionShared} from '@src/hooks/useIsCollectionShared';
import {useSetParamsForMapClick} from '@src/hooks/useSetParamsForMapClick';
import {useTransformListingsToCards} from '@src/hooks/useTransformListingsToCards';
import {
  getCurrentView,
  getSidePanel,
  useSelector,
  useStore,
  clearSelectedListings,
  useIsAgent,
  useDispatch,
  setReferringListingPath,
  setNavListingIds,
  setNavTabCounts,
  setListingCount,
} from '@src/store';
import {ProcessedListing, ProcessedListingCard, CollectionListing} from '@src/types';
import {isAIBuyerMatchingInCollectionsEnabled} from '@src/utils/optyUtils';

import styles from './Content.module.pcss';
import {ContentGrid} from './ContentGrid';
import {ErrorContent} from './ErrorContent';
import {UnsharedTabMapWrapper, DefaultMapWrapper} from './MapWrapper';

export const convertSavedSearchListings = (
  savedSearchListings: ProcessedListing[],
  savedSearchListingCards?: ProcessedListingCard[]
): CollectionListing[] => {
  const idsToCards = new Map();

  if (savedSearchListingCards) {
    savedSearchListingCards.forEach((card) => idsToCards.set(card.listingIdSHA, card));
  }

  return savedSearchListings.map((listing) => ({
    listingId: listing.listingIdSHA,
    listingData: listing,
    listingCardData: idsToCards.get(listing.listingIdSHA),
  }));
};

export const Content = ({mode, collectionId}: {mode: CollectionMode; collectionId: string}) => {
  const {
    filterId,
    filterByOpenHouses = false,
    address,
    filterByListingStatus = false,
    filterByLikelyToSell = false,
    savedSearchId: urlSavedSearchId,
  } = useCurrentUrlData();
  const view = useSelector(getCurrentView);
  const isAgent = useIsAgent();
  const sidePanel = useSelector(getSidePanel);
  const {
    page,
    pageSize,
    isLoading,
    error,
    totalItems,
    collectionListings,
    listingIds,
    tabCounts,
    filterCounts,
    filterDropdownCounts,
  } = useGetCollectionListingsPaginated();
  const {data: defaultSavedSearch = {}, isLoading: isSavedSearchLoading} =
    useGetCollectionSearchResults(collectionId);
  const {listings: savedSearchListings = [], totalNumResults = 0} = defaultSavedSearch;
  const {data: {listingCards = []} = {}, isLoading: isCardsLoading} = useTransformListingsToCards(
    savedSearchListings,
    isAgent
  );
  const convertedSavedSearchListings = convertSavedSearchListings(
    savedSearchListings,
    listingCards
  );
  const savedSearchId = urlSavedSearchId || defaultSavedSearch?.savedSearchId || '';
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const [showMap] = useShowMap();
  const widths = useDeviceWidth();
  const store = useStore();
  const {data: {collection} = {}} = useGetCollection({collectionId});
  const {isCollectionShared} = useIsCollectionShared({
    collectionId: collection?.collectionId,
  });
  const unsharedMode = mode === COLLECTION_MODE.UNSHARED;
  const setParamsForMapClick = useSetParamsForMapClick();
  const onMapListingClick = useMemo(
    () =>
      ({listing}: {listing: ProcessedListing}) => {
        const handleListingClick = isAgent
          ? AGENT_CARD_MODES[mode as AgentCollectionMode].onListingClick
          : CONSUMER_CARD_MODES[mode as ConsumerCollectionMode].onListingClick;
        const listingId = listing.listingIdSHA;

        if (listingId) {
          dispatch(setReferringListingPath('map'));
          handleListingClick({setParams: setParamsForMapClick, listingId});
        }
      },
    [isAgent, mode, setParamsForMapClick, dispatch]
  );
  const hasSavedSearch = !!collection?.savedSearches?.length;

  useEffect(() => {
    dispatch(setNavTabCounts(tabCounts));
  }, [dispatch, tabCounts]);

  useEffect(() => {
    if (unsharedMode) {
      const savedSearchListingIds = convertedSavedSearchListings
        .map((listing) => {
          return listing.listingId;
        })
        .filter((listingId): listingId is string => Boolean(listingId));

      dispatch(setNavListingIds(savedSearchListingIds));
    } else {
      dispatch(setNavListingIds(listingIds));
    }
  }, [dispatch, listingIds, unsharedMode, convertedSavedSearchListings]);

  useEffect(() => {
    store.dispatch(clearSelectedListings());
  }, [mode, store]);

  const [isCollectionViewed, setCollectionViewed] = React.useState(false);

  useEffect(() => {
    if (isCollectionViewed) return;
    if (isLoading) return;
    if (!collectionId) return;
    setCollectionViewed(true);
    collectionViewed({
      collection_id: collectionId,
      product: 'collections',
      product_path: 'collections',
      referring_product: '',
      referring_product_path: '',
    });
  }, [isCollectionViewed, isLoading, collectionId]);

  const [previousSavedSearchId, setSavedSearchId] = React.useState(savedSearchId);

  useEffect(() => {
    if (previousSavedSearchId === savedSearchId) return;
    if (isSavedSearchLoading) return;
    if (mode !== COLLECTION_MODE.UNSHARED) return;
    setSavedSearchId(savedSearchId);
    savedSearchViewed({
      saved_search_id: savedSearchId,
      product: 'collections',
      product_path: 'collections',
      referring_product: '',
      referring_product_path: '',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [savedSearchId, isSavedSearchLoading, mode]);

  const ltsMode = mode === COLLECTION_MODE.LIKELY_TO_SELL;
  const hasSearchAddress = !!address;
  const hasFilters =
    filterId !== 'all' || filterByOpenHouses || !!filterByListingStatus || filterByLikelyToSell;
  const listingsToUse = unsharedMode ? convertedSavedSearchListings : collectionListings;
  const itemCount = unsharedMode ? totalNumResults : totalItems;
  const noListing = ltsMode ? tabCounts?.likelyToSell === 0 : itemCount === 0;
  const shouldHideActionBar = !hasFilters && !hasSearchAddress && noListing;
  const filtersEnabled = isAgent && mode === COLLECTION_MODE.SHARED && !shouldHideActionBar;

  useEffect(() => {
    dispatch(setListingCount(itemCount));
  }, [itemCount, dispatch]);

  if (error) {
    return <ErrorContent error={error} />;
  }

  return (
    <>
      <ContentGrid
        filters={
          filtersEnabled && <Filters filterCounts={filterCounts} collectionId={collectionId} />
        }
        infoBox={isAgent && <InfoBox mode={mode} isCurate={collection?.isCurate} />}
        actionBar={
          !shouldHideActionBar &&
          !ltsMode && (
            <ActionBar
              collectionId={collectionId}
              listingsOnPage={listingsToUse}
              size={widths.size}
              mode={mode}
              totalItems={itemCount}
              filterDropdownCounts={filterDropdownCounts}
            />
          )
        }
        tabs={
          <>
            <Tabs
              isAgent={isAgent}
              collectionId={collectionId}
              tabCounts={tabCounts}
              savedSearchId={urlSavedSearchId}
            />
          </>
        }
        rightBar={
          showMap &&
          (unsharedMode ? (
            <UnsharedTabMapWrapper
              isAgent={isAgent}
              onListingClick={onMapListingClick}
              searchQuery={defaultSavedSearch?.searchQuery}
            />
          ) : (
            <DefaultMapWrapper
              isAgent={isAgent}
              listings={listingsToUse}
              onListingClick={onMapListingClick}
            />
          ))
        }
        shouldShowSidePanel={
          // eslint-disable-next-line @typescript-eslint/no-extra-parens
          (isAgent && mode === COLLECTION_MODE.SHARED && sidePanel === SIDE_PANEL_IDS.tour) ||
          sidePanel === SIDE_PANEL_IDS.activity
        }
        isLoading={isLoading}
      >
        <div className={styles.contentWrap}>
          {ltsMode &&
          isAgent &&
          hasSavedSearch &&
          isAIBuyerMatchingInCollectionsEnabled() &&
          tabCounts?.likelyToSell ? (
            <LikelyToSellBanner />
          ) : null}
          {!isLoading && !isSavedSearchLoading && noListing && (
            <EmptyState mode={mode} hasFilters={hasFilters} hasSearchAddress={hasSearchAddress} />
          )}
          <div
            className={classnames(styles.listingsArea, {
              [styles.hide]: noListing && !isLoading,
            })}
          >
            {view === 'card' ? (
              <ListingGrid
                mode={mode}
                collectionListings={listingsToUse}
                collectionId={collectionId}
                isCollectionShared={isCollectionShared}
                isCardsLoading={isCardsLoading}
              />
            ) : (
              <ListingTable
                mode={mode}
                collectionListings={listingsToUse}
                collectionId={collectionId}
                isCollectionShared={isCollectionShared}
              />
            )}
          </div>
          <div className={styles.paginationArea}>
            <Pagination
              page={page}
              totalItems={ltsMode ? tabCounts?.likelyToSell || 0 : itemCount}
              pageSize={pageSize}
              setPage={(page: number) => {
                setParams({page});
              }}
              setPageSize={(pageSize: number) => {
                setParams({pageSize});
              }}
              size="xs"
              data-testid="pagination-section"
            />
          </div>
        </div>
      </ContentGrid>
    </>
  );
};
