import React from 'react';

import {usePoptip} from '@uc/cx.react';

export function useEllipsisPoptip() {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const getEllipsisPoptipAnchorProps = (...args: Parameters<typeof getPoptipAnchorProps>) => {
    const poptipAnchorProps = getPoptipAnchorProps(...args);
    const ellipsisPoptipAnchorProps = {
      ...poptipAnchorProps,
      onMouseEnter: (ev: React.SyntheticEvent<HTMLElement, Event>) => {
        if (ev.currentTarget.offsetWidth < ev.currentTarget.scrollWidth) {
          (
            poptipAnchorProps as {
              onMouseEnter: (ev: React.SyntheticEvent<Element, Event>) => void;
            }
          ).onMouseEnter(ev);
        }
      },
    };

    return ellipsisPoptipAnchorProps;
  };

  return {poptipProps, getPoptipAnchorProps: getEllipsisPoptipAnchorProps};
}
