import classnames from 'classnames';
import React from 'react';

import {Table, Th, Tr, Poptip, usePoptip} from '@uc/cx.react';

import {LikelyToSell} from '@src/components/LikelyToSell';
import {COLUMN_ID} from '@src/components/ListingTable/listingTableColumns';
import {CollectionMode, COLLECTION_MODE, MODAL_URL_IDS} from '@src/constants';
import {useUpdateCurrentUrl, useCurrentUrlData} from '@src/hooks/url';
import {useDeviceWidth} from '@src/hooks/useDeviceWidth';
import {useListingTableData} from '@src/hooks/useListingTableData';
import {
  useDispatch,
  useSelectedIdsSet,
  useIsAgent,
  useCurrentColumns,
  setHoveredListing,
  setReferringListingPath,
} from '@src/store';
import {CollectionListing, Header} from '@src/types';
import {getListingIds} from '@src/utils/getListingIds';

import {SelectOptions} from '../ActionBar/OptionsGroup/SelectOptions';
import {ColumnCell} from './ColumnCell';
import styles from './ListingTable.module.pcss';

export const ListingTable = ({
  collectionId,
  collectionListings,
  isCollectionShared,
  mode,
}: {
  collectionId: string;
  collectionListings?: CollectionListing[];
  isCollectionShared: boolean;
  mode: CollectionMode;
}) => {
  const dispatch = useDispatch();
  const isAgent = useIsAgent();
  const {isDesktop} = useDeviceWidth();
  const {currentColumns} = useCurrentColumns({isCollectionShared, collectionListings});
  const listingIds = getListingIds(collectionListings);
  const {
    headers,
    tableRef,
    getTableProps,
    getCellProps,
    getHeaderProps,
    getDragHeaderProps,
    getResizerProps,
  } = useListingTableData({columns: currentColumns});
  const selected = useSelectedIdsSet();
  const urlData = useCurrentUrlData();
  const setUrl = useUpdateCurrentUrl();
  const {poptipProps, getPoptipAnchorProps} = usePoptip();
  const {sortId, collectionMode} = urlData;
  const head = headers.map((header) => {
    const headerProps = getHeaderProps({header});
    const dragHeaderProps = getDragHeaderProps({header});
    const resizerProps = getResizerProps({header});
    const {sort} = header.data;
    const isAsc = sortId === sort?.asc;
    const isDesc = sortId === sort?.desc;
    const isSortSelected = isAsc || isDesc;
    const thStyle = {width: header.width};
    const renderCellLabel = (header: Header) => {
      switch (header.id) {
        case COLUMN_ID.SELECT:
          return (
            <SelectOptions
              mode={collectionMode as CollectionMode}
              listingsOnPage={collectionListings}
              size={24}
              collectionId={collectionId}
            />
          );
        case COLUMN_ID.PRICE:
          return isAgent ? header.data.label : 'Price';
        case COLUMN_ID.CLIENT_ACTION:
          return isDesktop ? (
            <>
              <span
                {...getPoptipAnchorProps(
                  'Listings that your client has favorited or marked not interested.',
                  'bottom'
                )}
              >
                {header.data.label}
              </span>
              <Poptip {...poptipProps} />
            </>
          ) : (
            <span>{header.data.label}</span>
          );
        case COLUMN_ID.MONTHLY_FEES:
          return isDesktop ? (
            <>
              <span
                {...getPoptipAnchorProps(
                  'Aggregate of any and all monthly fees: HOA fees, maintenance charges, condo fees',
                  'bottom'
                )}
              >
                {header.data.label}
              </span>
              <Poptip {...poptipProps} />
            </>
          ) : (
            <span>{header.data.label}</span>
          );
        default:
          return header.data.label;
      }
    };

    return (
      <Th
        key={`${header.id}-tableHeader`}
        {...headerProps}
        className={classnames(styles.tableHeader, {
          [styles.tableHeaderSelect]: header.id === COLUMN_ID.SELECT,
        })}
        dragHeaderProps={{...dragHeaderProps}}
        resizerProps={{...resizerProps}}
        style={thStyle}
        isSortable={Boolean(sort)}
        isSortSelected={isSortSelected}
        sortItemProps={
          sort
            ? {
                tabIndex: 0,
                onClick: () => {
                  let targetSortId;

                  if (isSortSelected) {
                    targetSortId = isAsc ? sort.desc : sort.asc;
                  } else {
                    targetSortId = sort.default;
                  }

                  setUrl({sortId: targetSortId});
                },
                onKeyDown: (event: React.KeyboardEvent<HTMLElement>) => {
                  console.log(event);
                },
                sortActive: isSortSelected,
                sortDirection: isAsc || !isSortSelected ? 'ASC' : 'DESC',
                title: `Sort by ${sort.id}`,
                'aria-label': `Sort by ${sort.id}`,
              }
            : undefined
        }
      >
        {renderCellLabel(header)}
      </Th>
    );
  });

  if (mode === COLLECTION_MODE.LIKELY_TO_SELL) {
    return <LikelyToSell />;
  }

  return (
    <div className={styles.listingTableWrap}>
      <Table
        className={classnames(styles.table, 'cx-react-table--layoutFixed')}
        caption="Collection listings"
        stickyHeader
        {...getTableProps()}
        ref={tableRef}
      >
        <thead>
          <Tr>{head}</Tr>
        </thead>
        <tbody>
          {collectionListings?.map((collectionListing) => {
            const {listingId} = collectionListing;

            if (!listingId) {
              return;
            }

            const isSelected = selected.has(listingId);
            const toggle = () => {
              dispatch(setReferringListingPath('table'));
              const modalParams =
                mode === COLLECTION_MODE.UNSHARED
                  ? {
                      modalId: MODAL_URL_IDS.listings,
                      listingIds: listingIds,
                      currentListingIndex: listingIds?.indexOf(listingId),
                    }
                  : {
                      modalId: MODAL_URL_IDS.comment,
                      listingIds: [listingId],
                      enableModalPagination: true,
                    };

              return setUrl(modalParams);
            };

            return (
              <Tr
                onClick={toggle}
                onMouseOver={() => dispatch(setHoveredListing(collectionListing))}
                onMouseOut={() => dispatch(setHoveredListing())}
                key={`${listingId}-tableRow`}
                selected={isSelected}
                className={classnames(styles.tableRow, {
                  [styles.tableRowSelected]: isSelected,
                })}
              >
                {headers.map((header) => {
                  const props = getCellProps({id: header.id});

                  return (
                    <ColumnCell
                      key={`${header.id}-${listingId}-columnCell`}
                      isSelected={isSelected}
                      isAgent={isAgent}
                      collectionId={collectionId}
                      collectionListing={collectionListing}
                      listingId={listingId}
                      cellProps={props}
                      header={header}
                      mode={mode}
                    />
                  );
                })}
              </Tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};
