import classnames from 'classnames';
import React from 'react';

import {Breakpoint, Button, Flyout, useFlyoutOnClick} from '@uc/cx.react';

import {ConsumerMenu} from '@src/components/ConsumerMenu';
import {useCollectionMenuData} from '@src/hooks/useCollectionMenuData';
import {useSavedSearches} from '@src/hooks/useSavedSearches';

import {CollectionMenuData} from '../ConsumerMenu/sections/CollectionMenu';
import styles from './ConsumerNav.module.pcss';

export const ConsumerNav = ({isDropdown = false}: {isDropdown?: boolean}) => {
  const data = useCollectionMenuData();
  const collectionMenuData: CollectionMenuData = {
    boards: data.boards,
    badgingSummaries: data.badgingSummaries,
  };
  const savedSearchMenuData = useSavedSearchMenuData();
  const {getFlyoutAnchorProps, flyoutProps, dismissFlyout} = useFlyoutOnClick();
  const menu = (
    <ConsumerMenu
      collectionMenuData={collectionMenuData}
      savedSearchMenuData={savedSearchMenuData}
    />
  );

  if (isDropdown) {
    return (
      <Breakpoint lt="tabletLandscape">
        <span className={styles.headerDropdown} data-testid="consumerNavDropdown">
          <Button
            {...getFlyoutAnchorProps()}
            variant="naked"
            icon="ChevronDown"
            className={styles.headerDropdownBtn}
            id="uc-workspace-headerDropdownMenu"
            aria-label="Workspace Navigation"
          />
          <Flyout
            {...flyoutProps}
            className={styles.headerDropdownMenu}
            onClick={() => dismissFlyout()}
            aria-labelledby="uc-workspace-headerDropdownMenu"
          >
            {menu}
          </Flyout>
        </span>
      </Breakpoint>
    );
  } else {
    return (
      <Breakpoint gte="tabletLandscape">
        <div
          className={classnames(styles.sideBar, 'cx-sideMenu')}
          data-testid="consumerNavSideMenu"
        >
          {menu}
        </div>
      </Breakpoint>
    );
  }
};

function useSavedSearchMenuData() {
  const {data: {savedSearches} = {}} = useSavedSearches();

  return (
    savedSearches && {
      savedSearches,
    }
  );
}
