import {useBusinessTrackerIds} from '@src/hooks/useBusinessTrackerIds';
import {useIsAgent} from '@src/store';
import {
  trackCollectionListingFlowAction,
  TrackCollectionListingFlowActionProps,
} from '@src/utils/trackCollectionListingFlowAction';

export const useTrackCollectionListingFlowAction = (collectionId: string) => {
  const trackerIds = useBusinessTrackerIds(collectionId);
  const isAgent = useIsAgent();

  return (
    props: Omit<
      TrackCollectionListingFlowActionProps,
      'businessTrackerIds' | 'isAgent' | 'collectionId'
    >
  ) => {
    trackCollectionListingFlowAction({
      ...props,
      businessTrackerIds: trackerIds,
      isAgent,
      collectionId,
    });
  };
};
