import React from 'react';

import {
  collectionPillFilterSelected,
  CollectionPillFilterSelectedProps,
} from '@uc/analytics-definitions';
import {Button} from '@uc/cx.react';

import {FilterId} from '@src/constants/filter';
import {useUpdateCurrentUrl, useCurrentUrlData} from '@src/hooks/url';
import {useBusinessTrackerIds} from '@src/hooks/useBusinessTrackerIds';
import {useIsAgent} from '@src/store';

import {getFilterOptions, FILTER_OPTION_LABELS} from './filterUtils';
import styles from './index.module.pcss';

export const Filters = (props: {filterCounts: Record<FilterId, number>; collectionId: string}) => {
  const isAgent = useIsAgent();
  const setParams = useUpdateCurrentUrl();
  const params = useCurrentUrlData();
  const filterOptions = getFilterOptions();
  const businessTrackerIds = useBusinessTrackerIds(props.collectionId);
  const onClickFilter = (id: React.Key) => {
    collectionPillFilterSelected({
      product: 'collections',
      product_path: 'filter pill',
      filter_name: FILTER_OPTION_LABELS[id as FilterId],
      collection_id: props.collectionId,
      is_agent: isAgent,
      business_tracker_ids: businessTrackerIds,
    } as CollectionPillFilterSelectedProps);
    setParams({filterId: id as FilterId});
  };
  const selectedId = params?.filterId;

  return (
    <div className={styles.filters} data-testid="filter-options">
      {filterOptions.map((filter) => (
        <Button
          className={styles.filterBtn}
          variant={selectedId === filter.id ? 'solid' : 'enclosed'}
          onClick={() => onClickFilter(filter.id)}
          key={filter.id}
          data-testid={filter.id}
        >
          {filter.label}&nbsp;
          <span>({props.filterCounts[filter.id]})</span>
        </Button>
      ))}
    </div>
  );
};
