import React, {useEffect, useState} from 'react';

import {Button, Modal, useModal, Checkbox} from '@uc/cx.react';

import {COLUMN_ID} from '@src/components/ListingTable/listingTableColumns';
import {ButtonGroup} from '@src/cx/ButtonGroup';
import {useDeviceWidth} from '@src/hooks';
import {useCurrentColumns, useDispatch, setColumns, useSelectedListings} from '@src/store';
import {HeaderConfig} from '@src/types';

import './CustomizeTable.pcss';

export const CustomizeTable = () => {
  const widths = useDeviceWidth();
  const {selectedListings} = useSelectedListings();
  const dispatch = useDispatch();
  const {modalProps, modalHeaderProps, isModalOpen, openModal, closeModal} = useModal();
  const {currentShownColumnIds, hideableColumns, currentColumns} = useCurrentColumns({
    isCollectionShared: true,
  });
  const [localCurrentIds, setLocalCurrentIds] = useState<Set<COLUMN_ID>>(currentShownColumnIds);

  // on open and close of modal reset locally shown IDs
  useEffect(() => {
    setLocalCurrentIds(currentShownColumnIds);
  }, [isModalOpen, setLocalCurrentIds, currentShownColumnIds]);

  const handleSave = () => {
    const ids = currentColumns.map((column) => {
      const hide = !localCurrentIds.has(column.id);

      return {
        hide,
        id: column.id,
      };
    });

    dispatch(setColumns(ids));
    closeModal();
  };
  const handleColumnChange = (column: HeaderConfig) => {
    return () => {
      const next = new Set(localCurrentIds);

      if (next.has(column.id)) next.delete(column.id);
      else next.add(column.id);
      setLocalCurrentIds(next);
    };
  };

  if (selectedListings.length > 0 && widths.size < 5) {
    return null;
  }

  return (
    <>
      <ButtonGroup>
        <Button
          size="sm"
          variant="enclosed"
          onClick={openModal}
          icon="Gear"
          className="customize-table-button"
          data-testid="openModalButton"
          aria-label={widths.size <= 4 ? 'Customize Table' : undefined}
        >
          Customize Table
        </Button>
      </ButtonGroup>
      <Modal {...modalProps} className="customize-table-columns-modal">
        <Modal.Header {...modalHeaderProps}>Customize Table Columns</Modal.Header>
        <Modal.Body>
          {hideableColumns.map((col) => {
            const checked = localCurrentIds.has(col.id);

            return (
              <Checkbox key={col.id} checked={checked} onChange={handleColumnChange(col)}>
                {col.label || col.id}
              </Checkbox>
            );
          })}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="enclosed" onClick={closeModal}>
            Cancel
          </Button>
          <Button onClick={handleSave} data-testid="Save">
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
