import React, {useMemo} from 'react';
// eslint-disable-next-line no-restricted-imports
import {QueryClient, QueryClientProvider} from 'react-query';

interface WrapperProps {
  children?: React.ReactNode;
}

const buildCache = () =>
  new QueryClient({
    defaultOptions: {
      queries: {
        retry: false,
        // 100ms stale time allows multiple components to share the same query during render cycle without triggering refetches
        // 10s stale time allows user to switch back and forth between tabs quickly without causing UI glitch
        staleTime: 10000,
        refetchOnWindowFocus: false,
        refetchOnReconnect: false,
        refetchOnMount: false,
      },
    },
  });

export const QueryCacheProvider = ({children}: WrapperProps) => {
  // memo a single new instance of the query cache for each
  // instance of QueryCacheProvider. This helps isolate caches between
  // test runs.
  const queryCache = useMemo(() => buildCache(), []);

  return <QueryClientProvider client={queryCache}>{children}</QueryClientProvider>;
};
