import {useQuery} from 'react-query';

import {getBoardsBadgesSummary} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';

const OPTIONS = {
  includeUnreadListingCommentsCount: true,
  includeUnreadDiscussionsCount: true,
};

export const useBoardsBadgesSummary = ({collectionIds}: {collectionIds?: string[]}) => {
  return useQuery([API_KEY.BOARDS_BADGES_SUMMARY, collectionIds], () => {
    return collectionIds?.length
      ? getBoardsBadgesSummary({boardIds: collectionIds, options: OPTIONS})
      : Promise.resolve(undefined);
  });
};
