import {useMutation} from 'react-query';

import {readListingUpdates, ReadListingUpdatesResponse, ApiError} from '@src/api';
import {BoardListingUpdateType} from '@src/types';

interface UseClearBadgesForListingsParams {
  collectionId: string;
  listingIdSHAs: string[];
}

export const useClearBadgesForListings = () => {
  const mutation = useMutation<
    ReadListingUpdatesResponse,
    ApiError,
    UseClearBadgesForListingsParams
  >(async ({collectionId, listingIdSHAs}: UseClearBadgesForListingsParams) => {
    const result = await readListingUpdates({
      collectionId,
      request: {
        listingIdSHAs,
        action: BoardListingUpdateType.ADDED_IN_REVIEW_STAGE,
      },
    });

    return result;
  });

  return mutation;
};
