import {FilterDrpopdownItemId, FILTER_DROPDOWN_ITEM_KEYS} from '@src/constants/filter';

import {FilterDropdownCountsProps} from '../types';

const FILTER_DROPDOWN_OPTION_LABELS: Record<FilterDrpopdownItemId, string> = {
  active: 'Active',
  comingSoon: 'Coming soon',
  sold: 'Sold',
  offMarket: 'Off market',
  pending: 'Pending',
  expired: 'Expired',
  openHouses: 'Open houses',
  likelyToSell: 'Likely to sell',
};

export function getListingStatusFilterItems(filterDropdownCounts: FilterDropdownCountsProps) {
  return FILTER_DROPDOWN_ITEM_KEYS.map((id: FilterDrpopdownItemId) => {
    return {
      id,
      label: FILTER_DROPDOWN_OPTION_LABELS[id],
      ['data-testid']: `filter_by_${id}_listings`,
      count: filterDropdownCounts[id],
    };
  });
}
