import {useMutation, useQueryClient} from 'react-query';

import {addComment} from '@src/api';
import {API_KEY} from '@src/constants/apiKey';
import {
  useCollectionListingPaginatedQueryKey,
  UseGetCollectionListingsPaginatedResponse,
} from '@src/hooks/useGetCollectionListingsPaginated';
import {GetCollectionListingByListingIdResponse} from '@src/types';

import {useIsInClientCommentModal} from './useIsInClientCommentModal';

export const useAddComment = () => {
  const client = useQueryClient();
  const isInClientCommentModal = useIsInClientCommentModal();
  const collectionListingsQueryKey = useCollectionListingPaginatedQueryKey();

  return useMutation(
    async ({
      collectionId,
      content,
      listingId,
    }: {
      collectionId: string;
      content: string;
      listingId?: string;
    }) => {
      const result = await addComment({
        collectionId: collectionId,
        content,
        listingId: listingId,
      });

      // Don't invalidate queries for client comment modal because of cache updates
      if (!isInClientCommentModal) {
        client.invalidateQueries();
      }

      return result;
    },
    {
      // Update the paginatedListings cache to include the new comment
      onSuccess: (result, {listingId, collectionId}) => {
        if (isInClientCommentModal) {
          const previousPageData = client.getQueryData(
            collectionListingsQueryKey
          ) as UseGetCollectionListingsPaginatedResponse;

          previousPageData?.currentPageListings?.find((listing) => {
            if (listing.listingId === listingId && result.comment) {
              if (listing.comments) {
                listing.comments?.push(result.comment);
              } else {
                listing.comments = [result.comment];
              }
            }
          });

          client.setQueryData(collectionListingsQueryKey, previousPageData);

          const collectionListingByIdQueryKey = [
            API_KEY.GET_COLLECTION_LISTING_BY_LISTING_ID,
            collectionId,
            listingId,
          ];
          const previousListingData = client.getQueryData(
            collectionListingByIdQueryKey
          ) as GetCollectionListingByListingIdResponse;
          const listing = previousListingData?.collectionListing;

          if (listing && listing.listingId === listingId && result.comment) {
            if (listing.comments) {
              listing.comments?.push(result.comment);
            } else {
              listing.comments = [result.comment];
            }
          }

          client.setQueryData(collectionListingByIdQueryKey, previousListingData);
        }
      },
    }
  );
};
