import {CollectionListing, ShowingStatus, formatTourStatus} from '@src/types';

export function showingActionable(collectionListing: CollectionListing) {
  return (
    typeof collectionListing.showing === 'undefined' ||
    collectionListing.showing.status === ShowingStatus.REQUESTED ||
    collectionListing.showing.status === ShowingStatus.REQUEST_CANCELLED
  );
}

export function showingText(collectionListing: CollectionListing, isAgent: boolean): string {
  if (!collectionListing || !collectionListing.showing) {
    if (isAgent) {
      return '';
    } else {
      return 'Request a Tour';
    }
  }

  const formattedTour = formatTourStatus(
    collectionListing.showing.status,
    collectionListing.showing.tourDate,
    isAgent
  );

  return isAgent ? formattedTour.tourStatusText.toUpperCase() : formattedTour.tourStatusText;
}
