import {useMemo, useEffect, useState} from 'react';
// eslint-disable-next-line no-restricted-imports
import {useQueryClient} from 'react-query';

import {collectionDeleted} from '@uc/analytics-definitions';

import {COLLECTION_MODE, EMPTY_PAGE_PATH} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {
  useGetCollection,
  useRefreshBoardQueryCache,
  useRefreshGetCollectionQueryCache,
} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useDispatch, setToast, useIsAgent} from '@src/store';
import {Collection} from '@src/types';

import {loadSettingsModal} from './loadSettingsModal';

const useOnClose = () => {
  const updateCurrentUrl = useUpdateCurrentUrl();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();

  return useMemo(
    () => () => {
      updateCurrentUrl({
        modalId: undefined,
      });
      refreshGetCollectionQueryCache();
    },
    [refreshGetCollectionQueryCache, updateCurrentUrl]
  );
};
const useOnError = () => {
  const onClose = useOnClose();
  const dispatch = useDispatch();

  return useMemo(
    () => () => {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'error',
          message: `We're having some trouble with this request. Please refresh the page to try again.`,
        })
      );
      onClose();
    },
    [dispatch, onClose]
  );
};
const useOnUpdate = () => {
  const updateCurrentUrl = useUpdateCurrentUrl();
  const {collectionMode} = useCurrentUrlData();
  const onClose = useOnClose();
  const dispatch = useDispatch();
  const refreshQueryCache = useRefreshBoardQueryCache();

  return useMemo(
    () => (collection: Collection) => {
      refreshQueryCache();
      dispatch(
        setToast({
          isOpen: true,
          variant: 'info',
          message: 'Collection updated',
        })
      );

      if (
        (collectionMode === COLLECTION_MODE.UNSHARED || collectionMode === COLLECTION_MODE.TRASH) &&
        !collection.isCurate
      ) {
        updateCurrentUrl({
          collectionMode: COLLECTION_MODE.SHARED,
          modalId: undefined,
        });
      } else if (collectionMode === COLLECTION_MODE.LIKELY_TO_SELL && collection.hideLikelyToSell) {
        updateCurrentUrl({
          collectionMode: COLLECTION_MODE.SHARED,
          modalId: undefined,
        });
      } else {
        onClose();
      }
    },
    [updateCurrentUrl, collectionMode, onClose, dispatch, refreshQueryCache]
  );
};
const useOnSuccess = () => {
  const setParams = useUpdateCurrentUrl();
  const isAgent = useIsAgent();
  const onUpdate = useOnUpdate();
  const onClose = useOnClose();
  const queryClient = useQueryClient();

  return useMemo(
    () =>
      async ({
        collection,
        collectionId,
        isUpdated,
        wasDeleted,
        wasLeft,
      }: {
        collection: Collection;
        collectionId: string;
        isUpdated: boolean;
        wasDeleted: boolean;
        wasLeft: boolean;
      }) => {
        if (wasDeleted && collectionId) {
          collectionDeleted({
            product: 'collections',
            product_path: 'collection setting modal',
            collection_ids: [collectionId],
          });
        }

        if (isAgent && (wasDeleted || wasLeft)) {
          window.location.href = '/app/workspaces/#!/collections';
          return;
        }

        if (!isAgent && (wasDeleted || wasLeft)) {
          await queryClient.resetQueries(API_KEY.GET_BOARDS);
          setParams({path: EMPTY_PAGE_PATH});
          return;
        }

        if (isUpdated) {
          onUpdate(collection);
        } else {
          onClose();
        }
      },
    [isAgent, queryClient, setParams, onUpdate, onClose]
  );
};
const useSettingsModal = (collectionId: string) => {
  const [isOpen, setIsOpen] = useState(false);
  const {data, isLoading} = useGetCollection({collectionId});
  const collection = data?.collection;
  const isUsersDefaultCollection = data?.collection?.isUsersDefaultCollection;
  const onClose = useOnClose();
  const onSuccess = useOnSuccess();
  const onError = useOnError();

  return useEffect(() => {
    if (isOpen || isLoading || !collection) {
      return;
    }

    loadSettingsModal({collection, isUsersDefaultCollection, onError, onClose, onSuccess});
    setIsOpen(true);
  }, [isOpen, isLoading, onClose, collection, isUsersDefaultCollection, onSuccess, onError]);
};

export const SettingsModal = ({collectionId}: {collectionId: string}) => {
  useSettingsModal(collectionId);
  return null;
};
