import React, {useCallback} from 'react';

import {CollectionModel, getCollectionModel} from '@uc/collection-model';

import {useBoard, useRefreshGetCollectionQueryCache} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {setToast, useCurrentUser, useDispatch} from '@src/store';
import {ProfileEntity, Invite} from '@src/types';
import {createModalComponent} from '@src/utils/createModalComponent';

const COLLECTION_INVITE_MODAL_URI = 'invite.react/3';

interface ConsumerInviteModalProps {
  collection?: CollectionModel;
  currentUserId: string;
  invitees: (ProfileEntity | Invite | undefined)[];
  maxInvitees: number;
}
const Modal = createModalComponent<ConsumerInviteModalProps, undefined, undefined>(
  COLLECTION_INVITE_MODAL_URI
);

export const ConsumerInviteModal = () => {
  const urlData = useCurrentUrlData();
  const collectionId = urlData.collectionId;
  const {data: boardsResponse} = useBoard({collectionId});
  const collection = boardsResponse?.board;
  const currentUserId = useCurrentUser()._id || '';
  const collectionModel = getCollectionModel(collection ?? {});
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();
  const onClose = useCallback(() => {
    setParams({modalId: undefined});
    refreshGetCollectionQueryCache();
  }, [refreshGetCollectionQueryCache, setParams]);
  const onSuccess = useCallback(() => {
    dispatch(
      setToast({
        isOpen: true,
        variant: 'info',
        message: 'Collaborators updated.',
      })
    );
    onClose();
  }, [dispatch, onClose]);
  const onError = useCallback(
    (error) => {
      if (error && error !== 'user clicked "Close"') {
        dispatch(
          setToast({
            isOpen: true,
            variant: 'error',
            message: `We're having some trouble with this request. Please refresh the page to try again.`,
          })
        );
      }

      onClose();
    },
    [dispatch, onClose]
  );

  return (
    <>
      {collection?.id && collectionModel && (
        <Modal
          params={{
            collection: collectionModel as CollectionModel,
            currentUserId: currentUserId,
            invitees: collectionModel.invitees,
            maxInvitees: 1000,
          }}
          onResolve={onSuccess}
          onError={onError}
        />
      )}
    </>
  );
};
