import React from 'react';
import {NavLink} from 'react-router-dom';

import {
  collectionCreated as logCollectionCreated,
  collectionCreateInitiated as logCollectionCreateInitiated,
} from '@uc/analytics-definitions';
import {Poptip, Button} from '@uc/cx.react';
import loadModal from '@uc/modal-loader';

import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useCreateBoard} from '@src/hooks/useCreateBoard';
import {useEllipsisPoptip} from '@src/hooks/useEllipsisPoptip';
import {useDispatch, setToast} from '@src/store';
import {Board, BoardBadgesSummary} from '@src/types';
import {getResolvedAssetVersion} from '@src/utils/getResolvedAssetVersion';

import styles from './CollectionMenu.module.pcss';

export interface CollectionMenuData {
  boards?: Board[];
  badgingSummaries?: Record<string, BoardBadgesSummary>;
}

export const CollectionMenu = ({data}: {data?: CollectionMenuData}) => {
  const dispatch = useDispatch();
  const createBoardAsync = useCreateBoard().mutateAsync;
  const setParams = useUpdateCurrentUrl();
  const {poptipProps, getPoptipAnchorProps} = useEllipsisPoptip();
  const handleCreateNew = () => {
    logCollectionCreateInitiated({
      product: 'collections',
      product_path: 'Nav Bar',
    });
    const modalVersion = getResolvedAssetVersion('modal--create-collection.react', 'latest');

    loadModal<{isPublic: boolean}, {boardName: string}>(`create-collection.react/${modalVersion}`, {
      isPublic: false,
    })
      .then(({boardName}) => {
        return createBoardAsync({boardName});
      })
      .then(({board = {}}) => {
        return board;
      })
      .then(async (collection) => {
        logCollectionCreated({
          collection_id: collection.id || '',
          product: 'collections',
          product_path: 'Nav Bar',
        });
        setParams({
          collectionId: collection.id,
        });
      })
      .catch((err) => {
        if (err) {
          dispatch(
            setToast({
              isOpen: true,
              variant: 'error',
              message: 'Collection creation failed. Please try again.',
            })
          );
        }
      });
  };

  return (
    <React.Fragment>
      <ul className="cx-sideMenu-section">
        <li className="cx-sideMenu-sectionTitle">Collections</li>
        {data?.boards?.map(({id, name}) => {
          if (!id) {
            return null;
          }

          const unreadCommentsCount =
            (data.badgingSummaries?.[id]?.unreadListingCommentsCount ?? 0) +
            (data.badgingSummaries?.[id]?.unreadDiscussionsCount ?? 0);

          return (
            <li key={id}>
              <NavLink
                className={`${styles.menuItem} cx-sideMenu-item`}
                to={`/${id}`}
                activeClassName="is-selected"
              >
                <div>
                  <div className="u-ellipsis" {...getPoptipAnchorProps(name, 'top')}>
                    {name}
                  </div>
                  {unreadCommentsCount > 0 && (
                    <div>
                      <span className={`cx-token ${styles.unreadCommentsCount}`}>
                        {unreadCommentsCount}
                      </span>
                      <span className={`textIntent-micro ${styles.unreadCommentsText}`}>
                        {unreadCommentsCount === 1 ? 'New comment' : 'New comments'}
                      </span>
                    </div>
                  )}
                </div>
              </NavLink>
            </li>
          );
        })}
        <Button variant="naked" className="cx-sideMenu-sectionAction" onClick={handleCreateNew}>
          Create New
        </Button>

        <Poptip {...poptipProps} />
      </ul>

      <div className="cx-sideMenu-divider"></div>
    </React.Fragment>
  );
};
