import {useMutation, useQueryClient} from 'react-query';

import {createBoard} from '@src/api';
import {TEMP_COLLECTION_ID} from '@src/constants';
import {API_KEY} from '@src/constants/apiKey';
import {Board, CreateBoardRequest, FindBoardsResponse} from '@src/types';

const excludeMosaic = true,
  excludeComments = true;

export const queryKey = [API_KEY.GET_BOARDS, excludeMosaic, excludeComments, undefined, undefined];

export const useCreateBoard = () => {
  const queryClient = useQueryClient();

  return useMutation((request: CreateBoardRequest) => createBoard(request), {
    onMutate: async ({boardName}) => {
      await queryClient.cancelQueries(queryKey);

      const previousBoards = queryClient.getQueryData<FindBoardsResponse>(queryKey);

      if (previousBoards) {
        queryClient.setQueryData<FindBoardsResponse>(queryKey, {
          ...previousBoards,
          // eslint-disable-next-line @typescript-eslint/no-extra-parens
          boards: [{id: TEMP_COLLECTION_ID, name: boardName}, ...(previousBoards.boards || [])],
        });
      }

      return previousBoards;
    },
    // eslint-disable-next-line max-params
    onError: (err, variables, previousBoards) => {
      if (previousBoards) {
        queryClient.setQueryData(queryKey, previousBoards);
      }
    },
    onSuccess: (data) => {
      const previousBoardsQuery = queryClient.getQueryData<FindBoardsResponse>(queryKey);
      const previousRealBoards = (previousBoardsQuery?.boards || []).filter(
        (b) => b.id !== TEMP_COLLECTION_ID
      );
      const newBoards =
        data?.board?.id && data?.board?.name
          ? [
              {
                id: data.board.id,
                name: data.board.name,
              } as Board,
            ].concat(previousRealBoards)
          : previousRealBoards;

      queryClient.setQueryData(queryKey, {
        ...previousBoardsQuery,
        boards: newBoards,
      });
    },
  });
};
