/* eslint-disable @typescript-eslint/no-type-alias */
import {mq} from '@uc/cx-design-tokens';
import {useMediaQuery} from '@uc/cx.react';

const {mobile, mobileLandscape, tablet, laptop, desktop, HDDesktop} = mq;

export type DeviceSize = 0 | 1 | 2 | 3 | 4 | 5 | 6;

export const BOUNDS = {
  xs: [undefined, mobile - 1],
  sm: [mobile, mobileLandscape - 1],
  md: [mobileLandscape, tablet - 1],
  lg: [tablet, laptop - 1],
  xl: [laptop, desktop - 1],
  xxl: [desktop, HDDesktop - 1],
  xxxl: [HDDesktop, undefined],
} as const;

type SizeKey = keyof typeof BOUNDS;

function toQuery([min, max]: Readonly<[min?: number, max?: number]>) {
  if (min && max) {
    return `(min-width: ${min}px) and (max-width: ${max}px)`;
  }

  if (min) {
    return `(min-width: ${min}px)`;
  }

  if (max) {
    return `(max-width: ${max}px)`;
  }

  throw new Error('Bounds must include at least a min or max');
}

export const QUERIES: Record<SizeKey, string> = {
  xs: toQuery(BOUNDS.xs),
  sm: toQuery(BOUNDS.sm),
  md: toQuery(BOUNDS.md),
  lg: toQuery(BOUNDS.lg),
  xl: toQuery(BOUNDS.xl),
  xxl: toQuery(BOUNDS.xxl),
  xxxl: toQuery(BOUNDS.xxxl),
};

export const SIZES = ['xs', 'sm', 'md', 'lg', 'xl', 'xxl', 'xxxl'] as const;

export function useDeviceWidth(): {
  isMobile: boolean;
  isTablet: boolean;
  isDesktop: boolean;
  xs: boolean;
  sm: boolean;
  md: boolean;
  lg: boolean;
  xl: boolean;
  xxl: boolean;
  xxxl: boolean;
  size: DeviceSize;
} {
  const xs = useMediaQuery(QUERIES.xs);
  const sm = useMediaQuery(QUERIES.sm) && !xs;
  const md = useMediaQuery(QUERIES.md) && !sm;
  const lg = useMediaQuery(QUERIES.lg) && !md;
  const xl = useMediaQuery(QUERIES.xl) && !lg;
  const xxl = useMediaQuery(QUERIES.xxl) && !xl;
  const xxxl = useMediaQuery(QUERIES.xxxl) && !xxl;

  return {
    isMobile: xs || sm,
    isTablet: md,
    isDesktop: lg || xl || xxl || xxxl,
    xs,
    sm,
    md,
    lg,
    xl,
    xxl,
    xxxl,
    size: xs ? 0 : sm ? 1 : md ? 2 : lg ? 3 : xl ? 4 : xxl ? 5 : 6,
  };
}

export type DeviceWidth = ReturnType<typeof useDeviceWidth>;
