import React, {useRef} from 'react';

import {CollectionListing} from '@src/types';
import {showingText} from '@src/utils/formatShowingStatus';

import styles from './RequestShowingButton.module.pcss';

export const RequestShowingButtonAgent = ({
  collectionListing,
}: {
  collectionListing: CollectionListing;
}) => {
  const anchorRef = useRef<HTMLButtonElement>(null);
  const showText = showingText(collectionListing, true);

  if (showText.length === 0) {
    return null;
  }

  return (
    <span ref={anchorRef} className={styles.button} data-testid="requestShowingSpan">
      {showText}
    </span>
  );
};
