import {BBox} from '@turf/helpers';
import {ViewportSettings} from '@uc-maps/api.react';
import {
  ContainerSize,
  getDiscreteZoomLevelForContainer,
  listingRelationToClusterFeature,
} from '@uc-maps/geospatial';
import {
  ClustersLayer,
  MapContainer,
  GoogleMapProvider,
  useMap,
  MapTypeId,
  MapLayout,
  BasemapToggle,
  ZoomButtons,
} from '@uc-maps/maps.react';
import React, {useState, useEffect} from 'react';

import {Button} from '@uc/cx.react';
import {SearchQuery} from '@uc/search-query';

import {DefaultMap} from '@src/components/Map/DefaultMap';
import {
  computeBbox,
  calculateMapCenter,
  calculateMapProviderState,
} from '@src/components/Map/MapUtils';
import {useShowMap, useDeviceWidth} from '@src/hooks';
import {useMapData} from '@src/hooks/useMapData';
import {
  useDispatch,
  useHoveredListing,
  useSelectedListings,
  selectListings,
  toggleSelectedListing,
} from '@src/store';
import {
  ProcessedListing,
  ProcessedListingRelation,
  MapClusterType,
  ClusterFeature,
  MapSearchResultsType,
} from '@src/types';

import {toProcessedListingRelation, toCollectionListing} from './MapUtils';
import './Map.pcss';

const initialZoomLevel = 8;

interface Props {
  searchResults: MapSearchResultsType;
  isAgent: boolean;
  onListingClick: ({listing}: {listing: ProcessedListing}) => void;
  searchQuery?: SearchQuery;
}

export const NewMap = ({searchResults = [], isAgent, onListingClick, searchQuery}: Props) => {
  const dispatch = useDispatch();
  const {isMobile} = useDeviceWidth();
  const [showMap, setShowMap] = useShowMap();
  const [computedBbox, setComputedBbox] = useState(computeBbox(searchResults));
  const {mapState, mapApi, getMapProps} = useMap({
    initialState: {
      basemap: MapTypeId.ROADMAP,
      viewport: {
        center: calculateMapCenter(computedBbox),
        zoomLevel: initialZoomLevel,
      },
      clusters: {
        active: null,
        search: [],
      },
    },
    onMapStateChange({prevState, nextState}) {
      if (prevState.viewport.bbox !== nextState.viewport.bbox) {
        setComputedBbox(nextState.viewport.bbox);
      }
    },
  });
  const mapSize = mapState.viewport.size;
  const hoveredListing = useHoveredListing();
  const {selectedListings} = useSelectedListings();
  const listingClusters = useMapData({
    searchResults,
    mapOptions: {
      bbox: computedBbox,
      containerSize: mapSize,
    },
    searchQuery,
  });
  const [selectedListingRelations, setSelectedListingRelations] = useState<
    ProcessedListingRelation[]
  >([]);
  const stringifiedListingClusters = JSON.stringify(listingClusters);
  const {setActiveCluster, setHoverCluster, setSearchClusters, setSelectedListingsForClusters} =
    mapApi;
  const onMouseOver = (cluster: ClusterFeature): void => setHoverCluster(cluster);
  const onMouseOut = (): void => setHoverCluster(null);
  const setMapPosition = (computedBbox: BBox, size: ContainerSize = mapSize) => {
    setComputedBbox(computedBbox);
    // when bbox is set, MapProvider calculates the zoom level and center and uses that
    // however, since the bbox does not always match the aspect ratio of the container
    // the zoom level is sometimes in-between (e.g., 11.6667)
    // the map does not like this, so it rounds to the nearest zoom level (e.g., 12)
    // which results in the edges of the bbox getting cut off
    // so we calculate the center and zoomLevel ourselves instead
    const zoomLevel = getDiscreteZoomLevelForContainer(computedBbox, size);
    const center = calculateMapCenter(computedBbox);

    mapApi.reposition({center, zoomLevel});
  };
  const loadMap = (size: ContainerSize) => {
    const initialBBox = computeBbox(searchResults);

    setMapPosition(initialBBox, size);
  };
  const onClusterClick = (cluster: ClusterFeature, targetViewPort: ViewportSettings) => {
    if (cluster.properties.type === MapClusterType.AGGREGATE_LISTING_BUBBLE) {
      const {center, zoomLevel} = calculateMapProviderState(targetViewPort, mapSize);

      mapApi.reposition({center, zoomLevel});
    }

    setActiveCluster(cluster);
  };
  const fetchPopoverListings = async (activeCluster: ClusterFeature) => {
    return activeCluster.properties.listings ?? [];
  };
  const popoverContentProps = {
    allowSelectListings: true,
    isAgent,
    onClose: () => {
      setActiveCluster(null);
    },
    listingCardProps: {
      onListingClick,
      showing: {
        wantToSee: undefined,
        status: undefined,
        tourDate: undefined,
      },
    },
  };

  useEffect(() => {
    setSearchClusters(JSON.parse(stringifiedListingClusters));
  }, [setSearchClusters, stringifiedListingClusters]);

  useEffect(() => {
    if (hoveredListing) {
      const hoveredCluster = listingRelationToClusterFeature({listing: hoveredListing.listingData});

      hoveredCluster && setHoverCluster(hoveredCluster);
      return;
    }

    setHoverCluster(null);
  }, [hoveredListing, setHoverCluster]);

  // When the selectedListings in app updates, sync up the map state
  useEffect(() => {
    const selectedListingRelations = toProcessedListingRelation(selectedListings);

    setSelectedListingRelations(selectedListingRelations);
    setSelectedListingsForClusters(selectedListingRelations);
  }, [selectedListings, setSelectedListingsForClusters]);

  // When the map state updates, sync up the selectedListings in app
  useEffect(() => {
    if (
      mapState.clusters.selectedListings &&
      selectedListingRelations !== mapState.clusters.selectedListings
    ) {
      const selectedListingRelationsInMap = [...mapState.clusters.selectedListings];
      const selectedCollectionListings = toCollectionListing({
        listingRelations: selectedListingRelationsInMap,
        allClusters: mapState.clusters.all,
        addPosition: true,
      });
      const preSelectedCollectionListings = toCollectionListing({
        listingRelations: selectedListingRelations,
      });
      const listingsDeselected = preSelectedCollectionListings.filter(
        (listing) =>
          selectedCollectionListings.findIndex(({listingId}) => listingId === listing.listingId) ===
          -1
      );

      // deselect the unchecked listings before set all selected listings
      listingsDeselected.forEach((listing) => dispatch(toggleSelectedListing(listing)));
      dispatch(selectListings(selectedCollectionListings));
    }
  }, [
    dispatch,
    mapState.clusters.selectedListings,
    mapState.clusters.all,
    selectedListingRelations,
  ]);

  return (
    <div data-testid="new-map-container" className="map-container">
      <MapContainer {...getMapProps()}>
        <MapLayout.Container>
          <MapLayout.BottomRight>
            <BasemapToggle variant="round" />
            <ZoomButtons />
          </MapLayout.BottomRight>
          <MapLayout.TopLeft>
            {isMobile && (
              <Button
                onClick={() => setShowMap(!showMap)}
                data-testid="new-map-back"
                icon="ArrowLeft"
                variant="enclosed"
                size="xs"
              />
            )}
          </MapLayout.TopLeft>
        </MapLayout.Container>
        {searchResults && searchResults.length > 0 ? (
          <GoogleMapProvider onInitialSize={loadMap}>
            <ClustersLayer
              onClick={onClusterClick}
              onMouseOver={onMouseOver}
              onMouseOut={onMouseOut}
              showHoverTooltip
              componentConfig={{
                ['saved-icon']: false,
              }}
              fetchPopoverListings={fetchPopoverListings}
              popoverContentProps={popoverContentProps}
            />
          </GoogleMapProvider>
        ) : (
          <DefaultMap />
        )}
      </MapContainer>
    </div>
  );
};
