import {useEffect, useRef} from 'react';

import loadModal from '@uc/modal-loader';

import {disableBodyScroll, enableBodyScroll} from '@src/utils/bodyScroll';

export const createModalComponent = <Params, ResolveProps, ErrorProps>(url: string) => {
  const Modal = (props: {
    params: Params;
    onResolve: (p: ResolveProps) => void;
    onError: (e: ErrorProps) => void;
    onBeforeOpen?: () => void;
    memoKey?: React.Key;
  }) => {
    const {onBeforeOpen, onResolve, onError, params} = props;
    const isMounted = useRef(true);

    useEffect(
      () => {
        onBeforeOpen?.();
        disableBodyScroll();
        isMounted.current = true;
        const modal = loadModal<Params, ResolveProps>(url, params);

        modal
          .then((resolveData) => {
            if (isMounted.current) onResolve(resolveData);
          })
          .catch((errorProps) => {
            if (isMounted.current) onError(errorProps);
          })
          .finally(() => {
            enableBodyScroll();
          });

        return () => {
          // close the modal without triggering onResolve or onError callbacks
          isMounted.current = false;
          modal.close();
          enableBodyScroll();
        };
      },
      // eslint-disable-next-line react-hooks/exhaustive-deps
      [props.memoKey]
    );
    return null;
  };

  return Modal;
};
