import React from 'react';

import {Button, Breakpoint, Flyout, Icon, OptionsMenu, useFlyoutOnClick} from '@uc/cx.react';
import formatListingUrl from '@uc/format-listing-url';

import {ProcessedListing} from '@src/types';

interface ModalHeaderProps {
  processedListing: ProcessedListing;
  close: () => void;
  currentListingIndexAcrossAllPages: number;
  currentPageListingIndex: number;
  totalListings: number;
  handleNavigator: (updatedIndex: number) => void;
}

export const ModalHeader: React.FC<ModalHeaderProps> = ({
  processedListing,
  close,
  currentListingIndexAcrossAllPages,
  currentPageListingIndex,
  totalListings,
  handleNavigator,
}: ModalHeaderProps): JSX.Element => {
  const {getFlyoutAnchorProps, flyoutProps} = useFlyoutOnClick();
  const onViewFullListingDetails = () => {
    const queryParams = {
      origin_type: 'ReviewListingsModal',
      origin: 'Collection',
    };

    if (processedListing) {
      const url = formatListingUrl(processedListing, queryParams);

      window.open(url || undefined, '_blank');
    }
  };
  const currentPageofModal = currentListingIndexAcrossAllPages + 1;
  const totalPagesofModal =
    totalListings >= currentPageofModal ? totalListings : currentPageofModal;

  return (
    <div className="uc-workspaceBoardReviewListingsModal-header reviewListingModal">
      <div className="uc-workspaceBoardReviewListingModal-headerPagination no-trashBtn">
        <Button
          variant="naked"
          icon="ChevronLeft"
          aria-label="Go To Previous Listing"
          data-testid="navigatorPrevious"
          disabled={currentListingIndexAcrossAllPages === 0}
          onClick={() => handleNavigator(currentPageListingIndex - 1)}
        />
        <span
          className="uc-workspaceBoardReviewListingModal-index"
          data-testid="header-currentPageofModal"
        >
          {currentPageofModal}
        </span>{' '}
        of{' '}
        <span
          className="uc-workspaceBoardReviewListingModal-index"
          data-testid="header-totalPagesofModal"
        >
          {totalPagesofModal}
        </span>
        <Button
          variant="naked"
          icon="ChevronRight"
          aria-label="Go To Next Listing"
          data-testid="navigatorNext"
          disabled={currentPageofModal === totalListings}
          onClick={() => handleNavigator(currentPageListingIndex + 1)}
        />
      </div>
      <Breakpoint is="mobile">
        <div className="uc-workspaceBoardReviewListingsModal-headerFlyout">
          <Button
            type="button"
            variant="naked--lowEmph"
            size="sm"
            aria-label="More Options"
            data-testid="moreOptonsBtn"
            {...getFlyoutAnchorProps('bottomRight')}
          >
            <svg className="cx-btn-icon">
              <use xlinkHref="#cx-icon-verticalEllipsis_16x16" />
            </svg>
          </Button>
          <Flyout {...flyoutProps}>
            <OptionsMenu size="lg">
              <OptionsMenu.Item indented onClick={onViewFullListingDetails}>
                <Icon.DoubleBoxedArrowUpRight className="cx-optionsMenu-accessory" />
                View full listing details
              </OptionsMenu.Item>
            </OptionsMenu>
          </Flyout>
        </div>
      </Breakpoint>
      <Breakpoint is={{mobile: false}}>
        <Button
          type="button"
          variant="enclosed"
          size="sm"
          onClick={onViewFullListingDetails}
          className="uc-workspaceBoardReviewListingsModal-viewListingBtn cx-enclosedBtn cx-enclosedBtn--sm"
        >
          <Icon.DoubleBoxedArrowUpRight className="cx-optionsMenu-accessory" />
          View full listing details
        </Button>
      </Breakpoint>
      <Button
        type="button"
        variant="naked--lowEmph"
        aria-label="Close Review Listings Modal"
        onClick={close}
        className="uc-workspaceBoardReviewListingsModal-closeBtn cx-nakedBtn cx-nakedBtn--lowEmph"
      >
        <Icon.X className="uc-listingsModal-btnIcon" />
      </Button>
    </div>
  );
};
