import React from 'react';

import {Config as AgentShareModalProps, ModalResult} from '@uc/modal--collection-share.react';

import {useRefreshGetCollectionQueryCache} from '@src/hooks';
import {useCurrentUrlData, useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {setToast, useCurrentUser, useDispatch, setRemindee, useRemindee} from '@src/store';
import {createModalComponent} from '@src/utils/createModalComponent';
import {isClientxInAppEducationEnabled} from '@src/utils/optyUtils';

const SHARE_MODAL_URI = 'collection-share.react/7.1';
const Modal = createModalComponent<AgentShareModalProps, ModalResult, ModalResult | Error>(
  SHARE_MODAL_URI
);

export const AgentShareModal = () => {
  const remindee = useRemindee();
  const {collectionId} = useCurrentUrlData();
  const {data: collectionResponse = {}} = useGetCollection({collectionId});
  const {collection = {}} = collectionResponse;
  const showInAppEducation = isClientxInAppEducationEnabled();
  const userId = useCurrentUser()._id;
  const dispatch = useDispatch();
  const setParams = useUpdateCurrentUrl();
  const refreshGetCollectionQueryCache = useRefreshGetCollectionQueryCache();
  const onClose = () => {
    if (remindee) {
      dispatch(setRemindee());
    }

    setParams({modalId: undefined});
    refreshGetCollectionQueryCache();
  };
  const onSuccess = (result?: ModalResult) => {
    if (result?.resultMessage) {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'info',
          message: result.resultMessage,
        })
      );
    }

    onClose();
  };
  const onError = (error?: Error | ModalResult) => {
    if ((error as Error)?.message?.includes('is API V3: true')) {
      dispatch(
        setToast({
          isOpen: true,
          variant: 'error',
          message:
            "We're having some trouble with this request. Please refresh the page to try again.",
        })
      );
    }

    onClose();
  };

  return (
    <>
      {collection && collection.collectionId && userId && (
        <Modal
          params={{
            userId,
            collection,
            showInAppEducation,
            remindee,
          }}
          onResolve={onSuccess}
          onError={onError}
        />
      )}
    </>
  );
};
