import React, {useEffect, useRef, useState, KeyboardEvent} from 'react';

import {Avatar, Button, Icon, Textarea, TextareaProps} from '@uc/cx.react';
import {UcListingCard} from '@uc/listing-card-components.react';

import {getTruncatedAvatars} from '@src/components/Header/Avatars/getTruncatedAvatars';
import {useDeviceWidth, useGetCollectionUsers} from '@src/hooks';
import {useCurrentUrlData} from '@src/hooks/url';
import {
  Comment as CommentType,
  CollectionListingReviewStage,
  ProcessedListingCard,
} from '@src/types';
import {ProfileEntity, IdentifiedUser} from '@src/types';
import {getCollectionOwnerAndCollaborators} from '@src/utils';
import {
  getDisplayedDateString,
  getUniqueDateString,
  areTimesWithinXMinutes,
  getDisplayedTime,
} from '@src/utils/dates';
import './CommentSection.pcss';
import {isClientHomeRootEnabled} from '@src/utils/optyUtils';

import {CommentsFooter} from './CommentsFooter';
import {formatComment} from './utils';

export interface CommentSectionProps {
  comments: CommentType[];
  currentUser: IdentifiedUser;
  onCommentDelete: (comment: CommentType) => Promise<string | void>;
  onPostComment: (content: string) => void;
  reviewStage: CollectionListingReviewStage | null;
  onShareListing: () => void;
  onDone: () => void;
  processedListingCard?: ProcessedListingCard;
  onShowListingDetails?: () => void;
}

interface CommentsByDate {
  uniqueDateString: string;
  displayedDateString: string;
  comments: CommentsByAuthor[];
}

interface Comment {
  content: string;
  id: string;
  timestamp: number;
  authorId: string;
}

interface CommentsByDisplayTime {
  displayTime: number;
  comments: Comment[];
}

interface CommentsByAuthor {
  author: ProfileEntity;
  comments: CommentsByDisplayTime[];
}

function getLast<T>(arr: T[]): T {
  return arr[arr.length - 1];
}

export const structureComments = (comments: CommentType[]): CommentsByDate[] => {
  const structuredComments: CommentsByDate[] = [];
  const getLastCommentsByDate = () => {
    return getLast(structuredComments);
  };
  const getLastCommentsByAuthor = () => {
    const commentsInLastDate = getLastCommentsByDate()?.comments;

    if (commentsInLastDate && commentsInLastDate.length > 0) {
      return getLast(commentsInLastDate);
    }

    return undefined;
  };
  const getMostRecentCommentGroup = () => {
    const commentsByLastAuthor = getLastCommentsByAuthor()?.comments;

    if (commentsByLastAuthor && commentsByLastAuthor.length > 0) {
      return getLast(commentsByLastAuthor);
    }

    return undefined;
  };
  const getMostRecentComment = () => {
    const mostRecentCommentGroup = getMostRecentCommentGroup();

    if (mostRecentCommentGroup?.comments && mostRecentCommentGroup.comments.length > 0) {
      return getLast(mostRecentCommentGroup.comments);
    }

    return undefined;
  };

  for (const comment of comments) {
    const lastCommentsByDate = getLastCommentsByDate();
    const todaysDate = getUniqueDateString(comment.createdAt as number);

    if (!lastCommentsByDate || todaysDate !== lastCommentsByDate.uniqueDateString) {
      structuredComments.push({
        comments: [],
        uniqueDateString: todaysDate,
        displayedDateString: getDisplayedDateString(comment.createdAt as number),
      });
    }

    let lastCommentsByAuthor = getLastCommentsByAuthor();

    if (!lastCommentsByAuthor || comment.author?._id !== lastCommentsByAuthor?.author?._id) {
      const lastCommentsByDate = getLastCommentsByDate();

      lastCommentsByDate?.comments.push({
        comments: [],
        author: comment.author as ProfileEntity,
      });
    }

    lastCommentsByAuthor = getLastCommentsByAuthor();

    const mostRecentComment = getMostRecentComment();

    if (
      !mostRecentComment ||
      !areTimesWithinXMinutes({
        times: [mostRecentComment?.timestamp, comment?.createdAt as number],
        minutes: 5,
      })
    ) {
      lastCommentsByAuthor?.comments.push({
        comments: [],
        displayTime: comment?.createdAt as number,
      });
    }

    const mostRecentCommentGroup = getMostRecentCommentGroup();

    mostRecentCommentGroup?.comments.push({
      content: comment?.content as string,
      timestamp: comment?.createdAt as number,
      id: comment?.commentId as string,
      authorId: comment?.author?._id as string,
    });
  }

  return structuredComments;
};

const EmptyScreen = () => {
  const {collectionId} = useCurrentUrlData();
  const {data: collectionUsers = {}} = useGetCollectionUsers({
    collectionId,
  });
  const collectionOwnerAndCollaborators = getCollectionOwnerAndCollaborators(collectionUsers);
  const collaboratorsAvatars = collectionOwnerAndCollaborators
    .filter((collaborator) => collaborator.userId && collaborator.person)
    .map((collaborator) => {
      return (
        <Avatar
          key={collaborator.userId}
          size="lg"
          image={collaborator.person?.imagePath}
          firstName={collaborator.person?.firstName}
          lastName={collaborator.person?.lastName}
          className="collection-header-avatar"
          data-testid="avatar-contacts"
        />
      );
    });
  const truncatedAvatars = getTruncatedAvatars({collaboratorsAvatars});

  return (
    <div className="modal-comments-empty">
      <div className="modal-comments-empty-icon-wrapper">
        {collaboratorsAvatars.length >= 2 && !isClientHomeRootEnabled() ? (
          <div className="modal-comments-empty-avatar-wrapper">{truncatedAvatars}</div>
        ) : (
          <Icon.ChatBubbles className="modal-comments-empty-icon" />
        )}
      </div>
      <h3 className="modal-comments-empty-header">No comments yet</h3>
      <h4 className="modal-comments-empty-subheader">Share your thoughts about this home!</h4>
    </div>
  );
};

export const CommentSection = (props: CommentSectionProps) => {
  const {
    comments,
    currentUser,
    onCommentDelete,
    onPostComment,
    onDone,
    reviewStage,
    onShareListing,
  } = props;
  const {isMobile, isTablet} = useDeviceWidth();
  const structuredComments = comments ? structureComments(comments) : [];
  const scrollableRef = useRef() as React.MutableRefObject<HTMLInputElement>;
  const [newMessageContent, setNewMessageContent] = useState('');
  const scrollToBottom = React.useCallback(() => {
    // scroll the thread down to the bottom...
    const scrollable = scrollableRef.current;

    if (scrollable && scrollable.scrollTo && comments?.length) {
      scrollable.scrollTo(0, scrollable.scrollHeight);
    }
  }, [scrollableRef, comments]);

  useEffect(() => {
    scrollToBottom();
  }, [comments, newMessageContent, scrollToBottom]);

  const isCommentsEmpty = structuredComments.length === 0;
  const onSubmit = () => {
    onPostComment && onPostComment(newMessageContent);
    setNewMessageContent('');
  };
  const handleEnter = (event: KeyboardEvent) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      newMessageContent.trim().length > 0 && onSubmit();
    }
  };

  return (
    <div className="modal-comment-section-wrapper">
      <div
        className={`modal-comment-section-header ${
          isMobile || isTablet ? 'modal-comment-section-header-mobile' : ''
        }`}
      >
        <div className="modal-comment-section-header-row-1">
          <span role="heading" aria-level={2}>
            Comments
          </span>
          {isMobile && (
            <div className="modal-comment-header-actions">
              <Button variant="naked" onClick={onDone} aria-label="Select details tab">
                Done
              </Button>
            </div>
          )}
        </div>
        {isMobile && props.processedListingCard && (
          <div className="modal-comment-section-header-row-2">
            <UcListingCard
              className="modal-comment-section-listing-card"
              data={props.processedListingCard}
              options={{userType: 'consumer', cardType: 'pill'}}
              onNavigate={(e) => {
                e.detail.event.preventDefault(); // do not open navigationPageLink
                props.onShowListingDetails?.();
              }}
            ></UcListingCard>
          </div>
        )}
      </div>
      <div className="modal-comment-thread-wrapper" ref={scrollableRef}>
        {isCommentsEmpty ? (
          <EmptyScreen />
        ) : (
          <CommentThread
            comments={structuredComments}
            onCommentDelete={onCommentDelete}
            currentUser={currentUser}
          />
        )}
      </div>
      {reviewStage === CollectionListingReviewStage.TRASH ? (
        <CommentsFooter onShareListing={onShareListing} />
      ) : (
        <div className="modal-comment-section-input">
          <ElasticTextarea
            onKeyDown={handleEnter}
            value={newMessageContent}
            label="Add a comment"
            onChange={(event) => {
              setNewMessageContent(event.target.value);
            }}
          />
          <div className="modal-comment-section-send-wrapper">
            <Button
              variant="naked"
              disabled={!newMessageContent.trim().length}
              className="modal-comment-send-btn"
              onClick={onSubmit}
            >
              Send
            </Button>
          </div>
        </div>
      )}
    </div>
  );
};

const CommentDateGroup: React.FC<{date: string}> = ({date, children}) => (
  <div
    className="modal-comment-thread-date-group"
    role="group"
    aria-label={`Comments from ${date}`}
  >
    <div className="modal-comment-thread-date-header" aria-hidden>
      {date}
    </div>
    {children}
  </div>
);
const CommentAuthorGroup: React.FC<{
  author: ProfileEntity;
  isCurrentUser: boolean;
}> = ({author, isCurrentUser, children}) => {
  if (isCurrentUser) {
    return (
      <div role="group" aria-label="Comments by me" className="modal-comment-thread-author-group">
        {children}
      </div>
    );
  } else {
    return (
      <div
        role="group"
        aria-label={`Comments by ${author.displayName}`}
        className="modal-comment-thread-author-group  modal-comment-thread-collaborator-author"
      >
        <Avatar
          className="modal-comment-thread-author-avatar"
          image={author.imagePath}
          firstName={author.firstName}
          lastName={author.lastName}
          size="lg"
        />
        <div>
          <div className="modal-comment-thread-author-header" aria-hidden>
            {author.displayName}
          </div>
          {children}
        </div>
      </div>
    );
  }
};
const CommentTimeGroup: React.FC<{
  author: ProfileEntity;
  comments: CommentsByDisplayTime;
  isCurrentUser: boolean;
  onCommentDelete: (comment: CommentType) => Promise<string | void>;
}> = ({isCurrentUser, comments, onCommentDelete}) => {
  if (isCurrentUser) {
    return (
      <div
        className="modal-comment-thread-time-group-wrapper comment-thread-by-me"
        role="group"
        aria-label={`Comments at ${comments.displayTime}`}
      >
        <div
          className="modal-comment-thread-time-group"
          role="group"
          aria-label={`Comments at ${comments.displayTime}`}
        >
          {comments.comments.map((comment) => (
            <div className="modal-comment-row" key={comment.id}>
              <Button
                aria-label={`delete comment: ${comment.content}`}
                className="modal-comment-delete-btn"
                variant="naked"
                icon="Trash"
                onClick={() => {
                  onCommentDelete({commentId: comment.id});
                }}
              />
              <pre
                className="modal-comment-bubble"
                dangerouslySetInnerHTML={{__html: formatComment(comment.content)}}
              />
            </div>
          ))}
        </div>
        <div className="modal-comment-thread-time-label comm" aria-hidden>
          {getDisplayedTime(comments.displayTime)}
        </div>
      </div>
    );
  } else {
    return (
      <div
        className="modal-comment-thread-time-group-wrapper"
        role="group"
        aria-label={`Comments at ${comments.displayTime}`}
      >
        <div className="modal-comment-thread-time-group-columns">
          <div className="modal-comment-thread-time-group">
            {comments.comments.map((comment) => (
              <div className="modal-comment-row" key={comment.id}>
                <pre
                  className="modal-comment-bubble"
                  dangerouslySetInnerHTML={{__html: formatComment(comment.content)}}
                />
              </div>
            ))}
          </div>
        </div>
        <div className="modal-comment-thread-time-label" aria-hidden>
          {getDisplayedTime(comments.displayTime)}
        </div>
      </div>
    );
  }
};

export interface CommentThreadProps {
  comments: CommentsByDate[];
  currentUser: IdentifiedUser;
  onCommentDelete: (comment: CommentType) => Promise<string | void>;
}

export const CommentThread = (props: CommentThreadProps) => {
  const getIsCurrentUser = (user: ProfileEntity | undefined): boolean => {
    return !!(user && props.currentUser && props.currentUser.personId === user._id);
  };

  return (
    <>
      {props.comments.map((commentsByDate) => (
        <CommentDateGroup
          key={commentsByDate.uniqueDateString}
          date={commentsByDate.displayedDateString}
        >
          {commentsByDate.comments.map((commentsByAuthor, i) => (
            <CommentAuthorGroup
              key={i}
              author={commentsByAuthor.author}
              isCurrentUser={getIsCurrentUser(commentsByAuthor.author)}
            >
              {commentsByAuthor.comments.map((commentsByTime, i) => (
                <CommentTimeGroup
                  comments={commentsByTime}
                  author={commentsByAuthor.author}
                  isCurrentUser={getIsCurrentUser(commentsByAuthor.author)}
                  key={i}
                  onCommentDelete={props.onCommentDelete}
                />
              ))}
            </CommentAuthorGroup>
          ))}
        </CommentDateGroup>
      ))}
    </>
  );
};

interface IProps extends TextareaProps {
  onChange: React.ChangeEventHandler<HTMLTextAreaElement>;
  value: string;
  label: string;
}

export const ElasticTextarea: React.FunctionComponent<IProps> = (props: IProps) => {
  const {onChange, value, label, ...rest} = props;

  return (
    <div className="comment-elastic-textarea-wrapper">
      <Textarea
        {...rest}
        value={value}
        onChange={onChange}
        aria-label={label}
        placeholder={label}
        labelIsHidden
        resize="none"
      />
      <pre aria-hidden="true">
        <span>{value}</span>
        <br />
      </pre>
    </div>
  );
};
