/* eslint-disable @typescript-eslint/no-type-alias */
import classnames from 'classnames';
import React, {useEffect, useRef} from 'react';

import {
  useOptionsMenu,
  usePoptip,
  Button,
  ButtonProps,
  Flyout,
  OptionsMenu,
  IconProps,
  Icon,
  Poptip,
} from '@uc/cx.react';
import {Alignment as FlyoutAlignment} from '@uc/cx.react/dist/src/Flyout/Flyout';

import {Alignment as PoptipAlignment} from '@src/types';

import styles from './Dropdown.module.pcss';

type Key = string | number;

const noop = () => {
  /* noop */
};

export type DropdownItem = {
  id: Key;
  label: string;
  icon?: IconProps['type'];
  header?: boolean;
  disabled?: boolean;
  selected?: boolean;
  ['data-testid']?: string;
  ['aria-label']?: string;
  poptipParams?: {message: string; alignment?: PoptipAlignment; style: React.CSSProperties};
};

function DropdownItemComponent({
  item,
  menuItemProps,
}: {
  item: DropdownItem;
  menuItemProps: unknown;
}) {
  const {poptipProps, getPoptipAnchorProps} = usePoptip();

  if (item.header) {
    return (
      <OptionsMenu.SectionHeader
        key={item.id}
        aria-label={item['aria-label']}
        {...getPoptipAnchorProps('Pop tip', 'rightBottom')}
      >
        {item.label}
        <Poptip {...poptipProps} />
      </OptionsMenu.SectionHeader>
    );
  }

  const ItemIcon = item.icon ? Icon[item.icon] : null;

  return item.disabled && item.poptipParams ? (
    <span {...getPoptipAnchorProps(item.poptipParams.message, item.poptipParams.alignment)}>
      <OptionsMenu.Item
        key={item.id}
        data-testid={item['data-testid']}
        style={{display: 'flex'}}
        {...menuItemProps}
      >
        {ItemIcon && <ItemIcon className="cx-optionsMenu-accessory" />}
        {item.label}
      </OptionsMenu.Item>
      <Poptip {...poptipProps} style={item.poptipParams.style} />
    </span>
  ) : (
    <OptionsMenu.Item
      key={item.id}
      data-testid={item['data-testid']}
      style={{display: 'flex', width: 'max-content'}}
      {...menuItemProps}
    >
      {ItemIcon && <ItemIcon className="cx-optionsMenu-accessory" />}
      {item.label}
    </OptionsMenu.Item>
  );
}

export function Dropdown({
  label,
  iconRight,
  iconLeft,
  items,
  className,
  variant,
  size = 'md',
  alignment,
  onItemSelect = noop,
  closeOnSelect = true,
  labelIsHidden = false,
  ...rest
}: {
  labelIsHidden?: boolean;
  iconRight?: IconProps['type'];
  iconLeft?: IconProps['type'];
  className?: string;
  label: string;
  variant?: ButtonProps['variant'];
  items: DropdownItem[];
  size?: ButtonProps['size'];
  closeOnSelect?: boolean;
  onItemSelect?: (id: Key, selected?: Set<Key>) => void;
  ['data-testid']?: string;
  alignment: FlyoutAlignment;
  disabled?: boolean;
}) {
  const {anchorProps, flyoutProps, menuProps, getItemProps} = useOptionsMenu({
    items,
    closeOnSelect,
    onItemSelect,
    controlled: true,
  });
  const IconRight = iconRight ? Icon[iconRight] : null;
  const IconLeft = iconLeft ? Icon[iconLeft] : null;
  const menuRef = useRef<HTMLUListElement>(null);

  useEffect(() => {
    const {isOpen, anchor} = flyoutProps;

    if (!isOpen && anchor) {
      (anchor as React.MutableRefObject<HTMLDivElement>).current.blur();
    }
  }, [flyoutProps]);

  return (
    <>
      <Button
        variant={variant}
        size={size}
        className={className}
        {...anchorProps}
        {...{['aria-label']: labelIsHidden ? label : undefined}}
        {...rest}
      >
        {IconLeft && (
          <IconLeft
            aria-hidden="true"
            className={classnames(
              styles.btnIcon,
              (iconRight || !labelIsHidden) && label && styles.marginRight
            )}
          />
        )}
        {labelIsHidden ? null : label}
        {IconRight && (
          <IconRight
            aria-hidden="true"
            className={classnames(styles.btnIcon, !labelIsHidden && label && styles.marginLeft)}
          />
        )}
      </Button>
      <Flyout className={styles.flyout} {...flyoutProps} alignment={alignment} zIndex={9999}>
        <OptionsMenu {...menuProps} aria-label={label} ref={menuRef}>
          {items.map((item, i) => (
            <DropdownItemComponent key={i} item={item} menuItemProps={getItemProps(item)} />
          ))}
        </OptionsMenu>
      </Flyout>
    </>
  );
}
