import {Recommendation} from '@src/components/LikelyToSell/RecommendationCard';
import {ContactAddress, Owner, ProcessedListing, ResourceType, TaskV2, Contact} from '@src/types';

interface PersonName {
  firstName?: string;
  lastName?: string;
}

function findOwnerInTransactionInfo(listing: ProcessedListing): PersonName | undefined {
  const owner = listing.publicInfo?.transactionInfo?.buyers?.length
    ? listing.publicInfo.transactionInfo.buyers[0]
    : undefined;

  return (
    owner && {
      firstName: (owner.name?.split(',')[1] || '').trim(),
      lastName: (owner.name?.split(',')[0] || '').trim(),
    }
  );
}

function findOwnerInTransactionDetails(listing: ProcessedListing): PersonName | undefined {
  const owner = listing.publicInfo?.transactionDetails?.buyers?.length
    ? listing.publicInfo.transactionDetails.buyers[0]
    : undefined;

  return (
    owner && {
      firstName: (owner.name?.split(',')[1] || '').trim(),
      lastName: (owner.name?.split(',')[0] || '').trim(),
    }
  );
}

function findOwnerInSummary(listing: ProcessedListing): PersonName | undefined {
  const listingSummary = listing?.detailedInfo?.listingDetails?.find(
    (details) => details.name?.toLowerCase() === 'summary'
  );
  const ownerSubcategory = listingSummary?.subCategories?.find(
    (category) => category.name?.toLowerCase() === 'owner'
  );
  const owner = ownerSubcategory?.fields?.find(
    (field) => field.key?.toLowerCase() === 'owner name'
  );

  return owner && {firstName: owner.values?.[0], lastName: ''};
}

function findOwnerInAssessor(listing: ProcessedListing): PersonName | undefined {
  const owner = listing?.detailedInfo?.assessorDetails?.ownerInfo?.owners?.find(
    (o: Owner) => (o.title || '').toLowerCase() === 'primary owner'
  );

  return owner && {firstName: owner.firstName, lastName: owner.lastName};
}

function findHomeOwnerInMetadata(recommendation: Recommendation): Contact {
  return recommendation.metaData?.ltsNetworkMetadata?.homeowners?.length
    ? recommendation.metaData.ltsNetworkMetadata.homeowners[0]
    : recommendation.metaData?.ltsNoConnectionMetadata?.homeowners?.length
    ? recommendation.metaData.ltsNoConnectionMetadata.homeowners[0]
    : {};
}

export function getHomeowner(recommendation: Recommendation): Contact {
  const {listing} = recommendation;

  if (!listing) {
    return {};
  }

  const contact: Contact = findHomeOwnerInMetadata(recommendation);
  const {firstName, lastName} =
    findOwnerInAssessor(listing) ||
    findOwnerInSummary(listing) ||
    findOwnerInTransactionDetails(listing) ||
    findOwnerInTransactionInfo(listing) ||
    {};

  contact.firstName = firstName ? convertToTitleCase(firstName) : '';
  contact.lastName = lastName ? convertToTitleCase(lastName) : '';
  contact.addresses = [listingToContactAddress(listing)];

  return contact;
}

export function listingToContactAddress(listing: ProcessedListing): ContactAddress {
  const {
    city = '',
    zipCode = '',
    state = '',
    street = '',
    streetNumber = '',
    streetType = '',
    preDirection = '',
    postDirection = '',
    unitNumber = '',
    unitType = '',
  } = listing?.location || {};
  const preDir = preDirection ? `${preDirection} ` : '';
  const postDir = postDirection ? ` ${postDirection}` : '';
  const streetAddress =
    street && streetNumber && streetType
      ? `${preDir}${streetNumber} ${street} ${streetType}${postDir}`
      : '';
  const canRenderFullAddress = streetAddress && city && state && zipCode;
  const street2 = `${unitType} ${unitNumber}`.trim();
  const fullAddress = canRenderFullAddress
    ? `${streetAddress}, ${street2 ? `${street2}, ` : ''}${city}, ${state} ${zipCode}`
    : '';

  return {
    full_address: fullAddress,
    zip: zipCode,
    city,
    state,
    street1: streetAddress,
    street2,
  };
}

export const extractDataFromTasks = (tasks: TaskV2[] | undefined): Recommendation[] => {
  const recommendations: Recommendation[] = [];

  if (!tasks) {
    return recommendations;
  }

  tasks.forEach((task: TaskV2) => {
    const recommendation: Recommendation = {taskId: task.id};

    recommendation.description = task.title;
    recommendation.createdAt = task.metadata?.createdAt;

    task.primaryResources?.forEach((resource) => {
      if (resource.resourceType === ResourceType.LISTING && resource.resourceId) {
        recommendation.listingIdSHA = resource.resourceId;
      }
    });

    task.attachments?.forEach((attachment) => {
      if (attachment.resourceType === ResourceType.LIKELIHOOD_TO_SELL && attachment.resourceId) {
        recommendation.score = attachment.resourceId;
      }
    });

    recommendations.push(recommendation);
  });

  return recommendations;
};

export const mergeIsAdded = (
  recommendations: Recommendation[],
  listingIdSHAs: string[] | undefined
): Recommendation[] => {
  if (!listingIdSHAs) {
    return recommendations;
  }

  recommendations.forEach((recommendation) => {
    if (recommendation.listingIdSHA && listingIdSHAs.includes(recommendation.listingIdSHA)) {
      recommendation.isAdded = true;
    }
  });

  return recommendations;
};

export const convertToTitleCase = (input: string) => {
  return input
    ? input
        .trim()
        .toLowerCase()
        .split(' ')
        .map((word) => word[0].toUpperCase() + word.slice(1, word.length))
        .join(' ')
    : '';
};
