import React, {Fragment} from 'react';

import {SUB_SPACE} from '@src/constants';

import {CollectionMenu, CollectionMenuData} from './sections/CollectionMenu';
import {SavedBuildingMenu} from './sections/SavedBuildingMenu';
import {SavedSearchMenu, SavedSearchMenuData} from './sections/SavedSearchMenu';
import './sideMenu.pcss';

export const ConsumerMenu = ({
  subSpaces = [SUB_SPACE.SAVED_SEARCH, SUB_SPACE.COLLECTION, SUB_SPACE.SAVED_BUILDING],
  collectionMenuData,
  savedSearchMenuData,
}: {
  subSpaces?: SUB_SPACE[];
  collectionMenuData?: CollectionMenuData;
  savedSearchMenuData?: SavedSearchMenuData;
}) => {
  return (
    <Fragment>
      {subSpaces.map((subSpace) => {
        switch (subSpace) {
          case SUB_SPACE.COLLECTION:
            return <CollectionMenu key={subSpace} data={collectionMenuData} />;
          case SUB_SPACE.SAVED_BUILDING:
            return <SavedBuildingMenu key={subSpace} />;
          case SUB_SPACE.SAVED_SEARCH:
            return <SavedSearchMenu key={subSpace} data={savedSearchMenuData} />;
          default:
            return null;
        }
      })}
    </Fragment>
  );
};
