import classnames from 'classnames';
import React, {useRef} from 'react';

import {Loading} from '@src/components/Loading';
import {useActivityFeed} from '@src/hooks/useActivityFeed';
import {useAddToTour} from '@src/hooks/useAddToTour';
import {useScrollToTop} from '@src/hooks/useScrollToTop';

import styles from './ContentGrid.module.pcss';

export const ContentGrid = ({
  tabs,
  filters,
  actionBar,
  children,
  rightBar,
  infoBox,
  shouldShowSidePanel,
  isLoading,
}: {
  tabs: React.ReactNode;
  children: React.ReactNode;
  rightBar?: React.ReactNode;
  actionBar: React.ReactNode;
  filters?: React.ReactNode;
  infoBox?: React.ReactNode;
  shouldShowSidePanel: boolean;
  isLoading?: boolean;
}) => {
  if (isLoading) {
    filters = null;
    actionBar = null;
    rightBar = null;
    infoBox = null;
    shouldShowSidePanel = false;
  }

  const ref = useRef() as React.MutableRefObject<HTMLDivElement>;
  const sidePanelRef = useRef(null) as {current: HTMLDivElement | null};

  useScrollToTop({target: ref.current});
  useAddToTour(sidePanelRef.current);
  useActivityFeed(sidePanelRef.current);

  const className = classnames([
    styles.wrapper,
    rightBar && styles.withRightBar,
    filters && styles.withFilters,
    infoBox && styles.withInfoBox,
  ]);
  const contentClassName = classnames([styles.content, isLoading && styles.transparent]);
  const sidePanelClassName = classnames(
    styles.sidePanelWrapper,
    shouldShowSidePanel && styles.sidePanelOpen
  );

  return (
    <div className={className}>
      <div className={styles.tabs}>{tabs}</div>
      {infoBox && <div className={styles.infoBox}>{infoBox}</div>}
      {filters && <div className={styles.filters}>{filters}</div>}
      <div className={styles.actionBar}>{actionBar}</div>
      {isLoading && (
        <div className={styles.content}>
          <div className={styles.loadingWrap}>
            <Loading />
          </div>
        </div>
      )}
      <div ref={ref} className={contentClassName}>
        {children}
      </div>
      {rightBar && <div className={styles.rightBar}>{rightBar}</div>}
      <div
        // eslint-disable-next-line @typescript-eslint/no-extra-parens
        ref={(dom) => (sidePanelRef.current = dom)}
        className={sidePanelClassName}
      />
    </div>
  );
};
