import {isNotInterestedIconWebAbTestEnabled} from '@src/utils/optyUtils';

enum NotInterestedIcons {
  Prohibited = 'Prohibited',
  ThumbsDownFilled = 'ThumbsDownFilled',
}

export const getNotInterestedIcon = () => {
  const enabled = isNotInterestedIconWebAbTestEnabled();

  if (enabled) {
    return NotInterestedIcons.ThumbsDownFilled;
  }

  return NotInterestedIcons.Prohibited;
};
