import React from 'react';

import {useCurrentUrlData} from '@src/hooks/url';
import {useSelectedListings} from '@src/store';

import {ActionBarProps} from '../types';
import {Favorites} from './Favorites/Favorites';
import {Matches} from './Matches/Matches';
import {NotInterested} from './NotInterested/NotInterested';
import styles from './OptionsGroupConsumer.module.pcss';

export const OptionsGroupConsumer = (props: ActionBarProps) => {
  const {selectedListings} = useSelectedListings();
  const urlData = useCurrentUrlData();
  const {collectionId} = urlData;
  const listingIds = selectedListings
    .filter((listing): listing is {listingId: string} => Boolean(listing.listingId))
    .map((listing) => listing.listingId);

  if (selectedListings.length === 0) {
    return null;
  }

  return (
    <div className={styles.wrapper}>
      {(() => {
        switch (props.mode) {
          case 'MATCHES':
            return <Matches collectionId={collectionId} listingIds={listingIds} />;
          case 'FAVORITES':
            return <Favorites collectionId={collectionId} listingIds={listingIds} />;
          case 'NOT_INTERESTED':
            return <NotInterested collectionId={collectionId} listingIds={listingIds} />;
          default:
            return null;
        }
      })()}
    </div>
  );
};
