import React, {Fragment} from 'react';

import {Button, Flyout, OptionsMenu, useFlyoutOnClick} from '@uc/cx.react';

interface FlyoutMenuProps {
  onViewFullListingDetails: () => void;
}

export const FlyoutMenu = (props: FlyoutMenuProps) => {
  const {getFlyoutAnchorProps, flyoutProps} = useFlyoutOnClick();

  return (
    <div className="header-menuWrapper">
      <Button
        variant="naked--lowEmph"
        aria-label="More Options"
        data-tn="header-button-menuToggle"
        data-testid="menuToggleBtn"
        {...getFlyoutAnchorProps('bottomRight')}
      >
        <svg className="cx-btn-icon">
          <use xlinkHref="#cx-icon-verticalEllipsis_16x16" />
        </svg>
      </Button>
      <Flyout {...flyoutProps}>
        <OptionsMenu size="lg">
          {/* Share button to be built in a later ticket: https://compass-tech.atlassian.net/browse/CDF-185
          <OptionsMenu.Item indented>Share</OptionsMenu.Item>
          */}
          <OptionsMenu.Item indented onClick={props.onViewFullListingDetails}>
            Open listing in new tab
          </OptionsMenu.Item>
          {/* single child to OptionsMenu will cause type error, put extra empty child to avoid it */}
          <Fragment />
        </OptionsMenu>
      </Flyout>
    </div>
  );
};
