import React from 'react';

import {usePoptipOnHover, Poptip, PoptipProps} from '@uc/cx.react';

import {CollectionListing} from '@src/types';

import './RequestTourTip.module.pcss';

interface Props {
  collectionListing: CollectionListing;
  alignment?: PoptipProps['alignment'];
  tipYPosition?: number;
  children: JSX.Element;
}

export function RequestTourTip(props: Props): JSX.Element {
  const {collectionListing, alignment, tipYPosition = 0, children} = props;
  const canRequestTour = Boolean(collectionListing?.canRequestTour);
  const {poptipProps, getPoptipAnchorProps} = usePoptipOnHover();
  const requestTourProps: PoptipProps = {
    alignment: alignment ?? 'top',
    children: 'This listing is not available for showings',
  };

  return (
    <>
      <div
        {...getPoptipAnchorProps(requestTourProps.children, requestTourProps.alignment)}
        data-tn="requestTourTip-anchor"
        data-testid="requestTourTipAnchor"
      >
        {children}
      </div>
      {!canRequestTour && (
        <Poptip
          {...poptipProps}
          style={{transform: `translateY(${tipYPosition}px)`}}
          className="requestTourTip"
          data-tn="requestTourTip-poptip"
          data-testid="requestTourPoptip"
        />
      )}
    </>
  );
}
