import {useCallback} from 'react';
import {useQueryClient} from 'react-query';

import {API_KEY} from '@src/constants/apiKey';

export const useRefreshGetCollectionQueryCache = () => {
  const queryClient = useQueryClient();

  return useCallback(async () => {
    queryClient.invalidateQueries(API_KEY.GET_COLLECTION_LISTINGS_PAGINATED);
    queryClient.invalidateQueries(API_KEY.GET_COLLECTION);
    queryClient.invalidateQueries(API_KEY.GET_COLLECTION_USERS);
    await queryClient.refetchQueries();
  }, [queryClient]);
};
