import {History} from 'history';

import {CollectionListing} from '@src/types';

import {
  Action,
  SET_VIEW,
  SET_IS_AGENT,
  SET_SOURCE,
  TOGGLE_SELECTED,
  CLEAR_SELECTED,
  RESET_STATE,
  SET_PAGE_SIZE,
  SELECT_LISTINGS,
  SET_TOAST,
  SET_CURRENT_USER,
  SET_APP_REFERRER,
  SET_CURRENT_LOCATION,
  SET_SHOW_MAP,
  SET_SIDEPANEL,
  SET_COLUMNS,
  SET_HOVERED_LISTING,
  SET_DELIVERABLE_ID,
  SET_SHOW_LTS_BANNER,
  SET_SHOW_FILTER_DROPDOWN,
  SET_REFERRING_LISTING_PATH,
  CLEAR_REFERRING_LISTING_PATH_AND_DELIVERABLE_ID,
  SET_NAV_LISTINGIDS,
  SET_NAV_TAB_COUNTS,
  SET_REMINDEE,
  SET_SHOWING_TIME_INFO,
  SET_TRASH_LISTINGS_MODAL_TRIGGER,
  SET_IS_TRASH_LISTINGS_IN_PROGRESS,
  SET_LISTING_COUNT,
} from './actions';
import {DEFAULT_TOAST} from './constants';
import {calculateCurrentNav, loadState} from './lib';
import {RootState, LocationState} from './types';

export function createRootReducer({history}: {history: History<LocationState>}) {
  return function root(state: RootState = loadState({history}), action: Action): RootState {
    switch (action.type) {
      case RESET_STATE: {
        return loadState({history});
      }

      case SET_SHOW_FILTER_DROPDOWN: {
        if (state.currentNav) {
          return {...state, showFilterDropdown: action.payload};
        }

        return state;
      }

      case SET_DELIVERABLE_ID: {
        return {
          ...state,
          deliverable_id: action.id,
        };
      }

      case SET_REFERRING_LISTING_PATH: {
        return {
          ...state,
          referringListingPath: action.payload,
        };
      }

      case CLEAR_REFERRING_LISTING_PATH_AND_DELIVERABLE_ID: {
        return {
          ...state,
          deliverable_id: undefined,
          referringListingPath: undefined,
        };
      }

      case SET_SHOW_MAP: {
        if (state.currentNav) {
          return {...state, showMap: action.payload};
        }

        return state;
      }

      case SET_CURRENT_LOCATION: {
        const next: RootState = {
          ...state,
          currentLocation: action.location,
          currentNav: calculateCurrentNav(state, action.location),
        };

        // If collectionId or collectionMode changes, then set state.showMap, state.showFilterDropdown and state.showSidePanel to false
        if (
          state.currentNav?.collectionId !== next.currentNav?.collectionId ||
          state.currentNav?.collectionMode !== next.currentNav?.collectionMode
        ) {
          next.showMap = false;
          next.sidePanel = undefined;
          next.showFilterDropdown = false;
        }

        if (
          next.currentNav &&
          action.action === 'PUSH' &&
          !state.currentNav?.modalId &&
          !next.currentNav.modalId
        ) {
          next.scrollId++;
        }

        return next;
      }

      case SET_VIEW: {
        return {...state, currentView: action.payload};
      }

      case CLEAR_SELECTED: {
        return {
          ...state,
          selectedListings: {},
        };
      }

      case SELECT_LISTINGS: {
        const trueById = action.payload.reduce((prev, listing) => {
          const {listingId} = listing;

          if (typeof listingId === 'string') {
            prev[listingId] = listing;
          }

          return prev;
        }, {} as Record<string, CollectionListing>);

        return {...state, selectedListings: {...state.selectedListings, ...trueById}};
      }

      case TOGGLE_SELECTED: {
        const {listingId} = action.payload;

        if (typeof listingId !== 'string') {
          return state;
        }

        const nextState = {...state.selectedListings};

        if (state.selectedListings[listingId]) {
          delete nextState[listingId];
        } else {
          nextState[listingId] = action.payload;
        }

        return {
          ...state,
          selectedListings: {
            ...nextState,
          },
        };
      }

      case SET_IS_AGENT: {
        const {currentUser} = state;

        return {...state, currentUser: {...currentUser, isAgent: action.payload}};
      }

      case SET_SOURCE: {
        return {...state, source: action.payload};
      }

      case SET_PAGE_SIZE: {
        const {collectionId, pageSize} = action.payload;

        return {
          ...state,
          pageSizes: {
            ...state.pageSizes,
            [collectionId]: pageSize,
          },
        };
      }

      case SET_TOAST: {
        return {
          ...state,
          toast: {...DEFAULT_TOAST, ...action.payload},
        };
      }

      case SET_CURRENT_USER: {
        return {
          ...state,
          currentUser: action.payload,
        };
      }

      case SET_APP_REFERRER: {
        const referrer = action.payload;

        return {
          ...state,
          referrer: referrer,
        };
      }

      case SET_SIDEPANEL: {
        if (state.currentNav) {
          return {...state, sidePanel: action.payload};
        }

        return state;
      }

      case SET_COLUMNS: {
        const collectionMode = state.currentNav?.collectionMode;

        if (collectionMode === undefined) {
          return state;
        }

        return {
          ...state,
          columnConfig: {
            ...state.columnConfig,
            [collectionMode]: action.payload,
          },
        };
      }

      case SET_HOVERED_LISTING: {
        return {
          ...state,
          hoveredListing: action.payload,
        };
      }

      case SET_SHOW_LTS_BANNER: {
        if (state.showLTSBanner) {
          return {...state, showLTSBanner: action.payload};
        }

        return state;
      }

      case SET_NAV_LISTINGIDS: {
        return {
          ...state,
          navListingIds: action.payload,
        };
      }

      case SET_NAV_TAB_COUNTS: {
        return {
          ...state,
          navTabCounts: action.payload,
        };
      }

      case SET_SHOWING_TIME_INFO: {
        return {
          ...state,
          showingTimeInfo: action.payload,
        };
      }

      case SET_REMINDEE: {
        return {
          ...state,
          remindee: action.payload,
        };
      }

      case SET_TRASH_LISTINGS_MODAL_TRIGGER: {
        return {
          ...state,
          trashListingsModalTrigger: action.payload,
        };
      }

      case SET_IS_TRASH_LISTINGS_IN_PROGRESS: {
        return {
          ...state,
          isTrashListingInProgress: action.payload,
        };
      }

      case SET_LISTING_COUNT: {
        return {
          ...state,
          listingCount: action.payload,
        };
      }

      default:
        return state;
    }
  };
}
