import {useCallback} from 'react';

import {getShowMap, useSelector, useDispatch, setShowMap} from '@src/store';

export const useShowMap = (): [boolean, (show: boolean) => void] => {
  const dispatch = useDispatch();
  const set = useCallback(
    (show: boolean) => {
      dispatch(setShowMap(show));
    },
    [dispatch]
  );

  return [Boolean(useSelector(getShowMap)), set];
};
