import {
  collectionListingFlowAction,
  CollectionListingFlowActionProps,
} from '@uc/analytics-definitions';

import {CollectionMode} from '@src/constants';

export enum PRODUCT_PATH {
  LISTING_CARD = 'listing card',
  LISTING_PAGE = 'listing page',
  TABLE_ROW = 'table row',
  ACTION_BAR = 'action bar',
  LISTING_MODAL = 'listing modal',
}

export interface TrackCollectionListingFlowActionProps {
  actionName: string;
  listingFlowAction?: string;
  productPath: PRODUCT_PATH;
  collectionId: string;
  businessTrackerIds: string[];
  isAgent: boolean;
  collectionMode?: CollectionMode;
  ab_test?: boolean;
  ab_test_version?: string;
}

export const trackCollectionListingFlowAction = ({
  actionName,
  listingFlowAction,
  productPath,
  collectionId,
  businessTrackerIds,
  isAgent,
  collectionMode,
  ab_test,
  ab_test_version,
}: TrackCollectionListingFlowActionProps) => {
  const event: CollectionListingFlowActionProps = {
    product: 'collections',
    product_path: productPath,
    listing_flow_action: listingFlowAction,
    action_name: actionName,
    collection_id: collectionId,
    collection_tab: collectionMode,
    is_agent: isAgent,
    business_tracker_ids: businessTrackerIds,
    ab_test,
    ab_test_version,
  };

  collectionListingFlowAction(event);

  return event;
};
