import React, {useCallback, useMemo, useEffect, useState} from 'react';

import {Radio, Modal, useModal, Checkbox, Button} from '@uc/cx.react';

import {ColumnDefinition} from '@src/types';

import {testSelectors, customizeColumnsModalTestSelectors} from './testSelectors';

import './ColumnsSelector.pcss';

export enum ColumnsType {
  DEFAULT = 'DEFAULT',
  CUSTOM = 'CUSTOM',
}

export interface SelectorColumn {
  id: string;
  exportDefinition?: ColumnDefinition;
}

export interface ColumnsSelectorProps {
  columnsType: ColumnsType;
  defaultColumnIds: Set<string>;
  allColumns: SelectorColumn[];
  onChange: (type: ColumnsType, columns: ColumnDefinition[]) => void;
}

const COLUMN_OPTIONS = [
  {
    value: ColumnsType.DEFAULT,
    label: 'Default columns',
  },
  {
    value: ColumnsType.CUSTOM,
    label: 'Custom columns',
  },
];

function filterColumns(allColumns: SelectorColumn[], columnIds: Set<string>) {
  return allColumns
    .filter((column) => columnIds.has(column.id))
    .map((column) => column.exportDefinition)
    .filter(
      (exportDefinition): exportDefinition is ColumnDefinition => exportDefinition !== undefined
    );
}

export const ColumnsSelector = ({
  columnsType = ColumnsType.DEFAULT,
  defaultColumnIds,
  allColumns,
  onChange = () => {
    /* istanbul ignore next */ return undefined;
  },
}: ColumnsSelectorProps) => {
  const {modalProps, modalHeaderProps, openModal, closeModal} = useModal();
  const [selectedColumnId, setSelectedColumnId] = useState<Set<string>>(defaultColumnIds);
  const allExportableColumns = useMemo(
    () => allColumns.filter((column) => column.exportDefinition !== undefined),
    [allColumns]
  );
  const defaultColumns = useMemo(
    () => filterColumns(allColumns, defaultColumnIds),
    [defaultColumnIds, allColumns]
  );
  const selectedColumns = useMemo(
    () => filterColumns(allColumns, selectedColumnId),
    [selectedColumnId, allColumns]
  );
  const onColumnsTypeChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;

      if (value === ColumnsType.DEFAULT) {
        onChange(ColumnsType.DEFAULT, defaultColumns);
      } else if (value === ColumnsType.CUSTOM) {
        onChange(ColumnsType.CUSTOM, selectedColumns);
      }
    },
    [onChange, defaultColumns, selectedColumns]
  );
  const onSetCustomColumnsClick = useCallback(() => {
    openModal();
    onChange(ColumnsType.CUSTOM, selectedColumns);
  }, [openModal, onChange, selectedColumns]);
  const handleColumnChange = (column: SelectorColumn) => {
    return () => {
      const next = new Set(selectedColumnId);

      if (next.has(column.id)) next.delete(column.id);
      else next.add(column.id);
      setSelectedColumnId(next);
    };
  };

  useEffect(() => {
    onChange(ColumnsType.DEFAULT, defaultColumns);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="googleSheetCmaModal-columnOptionsContainer">
      <div className="cx-formElement-label">
        <strong>Spreadsheet Columns</strong>
      </div>
      {COLUMN_OPTIONS.map((option) => (
        <div key={option.value} className="googleSheetCmaModal-radioEach">
          <Radio
            name="columnsType"
            checked={columnsType === option.value}
            value={option.value}
            onChange={onColumnsTypeChange}
            data-testid={testSelectors.selectedColumnType(option.value)}
          >
            {option.label}
            {option.value === ColumnsType.CUSTOM && (
              <button
                className="cx-nakedBtn googleSheetCmaModal-setColumnsBtn"
                onClick={onSetCustomColumnsClick}
                data-testid={testSelectors.customColumnBtn}
              >
                Set custom columns
              </button>
            )}
          </Radio>
        </div>
      ))}
      <Modal
        {...modalProps}
        className="customize-table-columns-modal"
        data-testid={customizeColumnsModalTestSelectors.modal}
      >
        <Modal.Header {...modalHeaderProps} hideCloseButton>
          Customize Table Columns
        </Modal.Header>
        <Modal.Body>
          {allExportableColumns.map((col) => {
            const checked = selectedColumnId.has(col.id);

            return (
              <Checkbox
                key={col.id}
                checked={checked}
                onChange={handleColumnChange(col)}
                data-testid={testSelectors.columnCheckbox(col.id)}
              >
                {col.exportDefinition?.displayName || col.id}
              </Checkbox>
            );
          })}
        </Modal.Body>
        <Modal.Footer>
          <Button
            onClick={() => {
              closeModal();
              onChange(ColumnsType.CUSTOM, selectedColumns);
            }}
            data-testid={customizeColumnsModalTestSelectors.backBtn}
          >
            OK
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};
