import {BBox} from '@turf/helpers';
import {ContainerSize, getGeoBoundingBox, Position} from '@uc-maps/geospatial';

import {convertPythonQueryToThrift, SearchQuery} from '@uc/search-query';

import {
  CollectionListing,
  ListingType,
  Availability,
  ClusterFeature,
  ProcessedListing,
  ProcessedListingRelation,
  Location,
  MapSearchResultsType,
} from '@src/types';

export function getMapSearchQuery({
  searchResults,
  mapOptions,
  searchQuery,
}: {
  searchResults: MapSearchResultsType;
  mapOptions: {bbox: BBox; containerSize: ContainerSize};
  searchQuery?: SearchQuery;
}) {
  let queryParams: SearchQuery;

  // it will pass searchQuery when in Unshared tab
  // and use the query params as part of the mapSearchQuery
  if (searchQuery) {
    queryParams = searchQuery;
  } else {
    const allListingTypes = [
      ListingType.LISTING,
      ListingType.BUILDING,
      ListingType.SALES,
      ListingType.SHORT_TERM_RENTAL,
    ];
    // When in the tabs which are not Unshared tab,
    // it will not pass searchQuery and the searchResults will be processed listings
    const listingIdSHAs = (searchResults as ProcessedListing[])
      .filter((listing) => listing?.listingIdSHA)
      .map((listing) => listing.listingIdSHA);
    const query = {
      listing_id_shas: listingIdSHAs,
      listingTypes: allListingTypes,
      agent_search: true,
      availability: Availability.BOTH,
    };

    queryParams = convertPythonQueryToThrift(query);
  }

  const mapSearchQuery = {
    ...queryParams,
    rawQuery: '',
    debugLevel: 0,
    mapSearchProperties: {
      enableClusterSummary: true,
      mapContainerPixels: mapOptions.containerSize,
      boundingBox: getGeoBoundingBox(mapOptions.bbox),
    },
  };

  return mapSearchQuery;
}

/**
 * Transforms CollectionListings to ProcessedListingRelation
 *
 * The only difference between the types is that
 * ProcessedListingRelation has one extra level nested, used in map
 * to decide which cluster will be selected
 *
 * @param {CollectionListing[]} listings
 * @returns {ProcessedListingRelation[]}
 */
export const toProcessedListingRelation = (
  listings: CollectionListing[]
  // eslint-disable-next-line @typescript-eslint/no-extra-parens
): ProcessedListingRelation[] =>
  listings?.length ? listings.map((listing) => ({listing: listing.listingData})) : [];

export const getListingCorrespondingPosition = ({
  listingIdSHA,
  allClusters,
}: {
  listingIdSHA: string;
  allClusters: ClusterFeature[];
}): Position | undefined => {
  const targetClusterFeature = allClusters.find((clusterFeature) => {
    const {
      properties: {listings = []},
    } = clusterFeature;
    const isInCurrentCluster = listings.some(
      (item) => item?.listing?.listingIdSHA === listingIdSHA
    );

    return isInCurrentCluster;
  });

  return targetClusterFeature?.geometry.coordinates;
};

export const toCollectionListing = ({
  listingRelations,
  allClusters,
  addPosition = false,
}: {
  listingRelations: ProcessedListingRelation[];
  allClusters?: ClusterFeature[];
  addPosition?: boolean;
}): CollectionListing[] => {
  if (!listingRelations?.length) return [];

  if (addPosition && allClusters) {
    listingRelations.forEach((item) => {
      const position = getListingCorrespondingPosition({
        listingIdSHA: item?.listing?.listingIdSHA ?? '',
        allClusters,
      });

      if (position) {
        const location = item?.listing?.location as Location;

        location.longitude = position[0];
        location.latitude = position[1];
      }
    });
  }

  return listingRelations
    .filter((item) => item.listing)
    .map((item) => {
      return {listingData: item.listing, listingId: item.listing?.listingIdSHA};
    });
};
