import React, {useEffect, useRef} from 'react';

import {CollectionLoadError} from '@src/components/CollectionLoadError';
import {ConsumerNav} from '@src/components/ConsumerNav';
import {CollectionNotFound} from '@src/components/EmptyStates/CollectionNotFound';
import {Header} from '@src/components/Header';
import {EmptyCollectionsHeader} from '@src/components/Header/EmptyCollectionsHeader';
import {CollectionMode, COLLECTION_MODE} from '@src/constants';
import {Layout} from '@src/cx/Layout';
import {useUpdateCurrentUrl} from '@src/hooks/url';
import {useGetCollection} from '@src/hooks/useGetCollection';
import {useGetCollectionListingsSummary} from '@src/hooks/useGetCollectionListingsSummary';
import {useIsAgent} from '@src/store';
import {CollectionListingReviewStage} from '@src/types';
import {isClientHomeRootEnabled} from '@src/utils/optyUtils';

import {Content} from './Content';
import {EmptyContent} from './EmptyContent';

interface BoardLayoutProps {
  collectionId: string;
  mode: CollectionMode;
}

const useNav = () => {
  const isAgent = useIsAgent();

  return isAgent || isClientHomeRootEnabled() ? undefined : <ConsumerNav />;
};

export const EmptyBoardLayout = () => {
  const isAgent = useIsAgent();
  const Nav = useNav();
  const Header = isAgent ? undefined : <EmptyCollectionsHeader />;

  return (
    <Layout header={Header} nav={Nav}>
      {<EmptyContent />}
    </Layout>
  );
};

export const NotFoundBoardLayout = () => {
  const isAgent = useIsAgent();
  const Nav = useNav();
  const Header = isAgent ? undefined : <EmptyCollectionsHeader />;

  return (
    <Layout header={Header} nav={Nav}>
      <CollectionNotFound />
    </Layout>
  );
};

export const BoardLayout = ({mode, collectionId}: BoardLayoutProps) => {
  const isAgent = useIsAgent();
  const Nav = useNav();
  const {data, error} = useGetCollection({collectionId});
  const {summary = {}} = useGetCollectionListingsSummary({collectionId}).data || {};
  const matches = summary.numListingsPerReviewStage?.[CollectionListingReviewStage.REVIEW] || 0;
  const firstUpdate = useRef(true);
  const updateCurrentUrl = useUpdateCurrentUrl();
  const [hasLandOnUnshared, setHasLandOnUnshared] = React.useState(
    mode === COLLECTION_MODE.UNSHARED
  );
  const [hasLandOnMatches, setHasLandOnMatches] = React.useState(mode === COLLECTION_MODE.MATCHES);

  useEffect(() => {
    if (data?.collection?.isPublic) {
      if (isAgent) {
        window.location.href = `/workspace/#/public-collection/${collectionId}`;
        return;
      }

      if (data?.collection?.prettyUrl) {
        window.location.href = `/c/${data.collection.prettyUrl}`;
        return;
      }
    }
  }, [isAgent, collectionId, data?.collection?.isPublic, data?.collection?.prettyUrl]);

  useEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    } else {
      setHasLandOnUnshared(false);
      setHasLandOnMatches(false);
    }
  }, [collectionId]);

  useEffect(() => {
    if (hasLandOnUnshared || hasLandOnMatches) return;

    if (mode === COLLECTION_MODE.SHARED && data?.collection?.isCurate) {
      setHasLandOnUnshared(true);
      updateCurrentUrl({collectionMode: COLLECTION_MODE.UNSHARED});
    }

    if (mode === COLLECTION_MODE.FAVORITES && matches > 0) {
      setHasLandOnMatches(true);
      updateCurrentUrl({collectionMode: COLLECTION_MODE.MATCHES});
    }
  }, [mode, updateCurrentUrl, hasLandOnMatches, hasLandOnUnshared, matches, data]);

  if (error) {
    const errorElement = <CollectionLoadError error={error} />;

    if (isAgent) {
      return errorElement;
    } else {
      return <Layout nav={Nav}>{errorElement}</Layout>;
    }
  }

  if (!data) {
    return null;
  }

  if (data.collection?.isPublic) {
    return <span>Redirecting to Public Collections</span>;
  }

  const content = <Content collectionId={collectionId} mode={mode} />;

  return (
    <Layout header={<Header collectionId={collectionId} mode={mode} />} nav={Nav}>
      {content}
    </Layout>
  );
};
