import classnames from 'classnames';
import React from 'react';

import {Icon} from '@uc/cx.react';

import styles from './Overlay.module.pcss';

export interface OverlayProps {
  show: boolean;
  icon?: 'Star' | 'Prohibited';
  title?: string;
}

export const Overlay = ({
  overlayProps,
  isNewListingCard,
}: {
  overlayProps: OverlayProps;
  isNewListingCard: boolean;
}) => {
  const {show, icon, title} = overlayProps;
  const className = show
    ? classnames([
        styles['card-body-overlay'],
        styles.transitionOut,
        isNewListingCard
          ? styles['new-listing-card-overlay']
          : styles['legacy-listing-card-overlay'],
      ])
    : styles['card-body-overlay'];
  const InnerIcon = () => {
    if (icon === 'Star') {
      return (
        <Icon.Star data-testid="starIcon" className={classnames(['cx-btn-icon', styles.icon])} />
      );
    } else if (icon === 'Prohibited') {
      return (
        <Icon.Prohibited
          data-testid="prohibitedIcon"
          className={classnames(['cx-btn-icon', styles.icon])}
        />
      );
    } else {
      return null;
    }
  };

  return (
    <div className={className}>
      <InnerIcon />
      <h2 className={styles.title}>{title}</h2>
    </div>
  );
};
